goog.provide('H.map.provider.ObjectProvider');

goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.map.Object');
goog.require('H.map.provider.Invalidations');
goog.require('H.map.provider.Provider');



//################################################  CONSTRUCTOR  #######################################################
/**
 * An abstract class to manage and provide map objects (Marker, Polyline, Polygon)
 * @publish
 * @export
 * @abstract
 *
 * @extends {H.map.provider.Provider}
 *
 * @constructor
 * @param {H.map.provider.Provider.Options=} opt_options
 */
H.map.provider.ObjectProvider = function(opt_options) {
  'use strict';
  H.lang.checkIllegalConstructor(this, H.map.provider.ObjectProvider, false);

  // Call the super constructor:
  H.map.provider.ObjectProvider.superClass_.constructor.call(this, opt_options);

  /**
   * The invalidations of this provider, seperated by object types
   * @private
   * @type {Array.<H.map.provider.Invalidations>}
   */
  this.invalidations_ = (function() {
    var invalidations = [],
        key;
    for (key in H.map.Object.Type) {
      invalidations[H.map.Object.Type[key]] = new H.map.provider.Invalidations();
    }
    return invalidations;
  })();
};
H.lang.inherits(H.map.provider.ObjectProvider, H.map.provider.Provider);


//###############################################  INVALIDATION  #######################################################
/**
 * Returns the accumulate invalidations of this provider's objects that have occurred.
 * @publish
 * @export
 * @param {H.map.Object.Type=} opt_type
 *        The type of objects to consider for the invalidations. If undefined, all types are taken into account.
 * @return {H.map.provider.Invalidations}
 */
H.map.provider.ObjectProvider.prototype.getInvalidations = function(opt_type) {
  'use strict';
  return this.invalidations_[opt_type || H.map.Object.Type['ANY']];
};


/**
 * To signal to this provider that a map object has been changed.
 * The method updates the Invalidations of this provider and the given map object and triggers dispatchUpdate()
 * @publish
 *
 * @export
 * @param {!H.map.Object} mapObject The map object to be invalidated
 * @param {H.math.BitMask} changes The flags indicating the types of occurred changes
 */
H.map.provider.ObjectProvider.prototype.invalidateObject = function(mapObject, changes) {
  'use strict';
  var mark,
      invalidations;
  if (changes !== H.map.provider.Invalidations.Flag.NONE) {
    invalidations = this.invalidations_[H.map.Object.Type['ANY']];
    mark = invalidations.getMark() + 1;
    invalidations.update(mark, changes);
    invalidations = this.invalidations_[mapObject.type];
    invalidations.update(mark, changes);
    mapObject.getInvalidations().update(mark, changes);
    this.dispatchUpdate(mapObject);
  }
};


//#################################################  SPATIALS  #########################################################
/**
 * Checks whether this provider is currently providing spatial map objects.
 * A concrete implementation of ObjectProvider must override it if it currently provides Spatials.
 * @publish
 * @export
 *
 * @method
 * @return {boolean}
 */
H.map.provider.ObjectProvider.prototype.providesSpatials = H.util.returnFalse;


/**
 * Returns all polyline, polygon, circle and rect objects which intersect with the provided area.
 * @publish
 * @export
 * @method
 * @abstract
 *
 * @param {H.geo.Rect} geoRect A rectangular area in geo space to intersect with
 * @param {number} zoomLevel The zoom level for which the objects are requested
 * @param {boolean} visiblesOnly Indicates whether only invisible objects are to be considered
 * @param {boolean} cacheOnly Indicates whether only cached objects are to be considered
 * @return {Array.<H.map.Spatial>} a list of intersecting objects
 */
H.map.provider.ObjectProvider.prototype.requestSpatials = goog.abstractMethod;


/**
 * Returns the spatial objects which intersect the given tile
 * @publish
 * @method
 * @abstract
 * @export
 *
 * @param {H.map.provider.SpatialTile} tile The tile for which the objects are requested
 * @param {boolean} visiblesOnly Indicates whether only invisible objects are to be considered
 * @param {boolean} cacheOnly Indicates whether only cached objects are to be considered
 * @return {Array.<H.map.Spatial>} a list of intersecting objects
 */
H.map.provider.ObjectProvider.prototype.requestSpatialsByTile = goog.abstractMethod;


//#################################################  MARKERS  ##########################################################
/**
 * Checks whether this provider is currently providing Marker map objects.
 * A concrete implementation of ObjectProvider must override it if it currently provides Markers.
 * @publish
 * @export
 * @method
 *
 * @return {boolean}
 */
H.map.provider.ObjectProvider.prototype.providesMarkers = H.util.returnFalse;


/**
 * Returns all Marker map objects which intersect with the provided rectangular area.
 * @publish
 * @export
 * @abstract
 * @method
 *
 * @param {H.geo.Rect} geoRect A rectangular area in geo space to intersect with
 * @param {number} zoomLevel The zoom level for which the objects are requested
 * @param {boolean} visiblesOnly Indicates whether only invisible objects are to be considered
 * @param {boolean} cacheOnly Indicates whether only cached objects are to be considered
 * @return {Array.<H.map.Marker>} a list of intersecting objects
 */
H.map.provider.ObjectProvider.prototype.requestMarkers = goog.abstractMethod;


//###############################################  DOM-MARKERS  ########################################################
/**
 * Checks whether this provider is currently providing DomMarker map objects.
 * A concrete implementation of ObjectProvider must override it if it currently provides Markers.
 * @publish
 * @export
 * @method
 *
 * @return {boolean}
 */
H.map.provider.ObjectProvider.prototype.providesDomMarkers = H.util.returnFalse;


/**
 * Returns all DomMarker map objects which intersect with the provided rectangular area.
 * @publish
 * @export
 * @abstract
 * @method
 *
 * @param {H.geo.Rect} geoRect A rectangular area in geo space to intersect with
 * @param {number} zoomLevel The zoom level for which the objects are requested
 * @param {boolean} visiblesOnly Indicates whether only invisible objects are to be considered
 * @param {boolean} cacheOnly Indicates whether only cached objects are to be considered
 * @return {Array.<H.map.DomMarker>} a list of intersecting objects
 */
H.map.provider.ObjectProvider.prototype.requestDomMarkers = goog.abstractMethod;
