goog.provide('H.geo.Strip');

goog.require('H.geo.Point');
goog.require('H.geo.Rect');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.math');



/**
 * A strip is a flat list of latitude, longitude, altitude tuples in a fixed order.
 * @export
 * @publish
 * @constructor
 * @throws {H.lang.InvalidArgumentError} in case of invalid lat, lng, alt values
 * @param {Array.<number>=} opt_latLngAlts
 *        An optional array of latitude, longitude and altitude triples to initialize the strip with.
 * @param {H.geo.AltitudeContext=} opt_ctx
 *        An optional altitude context for all altitudes contained in this strip.
 */
// TODO: [JH] split the strip into 2 arrays: 1 lat,lng Float32 array and on sparse
// array for altitudes use altitude only when needed
// TODO: Additional argument "opt_skipValidation" to skip the validation
H.geo.Strip = function(opt_latLngAlts, opt_ctx) {
  'use strict';

  if (opt_latLngAlts) {
    H.geo.Strip.validateLatLngAlts_(opt_latLngAlts, H.geo.Strip, 0);
  }

  this.bounds_ = null;

  /**
   * The list of lat, lng, alt values which define the strip
   * @private
   * @type {Array.<number>}
   */
  this.latLngAlts_ = opt_latLngAlts || [];

  /**
   * The altitude context for all altitudes contained in this strip
   * @private
   * @type {H.geo.AltitudeContext|undefined}
   */
  this.ctx_ = opt_ctx;

  this.DBCs_ = this.countDBCs_(0, this.latLngAlts_.length);
};


/**
 * This method counts the amount of legs crossing the date border within
 * the boundaries specified.
 * @param {number} start the start index from which to check
 * @param {number} end the end index to which to check
 * @return {number} the number of legs that cross the date border
 * @private
 */
H.geo.Strip.prototype.countDBCs_ = function(start, end) {
  'use strict';
  var latLngAlts = this.latLngAlts_,
      i,
      count = 0;

  // clamp start and end values:
  start = H.math.max(start, 0);
  end = H.math.min(end, latLngAlts.length);

  // We start at start + 1 to point to the longitude slot.
  // We end one before the end index, equiv. (end - 3) + 1
  // because the last leg goes from current index to next index
  end -= 2;
  for (i = start + 1; i < end; ) {
    count += H.geo.Strip.isDBC(latLngAlts[i], latLngAlts[i += 3]);
  }
  return count;
};


/**
 * This method pushes a lat, lng, alt to the end of this strip.
 * @export
 * @publish
 * @throws {H.lang.InvalidArgumentError} in case of invalid lat, lng, alt values
 * @param {H.geo.Latitude} lat
 * @param {H.geo.Longitude} lng
 * @param {H.geo.Altitude} alt
 */
H.geo.Strip.prototype.pushLatLngAlt = function(lat, lng, alt) {
  'use strict';
  this.pushLatLngAlt_(lat, lng, alt, this.pushLatLngAlt);
};


/**
 * This method pushes a lat, lng, alt to the end of this strip.
 * See {@link H.geo.validateLat}, {@link H.geo.validateLng}, {@link H.geo.validateAlt}
 * @private
 * @throws {H.lang.InvalidArgumentError} in case of invalid lat, lng, alt values
 * @param {H.geo.Latitude} lat
 * @param {H.geo.Longitude} lng
 * @param {H.geo.Altitude} alt
 * @param {Function} caller The caller to use for InvalidArgumentError.
 */
H.geo.Strip.prototype.pushLatLngAlt_ = function(lat, lng, alt, caller) {
  'use strict';
  var len = this.latLngAlts_.length;
  lat = H.geo.validateLat(lat, caller);
  lng = H.geo.validateLng(lng, caller);
  this.latLngAlts_.push(
      H.geo.validateLat(lat, caller),
      H.geo.validateLng(lng, caller),
      alt === H.lang.UNDEF ? 0 : H.geo.validateAlt(alt, caller));

  // Do we had already coordinates before?
  if (len) {
    // Adjust the DBC count:
    this.DBCs_ += H.geo.Strip.isDBC(this.latLngAlts_[len - 2], lng);
  }

  // Make sure to invalidate the bounding box:
  this.bounds_ = null;
};


/**
 * This method splices the strip at the provided index, removing the specified
 * number of items at that index and inserting the lat, lng, alt array.
 * @export
 * @publish
 * @throws {H.lang.InvalidArgumentError} in case of invalid opt_latLngAlts argument
 * @param {number} index The index at which to splice
 * @param {number=} opt_nRemove The number of lat, lng, alt values to remove
 * @param {Array.<number>=} opt_latLngAlts The lat, lng, alt values to add
 * @return {Array.<number>} an array of removed elements
 */
H.geo.Strip.prototype.spliceLatLngAlts = function(index, opt_nRemove, opt_latLngAlts) {
  'use strict';
  if (opt_latLngAlts) {
    H.geo.Strip.validateLatLngAlts_(opt_latLngAlts, this.spliceLatLngAlts, 2);
  }

  var removed;

  //if we are removing something, we must count the number of DBCs that
  //are being removed
  if (opt_nRemove) {
    this.DBCs_ -= this.countDBCs_(index - 3, index + opt_nRemove + 3);
  }

  if (!opt_latLngAlts) {
    removed = this.latLngAlts_.splice(index, opt_nRemove);
  } else {
    // TODO This can cause a "Maximum call stack size exceeded" Error for large opt_latLngAlts arrays:
    removed = this.latLngAlts_.splice.apply(this.latLngAlts_, [index, opt_nRemove].concat(opt_latLngAlts));
  }

  //if we are adding something we must now count the number of DBCs added
  if (opt_latLngAlts) {
    this.DBCs_ += this.countDBCs_(index - 3, index + opt_latLngAlts.length + 3);
  }

  //if we have removed something and not added anything we need to check the
  //newly created leg around the removal area
  if (opt_nRemove && !opt_latLngAlts) {
    this.DBCs_ += this.countDBCs_(index - 3, index - 3);
  }

  //make sure to invalidate the bounding box
  this.bounds_ = null;

  return removed;
};


/**
 * This method inserts one set of lat, lng, alt values into the strip at the
 * specified index.
 * @param {number} index the index at which to add the element
 * @param {H.geo.Latitude} lat the latitude to insert
 * @param {H.geo.Longitude} lng the longitude to insert
 * @param {H.geo.Altitude} alt the altitude to insert
 * @export
 * @publish
 */
H.geo.Strip.prototype.insertLatLngAlt = function(index, lat, lng, alt) {
  'use strict';
  this.spliceLatLngAlts(index, 0, [lat, lng, alt]);
};


/**
 * This method removes one set of lat, lng, alt values from the strip at the
 * specified index.
 * @param {number} index
 * @export
 * @publish
 */
H.geo.Strip.prototype.removeLatLngAlt = function(index) {
  'use strict';
  this.spliceLatLngAlts(index, 3);
};


/**
 * This method pushes the lat, lng, alt values of a {@link H.geo.Point} to the end
 * of this strip.
 * @export
 * @publish
 * @throws {H.lang.InvalidArgumentError} in case of invalid geoPoint argument
 * @param {H.geo.IPoint} geoPoint
 */
H.geo.Strip.prototype.pushPoint = function(geoPoint) {
  'use strict';
  this.pushLatLngAlt_(geoPoint['lat'], geoPoint['lng'], geoPoint['alt'], this.pushPoint);
};


/**
 * This method inserts the lat, lng, alt values of a {@link H.geo.Point} into the list
 * at the specified index.
 * @param {number} pointIndex
 * @param {H.geo.IPoint} geoPoint
 * @export
 * @publish
 */
H.geo.Strip.prototype.insertPoint = function(pointIndex, geoPoint) {
  'use strict';
  this.insertLatLngAlt(pointIndex * 3, geoPoint['lat'], geoPoint['lng'], geoPoint['alt']);
};


/**
 * This method removes one set of lat, lng, alt values from this strip at the virtual point
 * index specified.
 * @param {number} pointIndex the virtual point index
 * @export
 * @publish
 */
H.geo.Strip.prototype.removePoint = function(pointIndex) {
  'use strict';
  this.removeLatLngAlt(pointIndex * 3);
};


/**
 * This method extracts a {@link H.geo.Point} from this strip at the virtual point
 * index. If the extracted point has an alt value, the strip's altitude context
 * will be supplied to the point.
 * @param {number} pointIndex the virtual point index in the strip
 * @param {H.geo.Point=} opt_out an optional point object to store the lat,
 * lng, alt values
 *
 * @return {H.geo.Point} returns either the 'opt_out' point object or a
 *  new point object.
 * @export
 * @publish
 */
H.geo.Strip.prototype.extractPoint = function(pointIndex, opt_out) {
  'use strict';
  var latLngAlts = this.latLngAlts_,
      latLngAltIndex = pointIndex * 3,
      lat = latLngAlts[latLngAltIndex],
      lng = latLngAlts[latLngAltIndex + 1],
      alt = latLngAlts[latLngAltIndex + 2];

  if (opt_out) {
    opt_out['lat'] = lat;
    opt_out['lng'] = lng;
    opt_out['alt'] = alt;
    if (alt !== undefined) {
      if (this.ctx_) {
        opt_out['ctx'] = this.ctx_;
      }
    }
  }
  return opt_out || new H.geo.Point(lat, lng, alt, H.lang.isDefAndNotNull(alt) ? this.ctx_ : undefined);
};


/**
 * This method is a utility method that iterates over the lat, lng, alt array
 * and calls the provided function for each 3 elements passing lat, lng and alt
 * and the virtual point index as arguments.
 * @param {function(H.geo.Latitude, H.geo.Longitude, H.geo.Altitude, number)} eachFn
 *  the function to be called for each 3 elements
 * @param {number=} opt_start an optional start index to iterate from
 * @param {number=} opt_end an optional end index to iterate to
 *
 * @example
 * var eachFn = function(lat, lng, alt, idx) {
 *   console.log("point", idx + ":", lat, lng, alt);
 * }
 * myStrip.eachPoint(eachFn); //iterates over each point and logs its latitude and longitude
 *
 * @export
 * @publish
 */
H.geo.Strip.prototype.eachLatLngAlt = function(eachFn, opt_start, opt_end) {
  'use strict';
  var latLngAlts = this.latLngAlts_,
      i = opt_start || 0,
      l = opt_end || latLngAlts.length;

  for (; i < l; i += 3) {
    eachFn(latLngAlts[i], latLngAlts[i + 1], latLngAlts[2], i / 3);
  }
};


/**
 * This method returns the number of times that legs in this strip cross the
 * date border.
 * @param {boolean=} opt_closed indicates whether the strip is closed (i.e. whether the strip's last and first
 *  coordinates form the closing leg of a polygon)
 * @return {number} the amount of times this strip crosses the date border.
 * @export
 * @publish
 */
H.geo.Strip.prototype.getDBCs = function(opt_closed) {
  'use strict';
  var dbcs = this.DBCs_,
      latLngAlts;
  if (opt_closed) {
    latLngAlts = this.latLngAlts_;
    dbcs += H.geo.Strip.isDBC(
        latLngAlts[latLngAlts.length - 2], // last longitutde
        latLngAlts[1] // first longitude
        );
  }

  return dbcs;
};


/**
 * This method return the number of points stored in this strip.
 * @return {number} the number of points in this strip
 * @export
 * @publish
 */
H.geo.Strip.prototype.getPointCount = function() {
  'use strict';
  return this.latLngAlts_.length / 3;
};


/**
 * This method returns the internal array keeping the lat, lng, alt values.
 * Modifying this array directly can destroy the integrity of this strip. Use it
 * only for read access.
 * @return {Array.<number>} returns the raw lat, lng, alt values of this strip
 * @export
 * @publish
 */
H.geo.Strip.prototype.getLatLngAltArray = function() {
  'use strict';
  return this.latLngAlts_;
};


/**
 * This method returns the bounding box of this strip.
 *
 * Note: The strip is treated as an open path. If the bounding rectangle for a
 * closed shape is required, the closing leg must be merged in an extra step.
 * @return {?H.geo.Rect} this strip's bounding rectangle
 * @export
 * @publish
 */
H.geo.Strip.prototype.getBounds = function() {
  'use strict';
  return this.bounds_ || (this.bounds_ = this.computeBounds_());
};


/**
 * This method returns a boolean value indicating whether this strip has a
 * valid computed bounding rectangle.
 *
 * @return {boolean} true if the strip has a properly computed bounding
 *  rectangle, false otherwise
 * @export
 */
H.geo.Strip.prototype.hasBounds = function() {
  'use strict';
  return this.bounds_ !== null;
};


/**
 * This method computes the bounding rectangle of this strip.
 * @return {?H.geo.Rect} the bounding rectangle of this strip
 * @private
 */
H.geo.Strip.prototype.computeBounds_ = function() {
  'use strict';
  var latLngAlts = this.latLngAlts_,
      l = latLngAlts.length,
      i = 3,
      lat1, lat2,
      lng1, lng2,
      latS, lngS,
      rect = null,
      MINLNG = H.geo.MIN_LONGITUDE,
      MAXLNG = H.geo.MAX_LONGITUDE,
      MAXLENGTH = H.geo.MAX_LONGITUDE_LENGTH,
      merge = H.geo.Rect.merge,
      abs = H.math.abs;

  if (l >= 3) {
    lat1 = lat2 = latLngAlts[0];
    lng1 = lng2 = latLngAlts[1];
    rect = new H.geo.Rect(lat1, lng1, lat2, lng2);
    for (; i < l; i += 3) {
      lat1 = latLngAlts[i - 3];
      lng1 = latLngAlts[i - 2];
      lat2 = latLngAlts[i];
      lng2 = latLngAlts[i + 1];

      if ((lng1 > lng2 && lng2 - lng1 > MAXLNG) ||
          (lng1 < lng2 && lng1 - lng2 < MINLNG) ||
          (lng2 < lng1 && abs(lng2 - lng1) < MAXLENGTH / 2)) {
        lngS = lng1;
        lng1 = lng2;
        lng2 = lngS;
      }
      if (lat1 < lat2) {
        latS = lat1;
        lat1 = lat2;
        lat2 = latS;
      }
      merge(
          rect.getTop(), rect.getLeft(), rect.getBottom(), rect.getRight(),
          lat1, lng1, lat2, lng2,
          rect);
    }
  }
  return rect;
};


/**
 * This method checks whether the given array has has a valid length (integral multiple of 3) and all lat/lng/alt
 * entries have valid values. The values of the array are modified if necessary.
 * See {@link H.geo.validateLat}, {@link H.geo.validateLng}, {@link H.geo.validateAlt}
 * @private
 * @throws {H.lang.InvalidArgumentError} in case of invalid values (only if opt_caller is defined)
 * @param {Array.<number>} latLngAlts The lat/lng/alt array to validate
 * @param {Function=} opt_caller The caller to use for InvalidArgumentError. If omitted no error is thrown.
 * @param {number=} opt_argNr The argument number to use for InvalidArgumentError.
 * @return {boolean} to indicate whether the lat/lng/alt array is valid
 */
H.geo.Strip.validateLatLngAlts_ = function(latLngAlts, opt_caller, opt_argNr) {
  'use strict';
  var i = latLngAlts.length,
      isValid = !(i % 3),
      alt;
  // Validate (and adjust) the single lat, lng and alt values:
  while (isValid && i) {
    isValid = !(
        // ECMA evaluates the LeftHandSideExpression of an assignment first, therefore "--i" on the lefthand side:
        H.lang.isNaN(latLngAlts[--i] = /** @type {number} */ (
            H.geo.validateAlt((alt = latLngAlts[i]) === H.lang.UNDEF ? 0 : alt))) ||
        H.lang.isNaN(latLngAlts[--i] =
            H.geo.validateLng(latLngAlts[i])) ||
        H.lang.isNaN(latLngAlts[--i] =
            H.geo.validateLat(latLngAlts[i]))
        );
  }
  if (!isValid && opt_caller) {
    throw new H.lang.InvalidArgumentError(opt_caller, opt_argNr, latLngAlts);
  }
  return isValid;
};


/**
 * This method checks whether two longitudes form a leg which crosses the date border.
 * @param {H.geo.Longitude} lng1 the start longitude of the leg
 * @param {H.geo.Longitude} lng2 the end longitude of the leg
 * @return {boolean} true if the leg crosses the date border, otherwise false
 * @export
 * @publish
 */
H.geo.Strip.isDBC = function(lng1, lng2) {
  'use strict';
  return ((lng1 / lng2 < 0) && H.math.abs(lng2 - lng1) > 180);
};


/**
 * This method initializes a new strip with an array of lat, lng values. Arrays
 * are expected to have an even length with the format {@code [lat, lng, lat, lng, ...]}.
 * @param {Array.<number>} latLngs the array of lat, lng value.
 * @return {H.geo.Strip} the strip containing the lat, lng values
 *
 * @throws {H.lang.InvalidArgumentError} throws an error in case the latLngs array has an odd length
 *
 * @export
 * @publish
 */
H.geo.Strip.fromLatLngArray = function(latLngs) {
  //TODO: [JH]: This is the naive approach. It should be checked again when the strip was properly
  // re-implemented as stated at the top of the file.
  var strip = new H.geo.Strip(),
      i = 0,
      length = latLngs.length;

  if (length % 2) {
    throw new H.lang.InvalidArgumentError(H.geo.Strip.fromLatLngArray, 0, latLngs);
  }

  while (i < length) {
    strip.pushLatLngAlt_(latLngs[i++], latLngs[i++], 0, H.geo.Strip.fromLatLngArray);
  }
  return strip;
};
