goog.provide('H.util.Disposable');

goog.require('goog.Disposable');
goog.require('H.lang');



/**
 * Object which can be safely disposed.
 * @publish
 * @export
 * @constructor
 * @extends {goog.Disposable}
 */
H.util.Disposable = goog.Disposable;


/**
 * Method adds a callback which will be triggered when
 * the object is disposed
 * @param {!Function} callback
 * @param {Object=} opt_scope
 * @method
 * @export
 * @publish
 */
H.util.Disposable.prototype.addOnDisposeCallback = goog.Disposable.prototype.addOnDisposeCallback;


/**
 * Method disposes target object
 * @param {Object} disposableObject
 * @method
 * @export
 */
H.util.dispose = goog.dispose;
