goog.provide('H.geo.Rect');

goog.require('H.geo');
goog.require('H.geo.Point');
goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.math');

// TODO We should also specify an IRect. This would cause a change from private top_, left_ bottom_, right_
//      to public properties top, left, bottom, right



/**
 * This class represents a rectangular geographic area. The area is defined
 * by four geographical coordinates two (left, right) longitudes and two (top, bottom)
 * latitudes.
 * @export
 *
 * @param {H.geo.Latitude} top the northern-most latitude
 * @param {H.geo.Longitude} left the left-most longitude
 * @param {H.geo.Latitude} bottom the southern-most latitude
 * @param {H.geo.Longitude} right the right-most latitude
 * @constructor
 * @publish
 */
H.geo.Rect = function(top, left, bottom, right) {
  'use strict';
  this.set_(
      H.geo.validateLat(top, H.geo.Rect, 0),
      H.geo.validateLng(left, H.geo.Rect, 1),
      H.geo.validateLat(bottom, H.geo.Rect, 2),
      H.geo.validateLng(right, H.geo.Rect, 3));
};


if (goog.DEBUG) {
  /**
   * @override
   */
  H.geo.Rect.prototype.toString = function() {
    return '[(' + this.top_ + ', ' + this.left_ + ') (' + this.bottom_ + ', ' + this.right_ + ')]';
  };
}


/**
 * To compare this rectangle with a supplied other rectangle for equality.
 *
 * @publish
 * @export
 *
 * @param {H.geo.Rect} other
 * @return {boolean} Whether the two rectangles are equal.
 */
H.geo.Rect.prototype.equals = function(other) {
  'use strict';
  // TODO When we specify an IRect we should except it as argument
  return this === other ||
      !!other &&
      this.top_ === other.top_ &&
      this.left_ === other.left_ &&
      this.bottom_ === other.bottom_ &&
      this.right_ === other.right_;
};


/**
 * This method modifies the values of a bounding box. It is only used
 * internally for memory and performance reasons.
 * @param {H.geo.Latitude} top the northern-most latitude
 * @param {H.geo.Longitude} left the left-most longitude
 * @param {H.geo.Latitude} bottom the southern-most latitude
 * @param {H.geo.Longitude} right the right-most latitude
 * @return {H.geo.Rect} this rect
 * @private
 */
H.geo.Rect.prototype.set_ = function(top, left, bottom, right) {
  'use strict';
  var tmpTop;
  this.left_ = left;
  this.right_ = right;
  if (top < bottom) {
    // flip top and bottom
    tmpTop = top;
    top = bottom;
    bottom = tmpTop;
  }
  this.top_ = top;
  this.bottom_ = bottom;

  this.topLeft_ = this.bottomRight_ = this.center_ = null;

  return this;
};


/**
 * This method returns the top-left corner of the rectangular area.
 * @return {H.geo.Point} the top-left corner of the area
 * @publish
 * @export
 */
H.geo.Rect.prototype.getTopLeft = function() {
  'use strict';

  if (!this.topLeft_) {
    this.topLeft_ = new H.geo.Point(this.top_, this.left_);
  }
  return this.topLeft_;
};


/**
 * This method returns the bottom-right corner of the rectangular area.
 * @return {H.geo.Point} the bottom-right corner of the area
 * @publish
 * @export
 */

H.geo.Rect.prototype.getBottomRight = function() {
  'use strict';

  if (!this.bottomRight_) {
    this.bottomRight_ = new H.geo.Point(this.bottom_, this.right_);
  }
  return this.bottomRight_;
};


/**
 * This method returns the north-most latitude of this rectangular area
 * @return {H.geo.Latitude} the north-most latitude of the area
 * @publish
 * @export
 */
H.geo.Rect.prototype.getTop = function() {
  'use strict';

  return this.top_;
};


/**
 * This method returns the south-most latitude of this rectangular area
 * @return {H.geo.Latitude} the south-most latitude of the area
 * @publish
 * @export
 */
H.geo.Rect.prototype.getBottom = function() {
  'use strict';

  return this.bottom_;
};


/**
 * This method returns the left-most longitude of this rectangular area
 * @return {H.geo.Longitude} the left-most longitude of the area
 * @publish
 * @export
 */
H.geo.Rect.prototype.getLeft = function() {
  'use strict';

  return this.left_;
};


/**
 * This method returns the right-most longitude of this rectangular area
 * @return {H.geo.Longitude} the right-most longitude of the area
 * @publish
 * @export
 */
H.geo.Rect.prototype.getRight = function() {
  'use strict';

  return this.right_;
};


/**
 * This method returns the center point of this rectangular area
 * @return {H.geo.Point} the center point of the area
 * @publish
 * @export
 */
H.geo.Rect.prototype.getCenter = function() {
  'use strict';

  if (!this.center_) {
    this.center_ = new H.geo.Point(
        this.bottom_ + (this.top_ - this.bottom_) / 2,
        H.geo.Rect.getBoxMidLng_(this.left_, this.getWidth())
        );
  }
  return this.center_;
};


/**
 * Returns this width of this rectangular area in decimal degrees.
 * @return {number} the width of this area
 * @publish
 * @export
 */
H.geo.Rect.prototype.getWidth = function() {
  'use strict';

  return H.geo.Rect.getBoxWidth_(this.left_, this.right_);
};


/**
 * Returns this height of this rectangular area in decimal degrees.
 * @return {number} the height of this area
 * @publish
 * @export
 */
H.geo.Rect.prototype.getHeight = function() {
  'use strict';

  return this.top_ - this.bottom_;
};


/**
 * Returns a boolean value indicating whether this rectangular area spans
 * across the date border.
 * @return {boolean} true if the area spans across the date border, otherwise false
 * @publish
 * @export
 */
H.geo.Rect.prototype.isCDB = function() {
  'use strict';

  return this.left_ > this.right_;
};


/**
 * The method checks if the area enclosed by the given bounding box is 0.
 * @return {boolean} true if the dimensions of the area a 0, otherwise false
 * @publish
 * @export
 */
H.geo.Rect.prototype.isEmpty = function() {
  'use strict';
  return !this.getWidth() && !this.getHeight();
};


/**
 * This method checks if the latitude and longitude supplied by the caller
 * lie within the area of this rectangular area.
 *
 * @export
 * @param {H.geo.Latitude} lat the latitude
 * @param {H.geo.Longitude} lng the longitude
 * @param {boolean=} opt_skipValidation a boolean flag indicating whether to
 *  check validity of the arguments
 * @return {boolean} true if the latitude and longitude are contained in this
 *  area, otherwise false
 * @publish
 */
H.geo.Rect.prototype.containsLatLng = function(lat, lng, opt_skipValidation) {
  'use strict';

  var center = this.getCenter(),
      resBoxCenter,
      resBox;

  if (!opt_skipValidation) {
    lat = H.geo.validateLat(lat, this.containsLatLng, 0);
    lng = H.geo.validateLng(lng, this.containsLatLng, 1);
  }

  // TODO Pass a opt_out argument to reuse a Rect instance
  resBox = this.mergeLatLng(lat, lng, opt_skipValidation);
  resBoxCenter = resBox.getCenter();

  return resBoxCenter['lat'] === center['lat'] &&
         resBoxCenter['lng'] === center['lng'] &&
         this.getHeight() === resBox.getHeight() &&
         this.getWidth() === resBox.getWidth();
};


/**
 * This method checks if the point supplied by the caller lies within the area of this rectangular area.
 * @param {H.geo.IPoint} geoPoint the point
 * @param {boolean=} opt_skipValidation a boolean flag indicating whether to
 *  check validity of the arguments
 * @return {boolean} true if the point is contained in this
 *  area, otherwise false
 * @publish
 * @export
 */
H.geo.Rect.prototype.containsPoint = function(geoPoint, opt_skipValidation) {
  'use strict';
  opt_skipValidation || H.geo.Point.validate(geoPoint, this.containsPoint, 0);

  return this.containsLatLng(geoPoint['lat'], geoPoint['lng'], opt_skipValidation);
};


/**
 * This method checks if the rectangular area supplied by the caller
 * is completely contained within the area of this rectangular area.
 * @param {H.geo.Rect} geoRect the rectangular area
 * @param {boolean=} opt_skipValidation a boolean flag indicating whether to
 *  check validity of the arguments
 * @return {boolean} true if the rectangular area is contained in this
 *  area, otherwise false
 * @publish
 * @export
 */
H.geo.Rect.prototype.containsRect = function(geoRect, opt_skipValidation) {
  'use strict';
  // TODO When we specify an IRect we should except it as argument

  var center = this.getCenter(),
      resBoxCenter,
      resBox;

  if (!opt_skipValidation) {
    if (!H.lang.isInstanceOf(geoRect, H.geo.Rect)) {
      throw new H.lang.InvalidArgumentError(this.containsRect, 0, geoRect);
    }
  }

  // TODO Pass a opt_out argument to reuse a Rect instance
  resBox = this.mergeRect(geoRect, opt_skipValidation);
  resBoxCenter = resBox.getCenter();

  return resBoxCenter['lat'] === center['lat'] &&
         resBoxCenter['lng'] === center['lng'] &&
         this.getHeight() === resBox.getHeight() &&
         this.getWidth() === resBox.getWidth();
};


/**
 * This method returns the smallest bounding box that covers this rectangular
 * area and the latitude and longitude supplied by the caller.
 * @param {H.geo.Latitude} lat the latitude
 * @param {H.geo.Longitude} lng the longitude
 * @param {boolean=} opt_skipValidation a boolean flag indicating whether to
 *  check validity of the arguments
 * @param {H.geo.Rect=} opt_out an optional rectangular area to store
 *  the result
 * @return {H.geo.Rect} either the rectangular area passed as out
 *  parameter or a new rectangular area
 * @publish
 * @export
 */
H.geo.Rect.prototype.mergeLatLng = function(lat, lng, opt_skipValidation, opt_out) {
  'use strict';

  if (!opt_skipValidation) {
    if (H.lang.isNaN(lat = H.geo.validateLat(lat))) {
      throw new H.lang.InvalidArgumentError(this.mergeLatLng, 0, lat);
    }
    if (H.lang.isNaN(lng = H.geo.validateLng(lng))) {
      throw new H.lang.InvalidArgumentError(this.mergeLatLng, 1, lng);
    }
  }

  return H.geo.Rect.merge(
      this.top_, this.left_, this.bottom_, this.right_,
      lat, lng, lat, lng,
      opt_out);
};


/**
 * This method returns the smallest bounding box that covers this rectangular
 * area and the point supplied by the caller.
 * @param {H.geo.IPoint} geoPoint the point to merge
 * @param {boolean=} opt_skipValidation a boolean flag indicating whether to
 *  check validity of the arguments
 * @param {H.geo.Rect=} opt_out an optional rectangular area to store
 *  the result
 * @return {H.geo.Rect} either the rectangular area passed as out
 *  parameter or a new rectangular area
 * @publish
 * @export
 */
H.geo.Rect.prototype.mergePoint = function(geoPoint, opt_skipValidation, opt_out) {
  'use strict';

  if (!opt_skipValidation) {
    H.geo.Point.validate(geoPoint, this.mergePoint, 0);
  }
  return this.mergeLatLng(geoPoint['lat'], geoPoint['lng'], opt_skipValidation, opt_out);
};


/**
 * This method returns the smallest bounding box that covers this rectangular
 * area and the rectangular area supplied by the caller.
 * @param {H.geo.Rect} geoRect the point to merge
 * @param {boolean=} opt_skipValidation a boolean flag indicating whether to
 *  check validity of the arguments
 * @param {H.geo.Rect=} opt_out an optional rectangular area to store
 *  the result
 * @return {H.geo.Rect} either the rectangular area passed as out
 *  parameter or a new rectangular area
 * @publish
 * @export
 */
H.geo.Rect.prototype.mergeRect = function(geoRect, opt_skipValidation, opt_out) {
  'use strict';
  // TODO When we specify an IRect we should except it as argument

  if (!opt_skipValidation) {
    if (!H.lang.isInstanceOf(geoRect, H.geo.Rect)) {
      throw new H.lang.InvalidArgumentError(this.mergeRect, 0, geoRect);
    }
  }

  return H.geo.Rect.merge(
      this.top_, this.left_, this.bottom_, this.right_,
      geoRect.top_, geoRect.left_, geoRect.bottom_, geoRect.right_,
      opt_out);
};


/**
 * This method returns the smallest bounding box that covers this rectangular
 * area and the rectangular area supplied by the caller.
 *
 * @publish
 * @export
 *
 * @param {H.geo.Latitude} top The top latitude of the rectangle to merge
 * @param {H.geo.Longitude} left The left longitude of the rectangle to merge
 * @param {H.geo.Latitude} bottom The bottom latitude of the rectangle to merge
 * @param {H.geo.Longitude} right The right longitude of the rectangle to merge
 * @param {boolean=} opt_skipValidation a boolean flag indicating whether to check validity of the arguments
 * @param {H.geo.Rect=} opt_out an optional rectangular area to store the result
 * @return {H.geo.Rect} either the rectangular area passed as out
 *  parameter or a new rectangular area
 */
H.geo.Rect.prototype.mergeTopLeftBottomRight = function(top, left, bottom, right, opt_skipValidation, opt_out) {
  'use strict';

  if (!opt_skipValidation) {
    top = H.geo.validateLat(top, this.mergeTopLeftBottomRight, 0);
    left = H.geo.validateLng(left, this.mergeTopLeftBottomRight, 1);
    bottom = H.geo.validateLat(bottom, this.mergeTopLeftBottomRight, 2);
    right = H.geo.validateLng(right, this.mergeTopLeftBottomRight, 3);
  }

  return H.geo.Rect.merge(
      this.top_, this.left_, this.bottom_, this.right_,
      top, left, bottom, right,
      opt_out);
};


/**
 * This method checks if the intersection of two bounding boxes is non-empty.
 *
 * @publish
 * @export
 *
 * @param {H.geo.Rect} geoRect a rectangular area to be tested for intersection with this rectangular area
 * @param {boolean=} opt_skipValidation a boolean flag indicating whether to check validity of the arguments
 * @return {boolean} a boolean value indicating whether the two areas intersect
 */
H.geo.Rect.prototype.intersects = function(geoRect, opt_skipValidation) {
  'use strict';
  // TODO When we specify an IRect we should except it as argument

  if (!opt_skipValidation) {
    if (!H.lang.isInstanceOf(geoRect, H.geo.Rect)) {
      throw new H.lang.InvalidArgumentError(this.intersects, 0, geoRect);
    }
  }

  var cond1 = this.left_ <= this.right_,
      cond2 = geoRect.left_ <= geoRect.right_,
      cond3 = this.left_ < geoRect.right_,
      cond4 = geoRect.left_ < this.right_;

  return this.bottom_ <= geoRect.top_ &&
      geoRect.bottom_ <= this.top_ &&
      (!cond1 && (!cond2 || cond3 || cond4) || !cond2 && (cond3 || cond4) || cond3 && cond4);
};


/**
 * This method returns the span between two longitudes
 * @param {H.geo.Longitude} left the left-most longitude
 * @param {H.geo.Longitude} right the right-most longitude
 * @return {number} the distance between the two longitudes in decimal degrees
 * @private
 */
H.geo.Rect.getBoxWidth_ = function(left, right) {
  'use strict';

  var len = right - left;
  len += len < 0 ? H.geo.MAX_LONGITUDE_LENGTH : 0;
  return len;
};


/**
 * This method returns longitude half-way between the left-most longitude
 * and the width.
 * @param {H.geo.Longitude} left The left-most longitude
 * @param {number} width The width
 * @return {H.geo.Longitude} the longitude half-way along the width
 * @private
 */
H.geo.Rect.getBoxMidLng_ = function(left, width) {
  'use strict';

  var midPoint = left + width / 2;
  midPoint -= midPoint > H.geo.MAX_LONGITUDE ? H.geo.MAX_LONGITUDE_LENGTH : 0;
  return midPoint;
};


/**
 * This method merges two rects by their values. The result of the merge is
 * a bounding rect which covers all provided rect bounds.
 * @param {H.geo.Latitude} topA the northern-most latitude
 * @param {H.geo.Longitude} leftA the left-most longitude of operand A
 * @param {H.geo.Latitude} bottomA the southern-most latitude of operand A
 * @param {H.geo.Longitude} rightA the right-most latitude of operand A
 * @param {H.geo.Latitude} topB the northern-most latitude of operand B
 * @param {H.geo.Longitude} leftB the left-most longitude of operand B
 * @param {H.geo.Latitude} bottomB the southern-most latitude of operand B
 * @param {H.geo.Longitude} rightB the right-most latitude of operand B
 * @param {H.geo.Rect=} opt_out an optional rect to store the results
 * @return {H.geo.Rect} either the opt_out rect or a new rect
 * @export
 * @publish
 */
H.geo.Rect.merge = function(topA, leftA, bottomA, rightA, topB, leftB, bottomB, rightB, opt_out) {
  'use strict';

  var epsilon = 1e-6,
      minLat,
      maxLat,
      widthA,
      midLngA,
      widthB,
      bMid,
      newMinLon,
      newMaxLon,
      distance,
      x;

  //latitude longitude
  minLat = H.math.min(bottomA, bottomB);
  maxLat = H.math.max(topA, topB);
  // This BBox
  widthA = H.geo.Rect.getBoxWidth_(leftA, rightA);
  midLngA = H.geo.Rect.getBoxMidLng_(leftA, widthA);
  //the new box
  widthB = H.geo.Rect.getBoxWidth_(leftB, rightB);
  bMid = H.geo.Rect.getBoxMidLng_(leftB, widthB);

  /*
   * Computation of new min/max longitude. We need to use error correction because we should
   * got something like 180.000000000003 insted of 180
   * due to JS handling with numbers. So we are using epsilon.
   */
  distance = bMid - midLngA;
  distance += (distance - epsilon) < 0 ? H.geo.MAX_LONGITUDE_LENGTH : 0;

  if (distance - epsilon < H.geo.MAX_LONGITUDE_LENGTH / 2) {
    newMinLon = leftA;
    newMaxLon = rightB;
  } else {
    distance = H.geo.MAX_LONGITUDE_LENGTH - distance;
    newMinLon = leftB;
    newMaxLon = rightA;
  }

  x = distance + widthA / 2 + widthB / 2;
  if (x + epsilon / 2 >= H.geo.MAX_LONGITUDE_LENGTH) {
    newMinLon = H.geo.MIN_LONGITUDE;
    newMaxLon = H.geo.MAX_LONGITUDE;
  } else if (x - epsilon / 2 < widthA) {
    newMinLon = leftA;
    newMaxLon = rightA;
  } else if (x - epsilon / 2 < widthB) {
    newMinLon = leftB;
    newMaxLon = rightB;
  }

  return opt_out ?
      opt_out.set_(maxLat, newMinLon, minLat, newMaxLon) :
      new H.geo.Rect(maxLat, newMinLon, minLat, newMaxLon);
};


/**
 * This method creates a rectangular area from a top-left and bottom-right point
 *  pair.
 * @param {H.geo.IPoint} topLeft the top-left corner of the area
 * @param {H.geo.IPoint} bottomRight the bottom-right corner of the area
 * @param {boolean=} opt_skipValidation a boolean flag indicating whether to
 *  check validity of the arguments
 * @return {H.geo.Rect} returns the rectangular area defined by the
 *  top-left and bottom-right corners
 * @publish
 * @export
 */
H.geo.Rect.fromPoints = function(topLeft, bottomRight, opt_skipValidation) {
  'use strict';

  if (!opt_skipValidation) {
    H.geo.Point.validate(topLeft, H.geo.Rect.fromPoints, 0);
    H.geo.Point.validate(bottomRight, H.geo.Rect.fromPoints, 1);
  }

  return new H.geo.Rect(
      topLeft['lat'],
      topLeft['lng'],
      bottomRight['lat'],
      bottomRight['lng']);
};


/**
 * This method creates the minimum rectangular area covering all of the points
 * in the argument array.
 * @param {Array.<H.geo.IPoint>} pointArray the array of points to cover
 * @param {boolean=} opt_skipValidation a boolean flag indicating whether to
 *  check validity of the arguments
 * @return {?H.geo.Rect} returns the minimum rectangular area covering
 *  the points or {@code null} if no point is covered
 * @publish
 * @export
 */
H.geo.Rect.coverPoints = function(pointArray, opt_skipValidation) {
  'use strict';

  if (!H.lang.isArrayLike(pointArray)) {
    throw new H.lang.InvalidArgumentError(H.geo.Rect.coverPoints, 0, pointArray);
  }

  var i = 1,
      l = pointArray.length,
      point,
      rect = null;
  if (l > 0) {
    point = pointArray[0];
    rect = H.geo.Rect.fromPoints(point, point, opt_skipValidation);
    for (; i < l; i++) {
      point = pointArray[i];
      rect.mergeLatLng(point['lat'], point['lng'], opt_skipValidation, rect);
    }
  }
  return rect;
};


/**
 * This method creates the minimum rectangular area covering all of the
 * coordinates in the argument array.
 *
 * @param {Array.<number>} latLngAltArray the array of coordinates to cover
 * @param {boolean=} opt_skipValidation a boolean flag indicating whether to
 *  check validity of the arguments
 * @return {H.geo.Rect|undefined} returns the minimum rectangular area covering
 *  the coordinates
 * @publish
 * @export
 */
H.geo.Rect.coverLatLngAlts = function(latLngAltArray, opt_skipValidation) {
  'use strict';

  if (!opt_skipValidation) {
    if (!latLngAltArray || latLngAltArray.length == null) {
      throw new H.lang.InvalidArgumentError(H.geo.Rect.coverLatLngAlts, 0, latLngAltArray);
    }
  }

  var i = 3,
      l = latLngAltArray.length,
      rect;
  if (l > 0) {
    rect = new H.geo.Rect(latLngAltArray[0], latLngAltArray[1], latLngAltArray[0], latLngAltArray[1]);
    for (; i < l; i += 3) {
      rect.mergeLatLng(latLngAltArray[i], latLngAltArray[i + 1], opt_skipValidation, rect);
    }
  }
  return rect;
};


/**
 * This method creates the minimum rectangular area covering all of the
 * rectangular areas in the argument array.
 * @param {Array.<H.geo.Rect>} rectArray the array of points to cover
 * @param {boolean=} opt_skipValidation a boolean flag indicating whether to
 *  check validity of the arguments
 * @return {H.geo.Rect|undefined} returns the minimum rectangular area covering
 *  the rectangular areas
 * @publish
 * @export
 */
H.geo.Rect.coverRects = function(rectArray, opt_skipValidation) {
  'use strict';
  // TODO When we specify an IRect we should except it as argument

  if (opt_skipValidation) {
    if (!rectArray || rectArray.length == null) {
      throw new H.lang.InvalidArgumentError(H.geo.Rect.coverRects, 0, rectArray);
    }
  }

  var i = 1,
      l = rectArray.length,
      rect;
  if (l > 0) {
    rect = new H.geo.Rect(rectArray[0].top_, rectArray[0].left_, rectArray[0].bottom_, rectArray[0].right_);
    for (; i < l; i++) {
      rect.mergeRect(rectArray[i], opt_skipValidation, rect);
    }
  }
  return rect;
};


/**
 * This method clones the given bounding rect and resizes the clone if necessary
 * until the location supplied by the caller is at its center.
 *
 * @param {H.geo.IPoint} center a point which is to be the center of the
 *  resized rectangular area
 * @param {H.geo.Rect=} opt_out an optional rectangular area to store
 *  the result
 * @return {H.geo.Rect} the resized rectangular area
 * @publish
 * @export
 */
H.geo.Rect.prototype.resizeToCenter = function(center, opt_out) {
  'use strict';
  H.geo.Point.validate(center, this.resizeToCenter, 0);

  var minLon = this.left_,
      maxLat = this.top_,
      maxLon = this.right_,
      minLat = this.bottom_,
      mid = this.getCenter()['lng'],
      dlat = center['lat'] - minLat - (maxLat - minLat) / 2,
      dlon = center['lng'] - mid,
      resultMinLon,
      resultMaxLon,
      resultMinLat,
      resultMaxLat;

  dlon = dlon > H.geo.MAX_LONGITUDE || dlon < H.geo.MIN_LONGITUDE ? -(mid + center['lng']) : dlon;

  resultMinLon = minLon + (dlon < 0 ? 2 * dlon : 0);
  resultMinLon = resultMinLon < H.geo.MIN_LONGITUDE ?
      H.geo.MAX_LONGITUDE_LENGTH + resultMinLon : resultMinLon;

  resultMaxLon = maxLon + (dlon > 0 ? 2 * dlon : 0);

  resultMaxLon = resultMaxLon > H.geo.MAX_LONGITUDE ?
      resultMaxLon - H.geo.MAX_LONGITUDE_LENGTH : resultMaxLon;

  //if the latitude takes an invalid value, we have to shrink the bounding rect to the world dimension
  resultMaxLat = dlat > 0 ? maxLat + 2 * dlat : maxLat;
  if (resultMaxLat >= 90) resultMaxLat = 90;

  resultMinLat = dlat < 0 ? minLat + 2 * dlat : minLat;
  if (resultMinLat <= -90) resultMinLat = -90;

  return opt_out ?
      opt_out.set_(resultMaxLat, resultMinLon, resultMinLat, resultMaxLon) :
      new H.geo.Rect(resultMaxLat, resultMinLon, resultMinLat, resultMaxLon);
};
