goog.provide('H.geo');

goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.math');


/**
 * This namespace contains functionality for geo-computation.
 * @namespace H.geo
 * @publish
 */


/**
 * The geometric mean radius of WGS84 is 6,371,000.7900 meters
 * Additional info: It's the volume equivalent radius, combining the equatorial radius (6,378,137 m)
 * and the polar radius (6,356,752.3142 m)
 *
 * @const
 * @type {number}
 */
H.geo.WGS84_MEAN_RADIUS = 6371000.7900;


/**
 * The circumference of the earth, based on the WGS84 mean radius
 * @const
 * @type {number}
 */
H.geo.WGS84_MEAN_CIRCUMFERENCE = 2 * H.math.PI * H.geo.WGS84_MEAN_RADIUS;


/**
 * Contexts for altitudes to specify the contextual origin of an altitude's value
 * @enum {string}
 * @export
 * @publish
 */
H.geo.AltitudeContext = {
  /** Ground level */
  'undefined': 'GL',
  /** Ground level */
  'GL': 'GL',
  /** Obstruction level */
  'OL': 'OL',
  /** Mean sea level */
  'SL': 'SL',
  /** Sea bed level */
  'SB': 'SB',
  /** WGS84 ellipsoid */
  'WE': 'WE',
  /** WGS84 geoid */
  'WG': 'WG'
};


/**
 * Minimal allowed longitude value
 * @const
 * @type {number}
 */
H.geo.MIN_LONGITUDE = -180;


/**
 * Maximal value for longitude
 * @const
 * @type {number}
 */
H.geo.MAX_LONGITUDE = 180;


/**
 * Minimal value for latitude
 * @const
 * @type {number}
 */
H.geo.MIN_LATITUDE = -90;


/**
 * Maximal value for latitude
 * @const
 * @type {number}
 */
H.geo.MAX_LATITUDE = 90;


/**
 * Maximal length on the longitude axis
 * @const
 * @type {number}
 */
H.geo.MAX_LONGITUDE_LENGTH = H.geo.MAX_LONGITUDE - H.geo.MIN_LONGITUDE;


/**
 * A geographic coordinate that specifies the north-south position of a point on the Earth's surface in the range
 * from -90 to + 90 degrees, inclusive.
 * @name H.geo.Latitude
 * @typedef {number}
 * @publish
 */
H.geo.Latitude;


/**
 * A Geographic coordinate that specifies the east-west position of a point on the Earth's surface in the range
 * from -180 to 180 degrees, inclusive.
 * @name H.geo.Longitude
 * @typedef {number}
 * @publish
 */
H.geo.Longitude;


/**
 * A Geographic coordinate that specifies the height of a point in meters. A value of {@code undefined} is
 * treated as {@code 0}.
 * @see H.geo.AltitudeContext
 * @name H.geo.Altitude
 * @typedef {number|undefined}
 * @publish
 */
H.geo.Altitude;


/**
 * Clamps the given latitude value into a range of {@code -90 ... +90}
 * @param {?} lat The latitude to validate
 * @param {Function=} opt_caller The caller to use for InvalidArgumentError. If omitted no error is thrown.
 * @param {number=} opt_argNr The argument number to use for InvalidArgumentError.
 * @return {H.geo.Latitude}
 */
H.geo.validateLat = function(lat, opt_caller, opt_argNr) {
  'use strict';
  var val = H.math.clamp(+lat, -90, 90);

  if (opt_caller && H.lang.isNaN(val)) {
    throw new H.lang.InvalidArgumentError(opt_caller, opt_argNr, lat);
  }
  return val;
};


/**
 * Modulo the given longitude value into a range of {@code -180 ... +180}
 * @param {?} lng The longitude to validate
 * @param {Function=} opt_caller The caller to use for InvalidArgumentError. If omitted no error is thrown.
 * @param {number=} opt_argNr The argument number to use for InvalidArgumentError.
 * @return {H.geo.Longitude}
 */
H.geo.validateLng = function(lng, opt_caller, opt_argNr) {
  'use strict';
  var val = +lng;
  if (val < -180 || val > 180) {
    val = H.math.modulo(val + 180, 360) - 180;
  }
  if (opt_caller && H.lang.isNaN(val)) {
    throw new H.lang.InvalidArgumentError(opt_caller, opt_argNr, lng);
  }
  return val;
};


/**
 * Validates the the given altitude value
 * @param {?} alt The altitude to validate
 * @param {Function=} opt_caller The caller to use for InvalidArgumentError. If omitted no error is thrown.
 * @param {number=} opt_argNr The argument number to use for InvalidArgumentError.
 * @return {H.geo.Altitude}
 */
H.geo.validateAlt = function(alt, opt_caller, opt_argNr) {
  'use strict';
  var val;
  if (alt !== H.lang.UNDEF && H.lang.isNaN(val = +alt) && opt_caller) {
    throw new H.lang.InvalidArgumentError(opt_caller, opt_argNr, alt);
  }
  return val;
};


/**
 * Checks that the given value is a one of the enumeration values of {@link H.geo.AltitudeContext}
 * @param {?} ctx The altitude context to validate
 * @param {Function=} opt_caller The caller to use for InvalidArgumentError. If omitted no error is thrown.
 * @param {number=} opt_argNr The argument number to use for InvalidArgumentError.
 * @return {boolean} whether the value is valid
 */
H.geo.validateCtx = function(ctx, opt_caller, opt_argNr) {
  'use strict';
  var invalid = !H.geo.AltitudeContext[ctx];
  if (opt_caller && invalid) {
    throw new H.lang.InvalidArgumentError(opt_caller, opt_argNr, ctx);
  }
  return !invalid;
};


/**
 * Method checks if argument passed belongs to H.geo.IPoint
 * @param {H.geo.IPoint} point
 * @return {boolean}
 */
H.geo.isIPoint = function(point) {
  if (!point || H.lang.isNaN(point['lat']) || H.lang.isNaN(point['lng'])) {
    return false;
  }
  return true;
};
