goog.provide('H.map.DomMarker');

goog.require('H.map.AbstractMarker');
goog.require('H.map.DomIcon');



//################################################  CONSTRUCTOR  #######################################################
/**
 * A marker with a visual representation in the form of a full styleable and scripteable DOM element.
 * DomMarker are predestinated if small amounts of markers with dynamic styled and/or scripted icons should be displayed
 * om the map (e.g. animated interactive SVG).
 * @publish
 * @export
 * @extends {H.map.AbstractMarker}
 * @final
 * @constructor
 * @param {H.geo.IPoint} position
 * @param {H.map.DomMarker.Options=} opt_options
 */
H.map.DomMarker = function(position, opt_options) {
  'use strict';
  H.map.DomMarker.superClass_.constructor.call(this, position, opt_options);
};
H.lang.inherits(H.map.DomMarker, H.map.AbstractMarker);


/**
 * @override
 */
H.map.DomMarker.prototype.type = H.map.Object.Type['DOM_MARKER'];


//#################################################  Z-ORDER  ##########################################################
/**
 * @override
 */
H.map.DomMarker.prototype.zOrderIdx = 2;


//###############################################  DEFAULT ICON  #######################################################
/**
 * @override
 */
H.map.DomMarker.prototype.createDefaultIcon = function(options) {
  'use strict';
  var icon = new H.map.DomIcon(options.svg, /** @type {H.map.DomIcon.Options} */ (options)),
      anchor = options.anchor;
  icon.elem.style.margin = -anchor['y'] + 'px 0 0 -' + anchor['x'] + 'px';
  return icon;
};


//###################################################  ICON  ###########################################################
/**
 * @override
 */
H.map.DomMarker.prototype.iconConstructor = H.map.DomIcon;


//################################################  OPTIONS  ###########################################################
/**
 * Options used to initialize a DomMarker
 * @publish
 *
 * @property {number=} min
 *           The minimum zoom level for which the object is visible, default is {@code -Infinity}
 * @property {number=} max
 *           The maximum zoom level for which the object is visible, default is {@code Infinity}
 * @property {boolean=} visibility
 *           Indicates whether the map object is visible at all, default is {@code true}.
 * @property {number=} zIndex
 *           The z-index value of the map object, default is {@code 0}
 * @property {(H.map.provider.Provider|null)=} provider
 *           The provider of this object. This property is only needed if a customized Implementation of ObjectProvider
 *           wants to instantiate an object.
 * @property {H.map.DomIcon=} icon
 *           The icon to use for the visual representation, if omitted a default icon is used.
 *
 * @typedef {{
 *   min: (number|undefined),
 *   max: (number|undefined),
 *   visibility: (boolean|undefined),
 *   zIndex: (number|undefined),
 *   provider: (H.map.provider.ObjectProvider|null|undefined),
 *   icon: (H.map.DomIcon|undefined)
 * }}
 */
H.map.DomMarker.Options;
