goog.provide('H.map.provider.SpatialTileProvider');

goog.require('H.map.provider.Provider');
goog.require('H.map.provider.SpatialTile');
goog.require('H.map.provider.TileProvider');
goog.require('H.util.Cache');
goog.require('H.util.SequentialQueue');
goog.require('H.util.shims');



/**
 * This tile provider renders spatial map objects (polylines and polygons) to tiles. Every tile represents a part of
 * the spatial object which is clipped according to requested tile coordinates.
 * @export
 * @final
 * @extends {H.map.provider.TileProvider}
 * @constructor
 * @param {H.map.provider.ObjectProvider} objectProvider The ObjectProvider which provides the objects to paint
 * @param {H.map.provider.SpatialTileProvider.Options} options The options for this SpatialTileProvider
 */
H.map.provider.SpatialTileProvider = function(objectProvider, options) {
  'use strict';

  H.map.provider.SpatialTileProvider.superClass_.constructor.call(this, options);
  this.provider_ = objectProvider;
  this.pixelRatio_ = options['pixelRatio'];
  this['tileSize'] = this['tileSize'] * H.math.round(this.pixelRatio_);

  // Bind functions
  this.onTileDropped_ = H.lang.bind(this.onTileDropped_, this);
  this.onDataDropped_ = H.lang.bind(this.onDataDropped_, this);
  this.processRenderTask_ = H.lang.bind(this.processRenderTask_, this);
  this.onRenderTaskDone_ = H.lang.bind(this.onRenderTaskDone_, this);
  this.onObjectProviderUpdate_ = H.lang.bind(this.onObjectProviderUpdate_, this);

  this.provider_.addEventListener(H.map.provider.Provider.prototype.EventType.UPDATE,
      this.onObjectProviderUpdate_, false, this);

  // Set up a canvas pool:
  this.canvasPool_ = [];
  // Set up a smaller 'full-rendered-tile' cache:
  this.tileCache_ = new H.util.Cache(options['tileCacheSize'] || 256, this.onTileDropped_);
  // Set up a cache for tiles that only have clip data:
  this.dataCache_ = new H.util.Cache(options['dataCacheSize'] || 512, this.onDataDropped_);
  // Set up a render queue:
  this.renderQueue_ = new H.util.SequentialQueue(20, this.processRenderTask_);
  // Set up an object to store pending tiles:
  this.pendingTileRequests_ = {};

  /**
   * @private
   * @type {number}
   */
  this.lastProcessSync_ = H.util.shims.now();
};
H.lang.inherits(H.map.provider.SpatialTileProvider, H.map.provider.TileProvider);


//################################################  TILE PROVIDER  #####################################################
/**
 * A listener for 'update' events from the ObjectProvider, which dispatches an 'update' event on this
 * SpatialTileProvider if the target is a map object of type 'SPATIAL'
 * @private
 * @param {H.util.Event} evt
 */
H.map.provider.SpatialTileProvider.prototype.onObjectProviderUpdate_ = function(evt) {
  if (evt.target.type === H.map.Object.Type['SPATIAL']) {
    this.dispatchUpdate(evt.target);
  }
};


/**
 * @override
 * @export
 */
H.map.provider.SpatialTileProvider.prototype.requestTile = function(x, y, z, cacheOnly) {
  'use strict';
  var tileKey = this['getTileKey'](x, y, z),
      tile,
      canvas;

  tile = /** @type {H.map.provider.Tile|undefined} */ (this.tileCache_.get(tileKey, cacheOnly));
  // Do we have a fully rendered tile?
  if (tile) {
    // the tile's canvas is perfectly good and can be used
    // OR
    // the tile needs re-painting BUT the layer is only asking for cached stuff (we are lax and return the tile anyways)
    if (cacheOnly || tile.isCanvasValid()) {
      return tile;
    } else {
      // JH: The dropping of cached tiles is disabled to avoid spatial flickering. Should be re-evaluated when
      // the render engine provides its interactive state and we can actually start providing cacheOnly properly.
      // REPLACED:
      // if (!cacheOnly) {
      //   this.tileCache_.drop(tileKey);
      // }
      // WITH:
      this.dataCache_.add(tileKey, tile, 1);
    }
  }

  tile = /** @type {H.map.provider.Tile|undefined} */ (this.dataCache_.get(tileKey, cacheOnly));
  // Do we have this tile's renderData?
  if (tile) {
    // If the tile's renderData is still valid OR renderData is maybe invalid BUT the layer asked for cached tiles only?
    if (cacheOnly || tile.isDataValid()) {
      // The we will still reuse the renderData:
      canvas = this.popCanvas_();
      if (!tile.render(canvas)) {
        // If render returns false, it did not draw anything on the canvas. In that case we push it back to the pool:
        this.canvasPool_.push(canvas);
      }
      // Propagate the tile to the cache:
      this.tileCache_.add(tileKey, tile, 1);
      return tile;
    } else {
      // JH: The dropping of cached tiles is disabled to avoid spatial flickering. Should be re-evaluated when
      // the render engine provides its interactive state and we can actually start providing cacheOnly properly.
      // if (!cacheOnly) {
      //   // If we are not returning cached stuff, we should remove this tile from the cache:
      //   this.dataCache_.drop(tileKey);
      // }
    }
  }

  // So, we haven't returned a tile yet. Then, if we are not providing
  // cached data, we can kick the tile renderer to produce it for us
  if (!cacheOnly) {
    this.queueForRendering_(tileKey, x, y, z);
  }
};


/**
 * Triggers the provider to render tiles synchronously.
 */
H.map.provider.SpatialTileProvider.prototype.processSync = function() {
  // Threshold synchronous processing to run every 33 milliseconds at max
  var now = H.util.shims.now(),
      lastSync = this.lastProcessSync_;
  if ((now - lastSync) > 33) {
    this.renderQueue_.processSync();
    this.lastProcessSync_ = now;
  }

};


/**
 * @override
 * @export
 */
H.map.provider.SpatialTileProvider.prototype.cancelTile = function(x, y, z) {
  'use strict';
  this.cancelTileByKey(this['getTileKey'](x, y, z));
};


/**
 * @override
 * @export
 */
H.map.provider.SpatialTileProvider.prototype.cancelTileByKey = function(tileKey) {
  'use strict';
  var request;
  // NOTE: Tiles can only be cancelled if they are not yet in processing
  if (request = this.pendingTileRequests_[tileKey]) {
    request['count']--;
    if (!request['count']) {
      request.cancel();
      delete this.pendingTileRequests_[tileKey];
    }
  }
};


//#################################################  RENDER QUEUE  #####################################################
/**
 * Queues a tile request for rendering
 * @param {string} tileKey
 * @param {number} x
 * @param {number} y
 * @param {number} z
 * @private
 */
H.map.provider.SpatialTileProvider.prototype.queueForRendering_ = function(tileKey, x, y, z) {
  'use strict';
  var request = this.pendingTileRequests_[tileKey];
  if (!request) {
    request = this.renderQueue_.push([tileKey, x, y, z], this.onRenderTaskDone_);
    request['count'] = 1;
    this.pendingTileRequests_[tileKey] = request;
  } else {
    request['count']++;
  }
};


/**
 * The funtion to process a task of the render queue
 * @param {Object} task
 * @param {Object} data
 * @private
 */
H.map.provider.SpatialTileProvider.prototype.processRenderTask_ = function(task, data) {
  'use strict';
  var tileKey = data[0],
      x = data[1],
      y = data[2],
      z = data[3],
      tile = new H.map.provider.SpatialTile(tileKey, x, y, z, this['tileSize'], this.pixelRatio_, this.provider_),
      canvas = this.popCanvas_();

  if (!tile.render(canvas)) {
    // A return value false means: "nothing rendered on the canvas", therefore we can push the tile back to the pool:
    this.canvasPool_.push(canvas);
  }
  data.push(tile);
  task.done(tile);
};


/**
 * Callback function for the async queue's process function
 * @param {H.util.AsyncQueue.Task} task the task
 * @param {*} result the result of the task
 * @param {string=} opt_error an optional error
 * @private
 */
H.map.provider.SpatialTileProvider.prototype.onRenderTaskDone_ = function(task, result, opt_error) {
  'use strict';
  var tileKey = task['data'][0];
  if (result) {
    this.tileCache_.add(tileKey, result, 1);
  }
  delete this.pendingTileRequests_[tileKey];
  this.dispatchUpdate();
};


//#####################################################  CACHE  ########################################################
/**
 * Callback for the tile-with-canvas cache
 * @param {string} tileKey the id of the dropped tile
 * @param {H.map.provider.SpatialTile} tile the dropped tile
 * @private
 */
H.map.provider.SpatialTileProvider.prototype.onTileDropped_ = function(tileKey, tile) {
  'use strict';
  // Release the canvas:
  if (tile['data']) {
    this.canvasPool_.push(tile.releaseCanvas());
  }
  // Put the tile to the next cache:
  this.dataCache_.add(tileKey, tile, 1);
};


/**
 * Callback for the tile-with-renderData cache
 * @param {string} tileKey the id of the dropped tile
 * @param {H.map.provider.SpatialTile} tile the dropped tile
 * @private
 */
H.map.provider.SpatialTileProvider.prototype.onDataDropped_ = function(tileKey, tile) {
  'use strict';
  // Cleanup the tile:
  tile.releaseData();
};


//##################################################  CANVAS POOL  #####################################################
/**
 * This method pops a canvas from the canvas pool or creates a new one if no
 * canvas elements are available in the pool.
 * @return {HTMLCanvasElement} a canvas
 * @private
 */
H.map.provider.SpatialTileProvider.prototype.popCanvas_ = function() {
  'use strict';
  return this.canvasPool_.pop() || document.createElement('canvas');
};


/**
 * Implementation of {@link H.util.Disposable#disposeInternal}
 */
H.map.provider.SpatialTileProvider.prototype.disposeInternal = function() {
  'use strict';
  var key;
  // First we try to cancel all pending tile requests:
  for (key in this.pendingTileRequests_) {
    this.pendingTileRequests_[key].cancel();
  }
  // Then we clear the caches:
  this.tileCache_.removeAll();
  this.dataCache_.removeAll();
  // Finally we clear the canvasPool:
  this.canvasPool_.length = 0;

  // And even more finally we call the shadows disposeInternal:
  H.map.provider.SpatialTileProvider.superClass_.disposeInternal.call(this);
};


/**
 * Configuration object which can be use to initialize the SpatialTileProvider.
 * @typedef {{
 *   tileSize: (number|undefined),
 *   tileCacheSize: (number|undefined),
 *   dataCacheSize: (number|undefined),
 *   pixelRatio: (number)
 * }}
 * @property {number=} tileSize the size of the tiles rendered by this layer
 *  for polylines and polygons (must be power of 2, default is {@code 256})
 * @property {number=} tileCacheSize the number of fully rendered spatial tiles
 *  that are cached for immediate reuse, default is {@code 32}
 * @property {number=} dataCacheSize the number of tiles to cache which have
 *  render data only, default is {@code 512}
 * @property {number} pixelRatio The pixelRatio to use for over-sampling in cases of high-resolution displays
 */
H.map.provider.SpatialTileProvider.Options;
