goog.provide('H.util.ParallelQueue');

goog.require('H.util.AsyncQueue');



/**
 * This class represents a task queue which processes the alloted number (maxParallel) work items in parallel.
 * The parallel queue can process long running tasks and will defer processing of queued items until a slot
 * is freed by an item finishing.
 *
 * Note: This class DOES NOT implement multi-threading. Rather it can be used for asynchronous tasks like
 * image loading which are done off-thread by the browser. For synchronous processing of work items consider
 * using the {@link H.util.SequentialQueue}
 *
 * @export
 * @constructor
 * @extends {H.util.AsyncQueue}
 * @param {number} maxParallel the maximum number of tasks to process in parallel
 * @param {function(H.util.AsyncQueue.Task, *)} processFn the function which implements the acutal processing of
 *  tasks
 */
H.util.ParallelQueue = function(maxParallel, processFn) {
  H.util.ParallelQueue.superClass_.constructor.call(
      this,
      new H.util.ParallelQueue.Mode(maxParallel),
      processFn);
};
goog.inherits(H.util.ParallelQueue, H.util.AsyncQueue);



/**
 * @constructor
 * @implements {H.util.AsyncQueue.Mode}
 * @param {number} maxParallel the maximum number of parallel tasks to process per cycle
 */
H.util.ParallelQueue.Mode = function(maxParallel) {
  if (isNaN(maxParallel) || maxParallel < 0) {
    throw new H.lang.InvalidArgumentError(H.util.ParallelQueue, 1,
        'Argument "maxParallel" must be a positive number');
  }
  this.maxParallel = maxParallel;
};


/**
 * @override
 */
H.util.ParallelQueue.Mode.prototype.next = function(queuedItems, processingItems) {
  if (queuedItems.length && processingItems.length < this.maxParallel) {
    return queuedItems.shift();
  } else {
    return null;
  }
};


/**
 * @override
 */
H.util.ParallelQueue.Mode.prototype.cycle = function() {};
