goog.provide('H.map.render.RenderEngine');


goog.require('H.lang.InvalidArgumentError');
goog.require('H.math.Rect');
goog.require('H.util.EventTarget');



/**
 * Abstract RenderEngine class. Render engines are used to render geographical position
 * set on the view model to the actual screen (viewport element). The rendered result may be
 * different for different engines, since every engine uses its onw capabilities and specific
 * implementation to present the current view model data in best possible way. In example
 * for 2D engines user will se mostly 2 dimensional flat map presented by tiles, on the other
 * hand 3D engines like panorama will display the same coordinates as a 'street view'.
 * In other words RenderEngine is responsible for managing the rendering loop as well as
 * for synchronizing rendered data with the view model and the data model.
 * It manages what and how gets rendered inside the current viewport.
 *
 *
 * @constructor
 * @param {H.map.ViewPort} viewPort
 * @param {H.map.ViewModel} viewModel
 * @param {H.map.DataModel} dataModel
 * @param {H.map.render.RenderEngine.Options} options
 * @extends {H.util.EventTarget}
 * @abstract
 * @export
 * @publish
 */
H.map.render.RenderEngine = function(viewPort, viewModel, dataModel, options) {
  'use strict';

  var rect = new H.math.Rect(0, 0, 0, 0);

  H.map.render.RenderEngine.superClass_.constructor.call(this);

  this.pixelRatio = options['pixelRatio'];


  /**
   * A cache for view-port bounds
   * @protected
   * @type {Object.<H.map.render.RenderEngine.ViewPortBoundsType, ?(H.math.Rect)>}
   */
  this.viewPortBounds = {
    1: rect.clone(),
    2: rect.clone(),
    4: rect.clone()
  };


  /**
   * @protected
   * @type {H.map.ViewPort}
   */
  this.viewPort = viewPort;

  if (!viewPort) {
    throw new H.lang.InvalidArgumentError(H.map.render.RenderEngine,
        0, viewPort);
  }
  this.updateViewPortBounds_();
  viewPort.addEventListener(viewPort.EventType.UPDATE, this.onResize, false, this);
  this.startInteraction = H.lang.bind(this.startInteraction, this);
  this.interaction = H.lang.bind(this.interaction, this);
  this.endInteraction = H.lang.bind(this.endInteraction, this);
  viewPort['startInteraction'] = this.startInteraction;
  viewPort['interaction'] = this.interaction;
  viewPort['endInteraction'] = this.endInteraction;


  /**
   * @protected
   * @type {H.map.ViewModel}
   */
  this.viewModel = viewModel;

  if (!viewModel) {
    throw new H.lang.InvalidArgumentError(H.map.render.RenderEngine,
        1, viewModel);
  }
  this.startControl = H.lang.bind(this.startControl, this);
  this.control = H.lang.bind(this.control, this);
  this.endControl = H.lang.bind(this.endControl, this);
  viewModel['startControl'] = this.startControl;
  viewModel['control'] = this.control;
  viewModel['endControl'] = this.endControl;
  viewModel.addEventListener(viewModel.EventType.UPDATE, this.onViewUpdate, false, this);


  /**
   * @protected
   * @type {H.map.DataModel}
   */
  this.dataModel = dataModel;

  if (!dataModel) {
    throw new H.lang.InvalidArgumentError(H.map.render.RenderEngine,
        2, dataModel);
  }

};
H.lang.inherits(H.map.render.RenderEngine, H.util.EventTarget);


/**
 * @protected
 * @type {number}
 */
H.map.render.RenderEngine.prototype.pixelRatio;


/**
 * The enumeration type of view-port bounds
 * @const
 * @enum {number}
 */
H.map.render.RenderEngine.ViewPortBoundsType = {
  /**
   * The view-bounds according the margined ViewPort's size, respecting the possibly shifted padding center
   * @const
   */
  MARGINED: 1,
  /**
   * The view-bounds according the visible ViewPort's size, respecting the possibly shifted padding center
   * @const
   */
  VISIBLE: 2,
  /**
   * The view-bounds according the padded ViewPort's size, respecting the possibly shifted padding center
   * @const
   */
  PADDED: 4
};


/**
 * To request the bounding box according the specified view port bounds type
 * @export
 * @abstract
 * @param {H.map.render.RenderEngine.ViewPortBoundsType} type The type of the requested view-bounds.
 * @param {number} margin A positive or negative margin in screen pixel to apply on each side of the bounds
 * @return {H.geo.Rect}
 * @method
 */
H.map.render.RenderEngine.prototype.getBBox = goog.abstractMethod;


/**
 * Updates the viewPortBounds_
 * @private
 */
H.map.render.RenderEngine.prototype.updateViewPortBounds_ = function() {
  'use strict';
  var viewPort = this.viewPort,
      width = viewPort['width'],
      height = viewPort['height'],
      padding = viewPort['padding'],
      paddingTop = padding['top'],
      paddingLeft = padding['left'],
      paddingBottom = padding['bottom'],
      paddingRight = padding['right'],
      margin = viewPort['margin'],
      left = -(width + paddingLeft - paddingRight) / 2 << 0,
      top = -(height + paddingTop - paddingBottom) / 2 << 0,
      right = width + left,
      bottom = height + top,
      viewPortBounds = this.viewPortBounds,
      pixelRatio = this.pixelRatio;

  viewPortBounds[H.map.render.RenderEngine.ViewPortBoundsType.MARGINED].set(
      (left - margin) * pixelRatio, (top - margin) * pixelRatio,
      (right + margin) * pixelRatio, (bottom + margin) * pixelRatio);
  viewPortBounds[H.map.render.RenderEngine.ViewPortBoundsType.VISIBLE].set(
      left * pixelRatio, top * pixelRatio,
      right * pixelRatio, bottom * pixelRatio);
  viewPortBounds[H.map.render.RenderEngine.ViewPortBoundsType.PADDED].set(
      (left + paddingLeft) * pixelRatio, (top + paddingTop) * pixelRatio,
      (right - paddingRight) * pixelRatio, (bottom - paddingBottom) * pixelRatio);

};


/**
 * Callback to react on "sizechange", "paddingchange" and "marginchange" events from the view port.
 * @protected
 */
H.map.render.RenderEngine.prototype.onResize = function() {
  'use strict';
  this.updateViewPortBounds_();
};


/**
 * @protected
 */
H.map.render.RenderEngine.prototype.disposeInternal = function() {
  'use strict';
  var viewModel = this.viewModel,
      viewPort = this.viewPort;

  viewModel.removeEventListener(viewModel.EventType.UPDATE, this.onViewUpdate, false, this);
  if (viewModel['startControl'] === this.startControl) {
    viewModel['startControl'] = H.map.ViewModel.prototype.startControl;
  }
  if (viewModel['control'] === this.control) {
    viewModel['control'] = H.map.ViewModel.prototype.control;
  }
  if (viewModel['endControl'] === this.endControl) {
    viewModel['endControl'] = H.map.ViewModel.prototype.endControl;
  }

  viewPort.removeEventListener(viewPort.EventType.UPDATE, this.onResize, false, this);
  if (viewPort['startInteraction'] === this.startInteraction) {
    viewPort['startInteraction'] =
        H.map.ViewPort.prototype.startInteraction;
  }
  if (viewPort['interaction'] === this.interaction) {
    viewPort['interaction'] = H.map.ViewPort.prototype.interaction;
  }
  if (viewPort['endInteraction'] === this.endInteraction) {
    viewPort['endInteraction'] = H.map.ViewPort.prototype.endInteraction;
  }
};


/**
 * Helper function to check if the interaction is bifocal,
 * returns true if all provided screen coords are numbers
 * @protected
 * @param {number|undefined} x
 * @param {number|undefined} y
 * @param {number|undefined} bx
 * @param {number|undefined} by
 * @return {boolean}
 */
H.map.render.RenderEngine.prototype.isBifocalInteraction = function(x, y, bx, by) {
  'use strict';
  return x === +x && y === +y && bx === +bx && by === +by;
};


/**
 * A method to signal the begin of a control operation. A currently ongoing
 * control operation stops without kinetic effect. A currently control
 * operation also stops if viewModel was updated or beginInteraction was
 * called.
 * @param {H.util.kinetics.IKinetics=} opt_kinetics A kinetics to use when
 * the control ends. If {@code null}, no kinetic effect is performed.
 * @param {number=} opt_atX The X component of the screen coordinate where a
 * view control operation is applied. If <code>NaN</code> the screen center is
 * taken instead.
 * @param {number=} opt_atY The Y component of the screen coordinate where a
 * view control operation is applied. If <code>NaN</code> the screen center is t
 * aken instead.
 * @abstract
 * @method
 */
H.map.render.RenderEngine.prototype.startControl = goog.abstractMethod;


/**
 * A method to process control operation.
 * If no control operation is currently ongoing ({@code beginControl} was not
 * triggered before or {@code endControl} was triggered meanwhile) the
 * {@code control} call is ignored.
 * @param {number} moveX The move of the camera in screen x axis as
 * levels per millisecond.
 * @param {number} moveY The move of the camera in screen x axis as
 * levels per millisecond.
 * @param {number} moveZ The move of the camera in screen z axis as levels
 * per millisecond.
 * @param {number} angleX The rotation of the camera on screen x axis as
 * degrees per millisecond.
 * @param {number} angleY The rotation of the camera on screen y axis as
 * degrees per millisecond.
 * @param {number} angleZ The rotation of the camera on screen z axis as
 * degrees per millisecond.
 * @param {number} zoom The zoom of the view as zoom levels per millisecond, ignored in <code>"cam"</code> mode.
 * @abstract
 * @method
 */
H.map.render.RenderEngine.prototype.control = goog.abstractMethod;


/**
 * A method to signal the end of an control operation. If an {@code kinetics}
 * argument was passed to {@code beginControl} call, the kinetic effect is
 * performed.
 * If no control operation is currently ongoing ({@code beginControl} was not
 * triggered before or an {@code endControl} was triggered meanwhile) the
 * {@code endControl} call is ignored.
 * @param {boolean=} opt_preventKinetics if set to true kinetics
 * will not be fired
 * @abstract
 * @method
 */
H.map.render.RenderEngine.prototype.endControl = goog.abstractMethod;


/**
 * A method to signal the begin of an interaction. A currently ongoing
 * interaction is stopped without kinetic effect.
 * Current interaction stopps if view model was changed or beginControl was
 * called.
 * @param {H.map.render.RenderEngine.InteractionModifiers} modifiers
 * @param {H.util.kinetics.IKinetics=} opt_kinetics
 * @abstract
 * @method
 */
H.map.render.RenderEngine.prototype.startInteraction = goog.abstractMethod;


/**
 * A method to process an interaction based on screen coordinates of the
 * view port. If {@code beginInteraction} was triggered with a {@code modifiers}
 * argument of {@code COORD} combined with any other modifier but
 * {@code interaction} is called without bifocal coordinate then the
 * {@code COORD} modifier is ignored.
 * If no interaction is currently ongoing ({@code beginInteraction} was not
 * triggered before or an {@code endInteraction} was triggered meanwhile) the
 * {@code interact} call is ignored.
 * @param {number} x viewport x coordinate
 * @param {number} y viewport y coordinate
 * @param {number=} opt_bx x coordinate for second pointer/touch if present
 * @param {number=} opt_by y coordinate for secong pointer/touch if present
 * @param {number=} opt_timestamp known timestamp which should be passed
 * @abstract
 * @method
 */
H.map.render.RenderEngine.prototype.interaction = goog.abstractMethod;


/**
 * Method signals the end of an interaction. If an {@code kinetics} was passed
 * to the previous {@code beginInteraction} call, the kinetic effect is
 * performed. If no interaction is currently ongoing ({@code beginInteraction}
 * was not triggered before or an {@code endInteraction} was triggered
 * meanwhile) the <code>endInteraction</code> call is ignored.
 * @param {boolean=} opt_preventKinetics
 * @abstract
 * @method
 */
H.map.render.RenderEngine.prototype.endInteraction = goog.abstractMethod;


/**
 * Method converts pixel coordinates relative to the viewport's origin into a
 * geographic coordinates.
 * @param {number} x The X-coordinate of the pixel position
 * @param {number} y The Y-coordinate of the pixel position
 * @return {H.geo.IPoint} The translated geographic position
 * @abstract
 * @method
 */
H.map.render.RenderEngine.prototype.screenToGeo = goog.abstractMethod;


/**
 * Method converts a geographic coordinates into a pixel position relative
 * to the viewport's origin.
 * @param {H.geo.IPoint} geoPoint
 * @return {?H.math.IPoint}
 * @abstract
 * @method
 */
H.map.render.RenderEngine.prototype.geoToScreen = goog.abstractMethod;


/**
 * Calculates the best CameraModel to show the provided bounding rectangle
 * @param {H.geo.Rect} bounds The geographical bounding rectangle to use
 * @param {boolean} keepCenter To indicate whether the center of the bounds should be the resulting center
 * @return {H.map.ViewModel.CameraData} The result, represented by the
 * properties zoom (number) and position (geo.Point)
 * @export
 * @abstract
 * @method
 */
H.map.render.RenderEngine.prototype.getCamForBounds = goog.abstractMethod;


/**
 * Method converts screen pixel coordinates to correct camera data object
 * @param {number} x X screen coordinate
 * @param {number} y Y screen coordinate
 * @return {H.map.ViewModel.CameraData}
 * @abstract
 * @method
 */
H.map.render.RenderEngine.prototype.screenToCameraData =
    goog.abstractMethod;


/**
 * Method returns visible objects under the given x/y screen coordinates. Array is sorted according to their drawing
 * order, topmost object has an index #0 in returned array.
 * @param {number} x x coordinate in screen space to probe renderer
 * @param {number} y y coordinate in screen space to probe renderer
 * @param {boolean=} opt_topMostOnly optional flag, if set method returns array with single element that is located
 * on top
 * @return {Array.<H.map.Object>}
 * @abstract
 * @method
 */
H.map.render.RenderEngine.prototype.getObjectsAt = goog.abstractMethod;


/**
 * Method returns object that contains data on engine's possible camera angles,
 * modes etc.
 * @return {Object}
 * @abstract
 * @method
 */
// TODO: capabilities reflection if necessary
H.map.render.RenderEngine.prototype.getCapabilities = goog.abstractMethod;


/**
 * Captures rectangular area of the surface.
 * Accepts only valid input: captured rect coordinates, should be contained by the surface
 * @param {number} x1 The X coordinate of the top left edge of the rect
 * @param {number} y1 The Y coordinate of the top left edge of the rect
 * @param {number} x2 The X coordinate of the bottom right edge of the rect
 * @param {number} y2 The Y coordinate of the bottom right edge of the rect
 * @return {HTMLCanvasElement} Captured
 */
H.map.render.RenderEngine.prototype.capture = function(x1, y1, x2, y2) {
  'use strict';
  return null;
};


/**
 * Method informs render engine about the current pointer position relative to
 * the viewport. The position can be used by the render engine to provide
 * hover effects, tool tips etc. If the x argument is NaN the render engine
 * treats the given position outside the viewport.
 * @param {number} x The X coordinate of the pointer.
 * @param {number} y The Y coordinate of the pointer.
 * @abstract
 * @method
 * @export
 */
H.map.render.RenderEngine.prototype.setPointer = goog.abstractMethod;


/**
 * Method performs enter animation if one is available for the engine.
 * @abstract
 * @method
 */
H.map.render.RenderEngine.prototype.enter = goog.abstractMethod;


/**
 * Method performs exit animation if one is available for the engine. Method
 * must not shut down the engine.
 * @param {function()} callback
 * @abstract
 * @method
 */
H.map.render.RenderEngine.prototype.exit = goog.abstractMethod;


/**
 * Protected method that facilitates engine's container fade out before exit
 * @protected
 * @param {Element} container
 * @param {function()} callback
 */
H.map.render.RenderEngine.prototype.fadeout = function(container, callback) {
  'use strict';
  container.style.transition = 'opacity 1.5s';
  container.addEventListener('transitionend', function(e) {
    callback();
  }, false);
  container.style.opacity = 0;
};


/**
 * Interaction modifiers is a set of bitmask values that can be passed to
 * {@link H.map.render.RenderEngine#beginInteraction}
 * @typedef {Object}
 * @export
 */
H.map.render.RenderEngine.InteractionModifiers = {
  /**
   * @type {number}
   */
  'ZOOM': 1,

  /**
   * @type {number}
   */
  'HEADING': 2,

  /**
   * @type {number}
   */
  'TILT': 4,

  /**
   * @type {number}
   */
  'INCLINE': 8,

  /**
   * @type {number}
   */
  'COORD': 16
};


//#################################################  EVENTS  ###########################################################
/**
 * Events fired by RenderingEngine
 * @enum {string}
 */
H.map.render.RenderEngine.prototype.EventType = {
  RENDER: 'render'
};


/**
 * Fired when one of the layers or the engine (all layers) has been rendered completely.
 * The event target references either the completed layer or the render engine itself
 * @publish
 * @event H.map.render.RenderEngine#render
 * @type {H.util.Event}
 */


//######################################################################################################################
/**
 * @export
 * @enum {number}
 */
H.map.render.RenderEngine.EngineType = {
  'P2D': 0,
  'PANORAMA': 1
};


/**
 * Options which may be used to initialize new RenderEngine instance
 *
 * @typedef {{
 *   pixelRatio: (number)
 * }}
 * @property {number} pixelRatio The pixelRatio to use for over-sampling in cases of high-resolution displays
 */
H.map.render.RenderEngine.Options;
