goog.provide('H.util.kinetics.KineticMove');

goog.require('H.lang');



/**
 * KineticMove class defines basic physics regarding the decelerated move
 * (kinetics) which is used by the map for kinetic move after the drag is performed.
 * It contains all needed functionality to calculate deceleration, path current
 * speed in time for the deccelerated/accelerated move.
 * @export
 * @final
 * @constructor
 * @param {number} acceleration
 * @param {number=} opt_speed
 */
H.util.kinetics.KineticMove = function(acceleration, opt_speed) {
  'use strict';

  if (H.lang.isNaN(this.acceleration_ = +acceleration)) {
    throw new H.lang.InvalidArgumentError(H.util.kinetics.KineticMove, 0, acceleration);
  }

  this.duration_ = 0;
  this.speed_ = 0;
  if (opt_speed) {
    this.setInitialSpeed(opt_speed);
  }
};


/**
 * This method sets initial speed for (dec/ac)celeration.
 * @param {number} speed
 * @return {H.util.kinetics.KineticMove} the instance itself
 * @export
 */
H.util.kinetics.KineticMove.prototype.setInitialSpeed = function(speed) {
  'use strict';
  this.speed_ = speed;
  this.duration_ = this.getDuration();
  return this;
};


/**
 * This method returns duration of the accelerated move for current initial speed.
 * @return {number}
 * @export
 */
H.util.kinetics.KineticMove.prototype.getDuration = function() {
  'use strict';
  return Math.abs(this.speed_ / this.acceleration_);
};


/**
 * This method returns current speed over time.
 * @param {number} time
 * @return {number}
 * @export
 */
H.util.kinetics.KineticMove.prototype.getCurrentSpeed = function(time) {
  'use strict';
  return this.speed_ + this.acceleration_ * time;
};


/**
 * This method returns current path in time.
 * @param {number} time
 * @return {number}
 * @export
 */
H.util.kinetics.KineticMove.prototype.getCurrentPath = function(time) {
  'use strict';
  return this.speed_ * time + this.acceleration_ * time * time / 2;
};


