goog.provide('H.map.ViewModel');

goog.require('H.geo.Point');
goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.map.ChangeEvent');
goog.require('H.map.IControl');
goog.require('H.util.ChangeEvent');
goog.require('H.util.EventTarget');
goog.require('H.util.events');



/**
 * This class represents a view of the map. It consists of a virtual camera
 * and a look-at point both of which have a position in geo-space and orientation
 * angles. The view model allows to change the values of these objects in order
 * to move or rotate the map or zoom in and out.
 *
 * The view model can be manipulated by using two objects: a virtual camera or
 * the a look-at point. A virtual camera has a position and orientation (pitch,
 * yaw and roll) in geo-space. A look-at point allows to change the postion and
 * orientation of the virtual camera with respect to the point that is being
 * looked at. The look-at point has a position in geo-space and orientation
 * (heading, tilt and incline) and a distance (the distance of the camera with
 * respect to the look-at point). Setting look-at point values influences the
 * camera's position and orientation and vice-versa.
 *
 * Modifying the view of a map is asynchronous as the view model will notify the
 * renderer of a change of its state and trigger the renderer.
 *
 * A map renderer can choose to ignore or even correct certain values depending
 * on its capabilities. For example a 2D map renderer will ignore tilt values
 * and correct tilt values to be 0 on the view model in order to keep the
 * integrity of the view model.
 *
 * In order to be notified of changes to the model that originate from the
 * renderer the view model dispatches an event "sync" whenever the
 * renderer synchronizes the requested changes to the view with it's own
 * internal state.
 *
 * @publish
 * @export
 * @extends {H.util.EventTarget}
 * @implements {H.map.IControl}
 * @final
 * @constructor
 */
H.map.ViewModel = function() {
  'use strict';

  H.map.ViewModel.superClass_.constructor.call(this);

  this.requested_ = {};

  this.renderedCameraData_ = {
    'position': new H.geo.Point(0, 0),
    'pitch': 0,
    'yaw': 0,
    'roll': 0,
    'fov': 70
  };

  this.renderedZoom_ = 0;

};
H.lang.inherits(H.map.ViewModel, H.util.EventTarget);


//#################################################  EVENTS  ###########################################################
/**
 * Events fired by ViewModel
 * @enum {string}
 */
H.map.ViewModel.prototype.EventType = {
  UPDATE: 'update',
  SYNC: 'sync'
};


/**
 * Fired when this view-model's data changes
 * @publish
 * @event H.map.ViewModel#update
 * @type {H.util.ChangeEvent}
 */

/**
 * Fired when the rendered map is synchronized with this view-model's data
 * @publish
 * @event H.map.ViewModel#sync
 * @type {H.map.ChangeEvent}
 */


//######################################################################################################################
/**
 * This method copies camera data to target object
 * @private
 *
 * @param {Object} target the target object to which to transfer the data values
 * @param {H.map.ViewModel.CameraData} source the values to be transferred to the target
 * @return {H.map.ViewModel.CameraData} object representing current camera (state) values
 */
H.map.ViewModel.prototype.transferCameraData_ = function(target, source) {
  'use strict';
  return /** @type {H.map.ViewModel.CameraData} */ (this.transferData_(target, source, this.CAMERA_KEYS_));
};


/**
 * A list of camera property keys
 * @private
 * @const
 * @type {Array.<string>}
 */
H.map.ViewModel.prototype.CAMERA_KEYS_ = H.lang.tokenize('position pitch yaw roll fov zoom');


/**
 * Mapping of camara property names to bit values according the bit values from H.map.ChangeEvent constants
 * which have the same names but upper case
 * @private
 * @type {Object.<string,number>}
 */
H.map.ViewModel.CamToSyncFlags_ = H.lang.tokenize('fov pitch position roll yaw zoom').
    reduce(function(table, key) {
      // e.g. 'fov': H.map.ChangeEvent.prototype['FOV']
      table[key] = H.map.ChangeEvent.prototype[key.toUpperCase()];
      return table;
    }, {});


/**
 * This method copies camera data from a source to a target object
 * @private
 *
 * @param {Object} target The target object to which to transfer the data values
 * @param {Object} source The values to be transferred to the target
 * @param {Array.<string>} keys The keys of the properties to transfer
 * @return {Object} the target object
 */
H.map.ViewModel.prototype.transferData_ = function(target, source, keys) {
  'use strict';
  var i = keys.length,
      key,
      targetVal,
      value;

  // reset flags and memorized values from previous synchronization
  /**
   * @private
   */
  this.syncFlags_ = 0;

  /**
   * @private
   * @type {H.map.ViewModel.CameraData}
   */
  this.oldCamData_ = /** @type {H.map.ViewModel.CameraData} */ ({});

  /**
   * @private
   * @type {H.map.ViewModel.CameraData}
   */
  this.newCamData_ = /** @type {H.map.ViewModel.CameraData} */ ({});

  while (i--) {
    key = keys[i];
    value = source[key];
    targetVal = target[key];
    if (H.lang.isDefAndNotNull(value)) {
      this.oldCamData_[key] = targetVal;
      this.newCamData_[key] = value;
      target[key] = value;
      // handle case where value is IPoint
      if ((H.geo.isIPoint(value) && H.geo.isIPoint(targetVal) &&
          (value['lat'] !== targetVal['lat'] ||
          value['lng'] !== targetVal['lng'])) ||
          // let primitive values through
          (!H.geo.isIPoint(value) && value !== targetVal)) {
        this.syncFlags_ |= H.map.ViewModel.CamToSyncFlags_[key];

      }
    } else {
      delete target[key];
    }
  }
  return target;
};


/**
 * This method returns the camera data, which is currently rendered.
 * @return {H.map.ViewModel.CameraData} the current rendered camera data
 * @publish
 * @export
 */
H.map.ViewModel.prototype.getCameraData = function() {
  'use strict';
  return this.transferCameraData_({}, this.renderedCameraData_);
};


/**
 * This method sets new camera data to be processed by the renderer.
 * @param {H.map.ViewModel.CameraData} data the values to be modified
 * @fires H.map.ViewModel#update
 * @return {H.map.ViewModel} this view model object
 * @publish
 * @export
 */
H.map.ViewModel.prototype.setCameraData = function(data) {
  'use strict';

  var position,
      animate = data['animate'];

  if (!data) {
    throw new H.lang.InvalidArgumentError(this.setCameraData, 0, data);
  }

  position = data['position'];

  if (position && !H.geo.isIPoint(position)) {
    throw new H.lang.InvalidArgumentError(this.setCameraData,
        0, 'position');
  }

  // new zoom takes precedence over requested in previous call
  // and rendered zoom is used as a fallback if no zoom was requested
  this.requested_['zoom'] = !H.lang.isNaN(data['zoom']) ? data['zoom'] :
      (!H.lang.isNaN(this.requested_['zoom']) ? this.requested_['zoom'] :
          this.renderedZoom_);

  this.requested_ = {
    'animate': animate,
    'zoom': this.requested_['zoom'],
    'camera': this.transferCameraData_({}, data)
  };

  this.dispatchEvent(new H.map.ViewModel.UpdateEvent(this.requested_));

  return this;
};


/**
 * A callback used by an engine to update current view values according to real rendered values.
 * @param {H.map.ViewModel.CameraData} renderedCameraData the updated
 *   cameraData from the renderer
 * @fires H.map.ViewModel#sync
 */
H.map.ViewModel.prototype.synchronize = function(renderedCameraData) {
  'use strict';
  var zoom = renderedCameraData['zoom'];
  this.transferCameraData_(this.renderedCameraData_, renderedCameraData);

  if (zoom !== this.renderedZoom_) {
    // zoom is a special case
    this.syncFlags_ |= H.map.ViewModel.CamToSyncFlags_['zoom'];
  }
  this.oldCamData_['zoom'] = this.renderedZoom_;
  this.newCamData_['zoom'] = zoom;
  this.renderedZoom_ = H.lang.isDefAndNotNull(zoom) ?
      zoom : this.renderedZoom_;

  this.renderedCameraData_['zoom'] = this.renderedZoom_;

  this.requested_ = {};
  this.dispatchEvent(new H.map.ChangeEvent(this.EventType.SYNC,
      this.newCamData_, this.oldCamData_, this.syncFlags_));
};


/**
 * This method sets a new zoom level to be processed by the renderer
 * @param {number} zoom the new zoom level
 * @param {boolean} animate
 * @fires H.map.ViewModel#update
 * @publish
 * @export
 */
H.map.ViewModel.prototype.setZoom = function(zoom, animate) {
  'use strict';

  if (H.lang.isNaN(zoom = +zoom)) {
    throw new H.lang.InvalidArgumentError(this.setZoom, 0, zoom);
  }
  this.requested_ = {
    'animate': animate,
    'zoom': zoom,
    'camera': this.requested_['camera'] || this.renderedCameraData_
  };
  this.dispatchEvent(new H.map.ViewModel.UpdateEvent(this.requested_));
};


/**
 * This method returns the zoom level that is currently rendered.
 * @return {number} current zoom level (scale)
 * @publish
 * @export
 */
H.map.ViewModel.prototype.getZoom = function() {
  'use strict';
  return this.renderedZoom_;
};


/**
 * This method returns the currently requested data.
 * @return {H.map.ViewModel.CameraData} last requested cam/view data
 * @publish
 * @export
 */
H.map.ViewModel.prototype.getRequestedCameraData = function() {
  'use strict';
  var requestedCameraData = this.requested_['camera'];
  requestedCameraData['zoom'] = H.lang.isNumber(this.requested_['zoom']) ?
      this.requested_['zoom'] : requestedCameraData['zoom'];
  return requestedCameraData;
};


/**
 * A method to signal the begin of a control operation.
 * @param {H.util.kinetics.IKinetics=} opt_kinetics kinetics settings
 * @param {number=} opt_atX x screen coordinate at which control has started
 * @param {number=} opt_atY y screen coordinate at which control has started
 * @abstract
 * @method
 * @export
 * @publish
 */
H.map.ViewModel.prototype.startControl = goog.abstractMethod;
// function(mode, opt_kinetics, opt_atX, opt_atY) {}


/**
 * A method to set the values for a continuously modification of the ViewModel on different axes.
 * If the current render engine doesn't support certain modifications then they are ignored.
 * @abstract
 * @method
 * @publish
 * @export
 * @param {number} moveX The movement on x-axis as levels per millisecond
 *        where a level correlates to the distance between camera and look-at point.
 * @param {number} moveY The movement on y-axis as levels per millisecond
 *        where a level correlates to the distance between camera and look-at point
 * @param {number} moveZ The movement on z-axis as levels per millisecond.
 * @param {number} angleX The rotation of on screen's x axis as degrees per millisecond.
 * @param {number} angleY The rotation of on screen's y axis as degrees per millisecond.
 * @param {number} angleZ The rotation of on screen's z axis as degrees per millisecond.
 * @param {number=} opt_zoom The modification of the zoom level as levels per millisecond
 */
H.map.ViewModel.prototype.control = goog.abstractMethod;
//H.map.ViewModel.prototype.control = function(moveX, moveY, moveZ, angleX, angleY, angleZ, opt_zoom) { };


/**
 * A method to signal the end of a control operation.
 * @param {boolean=} opt_preventKinetics A flag to indicate whether a kinetic effect is performed
 * @param {function(H.map.ViewModel.CameraData)=} opt_adjustView An callback to adjust the final ViewModel by
 *        modifying the passed camera data.
 * @abstract
 * @method
 * @export
 * @publish
 */
H.map.ViewModel.prototype.endControl = goog.abstractMethod; // function(opt_preventKinetics) { };


/**
 * Defines camera's properties.
 * @typedef {{
 *   zoom: (number|undefined),
 *   position: (H.geo.IPoint),
 *   pitch: (number|undefined),
 *   yaw: (number|undefined),
 *   roll: (number|undefined)
 * }}
 * @property {number=} zoom zoom level to be used by rendering engine
 * @property {H.geo.IPoint} position the position of the virtual camera in geo-space
 * @property {number=} pitch the rotation of the virtual camera along its local x-axis
 * @property {number=} yaw the rotation of the virtual camera along its local y-axis
 * @property {number=} roll the rotation of the virtual camera along its local z-axis
 * @property {number=} fov
 * @publish
 */
H.map.ViewModel.CameraData;


/**
 * Defines currently requested view data, which will be rendered by the map.
 * @publish
 *
 * @typedef {{
 *   camera: (H.map.ViewModel.CameraData|undefined),
 *   zoom: (number|undefined),
 *   animate: (boolean|undefined)
 * }} H.map.ViewModel.RequestedData
 *
 * @property {H.map.ViewModel.CameraData=} camera The requested camera properties
 * @property {number=} zoom The requested zoom level
 * @property {boolean=} animate indicates if the requested transition should be animated
 */
H.map.ViewModel.RequestedData;



//#########################################  ViewModel.UpdateEvent  ####################################################
/**
 * Update event is fired whenever view model data is changed. It contains property which hold currently requested data
 * @constructor
 * @param {H.map.ViewModel.RequestedData} requested
 * @extends {H.util.Event}
 * @publish
 */
H.map.ViewModel.UpdateEvent = function(requested) {
  'use strict';
  H.map.ViewModel.UpdateEvent.superClass_.constructor.call(this, H.map.ViewModel.prototype.EventType.UPDATE);

  /**
   * The requested view data
   * @type {H.map.ViewModel.RequestedData}
   */
  this['requested'] = requested;
};
H.lang.inherits(H.map.ViewModel.UpdateEvent, H.util.Event);
