goog.provide('H.map.provider.Tile');



/**
 * Generic Tile object which represents a part of the world
 * fiting into the Tile area represented by the Tiel coordinates (x - row, y - column)
 * and the zoom level (z). Number of tiles at particular zoom level (which means
 * number of areas into world is being splitted) is defined
 * as following: numberOfRows = numberOfColumns = 2^zoomlevel
 * @publish
 * @export
 * @constructor
 * @param {number} x x tile coordinate (row)
 * @param {number} y y tile coordinate (column)
 * @param {number} z tile zoom level
 * @param {*} data generic data object which cooresponds to the given coordinates
 */
H.map.provider.Tile = function(x, y, z, data) {
  'use strict';

  /**
   * Unique tile key generated by provider
   * @type {string}
   * @name H.map.provider.Tile#key
   * @publish
   */
  this['key'] = '';

  /**
   * Tile column
   * @type {number}
   * @name H.map.provider.Tile#x
   * @publish
   */
  this['x'] = x;

  /**
   * Tile row
   * @type {number}
   * @name H.map.provider.Tile#y
   * @publish
   */
  this['y'] = y;

  /**
   * Tile zoom level
   * @type {number}
   * @name H.map.provider.Tile#z
   * @publish
   */
  this['z'] = z;

  /**
   * Tile data (an image for example)
   * @type {*}
   * @name H.map.provider.Tile#data
   * @publish
   */
  this['data'] = data;

  /**
   * This property holds a boolean flag indicating whether this tile is
   * still valid (true) or whether it should be re-fetched (false)
   * @type {boolean}
   * @name H.map.provider.Tile#valid
   * @publish
   */
  this['valid'] = true;
};

