goog.provide('H.map.provider.ImageTileProvider');

goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.map.provider.RemoteTileProvider');
goog.require('H.net.ImageLoader');



/**
 * An ImageTileProvider uses network service to provide bitmap images as tiles.
 * @publish
 * @export
 * @final
 *
 * @extends {H.map.provider.RemoteTileProvider}
 * @constructor
 * @param {H.map.provider.ImageTileProvider.Options} options configuration for tile provider
 */
H.map.provider.ImageTileProvider = function(options) {
  'use strict';
  var getURL = 'getURL';

  if (!options || !H.lang.isFunction(options[getURL])) {
    throw new H.lang.InvalidArgumentError(H.map.provider.ImageTileProvider, 0, 'options.' + getURL);
  }

  H.map.provider.ImageTileProvider.superClass_.constructor.call(this, options);

  /**
   * Creates the URL for a a specified tile
   * @private
   * @param {number} x The x component of the tile address
   * @param {number} y The y component of the tile address
   * @param {number} z The zoom component of the tile address
   * @return {string} the URL for the specified tile
   */
  this.getURL_ = options[getURL];

  this['tileSize'] = options['tileSize'] || 256;

  /**
   * @override
   */
  this['entryWeight'] = this['tileSize'];

  /**
   * Current cross-origin settings for loading images
   * @private
   * @type {string}
   */
  this.crossOrigin_ = options['crossOrigin'];
};
H.lang.inherits(H.map.provider.ImageTileProvider, H.map.provider.RemoteTileProvider);


/**
 * Size of a tile image supported by the provider
 * @type {number}
 * @publish
 */
H.map.provider.ImageTileProvider.prototype.tileSize;


/**
 * @override
 * @export
 */
H.map.provider.ImageTileProvider.prototype.requestInternal = function(x, y, z, onResponse, onError) {
  'use strict';
  var that = this,
      url = this.getURL_(x, y, z),
      req,
      timeoutId = H.global.setTimeout(function() {
        req = H.net.images.load(url, onResponse, onError, that.crossOrigin_);
      }, 150);

  // request throttling: chrome has issues with infinitely pending sockets, that's why we
  // don't want to issue request immediately and then cancel it.
  return {
    'cancel': function() {
      if (req) {
        req['cancel']();
      } else {
        H.global.clearTimeout(timeoutId);
      }
    }
  };
};


/**
 * Options which are used to initialize the ImageTileProvider object.
 * @typedef {{
 *   min: (number|undefined),
 *   max: (number|undefined),
 *   getURL: function(number, number, number): string,
 *   crossOrigin: (string|undefined),
 *   tileSize: (number|undefined)
 * }}
 * @publish
 * @property {number=} min The minimal supported zoom level, default is {@code 0}
 * @property {number=} max The max maximal supported zoom level, default is {@code 22}
 * @property {function(number, number, number)} getURL The function to create an URL for s specified tile
 * @property {boolean=} crossOrigin The string to be set for the crossOrigin attribute for loaded images
 * @property {number=} tileSize The size of a single tile in pixels, default is {@code 256}
 */
H.map.provider.ImageTileProvider.Options;
