goog.provide('H.map.provider.MarkerTileProvider');

goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.map.provider.RemoteTileProvider');



/**
 * A MarkerTileProvider uses network service to provide markers on tile basis.
 * @publish
 * @export
 * @final
 *
 * @extends {H.map.provider.RemoteTileProvider}
 * @constructor
 * @param {H.map.provider.MarkerTileProvider.Options} options configuration for tile provider
 */
H.map.provider.MarkerTileProvider = function(options) {
  'use strict';
  var requestData = 'requestData';
  if (!options || !H.lang.isFunction(options[requestData])) {
    throw new H.lang.InvalidArgumentError(H.map.provider.MarkerTileProvider, 0, 'options.' + requestData);
  }

  H.map.provider.MarkerTileProvider.superClass_.constructor.call(this, options);

  /**
   * User supplied function that fetches and produces map objects
   * @type {function(number, number, number, function(Array.<H.map.AbstractMarker>), Function):
   *      H.util.ICancelable}
   * @private
   */
  this.requestData_ = options['requestData'];

  /**
   * If set to true provides gives back dom markers
   * @type {boolean}
   * @private
   */
  this.providesDomMarkers_ = !!options['providesDomMarkers'] || false;
};
H.lang.inherits(H.map.provider.MarkerTileProvider, H.map.provider.RemoteTileProvider);


/**
 * @override
 * @export
 */
H.map.provider.MarkerTileProvider.prototype.requestInternal = function(x, y, z, onResponse, onError) {
  'use strict';
  var that = this,
      filterMarkers = function(data) {
        var filteredData = [],
            icon,
            marker,
            i = data.length;
        while (i--) {
          marker = data[i];
          // bind marker to provider and tile
          marker.tileKey = that['getTileKey'](x, y, z);
          marker.setParentEventTarget(that);

          icon = marker.getIcon();
          if (H.lang.isInstanceOf(marker, H.map.DomMarker) || icon.getState() === icon.State['READY']) {
            filteredData.push(data[i]);
          }
        }
        onResponse(/** @type {Array.<(H.map.Object|null)>} */ (filteredData));
      };
  return this.requestData_(x, y, z, filterMarkers, onError);
};


/**
 * To signal to this provider that a map object has been changed.
 * The method marks tile, that contains that objectm as invalid and triggers dispatchUpdate()
 * @publish
 *
 * @export
 * @param {!H.map.AbstractMarker} marker The map object to be invalidated
 * @param {H.math.BitMask} flags The flags indicating the types of occurred changes
 */
H.map.provider.MarkerTileProvider.prototype.invalidateObject = function(marker, flags) {
  'use strict';
  var tile;
  if (flags !== H.map.provider.Invalidations.Flag.NONE) {
    tile = /** @type {H.map.provider.Tile} */ (this.getCache().get(marker.tileKey));
    if (tile) {
      tile['valid'] = false;
    }
    this.dispatchUpdate();
  }
};


/**
 * Checks whether this provider is currently providing {@link H.map.DomMarker} map objects.
 * @publish
 * @export
 * @return {boolean}
 */
H.map.provider.MarkerTileProvider.prototype.providesDomMarkers = function() {
  'use strict';
  return this.providesDomMarkers_;
};


/**
 * Options which are used to initialize the MarkerTileProvider object.
 * @typedef {{
 *   min: (number|undefined),
 *   max: (number|undefined),
 *   requestData: (function(number, number, number, function(Array.<H.map.AbstractMarker>), Function):
 *      H.util.ICancelable),
 *   providesDomMarkers: (boolean|undefined)
 * }}
 * @publish
 * @property {number=} min The minimal supported zoom level, default is {@code 0}
 * @property {number=} max The maximal supported zoom level, default is {@code 22}
 * @property {function(number, number, number, function(Array.<H.map.AbstractMarker>), Function):
 *   H.util.ICancelable} requestData function that fetches marker data and creates array of
 * {@code H.map.AbstractMarker} that is passed success callback, if function fails to fetch data
 * onError callback must be called
 * @property {boolean=} providesDomMarkers indicates if markers provided are of type
 * {@code H.map.DomMarker} or {@code H.map.Marker}, default is {@code H.map.Marker}
 */
H.map.provider.MarkerTileProvider.Options;
