goog.provide('H.map.GeoShape');

goog.require('H.geo.Rect');
goog.require('H.geo.Strip');
goog.require('H.map.Object');
goog.require('H.map.Spatial');
goog.require('H.map.provider.Invalidations');
goog.require('H.math');
goog.require('H.util.ChangeEvent'); // TODO "stripchange" event deprecated since 3.0.7.0, to remove in 3.0.8.0



//###############################################  CONSTRUCTOR  ########################################################
/**
 * This class represents a spatial shape in geographic space. It is defined by a path
 * containing the vertices of the shape (lat, lng, alt values).
 * @publish
 * @export
 * @extends {H.map.Spatial}
 * @abstract
 * @constructor
 * @param {boolean} isClosed Indicates whether this geographical shape is closed (a polygon)
 * @param {H.geo.Strip} strip The strip describing the shape of the spatial object
 * @param {H.map.Spatial.Options} options The options to apply
 */
H.map.GeoShape = function(isClosed, strip, options) {
  'use strict';

  // As long as GeoShape is published we have to protect direct instantiation or inheritance other than Polyline and
  // Polygon because there is a "special treatment" in setStrip():
  if (!H.lang.isInstanceOf(this, H.map.Polygon) &&
      !H.lang.isInstanceOf(this, H.map.Polyline)) {
    throw new Error('Illegal constructor call ' + H.lang.getSymbolPath(H.map.GeoShape));
  }

  H.map.GeoShape.superClass_.constructor.call(this, isClosed, options);
  this.setStrip(strip);
};
H.lang.inherits(H.map.GeoShape, H.map.Spatial);


//#################################################  EVENTS  ###########################################################
/**
 * Fired when this spatial's strip changes.
 * @publish
 * @deprecated since 3.0.7.0, will be removed in 3.0.8.0
 * @event H.map.GeoShape#stripchange
 * @type {H.util.ChangeEvent}
 */


//##################################################  STRIP  ###########################################################
/**
 * The strip to describe the shape in geo-space
 * @protected
 * @type {?H.geo.Strip}
 */
H.map.GeoShape.prototype.strip = null;


/**
 * This method returns the strip which represents the shape of the spatial object.
 * @publish
 * @return {H.geo.Strip} the strip
 * @export
 */
H.map.GeoShape.prototype.getStrip = function() {
  'use strict';
  return this.strip;
};


/**
 * This method sets the geo-information for the spatial object
 * @publish
 * @export
 * @param {?H.geo.Strip} strip The strip which represents the shape of the spatial object.
 * @return {H.map.GeoShape} the Spatial instance itself
 */
H.map.GeoShape.prototype.setStrip = function(strip) {
  'use strict';
  var currentStrip;
  if (strip !== null && !H.lang.isInstanceOf(strip, H.geo.Strip)) {
    throw new H.lang.InvalidArgumentError(H.map.GeoShape.prototype.setStrip, 0, strip);
  }
  // Sub-classes whose computes the strip on demand (e.g. Circle and Rect)
  currentStrip = this.strip;
  this.strip = strip;
  // This is a "special treatment" for nullable strips. We allow only Polyline and Polygon to inherit from
  // Spatial. Booth classes have to assure, that either a proper strip is provided during construction time or
  // the strip is null and is computed on demand by a sub-class during getStrip() and getBounds().
  // The Polygon#setStrip allows it's sub classes to set strip to null (e.g. when a circle's center has been changed)
  // For this reason we must not trigger an "stripchange" event or an invalidate() call if the current strip is null,
  // otherwise we get a race-condition
  if (currentStrip !== null) {
    // TODO "stripchange" event deprecated since 3.0.7.0, to remove in 3.0.8.0
    this.dispatchEvent(new H.util.ChangeEvent('stripchange', strip, currentStrip));
    this.invalidate(H.map.provider.Invalidations.Flag.SPATIAL);
  }
  return this;
};


//#################################################  BOUNDS  ###########################################################
/**
 * This method returns the bounding rectangle for this object. The rectangle
 * is the smallest rectangle which encloses all points of the spatial object.
 * @export
 * @publish
 * @abstract
 * @method
 * @return {H.geo.Rect}
 */
H.map.GeoShape.prototype.getBounds = goog.abstractMethod;


//##########################################  GEOMETRY & PROJECTION  ###################################################
/**
 * Projects geo-strips to x,y strips
 * DON'T publish, API-internal use only by SpatiaTile!
 * @param {Array.<Array.<number>>} strips The geo-strips to project
 * @param {boolean} isClosed A flag to indicate whether the strip is closed
 * @param {H.geo.PixelProjection} pxProjection
 * @return {Array.<Array.<H.math.Point>>} the projected strips
 */
H.map.GeoShape.prototype.projectGeoStrips = function(strips, isClosed, pxProjection) {
  'use strict';
  var coords = [],
      length = strips.length,
      i,
      strip;
  for (i = 0; i < length; i++) {
    strip = strips[i];
    if (strip.length) {
      coords.push(this.projectDenormalized_(strip, isClosed, pxProjection));
    }
  }
  return coords;
};


/**
 * Returns an array of coordinates which are denormalized but they cover the shape. Denormalized means
 * that returned array of projected coordinates should contain points computed out of longitudes which are
 * bigger than 180 or smaller than -180 degrees.
 * @param {Array.<number>} coords Array of lat, lng...
 * @param {boolean} isClosed Flag if the shape is closed
 * @param {H.geo.PixelProjection} pxProjection The pixel projection to use
 * @param {number=} opt_shift Optional value to determine of shifting to left or right world (+-360)
 * @return {Array.<H.math.Point>} Array of 2D points
 * @private
 */
H.map.GeoShape.prototype.projectDenormalized_ = function(coords, isClosed, pxProjection, opt_shift) {
  'use strict';
  var strip = [],
      length = coords.length,
      i = 0;
  this.denormalizeLastLng_ = 0;
  opt_shift = opt_shift || 0;
  while (i < length) {
    strip.push(this.denormalize_(coords[i++], coords[i++], opt_shift, pxProjection));
  }

  // Adding a closing leg for polygons. This leg is necessary when we need to compute filling etc.
  if (isClosed) {
    strip.push(this.denormalize_(coords[0], coords[1], opt_shift, pxProjection));
  }
  return strip;
};


/**
 * To keep the last longitude, needed by denormalize_()
 * Must be reset to 0 for each execution of projectDenormalized_ before calling denormalize_
 * @private
 * @type {number}
 */
H.map.GeoShape.prototype.denormalizeLastLng_;


/**
 * To create a Point with denormalized longitudes, necessary for sequential longitudes with a delta > 180 degrees
 * @private
 * @param {number} lat
 * @param {number} lng
 * @param {number} shift
 * @param {H.geo.PixelProjection} pxProjection
 * @return {H.math.Point}
 */
H.map.GeoShape.prototype.denormalize_ = function(lat, lng, shift, pxProjection) {
  'use strict';
  var lastLng = this.denormalizeLastLng_;
  if (lastLng && H.math.abs(lastLng - lng) > 180) {
    lng += lastLng < 0 ? -360 : 360;
  }
  this.denormalizeLastLng_ = lng;
  return pxProjection.latLngToPixel(lat, lng + shift);
};
