goog.provide('H.map.render.p2d.canvas.TileRenderer');

goog.require('H.lang.InvalidArgumentError');



/**
 * Image renderer, places image tiles provided by the layer on the given surface.
 * @constructor
 * @implements {H.map.render.p2d.IPickable}
 * @param {H.map.layer.TileLayer} layer
 * @param {H.map.render.p2d.canvas.Surface} surface
 * @param {H.map.render.p2d.canvas.TileRenderer.Options=} opt_options
 */
H.map.render.p2d.canvas.TileRenderer = function(layer, surface, opt_options) {
  'use strict';
  if (!layer) {
    throw new H.lang.InvalidArgumentError(H.map.render.p2d.canvas.TileRenderer, 0, layer);
  }
  if (!surface) {
    throw new H.lang.InvalidArgumentError(H.map.render.p2d.canvas.TileRenderer, 1, surface);
  }

  /**
   * @private
   */
  this.layer_ = layer;

  /**
   * @private
   */
  this.surface_ = surface;

  /**
   * @private
   */
  this.placedTiles_ = {};

  this.enableSubpixelRendering_ = opt_options && opt_options['enableSubpixelRendering'];
};


/**
 * This method requests all tiles for the given bounding box and zoom level and places them on the surface.
 * @param {H.geo.Rect} bbox
 * @param {H.math.Point} screenCenter
 * @param {H.geo.PixelProjection} projection
 * @param {number} zoomLevel The zoom level for which the objects should be rendered
 * @param {boolean} cacheOnly Indicates whether only cached objects are to be considered
 * @param {H.math.Point} prioCenter The priority center as an offset in screen pixel relative to the center
 * @return {boolean} returns true if rendering was completed (ex. all tiles were provided and placed)
 */
H.map.render.p2d.canvas.TileRenderer.prototype.render = function(bbox, screenCenter, projection,
    zoomLevel, cacheOnly, prioCenter) {
  'use strict';

  // TODO: getBestZoomLevel
  var zoom = H.math.round(zoomLevel),
      layer = this.layer_,
      layerProjection = layer['pixelProjection'],
      // we reassign zoom, needed if layer minimum exponent !== engine minimum exponent (see capabilities zoom)
      adjustedZoom = zoom + (projection['exp'] - layerProjection['exp']),
      min = layer['min'],
      max = layer['max'],
      response,
      total,
      tiles,
      tileSize = layer['tileSize'],
      layerOpacity = layer['opacity'],
      tile,
      surface = this.surface_,
      surfaceOpacity = surface.getOpacity(),
      width = surface.getSize().width,
      projectionWidth = projection['w'],
      offsetX = projection['x'],
      offsetY = projection['y'],
      len,
      tileX,
      tileY,
      lastX,
      scale,
      origTileSize,
      repeatXLeftBorder,
      repeatXRightBorder,
      placedTiles = {},
      i,
      data;

  if (zoom < min || zoom > max) {
    return true;
  }

  response = layer.requestTiles(bbox, adjustedZoom, cacheOnly, prioCenter);
  total = response['total'];
  tiles = response['tiles'];
  len = tiles.length;

  /**
   * Initial value is undefined, will be set in each render call
   * @private
   * @type {(H.geo.PixelProjection|undefined)}
   */
  this.projection_ = projection;

  /**
   * Initial value is undefined, will be set in each render call
   * @private
   * @type {H.math.Point|undefined}
   */
  this.screenCenter_ = screenCenter;

  /**
   * Initial value is undefined, will be set in each render call
   * @private
   * @type {(number|undefined)}
   */
  this.zoomLevel_ = adjustedZoom;

  if (surfaceOpacity !== layerOpacity) {
    surface.setOpacity(layerOpacity);
  }

  tileSize = tileSize * projectionWidth / (H.math.pow(2, adjustedZoom) * tileSize);
  origTileSize = tileSize;

  if (!this.enableSubpixelRendering_) {

    // if tile size is fractional floor tile size and recalculate world center
    if (tileSize % 1) {
      tileSize = H.math.floor(tileSize);
      scale = origTileSize / tileSize;
      offsetX = offsetX / scale;
      offsetY = offsetY / scale;
      projectionWidth = H.math.pow(2, adjustedZoom) * tileSize;
    }

    // floor world center in even if tile size is integral
    offsetX = H.math.floor(offsetX);
    offsetY = H.math.floor(offsetY);
  }
  this.renderedTileSize_ = origTileSize;

  repeatXLeftBorder = -tileSize;
  repeatXRightBorder = width + tileSize;

  for (i = 0; i < len; i++) {
    tile = tiles[i];
    // TODO Sometimes we get SpatialTile instances which have unexpectedly a data value of null:
    if (data = tile['data']) {
      // calculate tile position
      tileX = screenCenter['x'] - (offsetX - tile['x'] * tileSize);
      tileY = screenCenter['y'] - (offsetY - tile['y'] * tileSize);

      surface.draw(data, tileX, tileY, tileSize, tileSize);

      // repeat tiles to the left from the main tile
      lastX = tileX;
      while ((lastX = lastX - projectionWidth) > repeatXLeftBorder) {
        surface.draw(data, lastX, tileY, tileSize, tileSize);
      }

      // repeat tiles to the right from the main tile
      lastX = tileX;
      while ((lastX = lastX + projectionWidth) < repeatXRightBorder) {
        surface.draw(data, lastX, tileY, tileSize, tileSize);
      }
    }
    // TODO: quick fix should be replaced with getTileKey call
    placedTiles[this.zoomLevel_ + '_' + tile.x + '_' + tile.y] = tile;
  }

  this.placedTiles_ = placedTiles;

  return len === total;
};


/**
 * Returns visible objects under the given x/y coordinates. Array is sorted according to their drawing
 * order, topmost object has an index #0 in returned array.
 * @param {number} x x pixel coordinate
 * @param {number} y y pixel coordinate
 * @param {boolean=} opt_topMostOnly optional flag, if set method returns array with single element that is located
 * on top
 * @return {Array.<H.map.Object>} array of map objects
 */
H.map.render.p2d.canvas.TileRenderer.prototype.getObjectsAt = function(x, y, opt_topMostOnly) {
  'use strict';
  if (H.lang.isDef(this.zoomLevel_)) {
    var tileSize = this.renderedTileSize_,
        scale = tileSize / this.layer_['tileSize'],
        projection = this.projection_,
        screenCenter = this.screenCenter_,
        tile,
        result,
        offsetX = projection['x'] + (x - screenCenter['x']),
        offsetY = projection['y'] + (y - screenCenter['y']),
        // TODO: getBestZoomLevel
        colWidth = H.math.floor(projection['w'] / tileSize),
        col = H.math.floor(offsetX / tileSize),
        fixedCol = col,
        row = H.math.floor(offsetY / tileSize);

    // adjust columns handle cases where it's from the left to CDB and multiple worlds case
    if (H.math.abs(fixedCol) >= colWidth) {
      fixedCol = fixedCol % colWidth;
    }
    if (fixedCol < 0) {
      fixedCol = colWidth + fixedCol;
    }

    tile = this.placedTiles_[this.zoomLevel_ + '_' + fixedCol + '_' + row];
    if (tile && tile.getObjectsAt) {
      result = tile.getObjectsAt((offsetX - tileSize * col) / scale,
          (offsetY - tileSize * row) / scale, opt_topMostOnly);
    }

  }
  return result || /** @type {Array.<H.map.Object>} */ (H.util.emptyArray);
};


/**
 * Options which can be used to initialize the TileRenderer object.
 * @typedef {Object} H.map.render.p2d.canvas.TileRenderer.Options
 * @property {boolean} enableSubpixelRendering
 */
H.map.render.p2d.canvas.TileRenderer.Options;
