goog.provide('H.geo.IProjection');



/**
 * Interface which describes map projection. Functions transform geo coordinates to
 * EPSG3857 coordinate reference system, which is x,y values in range 0..1
 * @interface
 * @publish
 */
H.geo.IProjection = function() {};


/**
 * This method transforms geographical lat/lng coordinates to a projected point in range {@code 0...1}
 * @param {number} lat latitude
 * @param {number} lng longitude
 * @param {H.math.Point=} opt_out an optional point to store the result
 * @return {H.math.Point}
 * @publish
 * @export
 */
H.geo.IProjection.prototype.latLngToPoint = function(lat, lng, opt_out) {};


/**
 * This method transforms projected x/y coordinates in range {@code 0...1} to a geographical point
 * @param {number} x
 * @param {number} y
 * @param {H.geo.Point=} opt_out an optional point to store the result
 * @return {H.geo.Point}
 * @publish
 * @export
 */
H.geo.IProjection.prototype.xyToGeo = function(x, y, opt_out) {};


/**
 * This method transforms a projected point in range {@code 0...1} to a geographical point
 * @param {H.math.IPoint} point
 * @param {H.geo.Point=} opt_out an optional point to store the result
 * @return {H.geo.Point}
 * @publish
 * @export
 */
H.geo.IProjection.prototype.pointToGeo = function(point, opt_out) {};


/**
 * This method transforms a geographical point to a projected point in range {@code 0...1}
 * @param {H.geo.IPoint} geoPoint
 * @param {H.math.Point=} opt_out an optional point to store the result
 * @return {H.math.Point}
 * @publish
 * @export
 */
H.geo.IProjection.prototype.geoToPoint = function(geoPoint, opt_out) {};
