goog.provide('H.map.render.p2d.InteractionUtils');

goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.math.Vector');



/**
 * Interaction utilities record current interaction values. In addition
 * utilities provide functionality to calculate current speed and direction which
 * is used fot he kinetic animation. Utilities also resolve interaction tolerances
 * which means that you can adjust the minimal required move or minimal time
 * from last interaction to trigger kinetic move. Tolerance will adjust calculated
 * speed and direction.
 *
 * Utilities also resolve bifocal interaction in terms of the direction and
 * move vectors
 *
 * @constructor
 * @param {H.map.render.p2d.InteractionUtils.Options=} opt_options
 */
H.map.render.p2d.InteractionUtils = function(opt_options) {
  'use strict';
  var opts = opt_options || {};
  this.minSpeed_ = opts['minSpeed'] || 3;
  this.vectorTolerance_ = opts['vectorTolerance'] || 3;
  this.maxSpeed_ = opts['maxSpeed'] || 300;
  this.timeTolerance_ = opts['timeTolerance'] || 50;
  this.points_ = [];
  this.running_ = false;
  this.isRecording_ = false;

  /**
   * @private
   * @type {?H.map.render.p2d.InteractionUtils.Point}
   */
  this.current_ = null;

  /**
   * @private
   * @type {?H.map.render.p2d.InteractionUtils.Point}
   */
  this.previous_ = null;
};


/**
 * Function returns a vector calculated from the itneraction delta objects
 * it resolves bifocality
 * @private
 * @param {?H.map.render.p2d.InteractionUtils.Point} point
 * @param {?H.map.render.p2d.InteractionUtils.Point} previousPoint
 * @return {H.math.Vector}
 */
H.map.render.p2d.InteractionUtils.prototype.getMoveVector_ = function(point, previousPoint) {
  'use strict';
  var p1,
      p2,
      distanceA,
      distanceB,
      vect,
      dx,
      dy;

  if (!previousPoint) {
    vect = new H.math.Vector(0, 0);
  } else {
    if (point['bifocal'] && previousPoint['bifocal']) {
      //both points are bifocal use center between points
      p1 = this.getBifocalCenter_(point);
      p2 = this.getBifocalCenter_(previousPoint);
    } else if (previousPoint['bifocal']) {
      //previous point is bifocal and current is not
      //take vector between current point and most close point from the prvious
      p1 = this.getMathPoint_(point);
      distanceA = this.getMathPoint_(previousPoint).distance(p1);
      distanceB = this.getMathPoint_(previousPoint, true).distance(p1);
      if (distanceA > distanceB) {
        p2 = this.getMathPoint_(previousPoint, true);
      } else {
        p2 = this.getMathPoint_(previousPoint);
      }
    } else if (point['bifocal'] && !previousPoint['bifocal']) {
      p2 = this.getMathPoint_(previousPoint);
      distanceA = this.getMathPoint_(point).distance(p2);
      distanceB = this.getMathPoint_(point, true).distance(p2);
      if (distanceA > distanceB) {
        p1 = this.getMathPoint_(point, true);
      } else {
        p1 = this.getMathPoint_(point);
      }
    } else {
      p1 = point;
      p2 = previousPoint;
    }

    //calculate the vector
    dx = p1['x'] - p2['x'];
    dy = p1['y'] - p2['y'];
    vect = new H.math.Vector(dx, dy);
  }

  return vect;

};


/**
 * Function returns a move from the recorded point list
 * by the given index. Takes indexed point and previous oneto calculate the move vector
 * @param {number} index
 * @private
 * @return {H.math.Vector}
 */
H.map.render.p2d.InteractionUtils.prototype.getRecordedMove_ = function(index) {
  'use strict';
  return this.getMoveVector_(this.points_[index], this.points_[index - 1]);
};


/**
 * Function returns distance between bifocal points of itneraction
 * @private
 * @param {?H.map.render.p2d.InteractionUtils.Point} point
 * @return {number}
 */
H.map.render.p2d.InteractionUtils.prototype.getBifocalDistance_ = function(point) {
  'use strict';
  var dx,
      dy;

  if (!point || H.lang.isNaN(point['bx']) || H.lang.isNaN(point['by'])) {
    return 0;
  } else {
    dx = point['bx'] - point['x'];
    dy = point['by'] - point['y'];
    return H.math.sqrt(dx * dx + dy * dy);
  }
};


/**
 * Function returns the center between bifocal interaction points
 * @private
 * @param {?H.map.render.p2d.InteractionUtils.Point} point
 * @return {H.math.Point|undefined}
 */
H.map.render.p2d.InteractionUtils.prototype.getBifocalCenter_ = function(point) {
  'use strict';
  var center,
      dx,
      dy;

  if (point && !H.lang.isNaN(point['bx']) && !H.lang.isNaN(point['by'])) {
    dx = point['x'] - point['bx'];
    dy = point['y'] - point['by'];
    center = new H.math.Point(point['x'] - dx / 2, point['y'] - dy / 2);
  }

  return center;
};


/**
 * Function gets point object from the record with x,y or bifocal x,y if
 * opt_isBifocal is set to true
 * @private
 * @param {H.map.render.p2d.InteractionUtils.Point} point
 * @param {boolean=} opt_isBifocal
 * @return {H.math.Point | null}
 */
H.map.render.p2d.InteractionUtils.prototype.getMathPoint_ = function(point, opt_isBifocal) {
  'use strict';
  return opt_isBifocal ? new H.math.Point(point['bx'], point['by']) :
      new H.math.Point(point['x'], point['y']);
};


/**
 * Stores the initial value for interaction
 * @param {H.map.render.RenderEngine.InteractionModifiers} modifiers
 * @param {boolean} isRecording
 */
H.map.render.p2d.InteractionUtils.prototype.start = function(modifiers, isRecording) {
  'use strict';

  if (H.lang.isNaN(modifiers)) {
    throw new H.lang.InvalidArgumentError(this.start, 0, modifiers);
  }

  if (this.running_) {
    this.clear(); //clear records new interaction start invalidates previous
  }
  //start and store first values
  this.modifiers_ = modifiers;
  this.running_ = true;
  this.isRecording_ = !!isRecording;
};


/**
 * Stores current interact values and the interaction vectors
 * @param {number} x
 * @param {number} y
 * @param {number=} opt_bx
 * @param {number=} opt_by
 * @param {number=} opt_timestamp
 */
H.map.render.p2d.InteractionUtils.prototype.store = function(x, y, opt_bx, opt_by, opt_timestamp) {
  'use strict';

  var invalidArgNr = H.lang.isNaN(x) ? 0 :
      H.lang.isNaN(y) ? 1 :
      H.lang.isDefAndNotNull(opt_bx) && H.lang.isNaN(opt_bx) ? 2 :
      H.lang.isDefAndNotNull(opt_by) && H.lang.isNaN(opt_by) ? 3 :
      H.lang.isDefAndNotNull(opt_timestamp) && H.lang.isNaN(opt_timestamp) ? 4 :
      H.lang.UNDEF;

  if (invalidArgNr >= 0) {
    throw new H.lang.InvalidArgumentError(this.store, invalidArgNr, arguments[invalidArgNr]);
  }

  if (this.running_) {
    opt_timestamp = opt_timestamp || (new Date()).getTime();

    this.previous_ = this.current_;
    this.current_ = /** @type {H.map.render.p2d.InteractionUtils.Point} */ ({
      'x': x,
      'y': y,
      'bx': H.lang.isDefAndNotNull(opt_bx) ? opt_bx : NaN,
      'by': H.lang.isDefAndNotNull(opt_by) ? opt_by : NaN,
      'ts': opt_timestamp,
      'bifocal': H.lang.isDefAndNotNull(opt_bx) && H.lang.isDefAndNotNull(opt_by)
    });

    if (this.isRecording_) {
      this.points_.push(this.current_);
    }
  }
};


/**
 * Marks interaction as ended and clears all the records if present
 */
H.map.render.p2d.InteractionUtils.prototype.clear = function() {
  'use strict';
  this.points_ = [];
  this.running_ = false;
  this.isRecording_ = false;
  this.previous_ = null;
  this.current_ = null;
};


/**
 * Function returns true if the passed modifier is included
 * in current interaction
 * @param {number} modifier
 * @return {boolean}
 */
H.map.render.p2d.InteractionUtils.prototype.has = function(modifier) {
  'use strict';
  if (H.lang.isNaN(modifier)) {
    throw new H.lang.InvalidArgumentError(this.has, 0, modifier);
  }
  return !!(this.modifiers_ & modifier);
};


/**
 * Function indicates if there is an interaction running
 * @return {boolean}
 */
H.map.render.p2d.InteractionUtils.prototype.isRunning = function() {
  'use strict';
  return this.running_;
};


/**
 * Function returns the interaction points list
 * @return {Array.<H.map.render.p2d.InteractionUtils.Point>}
 */
H.map.render.p2d.InteractionUtils.prototype.getPoints = function() {
  'use strict';
  return this.points_;
};


/**
 * Returns the current direction of the interaction as normalized
 * vector
 * @return {H.math.Vector}
 */
H.map.render.p2d.InteractionUtils.prototype.getDirection = function() {
  'use strict';
  var i = this.points_.length,
      num = 0,
      vector = new H.math.Vector(0, 0);
  if (this.isRecording_) {
    while (--i && num < this.vectorTolerance_) {
      vector = vector.add(this.getRecordedMove_(i));
      num++;
    }
  } else {
    vector = this.getMoveVector_(this.current_, this.previous_);
  }

  return vector.normalize();
};


/**
 * Returns the current interaction speed based on recorded move vectors, or if
 * recording is not on from the current and previous point
 * We are not using time to measure the speed since we cannto rely on events
 * timestamp (it can give you i.e 5,5 vector in 1ms difference)
 * @param {number=} opt_timeStamp
 * @return {number}
 */
H.map.render.p2d.InteractionUtils.prototype.getSpeed = function(opt_timeStamp) {
  'use strict';
  opt_timeStamp = opt_timeStamp || Date.now();
  var i = this.points_.length,
      last = this.points_[i - 1],
      speed,
      num = 0,
      mag = 0;

  if (this.isRecording_) {
    while (--i && num < this.vectorTolerance_) {
      mag += this.getRecordedMove_(i).magnitude();
      num++;
    }
    speed = mag / num;
  } else {
    last = this.current_;
    speed = this.getMoveVector_(this.current_, this.previous_).magnitude();
  }

  if (speed >= this.minSpeed_ && (opt_timeStamp - last['ts'] <= this.timeTolerance_)) {
    speed = (speed > this.maxSpeed_) ? this.maxSpeed_ : speed;
  } else {
    speed = 0;
  }
  return speed;
};


/**
 * Function return last move vector
 * @return {H.math.Vector}
 */
H.map.render.p2d.InteractionUtils.prototype.getMoveVector = function() {
  'use strict';
  return this.getMoveVector_(this.current_, this.previous_);
};


/**
 * Function returns distance between points
 * @return {number}
 */
H.map.render.p2d.InteractionUtils.prototype.getBifocalDistance = function() {
  'use strict';
  return this.getBifocalDistance_(this.current_);
};


/**
 * Function returns center between of the last interaction records or undefined
 * if there is no
 * @return {H.math.Point|undefined}
 */
H.map.render.p2d.InteractionUtils.prototype.getBifocalCenter = function() {
  'use strict';
  return this.getBifocalCenter_(this.current_);
};


/**
 * Function returns new zoom level according to current one and change
 * of distance between interaction points (pinch zoom)
 * @param {number} zoomLevel
 * @return {number}
 */
H.map.render.p2d.InteractionUtils.prototype.getBifocalZoom = function(zoomLevel) {
  'use strict';
  var prevDistance,
      current = this.current_,
      currentDistance;

  if (H.lang.isNaN(zoomLevel)) {
    throw new H.lang.InvalidArgumentError(this.getBifocalZoom, 0, zoomLevel);
  }

  if (current && this.previous_) {
    prevDistance = this.getBifocalDistance_(this.previous_);
    if (prevDistance !== 0) {
      currentDistance = this.getBifocalDistance_(this.current_);
      zoomLevel = H.math.log(H.math.pow(2, zoomLevel) *
          (currentDistance / prevDistance)) / H.math.LN2;
    }
  }

  return zoomLevel;

};


/**
 * Options which can be used to initialize the interaction utilities
 * @typedef {{
 *   minSpeed: (number|undefined),
 *   maxSpeed: (number|undefined),
 *   vetorTolerance: (number|undefined)
 * }}
 * @property {number} minSpeed minimal speed to trigger move, values lower than this value will be 0
 * @property {number} maxSpeed maximum speed values greater will be narrowde to this value
 * @property {number} vectorTolerance value which indicates how many vectors (if available) should
 * be taken into account then calculating final speed and direction
 */
H.map.render.p2d.InteractionUtils.Options;


/**
 * Represents interaction point, which might be a single or bi focal interaction.
 * @typedef {{
 *   x: number,
 *   y: number,
 *   bx: (number|undefined),
 *   by: (number|undefined),
 *   isBifocal: (boolean|undefined),
 *   ts: number
 * }}
 * @property {number} x position on x-axis for the interaction
 * @property {number} y position on y-axis for the interation
 * @property {number} bx position of the second point of interaction on x-axis
 * @property {number} by position of the second point of interaction on y-axis
 * @property {boolean} isBifocal flag indicating the this is bifocal point and has x,y and bx,by
 * @property {number} ts timestamp for the interaction
 */
H.map.render.p2d.InteractionUtils.Point;

