goog.provide('H.map.render.p2d.dom.MarkerRenderer');

goog.require('H.map.render.p2d.MarkerRenderer');
goog.require('H.util');



//###############################################  CONSTRUCTOR  ########################################################
/**
 * This renderer fetches Marker objects from the associated layer and render them on a Canvas Surface.
 * @constructor
 * @extends {H.map.render.p2d.MarkerRenderer}
 * @param {H.map.layer.IMarkerLayer} layer
 * @param {H.map.render.p2d.dom.Surface} surface
 * @param {H.map.render.p2d.MarkerRenderer.Options=} opt_options
 */
H.map.render.p2d.dom.MarkerRenderer = function(layer, surface, opt_options) {
  'use strict';
  H.map.render.p2d.dom.MarkerRenderer.superClass_.constructor.apply(this, arguments);

  /**
   * The number of rendered markers
   * @private
   * @type {number}
   */
  this.markerCount_ = 0;

  /**
   * Temporary fix. This is needed to fetch objects from dom surface correctly
   * @private
   */
  this.pixelRatio_ = surface.getSize().pixelRatio;

  /**
   * List of nodes waiting for onAttach
   * to be fired
   * @private
   */
  this.onAttachNodes_ = [];

  this.fireOnAttach_ = H.lang.bind(this.fireOnAttach_, this);

  /**
   * requested next frame to fire onAttach
   * @private
   */
  this.nextFrame_ = null;

};
goog.inherits(H.map.render.p2d.dom.MarkerRenderer, H.map.render.p2d.MarkerRenderer);


//#################################################  RENDERING  ########################################################
/**
 * Concrete implementation of H.map.render.p2d.MarkerRenderer.prototype.fetchMarkers
 * @override
 */
H.map.render.p2d.dom.MarkerRenderer.prototype.fetchMarkers = function() {
  return this.layer.requestDomMarkers.apply(this.layer, arguments);
};


/**
 * Overrides the super implementation, because before render() we need to save the placedMarkerData
 * and afterwards we have cleanup the dom.Surface and notify icon listeners
 * @override
 */
H.map.render.p2d.dom.MarkerRenderer.prototype.render = function() {
  'use strict';
  var ret;
  //fire onAttach on all nodes (if any) in onAttachNodes_
  this.fireOnAttach_();

  // Start at z-index 0 in every render frame:
  this.nextZIndex_ = 0;

  ret = H.map.render.p2d.dom.MarkerRenderer.superClass_.render.apply(this, arguments);

  // We have to remove all previous but no longer rendered marker icon nodes:
  this.clearSurfaceNodes_(/** @type {H.map.render.p2d.dom.Surface} */(this.surface), this.prevMarkerDataDict);

  //return false if we have node on which we need to fire onAttach
  //false will request new animation frame
  return this.onAttachNodes_.length === 0 && ret;
};


/**
 * Method goes through freshly attached nodes and fires on attach on them
 * @private
 */
H.map.render.p2d.dom.MarkerRenderer.prototype.fireOnAttach_ = function() {
  'use strict';
  var i = this.onAttachNodes_.length,
      marker,
      icon,
      node;
  while (i--) {
    node = this.onAttachNodes_[i];
    marker = node.$marker$;
    icon = node.$marker$.getIcon();
    icon.onAttach(node, icon, marker);
  }
  this.onAttachNodes_.length = 0;
};


/**
 * Concrete implementation of H.map.render.p2d.MarkerRenderer.prototype.renderMarker
 * @override
 */
H.map.render.p2d.dom.MarkerRenderer.prototype.renderMarker = function(data, marker, icon, surface, dx, dy) {
  'use strict';

  // Just to satisfy CC's type checks, code will be dropped by compiler:
  marker = /** @type {H.map.DomMarker} */(marker);
  icon = /** @type {H.map.DomIcon} */(icon);
  surface = /** @type {H.map.render.p2d.dom.Surface} */(surface);

  var invalidations = marker.getInvalidations(),
      node,
      wasAttached;

  // Do we have a previous rendered icon node for this marker?
  if (data.detail) {
    // Do we have a visual invalidation since last rendering?
    if (invalidations.isVisual(data.invalMark)) {
      this.clearSurfaceNode_(surface, data);
    } else {
      node = /** @type {Element} */(data.detail);
    }
  }
  // If we don't have a reuseable node we have to get a new clone and update the markerData:
  if (!node) {
    data.detail = node = icon.getClone(marker, this.ownerDocument);
    this.markerCount_++;
    if (H.lang.isFunction(icon.onAttach)) {
      this.onAttachNodes_.push(node);
    }
  }
  // Save the marker's invalidation mark at render tile:
  data.invalMark = invalidations.getMark();

  // Prepare the node and "draw" it":
  node.style.zIndex = this.nextZIndex_++;
  node.$marker$ = marker;
  surface.draw(node, dx, dy, 0, 0);
};


/**
 * The z-index to use within the next renderMarker() call. It is reset at the begin of each render() call
 * @private
 * @type {number}
 */
H.map.render.p2d.dom.MarkerRenderer.prototype.nextZIndex_;


//################################################  HIT TESTING  #######################################################
/**
 * @override
 */
H.map.render.p2d.dom.MarkerRenderer.prototype.getObjectsAt = function(x, y, opt_topMostOnly) {
  'use strict';
  var displayStr = 'display',
      pixelRatio = this.pixelRatio_,
      ownerDocument,
      docElement,
      surfaceElement,
      boundingRect,
      objects,
      hiddenNodeStyles,
      element,
      hidden,
      target,
      style,
      parentElement,
      prevDisplayProperty,
      prevDisplayImportance,
      i;

  // Do we have placed markers:
  if (this.markerCount_) {

    // TODO Temporary fix.
    // Go back to screen coords because we use elementFromPoint to get elements from the DOM.
    // Will be removed after renderer cleanup
    x /= pixelRatio;
    y /= pixelRatio;

    ownerDocument = this.ownerDocument,
    docElement = ownerDocument.documentElement;
    surfaceElement = this.surface.getElement();
    boundingRect = surfaceElement.getBoundingClientRect();
    x += boundingRect['left'];
    y += boundingRect['top'];

    // Is there a element at this point which is neither the surface- nor the document-element?
    while ((element = target = ownerDocument.elementFromPoint(x, y)) &&
        element !== surfaceElement && element !== docElement) {

      // TODO IE doesn't provide contains() method on SVG nodes, we have to use compareDocumentPosition() instead!
      // Find the most distant element in ancestor-or-self axis which NOT contains (or is) the surface element:
      while ((parentElement = element.parentNode) &&
          (!H.lang.isFunction(parentElement.contains) || !parentElement.contains(surfaceElement))) {
        element = parentElement;
      }

      // Did we found a marker icon? (We ignore SVG elments if they are the "root" element of a marker!)
      if (parentElement === surfaceElement && (element !== target || element.nodeName !== 'svg')) {
        // Collect the marker:
        (objects || (objects = [])).push(element.$marker$);
        // If only top most marker requested we stop the discovery loop
        if (opt_topMostOnly) {
          break;
        }
      }

      //we need to hide something
      if (!hiddenNodeStyles) {
        hiddenNodeStyles = [];
      }

      // Hide this "interferingly" element to possibly find other markers underneath:
      style = element.style;
      hiddenNodeStyles.push({
        displayProperty: style.getPropertyValue(displayStr),
        displayImportance: style.getPropertyPriority(displayStr),
        style: style
      });
      style.setProperty(displayStr, 'none', 'important');
    }

    // Restore the styles of the hidden elements if necessary:
    if (hiddenNodeStyles) {
      i = hiddenNodeStyles.length;
      while (i--) {
        hidden = hiddenNodeStyles[i];
        //remove display: none !important
        hidden.style.removeProperty(displayStr);
        //set back to previous inline display if available
        if (H.lang.isString(hidden.displayProperty)) {
          hidden.style.setProperty(displayStr, hidden.displayProperty, hidden.displayImportance);
        }
      }
    }
  }

  return objects || H.util.emptyArray;
};


//################################################  UTILITIES  #########################################################
/**
 * To trigger surface.clear(node) and invoke the onDetach callback:
 * @private
 * @param {H.map.render.p2d.dom.Surface} surface
 * @param {H.map.render.p2d.MarkerRenderer.MarkerData} data
 */
H.map.render.p2d.dom.MarkerRenderer.prototype.clearSurfaceNode_ = function(surface, data) {
  'use strict';
  var icon,
      node,
      marker;
  /** @type {H.map.render.p2d.dom.Surface} */(surface).clear(node = /** @type {Element} */(data.detail));
  delete node.$marker$;
  this.markerCount_--;

  (marker = /** @type {!H.map.DomMarker} */(data.marker)).getIcon().disposeClone(node, marker);
};


/**
 * To call clearSurfaceNode_() for all entries of the given dictionary
 * @private
 * @param {H.map.render.p2d.dom.Surface} surface
 * @param {!Object.<string, !H.map.render.p2d.MarkerRenderer.MarkerData>} dataDict
 */
H.map.render.p2d.dom.MarkerRenderer.prototype.clearSurfaceNodes_ = function(surface, dataDict) {
  'use strict';
  var id;
  for (id in dataDict) {
    this.clearSurfaceNode_(surface, dataDict[id]);
  }
};


//################################################  DISPOSABLE  ########################################################
/**
 * @override
 */
H.map.render.p2d.dom.MarkerRenderer.prototype.disposeInternal = function() {
  'use strict';
  this.onAttachNodes_.length = 0;
  this.clearSurfaceNodes_(/** @type {H.map.render.p2d.dom.Surface} */(this.surface), this.markerDataDict);
  /** @type {H.util.Disposable} */(this.surface).dispose();
  H.map.render.p2d.dom.MarkerRenderer.superClass_.disposeInternal.call(this);
};

