goog.provide('H.util.EventTarget');
goog.require('goog.events.EventTarget');



/**
 * EventTarget enabled listening and dispatching events on all instances and
 * derived classes.
 *
 * @publish
 * @export
 * @extends {goog.events.EventTarget}
 * @constructor
 */
H.util.EventTarget = goog.events.EventTarget;


/**
 * @method H.util.EventTarget#setParentEventTarget
 * @param {H.util.EventTarget} parent the new parent event target
 * @export
 */
H.util.EventTarget.prototype.setParentEventTarget = goog.events.EventTarget.prototype.setParentEventTarget;


/**
 * @method H.util.EventTarget#getParentEventTarget
 * @return {?H.util.EventTarget} the current parent event target
 * @export
 */
H.util.EventTarget.prototype.getParentEventTarget = goog.events.EventTarget.prototype.getParentEventTarget;


/**
 * This method allows to listen for specific event triggered by the object
 * @param {string} type name of event
 * @param {!Function} handler event handler function
 * @param {boolean=} opt_capture if set to true will listen in the capture phase (bubble otherwise)
 * @param {Object=} opt_scope scope for the handler function
 * @method
 * @export
 * @publish
 */
H.util.EventTarget.prototype.addEventListener = goog.events.EventTarget.prototype.addEventListener;


/**
 * This method will removed previously added listener from the event target
 * @param {string} type name of event
 * @param {!Function} handler previously added event handler
 * @param {boolean=} opt_capture if set to true will listen in the capture phase (bubble otherwise)
 * @param {Object=} opt_scope scope for the handler function
 * @method
 * @export
 * @publish
 */
H.util.EventTarget.prototype.removeEventListener = goog.events.EventTarget.prototype.removeEventListener;


/**
 * This method will dispatch event on the event target object
 * @param {H.util.Event|string} evt event object or event name
 * @method
 * @export
 * @publish
 */
H.util.EventTarget.prototype.dispatchEvent = goog.events.EventTarget.prototype.dispatchEvent;


/**
 * Removes listeners from this object. Classes that extend EventTarget may
 * need to override this method in order to remove references to DOM Elements
 * and additional listeners.
 * @method
 * @export
 * @publish
 */
H.util.EventTarget.prototype.dispose = goog.events.EventTarget.prototype.dispose;


/**
 * This method adds callback which is triggered when the object is being disposed
 * @method
 * @param {!Function} callback
 * @param {Object=} opt_scope
 * @export
 * @publish
 */
H.util.EventTarget.prototype.addOnDisposeCallback = goog.events.EventTarget.prototype.addOnDisposeCallback;

