goog.provide('H.math.Vector');

goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.math');

// TODO Remove Vector, use Point instead

// <== why ? vectors and points
// are by definition 2 different things... event though they do have same property names
// why not remove point instead and use Vector everywhere :) how that would look ?



/**
 * Class represents a vector in 2-dimensional space. Vector is
 * defined by two properties x and y.
 * @export
 * @final
 * @constructor
 * @param {number} x delta on x-axis
 * @param {number} y delta on y-axis
 */
H.math.Vector = function(x, y) {
  'use strict';

  if (H.lang.isNaN(this.x = +x)) {
    throw new H.lang.InvalidArgumentError(H.math.Vector, 0, x);
  }
  if (H.lang.isNaN(this.y = +y)) {
    throw new H.lang.InvalidArgumentError(H.math.Vector, 1, y);
  }

};


/**
 * @type {number}
 * @expose
 */
H.math.Vector.prototype.x;


/**
 * @type {number}
 * @expose
 */
H.math.Vector.prototype.y;


/**
 * This method adds another vector to the current one.
 * @param {H.math.Vector} other
 * @return {H.math.Vector}
 * @export
 */
H.math.Vector.prototype.add = function(other) {
  'use strict';
  return new H.math.Vector(
      this.x + other.x,
      this.y + other.y
  );
};


/**
 * This method substracts another vector from current one.
 * @param {H.math.Vector} other
 * @return {H.math.Vector}
 * @export
 */
H.math.Vector.prototype.subtract = function(other) {
  'use strict';
  return new H.math.Vector(
      this.x - other.x,
      this.y - other.y
  );
};


/**
 * This method creates the 'dot' product of the given vector and the current one.
 * provided by the caller.
 * @param {H.math.Vector} other
 * @return {number}
 * @export
 */
H.math.Vector.prototype.dot = function(other) {
  'use strict';
  if (!H.lang.isInstanceOf(other, H.math.Vector)) {
    throw new H.lang.InvalidArgumentError(this.dot, 0, other);
  }
  var product = this.multiply(other);
  return (product.x + product.y);
};


/**
 * This method creates a vector that represents the product of the given vector and
 * the one provided by the caller.
 * @param {H.math.Vector} other
 * @return {H.math.Vector}
 * @export
 */
H.math.Vector.prototype.multiply = function(other) {
  'use strict';
  if (!H.lang.isInstanceOf(other, H.math.Vector)) {
    throw new H.lang.InvalidArgumentError(this.multiply, 0, other);
  }
  return new H.math.Vector(
      this.x * other.x,
      this.y * other.y
  );
};


/**
 * This method multplies all vector values by given scale.
 * @param {number} scale
 * @return {H.math.Vector}
 * @export
 */
H.math.Vector.prototype.rescale = function(scale) {
  'use strict';
  return new H.math.Vector(
      this.x * scale,
      this.y * scale
  );
};


/**
 * This method returns the length of the vector (magnitude).
 * @return {number}
 * @export
 */
H.math.Vector.prototype.magnitude = function() {
  'use strict';
  return H.math.sqrt(this.dot(this));
};


/**
 * This function returns normalized vector. Normalized vector is
 * a vector which has a magnitude equal to 1.
 * @return {H.math.Vector}
 * @export
 */
H.math.Vector.prototype.normalize = function() {
  'use strict';
  var mag = this.magnitude();
  if (mag !== 0) {
    return new H.math.Vector(
        this.x / mag,
        this.y / mag
    );
  } else {
    return this;
  }

};


