goog.provide('H.math.simplifyDP');

goog.require('H.math');


/**
 * A Douglas-Peucker simplification algorithm for 2D points
 * @param {Array.<H.math.Point>} points The list of points to simplify
 * @param {number} epsilon The tolerance values
 * @param {number=} opt_minIterations The minimum number of iterations for the simplification
 * @param {number=} opt_begin The start index in the points list to start from
 * @param {number=} opt_end The end index in the points list to end at
 * @return {Array.<H.math.Point>} The simplified points
 */
H.math.simplifyDP = function(points, epsilon, opt_minIterations, opt_begin, opt_end) {
  'use strict';
  if (opt_begin === undefined) {
    opt_begin = 0;
    opt_end = points.length - 1;
  }

  var result,
      distSq,
      epsSq = epsilon * epsilon,
      maxDistSq = 0,
      idx = opt_begin,
      x1 = points[opt_begin]['x'],
      y1 = points[opt_begin]['y'],
      x2 = points[opt_end]['x'],
      y2 = points[opt_end]['y'],
      i = opt_begin + 1,
      p,
      x,
      y,
      // slope breaks when start and end point of the line are
      // on the same x value. slope becomes Infinity and breaks
      // the algorithm for finding the maxDist
      slope = (x1 !== x2) ? (y2 - y1) / (x2 - x1) : null;

  // Find the point with the maximum distance
  while (i < opt_end) {
    p = points[i++];
    x = p['x'];
    y = p['y'];
    if (maxDistSq < (distSq = H.math.min(
        // Distance to the linear equation:
        slope !== null ?
            H.math.pow(H.math.abs(y - slope * x - (y1 - slope * x1)), 2) / (H.math.pow(slope, 2) + 1) :
            H.math.abs(x - x1),
        // Distance to p1:
        H.math.pow(x - x1, 2) + H.math.pow(y - y1, 2),
        // Distance to p2:
        H.math.pow(x - x2, 2) + H.math.pow(y - y2, 2)
        ))) {
      maxDistSq = distSq;
      idx = i - 1;
    }
  }
  // If max distance is greater than epsilon or forced levels, recursively simplify:
  if (maxDistSq >= epsSq || opt_minIterations > 0) {
    result = H.math.simplifyDP(points, epsilon, opt_minIterations = (opt_minIterations || 0) - 1, opt_begin, idx);
    // Remove the end point to prevent doubling:
    result.splice(result.length - 1, 1);
    result = result.concat(H.math.simplifyDP(points, epsilon, opt_minIterations, idx, opt_end));
  } else {
    result = [points[opt_begin], points[opt_end]];
  }
  return result;
};
