goog.provide('H.util.Event');
goog.require('goog.events.Event');



/**
 * Base Event class which is used for all events dispatched by any EventTarget
 * within the api.
 * @publish
 * @export
 * @constructor
 * @extends {goog.events.Event}
 */
H.util.Event = goog.events.Event;

/**
 * Object which triggered the event
 * @publish
 * @type {*}
 * @name H.util.Event#target
 */

/**
 * Object which has listener attached
 * @publish
 * @type {*}
 * @name H.util.Event#currentTarget
 */

/**
 * Name of the dispatched event
 * @publish
 * @type {string}
 * @name H.util.Event#type
 */

/**
 * Indicates if preventDefault was called on the current event
 * @publish
 * @type {boolean}
 * @name H.util.Event#defaultPrevented
 */

/**
 * Sets defaultPrevented to true. Which can be used to prevent
 * some default behavior.
 * @publish
 * @method
 * @name H.util.Event#preventDefault
 */


/**
 * Stops propagation for current event.
 * @method
 * @export
 * @publish
 */
H.util.Event.prototype.stopPropagation = goog.events.Event.prototype.stopPropagation;


//######################################################################################################################
//############################################### eventPhase Patch #####################################################
//######################################################################################################################


/**
 * The current event phase is the capturing phase.
 * @publish
 * @type {number}
 */
H.util.Event.prototype['CAPTURING_PHASE'] = 1;


/**
 * The event is currently being evaluated at the target.
 * @publish
 * @type {number}
 */
H.util.Event.prototype['AT_TARGET'] = 2;


/**
 * The current event phase is the bubbling phase
 * @publish
 * @type {number}
 */
H.util.Event.prototype['BUBBLING_PHASE'] = 3;


/**
 * Used to indicate which phase of event flow is currently being evaluated.
 * @publish
 * @type {number}
 */
H.util.Event.prototype['eventPhase'] = 0;
