goog.provide('H.math');


/**
 * This namespace contains mathematical constants, aliased Math functions
 * and math helper functions.
 * @namespace H.math
 * @publish
 */

/*
 *######################################################################################################################
 * Definition of some mathematical aliases, closure compiler will convert them into variable declarations.
 * The advantages are:
 *  - For every invocation of a Math function we spare one scope lookup and one property lookup
 *  - For every read access of a Math constant we spare all scope and property lookups (inline)
 *
 * // SOURCE CODE
 * var H = {};
 * H.math = {};
 * H.math.Math  = Math;
 * H.math.ceil  = H.math.Math.ceil;
 * H.math.floor = H.math.Math.floor;
 * foo = H.math.floor(1) + H.math.ceil(2) +
 *       H.math.floor(3) + H.math.ceil(4);
 * bar = Math.floor(1) + Math.ceil(2) +
 *       Math.floor(3) + Math.ceil(4);
 *
 * // WILL COMPILED TO:
 * var a = Math, b = a.ceil, c = a.floor;
 * foo = c(1) + b(2) + c(3) + b(4);
 * bar = Math.floor(1) + Math.ceil(2) + Math.floor(3) + Math.ceil(4);
 */


/** @ignore */
H.math.Math = Math;


/** @ignore */
H.math.random = H.math.Math.random;


/** @ignore */
H.math.min = H.math.Math.min;


/** @ignore */
H.math.max = H.math.Math.max;


/** @ignore */
H.math.round = H.math.Math.round;


/** @ignore */
H.math.floor = H.math.Math.floor;


/** @ignore */
H.math.ceil = H.math.Math.ceil;


/** @ignore */
H.math.abs = H.math.Math.abs;


/** @ignore */
H.math.log = H.math.Math.log;


/** @ignore */
H.math.sqrt = H.math.Math.sqrt;


/** @ignore */
H.math.pow = H.math.Math.pow;


/** @ignore */
H.math.exp = H.math.Math.exp;


/** @ignore */
H.math.sin = H.math.Math.sin;


/** @ignore */
H.math.asin = H.math.Math.asin;


/** @ignore */
H.math.cos = H.math.Math.cos;


/** @ignore */
H.math.acos = H.math.Math.acos;


/** @ignore */
H.math.tan = H.math.Math.tan;


/** @ignore */
H.math.atan = H.math.Math.atan;


/** @ignore */
H.math.atan2 = H.math.Math.atan2;


/**
 * @ignore
 * @const
 */
H.math.LN2 = H.math.Math.LN2;


/**
 * @ignore
 * @const
 */
H.math.PI = H.math.Math.PI;


/**
 * @ignore
 * @const
 */
H.math.PI_OVER_2 = H.math.PI / 2;


/**
 * @ignore
 * @const
 */
H.math.PI_OVER_4 = H.math.PI / 4;


/**
 * @ignore
 * @const
 */
H.math.TAU = H.math.PI * 2;


/**
 * @ignore
 * @const
 */
H.math.PI3 = H.math.PI * 3;


/**
 * @ignore
 * @const
 */
H.math.PI_OVER_180 = H.math.PI / 180;


/**
 * @ignore
 * @const
 */
H.math._180_OVER_PI = 180 / H.math.PI;


/**
 * @ignore
 * @const
 */
H.math.Infinity = 1 / 0;


/*####################################################################################################################*/


/**
 * A signed 32 bit integer (JS restriction) where bit operator can be applied to.
 * The range is [-2,147,483,648 ... 2,147,483,647] or [-2^31 ... 2^31  1]
 * @publish
 * @typedef {number}
 */
H.math.BitMask;


/**
 * The minimal index to use for increasing indeces. In DEBUG case the value is 0 (because it's more readable),
 * otherwise -2^53, the minimal number which can be stored loss less in IEEE-754 (64 bit float)
 * @const
 * @type {number}
 */
H.math.MIN_IDX = goog.DEBUG ? 0 : H.math.pow(-2, 53); // More readable numbers [0...n] in Debug-Mode


/**
 * The % operator in JavaScript returns the remainder of dividend / divisor, but differs from
 * some other languages in that the result will have the same sign as the
 * dividend. For example, -1 % 8 == -1, whereas in some other languages
 * (such as Python) the result would be 7. This function emulates the more
 * correct modulo behavior, which is useful for certain applications such as
 * calculating an offset index in a circular list.
 * @param {number} dividend The dividend.
 * @param {number} divisor The divisor.
 * @return {number}
 */
H.math.modulo = function(dividend, divisor) {
  'use strict';
  var modulo;
  // If modulo and divisor has the same sign return the modulo, otherwise add the divisor
  return (modulo = dividend % divisor) < 0 === divisor < 0 ? modulo : modulo + divisor;
};


/**
 * Function returns the value narrowed to range
 * @param {number} value
 * @param {number} min
 * @param {number} max
 * @return {number}
 */
H.math.clamp = function(value, min, max) {
  'use strict';
  return ((value -= 0) > max) ? max : (value < min) ? min : value;
};


/**
 * Detects if a is equal be considering the given tolerance.
 * @param {number} a The value to compare to b.
 * @param {number} b The value to compare to a.
 * @param {number} tolerance The value to compare to a.
 * @return {boolean} true, if a matches b considering the given tolerance.
 */
H.math.match = function(a, b, tolerance) {
  'use strict';
  return (a >= (b - tolerance) && a <= (b + tolerance));
};


/**
 * Checks whether a value is in range.
 * It is checking both cases (extreme1 < extreme2 and extreme1 >= extreme2).
 * Moreover the range is enlarged by the given tolerance
 * @param {number} value The value which will be compared to the other ones.
 * @param {number} extreme1 The first extreme value of the range.
 * @param {number} extreme2 The second extreme value of the range.
 * @param {number} tolerance The value by which the range will be enlarged.
 * @return {boolean} true, if a matches b considering the given tolerance.
 */
H.math.inRange = function(value, extreme1, extreme2, tolerance) {
  'use strict';
  if (!(tolerance -= 0)) {
    tolerance = 0;
  }
  return extreme1 < extreme2 ?
      value >= extreme1 - tolerance && value <= extreme2 + tolerance :
      value >= extreme2 - tolerance && value <= extreme1 + tolerance;
};


/**
 * Returns the distance between the given point and given line (vecor).
 * The algorithm is based on vector calculation, working plane, vector cross product and so on
 * @param {number} x X value of the point.
 * @param {number} y Y value of the point.
 * @param {number} ax X value of the first vector point.
 * @param {number} ay Y value of the first vector point.
 * @param {number} bx X value of the second vector point.
 * @param {number} by Y value of the second vector point.
 * @return {number} true, if a matches b considering the given tolerance.
 */
H.math.getVectorDistance = function(x, y, ax, ay, bx, by) {
  'use strict';
  return H.math.sqrt(H.math.pow(((x - bx) * (ay - by) - (y - by) * (ax - bx)), 2) /
      (H.math.pow((ax - bx), 2) + H.math.pow((ay - by), 2)));
};


/**
 * Enumeration is used by isCoveredBy and signifies which part of the shape covers point.
 * @const
 * @enum {number}
 */
H.math.CoverType = {
  NONE: 0,
  VERTEX: 1,
  EDGE: 2,
  SURFACE: 3
};


/**
 * This method checks if the x/y coordinate is covered by the given shape.
 * @name jslocation.util.Point.isCoveredBy
 * @publish
 * @param {number} x The x component of the coordinate to check
 * @param {number} y The y component of the coordinate to check
 * @param {Array.<number>} path The path of a polygon; the closing leg is not required
 * @param {number} lineWidth The width of the line
 * @param {boolean=} opt_closed A Boolean indicating if the path should
 * be treated as closed (polygon)
 *
 * @return {H.math.CoverType}
 */
H.math.isCoveredBy = function(x, y, path, lineWidth, opt_closed) {
  'use strict';
  var len = path.length,
      match = H.math.match,
      getVectorDistance = H.math.getVectorDistance,
      i = len,
      x1, y1,
      x2, y2,
      x1Prev = path[0],
      yi,
      xi,
      hit = H.math.CoverType.NONE,
      above = 0,
      beneath = 0,
      tol = lineWidth / 2 || 0,
      lastPos = opt_closed ? 1 : 3;

  /* Note: we have to walk through the whole shape unless the a vertex hit has been detected, because
   * even if we detected an edge hit, there could be a vertex of a crossing line, too. As the vertex has
   * the highest priority we have to check at least all shape points for a direct hit.
   */
  while (hit != H.math.CoverType.VERTEX && i > lastPos) {
    y1 = path[--i];
    x1 = path[--i];
    y2 = path[i ? i - 1 : (len + (i - 1)) % len];
    x2 = path[i ? i - 2 : (len + (i - 2)) % len];

    // Does X-Ray intersect point x1/y1 or x2/y2? (considering the line width)
    if (match(x1, x, tol) && match(y1, y, tol) || match(x2, x, tol) && match(y2, y, tol)) {
      hit = H.math.CoverType.VERTEX;
    }
    /* X-Ray intersects the first point of the leg?
     * Note: we can skip the following if clause if a hit (can only be "E" here) has been already detected.
     * We are only looking further for a potential direct vertex hit.
     */
    else if (!hit && x1 === x) {
      // It's a vertical leg and direct hitting?
      (hit = (x2 === x && (y1 < y && y2 > y || y1 > y && y2 < y)) ?
          H.math.CoverType.EDGE : H.math.CoverType.NONE) ||
          // Current & previous leg placed opposite to the X-Ray (previous leg could also go along the X-Ray)?
          (x1Prev <= x && x2 > x || x1Prev >= x && x2 < x) &&
          // Count the y1 point:
          (y1 >= y ? ++above : ++beneath);

      // In case of a line width bigger than 0, we have to check here, if the point is part of the edge,
      // because otherwise it won't be checked anymore for "x1 === x".
      hit = H.math.inRange(y, y1, y2, tol) && getVectorDistance(x, y, x1, y1, x2, y2) <= tol ?
          H.math.CoverType.EDGE :
          H.math.CoverType.NONE;
    }
    /* X-Ray intersects the leg BETWEEN the points?
     * Note: we can skip the following if clause if a hit (can only be "E" here) has been already detected.
     * We are only looking further for a potential direct vertex hit.
     */
    else if (!hit && H.math.inRange(x, x1, x2, tol)) {
      // Direct hitting the leg?
      // This if clause is only for deciding further on, if the inner shape of a polygon has been hit.
      if (x1 < x && x2 > x || x1 > x && x2 < x) {
        // in 2.5 it was as follows
        // hit = y === (yi = y1 + (y2 - y1) * ((x - x1) / (x2 - x1))),
        yi = y1 + (y2 - y1) * ((x - x1) / (x2 - x1)),
        // Count the intersection point yi:
        above += yi > y,
        beneath += yi < y;
      }

      // Checks if an edge has been hit considering the line width.
      hit = H.math.inRange(y, y1, y2, tol) && getVectorDistance(x, y, x1, y1, x2, y2) <= tol ?
          H.math.CoverType.EDGE :
          H.math.CoverType.NONE;
    }

    x1Prev = x1;
  }
  if (opt_closed && hit === H.math.CoverType.SURFACE && hit || hit) {
    return hit;
  }
  // Double check, because points counted if X-Ray goes along the previous leg:
  if (opt_closed && beneath !== 0 && (above % 2) !== 0) {
    return H.math.CoverType.SURFACE;
  }
  return H.math.CoverType.NONE;
};


/**
 * Returns sign of the number or 0 if 0
 * @param {number} num
 * @return {number} 1, -1 or 0
 */
H.math.sign = function(num) {
  'use strict';
  return num === 0 ? 0 : num > 0 ? 1 : -1;
};

