goog.provide('H.map.SpatialStyle');

goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.util');



/**
 * The SpatialStyle class represents a style with which spatial objects like polylines and polygons are drawn.
 * A SpatialStyle instance is always treated as immutable to avoid inconstiencies and must not modified.
 * @export
 * @publish
 * @final
 * @constructor
 * @param {(H.map.SpatialStyle|H.map.SpatialStyle.Options)=} opt_options The optional style attributes
 */
H.map.SpatialStyle = function(opt_options) {
  'use strict';
  this.mergeOptions_(H.map.SpatialStyle, opt_options);
};


/**
 * To merge style attributes into this spatial style instance
 * @private
 * @throws {H.lang.InvalidArgumentError}
 * @param {Function} caller The caller to use for InvalidArgumentError.
 * @param {(H.map.SpatialStyle|H.map.SpatialStyle.Options)=} opt_options The optional style attributes
 * @return {H.map.SpatialStyle}
 */
H.map.SpatialStyle.prototype.mergeOptions_ = function(caller, opt_options) {
  'use strict';
  var i,
      key,
      value,
      isSpatialStyle,
      isEffective;

  if (opt_options) {
    i = H.map.SpatialStyle.ATTRIBUTE_COUNT_;
    isSpatialStyle = H.lang.isInstanceOf(opt_options, H.map.SpatialStyle);
    while (i--) {
      key = H.map.SpatialStyle.ATTRIBUTE_NAMES_[i];
      if (opt_options.hasOwnProperty(key)) {
        value = opt_options[key];
        // If the opt_options are already an instance of SpatialTile we store the property without additional checks:
        if (isSpatialStyle) {
          this[key] = value;
        } else {
          if ((value = H.map.SpatialStyle.validators_[i](value)) === H.map.SpatialStyle.INVALID_) {
            throw new H.lang.InvalidArgumentError(caller, 0, key + ': ' + opt_options[key]);
          }
          // Only if the value is not equal to the current/prototypical value and not undefined we store it:
          if (value !== this[key] && value !== H.lang.UNDEF) {
            this[key] = value;
          }
        }
      }
    }

    // Update isStroking:
    isEffective = isSpatialStyle ?
        opt_options.isStroking : this['lineWidth'] > 0 && H.util.isNonTransparent(this['strokeColor']);
    this.isStroking = isEffective;

    // Update isFilling:
    isEffective = isSpatialStyle ? opt_options.isFilling : H.util.isNonTransparent(this['fillColor']);
    this.isFilling = isEffective;

  }

  return this;
};


//############################################# EFFECTIVE STYLES #######################################################
/**
 * Idicates wheter the style is effectively filling:
 * The fill-color is neither "none" nor it is non-transparent
 * DON'T publish, API internally use only by SpatialTile!
 * @type {boolean}
 */
H.map.SpatialStyle.prototype.isFilling = true;


/**
 * Idicates wheter the style is effectively stroking:
 * The lineWidth is greater than 0 and the stroke-color is neither "none" nor it is non-transparent
 * DON'T publish, API internally use only by SpatialTile!
 * @type {boolean}
 */
H.map.SpatialStyle.prototype.isStroking = true;


//######################################## DEFAULT ATTRIBUTES ##########################################################
/**
 * The list of all style's attribute names
 * @private
 * @const
 * @type {Array.<string>}
 */
H.map.SpatialStyle.ATTRIBUTE_NAMES_ = [
  // The list will be populated during definition of the prototype attributes in the code below
];


/**
 * The color of the stroke in CSS syntax, default is {@code 'rgba(0, 85, 170, 0.6)'}.
 * @name H.map.SpatialStyle.prototype.strokeColor
 * @publish
 * @type {string}
 */
H.map.SpatialStyle.prototype[H.map.SpatialStyle.ATTRIBUTE_NAMES_[0] = 'strokeColor'] = 'rgba(0,85,170,.6)';


/**
 * The filling color in CSS syntax, default is {@code 'rgba(0, 85, 170, 0.4)'}.
 * @name H.map.SpatialStyle.prototype.fillColor
 * @publish
 * @type {string}
 */
H.map.SpatialStyle.prototype[H.map.SpatialStyle.ATTRIBUTE_NAMES_[1] = 'fillColor'] = 'rgba(0,85,170,.4)';


/**
 * The width of the line in pixels, default is {@code 2}.
 * @name H.map.SpatialStyle.prototype.lineWidth
 * @publish
 * @type {number}
 */
H.map.SpatialStyle.prototype[H.map.SpatialStyle.ATTRIBUTE_NAMES_[2] = 'lineWidth'] = 2;


/**
 * The style of the end caps for a line, default is {@code 'round'}.
 * @name H.map.SpatialStyle.prototype.lineCap
 * @publish
 * @type {H.map.SpatialStyle.LineCap}
 */
H.map.SpatialStyle.prototype[H.map.SpatialStyle.ATTRIBUTE_NAMES_[3] = 'lineCap'] = 'round';


/**
 * The type of corner created, when two lines meet, default is {@code 'miter'}.
 * @name H.map.SpatialStyle.prototype.lineJoin
 * @publish
 * @type {H.map.SpatialStyle.LineJoin}
 */
H.map.SpatialStyle.prototype[H.map.SpatialStyle.ATTRIBUTE_NAMES_[4] = 'lineJoin'] = 'miter';


/**
 * The miter length is the distance between the inner corner and the outer corner where two lines meet.
 * The default is {@code 10}.
 * @name H.map.SpatialStyle.prototype.miterLimit
 * @publish
 * @type {number}
 */
H.map.SpatialStyle.prototype[H.map.SpatialStyle.ATTRIBUTE_NAMES_[5] = 'miterLimit'] = 10;


/**
 * The line dash pattern as an even numbered list of distances to alternately produce a line and a space.
 * The default is {@code [ ]}.
 * @name H.map.SpatialStyle.prototype.lineDash
 * @publish
 * @type {Array.<number>}
 */
H.map.SpatialStyle.prototype[H.map.SpatialStyle.ATTRIBUTE_NAMES_[6] = 'lineDash'] = H.util.emptyArray;


/**
 * The phase offset of the line dash pattern
 * The default is {@code 0}.
 * @name H.map.SpatialStyle.prototype.lineDashOffset
 * @publish
 * @type {number}
 */
H.map.SpatialStyle.prototype[H.map.SpatialStyle.ATTRIBUTE_NAMES_[7] = 'lineDashOffset'] = 0;


/**
 * The count of all style properties
 * @private
 * @const
 * @type {number}
 */
H.map.SpatialStyle.ATTRIBUTE_COUNT_ = H.map.SpatialStyle.ATTRIBUTE_NAMES_.length;


/**
 * This method checks value-equality with another style.
 * @param {H.map.SpatialStyle|H.map.SpatialStyle.Options} other the style to compare with
 * @return {boolean} true if the styles are value-equal, otherwise false
 * @export
 * @publish
 */
H.map.SpatialStyle.prototype.equals = function(other) {
  'use strict';
  var i,
      key,
      equal = this === other;

  if (!equal && other) {
    for (i = 0; i < H.map.SpatialStyle.ATTRIBUTE_COUNT_; i++) {
      key = H.map.SpatialStyle.ATTRIBUTE_NAMES_[i];
      if (this[key] !== other[key]) {
        break;
      }
    }
    equal = i === H.map.SpatialStyle.ATTRIBUTE_COUNT_;
  }
  return equal;
};


/**
 * Returns a copy of spatial style object and replaces the passed style attributes into it.
 * Must NOT published, API internally used only!
 * @export
 * @publish
 * @param {H.map.SpatialStyle.Options=} opt_attributes The style attributes to set on the copied style instance
 * @return {H.map.SpatialStyle}
 */
H.map.SpatialStyle.prototype.getCopy = function(opt_attributes) {
  'use strict';
  return new H.map.SpatialStyle(this).mergeOptions_(this.getCopy, opt_attributes);
};


/**
 * Special return value for validators_ to signalize an invalid value
 * @private
 * @type {Object}
 */
H.map.SpatialStyle.INVALID_ = {};


/**
 * The validators for all style properties, indexes aligned with ATTRIBUTE_NAMES_.
 * A check against undefined should be done beforehand!
 * A validator returns .INVALID_ to signalize that the value is invalid.
 * @private
 * @type {Array.<function(?):(string|number|Object)>}
 */
H.map.SpatialStyle.validators_ = [
  /** gjslint needs this comment  :-/
   * @param {?} strokeColor
   * @return {string|Object}
   */
  function(strokeColor) {
    return strokeColor ?
        H.lang.String(strokeColor) :
        H.map.SpatialStyle.INVALID_;
  },
  function(fillColor) {
    return fillColor ?
        H.lang.String(fillColor) :
        H.map.SpatialStyle.INVALID_;
  },
  function(lineWidth) {
    return lineWidth >= 0 && lineWidth <= 100 ?
        +lineWidth :
        H.map.SpatialStyle.INVALID_;
  },
  function(lineCap) {
    return (lineCap = H.lang.String(lineCap)) === 'butt' || lineCap === 'square' || lineCap === 'round' ?
        lineCap :
        H.map.SpatialStyle.INVALID_;
  },
  function(lineJoin) {
    return lineJoin === 'round' || lineJoin === 'bevel' || lineJoin === 'miter' ?
        lineJoin :
        H.map.SpatialStyle.INVALID_;
  },
  function(miterLimit) {
    return miterLimit > 0 && miterLimit <= 100 ?
        +miterLimit :
        H.map.SpatialStyle.INVALID_;
  },
  function(lineDash) {
    return lineDash && lineDash.every && lineDash.every(H.lang.isNumberLike) ?
        lineDash :
        H.map.SpatialStyle.INVALID_;
  },
  function(lineDashOffset) {
    return H.lang.isNaN(+lineDashOffset) ?
        H.map.SpatialStyle.INVALID_ :
        +lineDashOffset;
  }
];


//############################################# TYPE DEFINITIONS #######################################################
/**
 * The style of the end caps for a line, one of {@code 'butt'}, {@code 'round'} or {@code 'square'}.
 * @typedef {string}
 * @publish
 */
H.map.SpatialStyle.LineCap;


/**
 * The type of corner created, when two lines meet, one of {@code 'round'}, {@code 'bevel'} or {@code 'miter'}.
 * @typedef {string}
 * @publish
 */
H.map.SpatialStyle.LineJoin;


/**
 * Options used to initialize a style.
 * If a property is not set, the default value from {@link H.map.SpatialStyle} is taken.
 * @publish
 *
 * @typedef {{
 *   strokeColor: (string|undefined),
 *   fillColor: (string|undefined),
 *   lineWidth: (number|undefined),
 *   lineCap: (H.map.SpatialStyle.LineCap|undefined),
 *   lineJoin: (H.map.SpatialStyle.LineJoin|undefined),
 *   miterLimit: (number|undefined),
 *   lineDash: (Array.<number>|undefined),
 *   lineDashOffset: (number|undefined)
 * }}
 * @property {string=} strokeColor
 *           The color of the stroke in CSS syntax.
 * @property {string=} fillColor
 *           The color of the stroke in CSS syntax.
 * @property {number=} lineWidth
 *           The width of the line in pixels, default is {@code 2}. The maximum supported line width is {@code 100}.
 * @property {H.map.SpatialStyle.LineCap=} lineCap
 *           The style of the end caps for a line.
 * @property {H.map.SpatialStyle.LineJoin=} lineJoin
 *           The type of corner created, when two lines meet.
 * @property {number=} miterLimit
 *           The miter limit in pixel, default is {@code 10}. The maximum supported miter limit is {@code 100}
 * @property {Array.<number>} lineDash
 *           The line dash pattern as an even numbered list of distances to alternately produce a line and a space.
 *           If the browser doesn't support this feature this style property is ignored.
 * @property {number=} lineDashOffset
 *           The phase offset of the line dash pattern
 */
H.map.SpatialStyle.Options;


/**
 * This constant represents the maximum line width which can be used for rendering.
 * @publish
 * @const
 * @type {number}
 */
H.map.SpatialStyle['MAX_LINE_WIDTH'] = 100;


/**
 * This static member defines the default style for spatial objects on the map.
 * It's value is
 * <code>
 * {
 *   strokeColor: '#05A',
 *   fillColor: 'rgba(0, 85, 170, 0.4)'
 *   lineWidth: 1,
 *   lineCap: 'round',
 *   lineJoin: 'miter',
 *   miterLimit: 10,
 *   lineDash: [ ],
 *   lineDashOffset: 0
 * }
 * </code>
 * @export
 * @publish
 *
 * @type {H.map.SpatialStyle}
 */
H.map.SpatialStyle.DEFAULT_STYLE = new H.map.SpatialStyle();
