goog.provide('H.map.Polyline');

goog.require('H.geo.QuadTree');
goog.require('H.geo.Strip');
goog.require('H.map.ArrowStyle');
goog.require('H.map.GeoShape');



/**
 * This class represents a polyline in geo-space. It is defined by a path
 * containing the vertices of a polyline (lat, lng, alt values) and a pen
 * to use when tracing the path on the map.
 * @publish
 * @export
 * @extends {H.map.GeoShape}
 * @final
 * @constructor
 * @param {H.geo.Strip} strip the strip describing this polygon's vertices
 * @param {H.map.Polyline.Options=} opt_options optional initialization parameters
 */
H.map.Polyline = function(strip, opt_options) {
  'use strict';
  var arrows;
  H.map.Polyline.superClass_.constructor.call(this, false, strip,
      /** @type {H.map.Spatial.Options} */ (opt_options));
  // If arrows option defined, we set the arrows property:
  if (opt_options && (arrows = opt_options['arrows'])) {
    this.setArrows(arrows);
  }
};
H.lang.inherits(H.map.Polyline, H.map.GeoShape);


/**
 * @override
 * @export
 */
H.map.Polyline.prototype.getBounds = function() {
  'use strict';
  return this.quadTree_.getBounds();
};


/**
 * @override
 * @export
 */
H.map.Polyline.prototype.setStrip = function(strip) {
  'use strict';
  var lastLat,
      lastLng,
      quadEntry,
      quadTree = this.quadTree_ = new H.geo.QuadTree();

  strip.eachLatLngAlt(function(lat, lng, alt, i) {
    if (i > 0) {
      quadEntry = quadTree.insertLine(lastLat, lastLng, lat, lng);
      quadEntry.$insertionIndex$ = i - 1;
    }
    lastLat = lat;
    lastLng = lng;
  });

  return H.map.Polyline.superClass_.setStrip.call(this, strip);
};


/**
 * To get the arrow style from this object or {@code undefined} if no arrow style is defined.
 * @export
 * @publish
 * @return {H.map.ArrowStyle|undefined}
 */
H.map.Polyline.prototype.getArrows = function() {
  'use strict';
  return this.arrows && this.arrows.getClone();
};


/**
 * To set the arrow style of this object.
 * @export
 * @publish
 * @param {(H.map.ArrowStyle|H.map.ArrowStyle.Options)=} opt_arrows the arrow style to be applied
 * @return {H.map.Polyline} the Polyline instance itself
 */
H.map.Polyline.prototype.setArrows = function(opt_arrows) {
  'use strict';
  var currentArrows = this.getArrows(),
      arrows = opt_arrows,
      changed = false;

  //possible cases:
  // A: arrows == undefined & currentArrows != undefined
  // B: arrows != undefined & currentArrows == undefined
  // C: arrows != undefined & currentArrows != undefined
  // D: arrows == undefined & currentArrows == undefined
  if (!arrows && currentArrows) {
    // CASE A: set and change
    delete this.arrows;
    changed = true;
  } else if (arrows) {
    if (!currentArrows || !currentArrows.equals(arrows)) {
      //CASE B & C : set and change.
      this.arrows = new H.map.ArrowStyle(/** @type {H.map.ArrowStyle.Options} */(arrows));
      changed = true;
    }
  }
  // CASE D: we do not care both are undefined, no change necessary

  if (changed) {
    this.invalidate(H.map.provider.Invalidations.Flag.VISUAL);
  }
  return this;
};


/**
 * This method clips this polyline against a rectangular area and returns the intersecting sub-lines.
 * @param {H.geo.Rect} geoRect
 * @return {Array.<Array.<number>>}
 * @export
 * @publish
 */
H.map.Polyline.prototype.clip = function(geoRect) {
  // TODO MAPSJS-448
  // Should be deprecated and replaced with something like
  //     GeoShape.instersects(bounds): boolean
  // which will be much more performant than calling getStripsForGeoRect_
  'use strict';
  // H.lang.deprecated(H.map.Polyline.prototype.clip);
  return this.getStripsForGeoRect_(geoRect);
};


//###########################################  CLIPPING & PROJECTION  ##################################################
/**
 * @override
 * @export
 */
H.map.Polyline.prototype.getGeometry = function(tile) {
  'use strict';
  var pxClipRect,
      data;

  data = this.getStripsForGeoRect_(tile.getGeoClipRect());
  // TODO We should project the WHOLE geometry only ONCE instead of project again and again overlapping parts of it:
  // TODO We should project only if there are any clipped data!
  data = this.projectGeoStrips(data, false, tile.getPxProj());
  // Clip the projected geometry against the pixel rectangle:
  pxClipRect = tile.getPxClipRect();
  data = H.math.clipping.clipStrips(data,
      pxClipRect['left'], pxClipRect['top'],
      pxClipRect['right'], pxClipRect['bottom']);

  // Satisfy CC - TODO Check what's wrong here!
  data = /** @type {Array.<Array.<H.math.Point>>} */ (data);

  return data;
};


/**
 * Compares two leg entries returned from a quad tree by their insertion index use for Array#sort()
 * @private
 * @param {H.util.QuadTree.Entry} entryA
 * @param {H.util.QuadTree.Entry} entryB
 * @return {number}
 */
H.map.Polyline.treeEntryIndexComparator_ = function(entryA, entryB) {
  'use strict';
  return entryA.$insertionIndex$ - entryB.$insertionIndex$;
};


/**
 * Computes a collection of strips which intersects with the given geographical rectangle
 * @private
 * @param {H.geo.Rect} geoRect The geographical rectangular area to query
 * @return {Array.<Array.<number>>}
 */
H.map.Polyline.prototype.getStripsForGeoRect_ = function(geoRect) {
  'use strict';

  // In general clipping of polyline is easy because we have just to check if any leg is crossing date border
  // and make sub strip from it. At the end all legs are clipped against the rectangle. There is no magic involved.

  var entries,
      entry,
      coords = this.strip.getLatLngAltArray(),
      length,
      i,
      j,
      index,
      offset,
      lastIndex = -2,
      lat,
      lastLat,
      lng,
      lastLng,
      strips = [],
      strip,
      clippedStrips,
      clippedStripsTmp,
      clippedStripTmp,
      coordArray,
      isDBC = H.geo.Strip.isDBC;

  // Query all legs which intersect the given rectangle and sort them in strip order:
  entries = this.quadTree_.intersectBounds(geoRect).
      sort(H.map.Polyline.treeEntryIndexComparator_);

  // Aggregate the single legs like   [a->b, b->c, e->f, f->g]
  // into a list of sub-strips like   [[a->b->c], [e->f->g]]
  length = entries.length;
  for (i = 0; i < length; i++) {
    entry = entries[i];
    index = entry.$insertionIndex$;
    offset = index * 3;
    // A hole in the (ordered) index sequence -> new strip
    if (lastIndex + 1 !== index) {
      strips.push(strip = [lastLat = coords[offset], lastLng = coords[offset + 1]]);
    }
    lastIndex = index;

    offset += 3;

    lat = coords[offset];
    lng = coords[offset + 1];

    strip.push(lastLat = lat, lastLng = lng);
  }

  // If a polyline is crossing the dateborder than it is clipped to sub shapes which are only on the "real" world
  // (longitude is between -180 and +180) and has only start- and/or end-points in the "virtual" world
  if (this.strip.getDBCs()) {
    //preprocessing polyline
    clippedStripsTmp = [];
    for (i = 0; i < strips.length; i++) {
      strip = strips[i];
      length = strip.length;
      if (length) {
        clippedStripTmp = [];
        for (j = 0; j < length; j += 2) {
          // Not date border crossing leg:
          if (j == 0 || j > 0 && (!isDBC(strip[j + 1], strip[j - 1]))) {
            clippedStripTmp.push(strip[j], strip[j + 1]);
          // Date border crossing leg:
          } else {
            clippedStripTmp.push(strip[j], strip[j + 1]);
            clippedStripsTmp.push(clippedStripTmp);
            // Creates new strip:
            clippedStripTmp = [];
            // Shifts new strip first leg outside the real world in longitude way:
            strip[j - 1] += strip[j - 1] < 0 ? +360 : -360;
            // Adds leg into new strip (previous coordinate and actual one):
            clippedStripTmp.push(strip[j - 2], strip[j - 1], strip[j], strip[j + 1]);
          }
        }
        // Don't add empty arrays, which should be generated by condition above:
        clippedStripsTmp.push(clippedStripTmp);
      }
    }
    strips = clippedStripsTmp;
  }
  return strips;
};


//###################################################  OPTIONS  ########################################################
/**
 * Options which are used to initialize a polyline
 * @publish
 * @typedef {{
 *   style: (H.map.SpatialStyle|H.map.SpatialStyle.Options|undefined),
 *   arrows: (H.map.ArrowStyle|H.map.ArrowStyle.Options|undefined),
 *   visibility: (boolean|undefined),
 *   zIndex: (number|undefined),
 *   min: (number|undefined),
 *   max: (number|undefined),
 *   provider: (H.map.provider.ObjectProvider|null|undefined)
 * }}
 *
 * @property {(H.map.SpatialStyle|H.map.SpatialStyle.Options)=} style the
 *           style to be used when tracing the polyline
 * @property {(H.map.ArrowStyle|H.map.ArrowStyle.Options)=} arrows
 *           The arrows style to be used when rendering the polyline.
 * @property {boolean=} visibility An optional boolean value indicating whether this
 *           map object is visible, default is {@code true}
 * @property {number=} zIndex The z-index value of the map object, default is {@code 0}
 * @property {number=} min
 *           The minimum zoom level for which the object is visible, default is {@code -Infinity}
 * @property {number=} max
 *           The maximum zoom level for which the object is visible, default is {@code Infinity}
 * @property {(H.map.provider.Provider|null)=} provider
 *           The provider of this object. This property is only needed if a customized Implementation of
 *           ObjectProvider wants to instantiate an object.
 */
H.map.Polyline.Options;
