goog.provide('H.lang.IllegalOperationError');

goog.require('H.lang');



/**
 * An error to throw if an illegal operation happens
 *
 * @example
 * Foo.prototype.bar = function() {
 *   if(this.isDisposed()) {
 *     throw new H.lang.IllegalOperationError(H.Foo.prototype.bar, 'Instance already disposed');
 *   }
 * }
 *
 * @publish
 * @export
 * @extends {Error}
 *
 * @constructor
 * @param {Function=} opt_caller The calling function
 * @param {*=} opt_message
 */
H.lang.IllegalOperationError = function(opt_caller, opt_message) {
  'use strict';
  var message = [];
  if (opt_caller) {
    message.push(H.lang.getSymbolPath(opt_caller));
  }
  if (arguments.length > 1) {
    message.push(opt_message);
  }
  this['message'] = message.join(' ');
};
goog.inherits(H.lang.IllegalOperationError, Error);


/** @override */
H.lang.IllegalOperationError.prototype = Error();


/** @override */
H.lang.IllegalOperationError.prototype['name'] = 'IllegalOperationError';
