goog.provide('H.util.animation.ease');

/**
 * This mamespace contains easing functions used for Animation class.
 * @namespace H.util.animation.ease
 * @publish
 */


/**
 * This function defines linear ease.
 * @publish
 * @export
 * @param {number} val A value in range [0..1] to translate
 * @return {number} the translated value
 */
H.util.animation.ease.LINEAR = function(val) {
  'use strict';
  return val;
};


/**
 * This function defines quadratic ease in.
 * @publish
 * @export
 * @param {number} val A value in range [0..1] to translate
 * @return {number} the translated value
 */
H.util.animation.ease.EASE_IN_QUAD = function(val) {
  'use strict';
  return val * val;
};


/**
 * This function defines quadratic ease out.
 * @publish
 * @export
 * @param {number} val A value in range [0..1] to translate
 * @return {number} the translated value
 */
H.util.animation.ease.EASE_OUT_QUAD = function(val) {
  'use strict';
  return -val * (val - 2);
};


/**
 * This function defines ease in and out with slope.
 * @publish
 * @export
 * @param {number} val A value in range [0..1] to translate
 * @return {number} the translated value
 */
H.util.animation.ease.EASE_IN_OUT_QUINT = function(val) {
  'use strict';
  val *= 2;
  return val < 1 ?
      H.math.pow(val, 5) / 2 :
      (val -= 2, H.math.pow(val, 5) / 2 + 1);
};


/**
 * This function defines ease out with circ function
 * @param {number} val Parameter between 0..1
 * which is translated to output number between 0..1 regarding transforming function.
 * @return {number} 0..1
 * @export
 * @publish
 */
H.util.animation.ease.EASE_OUT_CIRC = function(val) {
  'use strict';
  return H.math.sqrt(2 * val - val * val);
};


