goog.provide('H.Map');

goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.map.ChangeEvent');
goog.require('H.map.DataModel');
goog.require('H.map.Group');
goog.require('H.map.Imprint');
goog.require('H.map.Object');
goog.require('H.map.ViewModel');
goog.require('H.map.ViewPort');
goog.require('H.map.layer.Layer');
goog.require('H.map.layer.ObjectLayer');
goog.require('H.map.provider.LocalObjectProvider');
goog.require('H.map.render.p2d.RenderEngine');
goog.require('H.util.ChangeEvent');
goog.require('H.util.EventTarget');



/**
 * Map class defines map instance in the application. By creating this
 * object you will initialize a visible map object which is attached
 * to the provided dom element. Map class is an entry point to all
 * operations related to layers, map objects and geo-screen transformations.
 * By specifying options you can initialize map with predefined view.
 *
 * @example
 * var platform = new H.service.Platform({
 *   app_id: "{your app id}",
 *   app_code: "{your app code}"
 * });
 * var maptypes = platform.createDefaultLayers();
 *
 * var map = new H.Map(document.getElementById('mapdiv'), maptypes.normal.map, {
 *   center: new H.geo.Point(0, 51),
 *   zoom: 8
 * });
 *
 * @publish
 * @export
 * @extends {H.util.EventTarget}
 * @final
 * @constructor
 * @param {Element} element html element into which the map will be rendered
 * @param {H.map.layer.Layer} baseLayer The layer to be used as the base (bottom most) layer.
 * @param {H.Map.Options=} opt_options additional map options
 */
H.Map = function(element, baseLayer, opt_options) {
  'use strict';
  var center,
      zoom,
      bounds,
      viewModel,
      dataModel,
      key,
      val;

  H.Map.superClass_.constructor.call(this);

  this.engineExitCallback_ = H.lang.bind(H.Map.prototype.engineExitCallback_, this);

  this.options_ = H.Map.adjustOptions_(opt_options, H.Map);

  this.viewModel_ = viewModel = new H.map.ViewModel();

  /**
   * flag to signal that mapviewchange has started
   * @private
   */
  this.mapviewchangeStarted_ = false;

  /**
   * Handle to mapviewchangeEnd timeout
   * @private
   */
  this.mapviewchangeEndHandle_;

  viewModel.addEventListener(viewModel.EventType.SYNC, this.onSync_, false, this);

  //initialize map root element and attach it to parent
  this.createMapElement_(element);

  this.viewPort_ = new H.map.ViewPort(this.element_, /** @type {H.map.ViewPort.Options} */ (this.options_));
  this.viewPort_.addEventListener(this.viewPort_.EventType.SYNC, this.onSync_, false, this);

  dataModel = this.dataModel_ = new H.map.DataModel(this.options_['layers']);

  dataModel.addEventListener(dataModel.EventType.SET, this.onDataModelChange_, false, this);
  dataModel.addEventListener(dataModel.EventType.ADD, this.onDataModelChange_, false, this);
  dataModel.addEventListener(dataModel.EventType.REMOVE, this.onDataModelChange_, false, this);

  // Setup the local object provider
  this.localObjectProvider_ = new H.map.provider.LocalObjectProvider();
  // Setup and add object layer
  this.objectLayer_ = new H.map.layer.ObjectLayer(
      this.localObjectProvider_, /** @type {H.map.layer.ObjectLayer.Options} */ (this.options_));
  this.addLayer(this.objectLayer_);

  this.baseLayer_ = null;
  this.dataModel_.add(baseLayer, 0);

  this.engine_ = this.createEngine_(this.options_['engineType'] ||
      H.Map.EngineType['P2D'], this.options_);

  center = this.options_['center'] || new H.geo.Point(0, 0);
  zoom = +this.options_['zoom'] || 0;
  bounds = this.options_['bounds'];

  this.onFirstRender_ = H.lang.bind(this.onFirstRender_, this);

  this.dispatchMapviewchangeEnd_ = H.lang.bind(
      this.dispatchMapviewchangeEnd_, this);

  if (H.lang.isInstanceOf(bounds, H.geo.Rect)) {
    this.setViewBounds(bounds);
  } else {
    this.setCenter(center);
    this.setZoom(zoom);
  }

  key = 'imprint';
  val = this.options_[key];
  // Check against null|undefined|Object:
  if (val !== H.lang.UNDEF && typeof val !== 'object') {
    throw new H.lang.InvalidArgumentError(H.Map, 1, key);
  }
  /**
   * @type {?H.map.Imprint}
   * @private
   */
  this.imprint_ = val === null ? val : new H.map.Imprint(this, val);

  val = this.options_['autoColor'];
  /**
   * @type {boolean}
   * @private
   */
  this.autoColor_ = val === H.lang.UNDEF || !!val;

  this.adjustColors_();

};
H.lang.inherits(H.Map, H.util.EventTarget);


/**
 * @private
 * @param {Object|undefined} options
 * @param {Function} symbol The symbol to use for the exception
 * @return {H.Map.Options}
 */
H.Map.adjustOptions_ = function(options, symbol) {
  'use strict';
  var key,
      val;
  key = 'pixelRatio';
  if (options) {
    val = options[key];
    if (val === H.lang.UNDEF) {
      options[key] = 1;
    } else if (!(val > 0)) {
      throw new H.lang.InvalidArgumentError(symbol, 0, 'pixelRatio' + ': ' + val);
    }
  } else {
    options = {};
    options[key] = 1;
  }
  if (!options['renderBaseBackground']) {
    options['renderBaseBackground'] = {'lower': 3, 'higher': 2};
  }
  return /** @type {H.Map.Options} */ (options);
};


/**
 * View model instance
 * @type {H.map.ViewModel}
 * @private
 */
H.Map.prototype.viewModel_;


/**
 * Viewport instance
 * @type {H.map.ViewPort}
 * @private
 */
H.Map.prototype.viewPort_;


/**
 * Engine instance
 * @type {H.map.render.RenderEngine}
 * @private
 */
H.Map.prototype.engine_;


/**
 * Data model instance
 * @type {H.map.DataModel}
 * @private
 */
H.Map.prototype.dataModel_;


/**
 * Threshold (ms) after which mapviewchangeend is fired.
 * @type {number}
 * @private
 */
H.Map.prototype.MAPVIEWCHANGEEND_THRESHOLD_ = 333; // was 100, see MAPSJS-248


/**
 * Root map html element
 * @type {Element}
 * @private
 */
H.Map.prototype.element_;


/**
 * Function disposes the map object
 * @protected
 */
H.Map.prototype.disposeInternal = function() {
  'use strict';
  //1) Imprint
  this.autoColor_ = false;
  this.imprint_ && this.imprint_.dispose();

  //2) Data model
  this.objectLayer_.dispose();
  this.localObjectProvider_.dispose();
  this.dataModel_.dispose();

  //3) View model
  //event listeners should be cleared by dispose anyways, no?
  //this.viewModel_.removeEventListener(viewModel.EventType.SYNC, this.onSync_, false, this);
  this.viewModel_.dispose();

  //4) Viewport
  this.viewPort_.dispose();

  //5) Engine
  this.engine_.dispose();

  //6) DOM elements
  this.element_.parentNode.removeChild(this.element_);
  this.element_ = null;

  H.util.EventTarget.prototype.disposeInternal.call(this);
};


/**
 * This method returns the map root html element
 * @return {Element}
 * @export
 * @publish
 */
H.Map.prototype.getElement = function() {
  'use strict';
  return this.element_;
};


/**
 * This method initializes map root element
 * @param {Element} containerElement element provided from the application
 * @private
 */
H.Map.prototype.createMapElement_ = function(containerElement) {
  'use strict';
  if (!H.lang.isInstanceOf(containerElement, Element)) {
    throw new H.lang.InvalidArgumentError(H.Map, 0, containerElement);
  }
  var el = this.element_ = /** @type {Element} */(containerElement.ownerDocument.createElement('div')),
      style = el.style,
      WEBKIT_TAP_HIGHLIGHT_COLOR = 'webkitTapHighlightColor',
      TRANSPARENT = 'transparent';
  style.position = 'relative';
  style.height = el.style.width = '100%';
  style.overflow = 'hidden';
  // Fix for MAPSJS-193:
  style[WEBKIT_TAP_HIGHLIGHT_COLOR] = TRANSPARENT;
  if (style[WEBKIT_TAP_HIGHLIGHT_COLOR] !== TRANSPARENT) {
    style[WEBKIT_TAP_HIGHLIGHT_COLOR] = 'rgba(0,0,0,0)';
  }

  containerElement.appendChild(el);
};


/**
 * To adjust the color of the imprint and UI
 * @private
 */
H.Map.prototype.adjustColors_ = function() {
  if (this.imprint_ && this.autoColor_) {
    // TODO Adjust also the color scheme of the UI
    this.imprint_.setOptions({'invert': this.baseLayer_.dark});
  }
};


/**
 * A listener for "add", "remove" or "set" events on this map's data model
 * @private
 * @param {H.util.OList.Event} event
 */
H.Map.prototype.onDataModelChange_ = function(event) {
  'use strict';
  var addedLayer = event['added'],
      removedLayer = event['removed'];

  if (addedLayer && H.lang.isFunction(addedLayer.getProvider)) {
    addedLayer.getProvider().setParentEventTarget(this);
  }

  if (removedLayer && H.lang.isFunction(removedLayer.getProvider)) {
    removedLayer.getProvider().setParentEventTarget(null);
  }

  if (event['idx'] === 0) {
    this.dispatchEvent(new H.util.ChangeEvent(
        this.EventType.BASELAYERCHANGE,
        // event#newValue, the new baseLayer (also stored in baseLayer_ property!):
        this.baseLayer_ =
            event.type !== this.dataModel_.EventType.REMOVE ?
            // We take the "added" layer in case of ADD or SET action
            addedLayer :
            // Otherwise we take the layer at idx 0 or null if the list contains no longer any layer
            this.dataModel_.getLength() > 0 ? // oth
                this.dataModel_.get(0) :
                H.lang.NULL,
        // event#newValue, the old baseLayer:
        event.type !== this.dataModel_.EventType.ADD ?
            // We take the "removed" layer in case of REMOVE or SET action
            removedLayer :
            // Otherwise we take the layer at idx 1 or null if the list contains only the new layer
            this.dataModel_.getLength() > 1 ?
                this.dataModel_.get(1) :
                H.lang.NULL
        ));
    this.adjustColors_();
  }
};


/**
 * A listener for "sync" event from view model to manage mapviewchange events.
 * @private
 * @param {H.map.ChangeEvent} event
 */
H.Map.prototype.onSync_ = function(event) {
  'use strict';
  // Fire only if something has changed
  if (event['modifiers']) {
    if (!this.mapviewchangeStarted_) {
      this.dispatchMapviewchange_('start');
      this.mapviewchangeStarted_ = true;
    }
    this.dispatchMapviewchange_('', event);
  }

  if (this.mapviewchangeStarted_) {
    if (this.mapviewchangeEndHandle_) {
      H.global.clearTimeout(this.mapviewchangeEndHandle_);
    }
    this.mapviewchangeEndHandle_ =
        H.global.setTimeout(this.dispatchMapviewchangeEnd_, this.MAPVIEWCHANGEEND_THRESHOLD_);
  }

};


/**
 * @param {string} type
 * @param {H.map.ChangeEvent=} opt_event
 * @private
 */
H.Map.prototype.dispatchMapviewchange_ = function(type, opt_event) {
  'use strict';
  this.dispatchEvent(
      opt_event ?
      new H.map.ChangeEvent(this.EventType.MAPVIEWCHANGE,
          opt_event['newValue'], opt_event['oldValue'], opt_event['modifiers']) :
      this.EventType.MAPVIEWCHANGE + type);
};


/**
 * Private method that dispatches mapviewchangeend event.
 * @private
 */
H.Map.prototype.dispatchMapviewchangeEnd_ = function() {
  'use strict';
  this.dispatchMapviewchange_('end');
  this.mapviewchangeStarted_ = false;
};


/**
 * This method sets the new center on a map.
 * @publish
 *
 * @param {H.geo.IPoint} center requested center to be set
 * @param {boolean=} opt_animate parameter indicates if animated transition
 * should be applied, default is {@code false}
 * @return {H.Map} the instance itself
 * @export
 */
H.Map.prototype.setCenter = function(center, opt_animate) {
  'use strict';
  this.viewModel_.setCameraData({
    'animate': !!opt_animate,
    'position': center
  });
  return this;
};


/**
 * This method returns currently rendered center of the map.
 * @return {H.geo.Point}
 * @publish
 * @export
 */
H.Map.prototype.getCenter = function() {
  'use strict';
  var camera = this.viewModel_.getCameraData() || {};
  return camera['position'];
};


/**
 * This method sets the zoom level on the map. Every zoom level
 * represents different scale i.e map at zoom level 2 is twice as large
 * as the map at zoom level 1.
 * @publish
 *
 * @param {number} zoom requested zoom level
 * @param {boolean=} opt_animate parameter indicates if animated transition
 * should be applied, default is {@code false}
 * @return {H.Map} the instance itself
 * @export
 */
H.Map.prototype.setZoom = function(zoom, opt_animate) {
  'use strict';
  this.viewModel_.setZoom(zoom, !!opt_animate);
  return this;
};


/**
 * This method returns currently rendered zoom level.
 * @publish
 * @return {number}
 * @export
 */
H.Map.prototype.getZoom = function() {
  'use strict';
  return this.viewModel_.getZoom();
};


/**
 * This method changes the map zoom while keeping target
 * screen coordinates specified as x,y at the same place
 * where they were before.
 * @param {number} zoom new zoom level
 * @param {number} x map viewport x-axis pixel coordinate
 * @param {number} y map viewport y-axis pixel coordinate
 * @export
 * @publish
 */
H.Map.prototype.zoomAt = function(zoom, x, y) {
  'use strict';
  this.engine_.zoomAt(zoom, x, y);
};


/**
 * This method sets the bounding rect to be displayed by the map. Maps
 * display the bounding rect in a way that it fits entirely in the current
 * viewport.
 * @publish
 *
 * @param {H.geo.Rect} boundingRect view bound which should be shown on map
 * @param {boolean=} opt_animate parameter indicates if animated transition
 * should be applied, default is {@code false}
 * @return {H.Map} the instance itself
 * @export
 */
H.Map.prototype.setViewBounds = function(boundingRect, opt_animate) {
  'use strict';
  if (!H.lang.isInstanceOf(boundingRect, H.geo.Rect)) {
    throw new H.lang.InvalidArgumentError(this.setViewBounds, 0, boundingRect);
  }

  var cameraModel = this.engine_.getCamForBounds(boundingRect, false);
  cameraModel['zoom'] = H.math.floor(cameraModel['zoom']);
  cameraModel['animate'] = opt_animate;
  this.viewModel_.setCameraData(cameraModel);
  return this;
};


/**
 * This method returns bounding rect for the current map view. Returned bounding rect
 * defines entire currently viewable area on the screen.
 *
 * @return {H.geo.Rect}
 * @publish
 * @export
 */
H.Map.prototype.getViewBounds = function() {
  'use strict';
  return this.engine_.getBBox(H.map.render.RenderEngine.ViewPortBoundsType.PADDED, 0);
};


/**
 * Calculates the best CameraModel to show the provided bounding rectangle
 * @publish
 * @throws {Error} This method throws an error if the argument is not a H.geo.Rect
 * @param {H.geo.Rect} rect The geographical bounding rectangle to use
 * @return {H.map.ViewModel.CameraData} The result, represented by the properties zoom
 *      (number) and position (geo.Point)
 * @export
 */
H.Map.prototype.getCameraDataForBounds = function(rect) {
  'use strict';
  if (!H.lang.isInstanceOf(rect, H.geo.Rect)) {
    throw new H.lang.InvalidArgumentError(this.getCameraDataForBounds, 0, rect);
  }
  // TODO As soon as we have a CameraModel which contains zoom, we have to adjust the @return type to CameraModel
  return this.engine_.getCamForBounds(rect, true);
};


/**
 * This method returns current map viewport. Viewport can be used
 * to modify padding and margin which will reflect the position
 * of the viewport center and the amount of extra data loaded (for margin)
 * @return {H.map.ViewPort}
 * @publish
 * @export
 */
H.Map.prototype.getViewPort = function() {
  'use strict';
  return this.viewPort_;
};


/**
 * This method returns current view model. View model can be used to modify
 * the current view or camera. {@link H.map.ViewModel}
 * @return {H.map.ViewModel}
 * @publish
 * @export
 */
H.Map.prototype.getViewModel = function() {
  'use strict';
  return this.viewModel_;
};


/**
 * This method returns the map&#39;s current layer collection.
 * @publish
 * @export
 *
 * @return {H.map.DataModel}
 */
H.Map.prototype.getLayers = function() {
  'use strict';
  return this.dataModel_;
};


/**
 * This method returns the imprint object for this map.
 * @export
 *
 * @return {H.map.Imprint}
 * @publish
 */
H.Map.prototype.getImprint = function() {
  return this.imprint_;
};


/**
 * This method captures desired region of the current map and the objects on it. Result
 * is returned as a HTML5 Canvas element. Origin of coordinate system for capturing is in the
 * top left corner of the viewport.
 *
 * In order to see the "capturable" UI controls in the final result, pass a reference of
 * {@link H.ui.UI} object. For now the only control which is "capturable" is the
 * {@link H.ui.ScaleBar}.
 *
 * Consider initializing map with desired pixel ratio for high resolution devices
 * (see {@link H.Map.Options}) as it has effect on the quality of the captured result.
 * @publish
 * @export
 *
 * @example
 * var map = new H.Map(mapContainer, defaultLayers.normal.map, {
 *   center: {lat: -40, lng: 178},
 *   zoom: 2
 * });
 * var ui = H.ui.UI.createDefault(map, defaultLayers);
 * map.capture(function(canvas) {
 *   // Here we've got the canvas with desired area of the map,
 *   // (from top left (0,0) corner to the bottom right (100,100) corner)
 * }, [ui], 0, 0, 100, 100);
 *
 * @throws {Error} This method throws an error if the callback parameter is not specified or
 *                 is not a function
 * @param {function(HTMLCanvasElement=)} callback Callback function to call once result of
 *                                                the capturing is ready
 * @param {Array.<H.util.ICapturable>=} opt_capturables Collection of "capturable" element(s) to
 *                                                           draw into the resulting canvas
 * @param {number=} opt_x1 The X coordinate of the left edge of the capturing rectangle defaults to 0
 * @param {number=} opt_y1 The Y coordinate of the top edge of the capturing rectangle defaults to 0
 * @param {number=} opt_x2 The X coordinate of the right edge of the capturing rectangle defaults to viewport width
 * @param {number=} opt_y2 The Y coordinate of the bottom edge of the capturing rectangle defaults to viewport height
 */
H.Map.prototype.capture = function(callback, opt_capturables, opt_x1, opt_y1, opt_x2, opt_y2) {
  'use strict';
  var engine = this.getEngine(),
      capabilities = engine.getCapabilities(),
      canvas,
      ctx,
      imprint = this.getImprint(),
      pixelRatio = this.options_['pixelRatio'],
      queue,
      capturables = opt_capturables || [],
      viewport = this.getViewPort(),
      finished = false,
      x1,
      y1,
      x2,
      y2;

  // Make sure we were passed a valid callback function
  if (!H.lang.isFunction(callback)) {
    throw new H.lang.InvalidArgumentError(this.capture, 0, callback);
  }

  // Apply default values
  opt_x1 = opt_x1 || 0,
  opt_y1 = opt_y1 || 0,
  opt_x2 = opt_x2 || viewport.width,
  opt_y2 = opt_y2 || viewport.height,
  // Make capturing area not bigger than the viewport size,
  // swap coordinates  to have correct top-left and bottom-right points
  x1 = H.math.max(0, H.math.min(opt_x1, opt_x2));
  y1 = H.math.max(0, H.math.min(opt_y1, opt_y2));
  x2 = H.math.min(viewport.width, H.math.max(opt_x1, opt_x2));
  y2 = H.math.min(viewport.height, H.math.max(opt_y1, opt_y2));

  // Continue only if capturing capability is supported by the engine
  if (!capabilities['capture']) {
    callback(null);
  } else {
    // Capture map content with given coordinates
    canvas = engine.capture(x1, y1, x2, y2);
    ctx = canvas.getContext('2d');

    // Set canvas size according to pixel ratio
    canvas.style['width'] = canvas.width / pixelRatio + 'px';
    canvas.style['height'] = canvas.height / pixelRatio + 'px';

    // Initlize sequential queue for capturing all "capturables".
    // We use the SequentialQueue to know when all the async captures finished,
    // so we can call callback
    queue = new H.util.SequentialQueue(10, processTask);

    // For each "capturable" create an entry in the queue
    capturables.filter(function(capturable) {
      // Skip non-capturable
      return H.lang.isFunction(capturable['capture']);
    }).forEach(function(capturable) {
      queue.push(capturable, H.util.doNothing);
    });
    // Push imprint into queue. And call final callback after the last task.
    queue.push(imprint, callbackOnce.bind(this, canvas));
    queue.processSync();
  }

  // Calls capture method for each capturable in the queue
  function processTask(task, capturable) {
    if (finished) {
      task.cancel();
    } else {
      // Save styling settings so that changes during "capture" won't persist on canvas context
      ctx.save();
      capturable['capture'](canvas, pixelRatio, function() {
        // Restore styling settings
        ctx.restore();
        task.done();
      }, function(err) {
        task.done(null, err);
        // We can't stop execution of SequentialQueue?
        // So we skip all remaining tasks on error
        callbackOnce(null);
      });
    }
  }
  function callbackOnce(result) {
    if (!finished) {
      finished = true;
      callback(result);
    }
  }
};


//#################################################  ENGINE  ###########################################################
/**
 * TODO: Exposed for QA purposes only.
 * @export
 * @return {H.map.render.RenderEngine}
 */
H.Map.prototype.getEngine = function() {
  'use strict';
  // TODO:
  // TF: We should always return the actual engine but not the new one!
  //     Instead we need a "enginechangestart" event which informs about the old and new engine type
  return this.newEngine_ || this.engine_;
};


/**
 * This method sets the rendering engine type for the map. Rendering engine
 * is responsible for displaying i.e tiles and data on the map.
 * @export
 * @publish
 * @param {H.Map.EngineType} type
 * @return {H.Map} the map itself
 */
H.Map.prototype.setEngineType = function(type) {
  'use strict';
  var oldType;
  if (this.engine_.type !== type && !this.inTransition_) {
    this.inTransition_ = true;
    oldType = this.engine_.type;
    this.newEngine_ = this.createEngine_(type);
    this.newEngine_.addEventListener('render', this.onFirstRender_);
  }
  return this;
};


/**
 * Listener for the first "render" event on the new engine to handle a engine switch triggered by setEngineType()
 * @private
 * @param {H.util.Event} event
 */
H.Map.prototype.onFirstRender_ = function(event) {
  'use strict';
  if (event.target === this.newEngine_) {
    // We are making the following assumptions here:
    // - On first render of the new engine it is considered to be 'ready' to take commands
    // - Enter of new engine and exit of old engine run concurrently
    // 'enginechange' is fired when the new engine exits to simplify the process.
    this.newEngine_.removeEventListener('render', this.onFirstRender_);
    this.newEngine_.enter();
    this.engine_.exit(this.engineExitCallback_);
  }
};


/**
 * Callback to use for engine switch when the old engine exits (bound in constructor)
 * @private
 */
H.Map.prototype.engineExitCallback_ = function() {
  'use strict';
  var oldType = this.engine_.type;
  this.engine_.dispose();
  this.engine_ = this.newEngine_;
  this.newEngine_ = H.lang.UNDEF;
  this.inTransition_ = false;
  this.dispatchEvent(new H.util.ChangeEvent(this.EventType.ENGINECHANGE,
      this.engine_.type, oldType));
};


/**
 * Helper methotod to create a RenderEngine
 * @private
 * @param {H.Map.EngineType} type
 * @param {Object=} opt_options
 * @return {H.map.render.RenderEngine}
 */
H.Map.prototype.createEngine_ = function(type, opt_options) {
  'use strict';
  var options = /** @type {H.map.render.p2d.RenderEngine.Options} */ (
      opt_options ? (H.Map.adjustOptions_(opt_options, this.createEngine_)) : this.options_);

  if (type === H.Map.EngineType['PANORAMA']) {
    // TODO: register engine instead of direct call to class
    if (this.imprint_ && this.autoColor_) {
      this.imprint_.setOptions({'invert': true});
    }
    return new H.global['H']['map']['render']['panorama']['RenderEngine'](this.viewPort_,
        this.viewModel_, this.dataModel_, options);
  }
  if (type === H.Map.EngineType['P2D']) {
    this.adjustColors_();
    return new H.map.render.p2d.RenderEngine(this.viewPort_, this.viewModel_, this.dataModel_, options);
  }
  throw new H.lang.InvalidArgumentError(this.setEngineType, 0, type);
};


//######################################################################################################################
/**
 * This method adds a layer to the map.
 * @param {H.map.layer.Layer} layer The map layer to be added
 * @param {number=} opt_idx index at which the new layer should be inserted
 * @return {H.Map} current map instance
 * @publish
 * @export
 */
H.Map.prototype.addLayer = function(layer, opt_idx) {
  'use strict';
  var layerIndex = this.dataModel_.indexOf(layer);
  //prevent from adding duplicates
  if (layerIndex === -1) {
    this.dataModel_.add(layer, H.lang.isNaN(opt_idx) ?
        this.dataModel_.getLength() - 1 : opt_idx);
  }

  return this;
};


/**
 * This method removes layer from the map.
 * @param {H.map.layer.Layer} layer The map layer to be removed
 * @return {H.Map} current map instance
 * @publish
 * @export
 */
H.Map.prototype.removeLayer = function(layer) {
  'use strict';
  var layerIndex = this.dataModel_.indexOf(layer);
  if (layer !== this.baseLayer_ && layer !== this.objectLayer_ && layerIndex !== -1) {
    this.dataModel_.removeAt(layerIndex);
  }

  return this;
};


/**
 * This method will set provided layer as base map. The layer will be inserted
 * as the bottom most layer in the map.
 *
 * @param {H.map.layer.Layer} layer The layer to use as base map
 * @return {H.Map} the instance itself
 * @publish
 * @export
 */
H.Map.prototype.setBaseLayer = function(layer) {
  'use strict';
  if (this.baseLayer_ !== layer) {
    this.dataModel_.set(0, layer);
    this.baseLayer_ = layer;
  }

  return this;
};


/**
 * To get the current base map layer.
 * @return {?H.map.layer.Layer}
 * @publish
 * @export
 */
H.Map.prototype.getBaseLayer = function() {
  'use strict';
  return this.baseLayer_;
};


/**
 * Returns the screen coordniates according to the given geographical coordinates.
 * This method returns a screen pixel coordinates for the provided geo point.
 * @param {H.geo.IPoint} geoPoint point on the map
 * @return {H.math.Point}
 * @publish
 * @export
 */
H.Map.prototype.geoToScreen = function(geoPoint) {
  'use strict';
  if (!H.geo.Point.validate(geoPoint)) {
    throw new H.lang.InvalidArgumentError(this.geoToScreen, 0, geoPoint);
  }
  return /** @type {H.math.Point} */ (this.engine_.geoToScreen(geoPoint));
};


/**
 * Returns the geographical coordinates according to the given screen coordinates.
 * @param {number} x map viewport x-axis pixel coordinate
 * @param {number} y map viewport y-axis pixel coordinate
 * @return {H.geo.Point}
 * @publish
 * @export
 */
H.Map.prototype.screenToGeo = function(x, y) {
  'use strict';
  var argNr;
  if (argNr = +H.lang.isNaN(x) || H.lang.isNaN(y) * 2) {
    throw new H.lang.InvalidArgumentError(this.screenToGeo, argNr - 1, arguments[argNr - 1]);
  }
  return /** @type {H.geo.Point} */ (this.engine_.screenToGeo(x, y));
};


/**
 * Returns the camera data according to the given screen coordinates.
 * Method converts screen pixel coordinates to correct camera data object
 * @param {number} x map viewport x-axis pixel coordinate
 * @param {number} y map viewport y-axis pixel coordinate
 * @return {H.map.ViewModel.CameraData}
 * @export
 * @publish
 */
H.Map.prototype.screenToCameraData = function(x, y) {
  'use strict';
  return this.engine_.screenToCameraData(x, y);
};


/**
 * This method adds an map object to the map. Map object can be a marker or a spatial object like polygon or polyline.
 *
 * @export
 * @publish
 *
 * @throws {Error} This method throws an error if the mapObject parameter is not a map object.
 * @param {!H.map.Object} mapObject The map object to add
 * @return {!H.map.Object} the added map object
 */
H.Map.prototype.addObject = function(mapObject) {
  'use strict';
  if (!H.lang.isInstanceOf(mapObject, H.map.Object)) {
    throw new H.lang.InvalidArgumentError(this.addObject, 0, mapObject);
  }
  this.localObjectProvider_.getRootGroup().addObject(mapObject);
  return mapObject;
};


/**
 * This method removes previously added map object from the map.
 *
 * @export
 * @publish
 *
 * @throws {Error} This method throws an error if the mapObject parameter is not a map object.
 * @param {!H.map.Object} mapObject The map object to remove
 * @return {!H.map.Object} the removed map object
 */
H.Map.prototype.removeObject = function(mapObject) {
  'use strict';
  if (!H.lang.isInstanceOf(mapObject, H.map.Object)) {
    throw new H.lang.InvalidArgumentError(this.removeObject, 0, mapObject);
  }
  this.localObjectProvider_.getRootGroup().removeObject(mapObject);
  return mapObject;
};


/**
 * This method retrieves the list of all objects which have been added to the map.
 *
 * Note: The returned list should be used for read access only. Modifying the list
 * directly can destroy the integrity of this map's object model. Please use the
 * map's {@code addObject/addObjects} and {@code removeObject/removeObjects} methods.
 *
 * @export
 * @publish
 *
 * @return {Array.<H.map.Object>} the list of all use objects which are currently on the map.
 */
H.Map.prototype.getObjects = function() {
  'use strict';
  // TODO We should document that this is an expensive way and point to tree navigation methods in Group
  return this.localObjectProvider_.getRootGroup().getObjects(true);
};


/**
 * This method adds an array of objects or an object group to the map.
 *
 * Note: Objects which were added to the map previously will not be added again.
 *
 * @publish
 * @export
 *
 * @throws {Error} This method throws an error if the mapObjects parameter is neither an object group, nor an array
 * @param {Array.<!H.map.Object>} mapObjects
 * @return {H.Map} the map instance
 */
H.Map.prototype.addObjects = function(mapObjects) {
  'use strict';
  var len,
      i;
  if (!H.lang.isArrayLike(mapObjects)) {
    throw new H.lang.InvalidArgumentError(this.addObjects, 0, mapObjects);
  }

  len = mapObjects.length;
  for (i = 0; i < len; i++) {
    this.addObject(mapObjects[i]);
  }
  return this;
};


/**
 * This method removes an array of object or an object group from the map.
 *
 * @publish
 * @export
 * @param {Array.<H.map.Object>|H.map.Group} mapObjects
 * @return {H.Map} the map instance
 *
 * @throws {Error} This method throws an error if the mapObjects parameter is
 * neither an object group, nor an array
 */
H.Map.prototype.removeObjects = function(mapObjects) {
  'use strict';
  if (!H.lang.isInstanceOf(mapObjects, H.map.Group) && !H.lang.isArrayLike(mapObjects)) {
    throw new H.lang.InvalidArgumentError(this.removeObjects, 0, mapObjects);
  }

  mapObjects.forEach(this.removeObject, this);
  return this;
};


//################################################  OBJECT AT  #########################################################
/**
 * Returns the top most z-ordered map object found under the specific screen coordinates.
 * Coordinates are viewport pixel coordinates starting from top left corner as (0, 0) point.
 * @publish
 * @export
 * @param {number} x map viewport x-axis pixel coordinate
 * @param {number} y map viewport y-axis pixel coordinate
 * @return {?H.map.Object} the entcountered top most map object or {@code null} if no object found
 */
H.Map.prototype.getObjectAt = function(x, y) {
  'use strict';
  var argNr,
      objects;
  if (argNr = +H.lang.isNaN(x) || H.lang.isNaN(y) * 2) {
    throw new H.lang.InvalidArgumentError(this.getObjectAt, argNr - 1, arguments[argNr - 1]);
  }
  objects = this.engine_.getObjectsAt(x, y, true);
  return objects[0] || H.lang.NULL;
};


/**
 * Returns a list of map objects in descending z-order found under the specific screen coordinates.
 * Coordinates are viewport pixel coordinates starting from top left corner as (0, 0) point.
 * @publish
 * @export
 * @param {number} x map viewport x-axis pixel coordinate
 * @param {number} y map viewport y-axis pixel coordinate
 * @return {Array.<!H.map.Object>}
 */
H.Map.prototype.getObjectsAt = function(x, y) {
  var argNr;
  if (argNr = +H.lang.isNaN(x) || H.lang.isNaN(y) * 2) {
    throw new H.lang.InvalidArgumentError(this.getObjectAt, argNr - 1, arguments[argNr - 1]);
  }
  return this.engine_.getObjectsAt(x, y, false);
};


//######################################################################################################################
/**
 * Types of engines
 * @enum {number}
 * @export
 * @publish
 */
H.Map.EngineType = {
  'P2D': 0,
  'PANORAMA': 1
};


/**
 * This type defines options which can be used to initialize the map.
 * @publish
 *
 * @property {H.geo.IPoint=} center
 *           The initial center of the map, default is {@code {lat:0, lng: 0}}
 * @property {number=} zoom
 *           The initial zoom level of the map, default is {@code 0} respectivly the minimal zoom level of the base map
 * @property {H.geo.Rect=} bounds
 *           The view bounds to be displayed on the map. If provided, it takes precedence over center and zoom.
 *           and zoom if provided)
 * @property {Array.<H.map.layer.Layer>=} layers
 *           A list of layers to render on top of the base map
 * @property {H.Map.EngineType=} engineType
 *           The initial engine type to use, default is {@code P2D}
 * @property {number} pixelRatio
 *           The pixelRatio to use for over-sampling in cases of high-resolution displays, default is {@code 1}
 * @property {?H.map.Imprint.Options=} imprint
 *           The imprint options or {@code null} to suppress the imprint
 * @property {H.Map.BackgroundRange=} renderBaseBackground
 *           Object describes how many cached zoom levels should be used as a base map background while base map tiles
 *           are loading. Example: {@code {lower: 3, higher: 2}}
 * @property {boolean=} autoColor
 *           Indicates wheter the UI's colors should automaticaly adjusted to the base layer, default is {@code true}.
 *           Up to now only the copyright style will be adjusted.
 *           See {@link H.map.layer.Layer.Options#dark}
 * @property {number=} margin
 *           The size in pixel of the supplemental area to render for each side of the map
 * @property {H.map.ViewPort.Padding=} padding
 *           The padding in pixels for each side of the map
 * @property {boolean=} fixedCenter
 *           Indicates whether the center of the map should remain unchanged if the viewport's
 *           size or or padding has been changed, default is {@code true}
 *
 * @typedef {{
 *   center: (H.geo.IPoint|undefined),
 *   zoom: (number|undefined),
 *   bounds: (H.geo.Rect|undefined),
 *   layers: (Array.<H.map.layer.Layer>|undefined),
 *   engineType: (H.Map.EngineType|undefined),
 *   pixelRatio: (number|undefined),
 *   imprint: (?H.map.Imprint.Options|undefined),
 *   renderBaseBackground: (H.Map.BackgroundRange|undefined),
 *   autoColor: (boolean|undefined),
 *   margin: (number|undefined),
 *   padding: (H.map.ViewPort.Padding|undefined),
 *   fixedCenter: (boolean|undefined)
 * }}
 */
H.Map.Options;


/**
 * It defines the number of lower and higher zoom levels, where cached content of the base map is rendered while
 * content of the current zoom level is still loading.
 * Example: if range was set to {@code {lower: 3, higher: 2}} and current level is 10 then rendering engine will
 * try to display cached tiles from lower zoom levels 7, 8, 9 and higher levels 11 and 12.
 * @publish
 * @property {number} lower
 *           The number of lower zoom levels to take into account, default is {@code 0}
 * @property {number} higher
 *           The number of higher zoom levels to take into account, default is {@code 0}
 * @typedef {{
 *   lower: (number),
 *   higher: (number)
 * }}
 */
H.Map.BackgroundRange; // TODO Use a generic class H.math.Range with min and max (not implemented yet)


