goog.provide('H.map.render.p2d.ControlUtils');

goog.require('H.lang');
goog.require('H.math');
goog.require('H.math.Point');
goog.require('H.math.Vector');
goog.require('H.util.EventTarget');



/**
 * Utilities for running control on engine. Utilities
 * provides input analysis and fire event which notifies
 * the engine if the control parameters has changed.
 * In addition provides functionality to calculate speed and direction
 * vectors used by the kinetics animation.
 *
 * @constructor
 * @extends {H.util.EventTarget}
 */
H.map.render.p2d.ControlUtils = function() {
  'use strict';
  this.lvlChangeToPixels_ = 91.401472; //@TODO verify if this is a good number :) it is from the old engine
  this.reset();
};
H.lang.inherits(H.map.render.p2d.ControlUtils, H.util.EventTarget);


/**
 * Function marks control as started and sets the hook point if
 * x, y specified
 * @param {number=} opt_atX
 * @param {number=} opt_atY
 */
H.map.render.p2d.ControlUtils.prototype.start = function(opt_atX, opt_atY) {
  'use strict';
  if (this.isRunning_) {
    this.reset();
  }

  if (!H.lang.isNaN(opt_atX) && !H.lang.isNaN(opt_atY)) {
    this['atX'] = opt_atX;
    this['atY'] = opt_atY;
  }

  this.isRunning_ = true;
};


//#################################################  EVENTS  ###########################################################
/**
 * Events fired by ControlUtils
 * @enum {string}
 */
H.map.render.p2d.ControlUtils.prototype.EventType = {
  ANIMATIONUPDATE: 'animationupdate'
};


/**
 * Fired when the current control amends or a new control starts
 * @event H.map.render.p2d.ControlUtils#animationupdate
 * @type {H.map.render.p2d.ControlUtils.Event}
 */



//##############################################  ControlOffset  #######################################################
/**
 * ControlUtils event which is fired to notify engine that it should
 * changed values for current control animation
 * @constructor
 * @extends {H.util.Event}
 * @param {H.map.render.p2d.ControlUtils.ControlOffset} offset offset values contains information
 * regarding the move vector changes along the axes
 */
H.map.render.p2d.ControlUtils.Event = function(offset) {

  H.map.render.p2d.ControlUtils.Event.superClass_.constructor.call(
      this, H.map.render.p2d.ControlUtils.prototype.EventType.ANIMATIONUPDATE);

  /**
   * Property contains new offset which needs to be applied to animation
   * @type {H.map.render.p2d.ControlUtils.ControlOffset}
   */
  this['offset'] = offset;
};
H.lang.inherits(H.map.render.p2d.ControlUtils.Event, H.util.Event);


/**
 * Function indicates if the control is running
 * @return {boolean}
 */
H.map.render.p2d.ControlUtils.prototype.isRunning = function() {
  'use strict';
  return this.isRunning_;
};


/**
 * Function sets the current control values and fires update animation event
 * if the move values has changed from the last time
 * @param {number} moveX move on x-axis in levels per millisecond
 * @param {number} moveY move on y-axis in levels per millisecond
 * @param {number} moveZ zoom change in levels per milisecond
 * @fires H.map.render.p2d.ControlUtils#animationupdate
 */
H.map.render.p2d.ControlUtils.prototype.set = function(moveX, moveY, moveZ) {
  'use strict';
  if (this.isRunning_ && (this.moveX_ !== moveX || this.moveY_ !== moveY || this.moveZ_ !== moveZ)) {
    this.moveX_ = moveX;
    this.moveY_ = moveY;
    this.moveZ_ = moveZ;
    this.dispatchUpdate_();
  }
};


/**
 * Function calculates move from levels to actual offset px change
 * @return {H.map.render.p2d.ControlUtils.ControlOffset}
 */
H.map.render.p2d.ControlUtils.prototype.getOffsetValues = function() {
  'use strict';
  return {
    'x': this.moveX_ * this.lvlChangeToPixels_,
    'y': this.moveY_ * this.lvlChangeToPixels_,
    'z': this.moveZ_
  };
};


/**
 * Function dispatches animationupdate event. Listener should
 * update the move animation with new offset values
 * @fires H.map.render.p2d.ControlUtils#animationupdate
 * @private
 */
H.map.render.p2d.ControlUtils.prototype.dispatchUpdate_ = function() {
  'use strict';
  var offset = this.getOffsetValues();
  this.dispatchEvent(new H.map.render.p2d.ControlUtils.Event(offset));
};


/**
 * Function resets the values for the control
 */
H.map.render.p2d.ControlUtils.prototype.reset = function() {
  'use strict';
  this.isRunning_ = false;
  this['atX'] = null;
  this['atY'] = null;
  this.moveX_ = 0;
  this.moveY_ = 0;
  this.moveZ_ = 0;
};


/**
 * Function return current control speed
 * @return {number}
 */
H.map.render.p2d.ControlUtils.prototype.getSpeed = function() {
  'use strict';
  var offset = this.getOffsetValues();
  return H.math.sqrt(offset['x'] * offset['x'] + offset['y'] * offset['y'] + offset['z'] * offset['z']);
};


/**
 * Function returns normalized direction
 * @return {Object}
 */
H.map.render.p2d.ControlUtils.prototype.getDirection = function() {
  'use strict';
  var offset = this.getOffsetValues(),
      mag = this.getSpeed() || 1;
  return {
    'x': offset['x'] / mag,
    'y': offset['y'] / mag,
    'z': offset['z'] / mag
  };
};


/**
 * Function disposes control utils
 * @protected
 */
H.map.render.p2d.ControlUtils.prototype.disposeInternal = function() {
  'use strict';
  this.reset();
  H.map.render.p2d.ControlUtils.superClass_.disposeInternal.call(this);
};


/**
 * Represents a change (offset) values which should be applied
 * to the current view in every animation frame
 * @typedef {Object} H.map.render.p2d.ControlUtils.ControlOffset
 * @property {number} x x-axis change
 * @property {number} y y-axis change
 * @property {number} z zoom level change
 */
H.map.render.p2d.ControlUtils.ControlOffset;


