goog.provide('H.map.layer.Layer');

goog.require('H.geo.PixelProjection');
goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.map.ICopyright');
goog.require('H.util.ChangeEvent'); // TODO "min/maxcghange" deprecated since 3.0.7.0, will be removed in 3.0.8.0
goog.require('H.util.EventTarget');



/**
 * The Layer class represents an object that is evaluated by the renderer in the
 * order in which it is added to the layers collection. It provides the basic
 * infrastructure for dispatching update events to the renderer in case new data
 * is available.
 *
 * A layer is defined by a minimum and maximum zoom level for which it can
 * provide data to the renderer and a projection which it uses to transform
 * geo-coordinates into 2D projected world coordinates.
 *
 * @constructor
 * @extends {H.util.EventTarget}
 * @param {H.map.layer.Layer.Options=} opt_options optional configuration object
 * @publish
 * @export
 */
H.map.layer.Layer = function(opt_options) {
  'use strict';
  H.map.layer.Layer.superClass_.constructor.call(this);
  var options = opt_options || {};

  /**
   * This property holds the minimum zoom level for which this layer can
   * provide data.
   * @type {number}
   * @name {H.map.layer.Layer#min}
   * @publish
   */
  this['min'] = 0;

  /**
   * This property holds the maximum zoom level for which this layer can
   * provide data.
   * @type {number}
   * @name {H.map.layer.Layer#max}
   * @publish
   */
  this['max'] = 22;

  /**
   * Indicates whether the content of this layer is mainly dark, default is {@code false}
   * Must NOT published, API internally used only!
   * @property {boolean}
   */
  this.dark = !!options['dark'];

  // set the optional min/max values afterwards (since setMin and setMax need
  // min/max to be set to do comparison checks)
  if (options.min) {
    this.setMin(options.min);
  }
  if (options.max) {
    this.setMax(options.max);
  }

  //pixel projection already validates projection and world size at 0
  /**
   * This property holds the layer's projection.
   * @type {H.geo.PixelProjection}
   * @name {H.map.layer.Layer#pixelProjection}
   * @publish
   */
  this['pixelProjection'] = new H.geo.PixelProjection(options['projection'], options['minWorldSize']);

};
goog.inherits(H.map.layer.Layer, H.util.EventTarget);


//#################################################  EVENTS  ###########################################################
/**
 * Event types fired by Layer.
 * @enum {string}
 */
H.map.layer.Layer.prototype.EventType = {
  UPDATE: 'update',
  MINCHANGE: 'minchange',
  MAXCHANGE: 'maxchange'
};


/**
 * Fired when this layer's data updates
 * @publish
 * @event H.map.layer.Layer#update
 * @type {H.util.Event}
 */


/**
 * Fired when this layer's minimum zoom level changes.
 * @publish
 * @deprecated since 3.0.7.0, will be removed in 3.0.8.0
 * @event H.map.layer.Layer#minchange
 * @type {H.util.ChangeEvent}
 */


/**
 * Fired when this layer's maximum zoom level changes.
 * @deprecated since 3.0.7.0, will be removed in 3.0.8.0
 * @event H.map.layer.Layer#maxchange
 * @type {H.util.ChangeEvent}
 * @publish
 */


//######################################################################################################################
/**
 * This method checks if a zoom level can be served by this layer.
 * @param {number} zoomLevel the zoom level to check
 * @return {boolean} true if this layer can provide data for the zoom level,
 *  otherwise false
 * @publish
 * @export
 */
H.map.layer.Layer.prototype.isValid = function(zoomLevel) { // TODO Odd name Layer#isValid to check a zoom level
  'use strict';
  return (this['min'] <= zoomLevel) && (zoomLevel <= this['max']);
};


/**
 * This method sets the minimum zoom level for which this layer will provide
 * tiles.
 * @param {number} min The new minimum zoom level of this layer
 * @return {H.map.layer.Layer} Returns this instance of the layer
 *
 * @throws {H.lang.InvalidArgumentError} Throws an exception if the
 *  {@code min} parameter is not a number or if it is larger that the current
 *  maximum zoom level
 *
 * @export
 * @publish
 */
H.map.layer.Layer.prototype.setMin = function(min) {
  var currentMin = this['min'];
  //is min different from the current value?
  if (min !== currentMin) {
    //is min a number?
    if (!H.lang.isNaN(min)) {
      //is min smaller than max?
      if (min <= this['max']) {
        this['min'] = min;
        // TODO "minchange" event deprecated since 3.0.7.0, will be removed in 3.0.8.0
        //dispatch property change for engine to update zoom ranges
        this.dispatchEvent(new H.util.ChangeEvent(
            this.EventType.MINCHANGE,
            min,
            currentMin));
        //trigger a new render cycle in case the map is currently out
        //of bounds to adjust view model
        //this.dispatchUpdate();
      } else {
        throw new H.lang.InvalidArgumentError(H.map.layer.Layer.prototype.setMin, 0, 'min > max');
      }
    } else {
      throw new H.lang.InvalidArgumentError(H.map.layer.Layer.prototype.setMin, 0, 'min is NaN');
    }
  }
  return this;
};


/**
 * This method sets the maximum zoom level for which this layer will provide
 * tiles.
 * @param {number} max The new maximum zoom level of this layer
 * @return {H.map.layer.Layer} Returns this instance of the layer
 *
 * @throws {H.lang.InvalidArgumentError} Throws an exception if the
 *  {@code max} parameter is not a number or if it is smaller that the current
 *  minimum zoom level
 *
 * @export
 * @publish
 */
H.map.layer.Layer.prototype.setMax = function(max) {
  var currentMax = this['max'];
  //is max different from the current value?
  if (max !== currentMax) {
    //is max a number?
    if (!H.lang.isNaN(max)) {
      //is max greater than min?
      if (max >= this['min']) {
        this['max'] = max;
        // TODO "maxchange" event deprecated since 3.0.7.0, will be removed in 3.0.8.0
        this.dispatchEvent(new H.util.ChangeEvent(
            this.EventType.MAXCHANGE,
            max,
            currentMax));
        //trigger a new render cycle in case the map is currently out
        //of bounds to adjust view model
        //this.dispatchUpdate();
      } else {
        throw new H.lang.InvalidArgumentError(H.map.layer.Layer.prototype.setMax, 0, 'max < min');
      }
    } else {
      throw new H.lang.InvalidArgumentError(H.map.layer.Layer.prototype.setMax, 0, 'max is NaN');
    }
  }
  return this;
};


/**
 * This method dispatches an update event to all registered listeners.
 * @protected
 * @fires H.map.layer.Layer#update
 */
H.map.layer.Layer.prototype.dispatchUpdate = function() {
  'use strict';
  this.dispatchEvent(this.EventType.UPDATE);
};


/**
 * This method disposes of the layer.
 * @override
 */
H.map.layer.Layer.prototype.disposeInternal = function() {
  'use strict';
  H.map.layer.Layer.superClass_.disposeInternal.call(this);
};


/**
 * This method returns the copyright current data provider.
 *
  * Note: This function must be overridden by any class inheriting layer.
  * The default implementation returns {@code null}.
 *
 * @param {H.geo.Rect} bounds the bounding area for which to retrieve the
 *  copyright information
 * @param {number} level the zoom level for which to retrieve the copyright
 *  information
 * @return {?Array.<H.map.ICopyright>} a list of copyright information
 *  objects for the provided area and zoom level
 * @publish
 * @export
 */
H.map.layer.Layer.prototype.getCopyrights = function(bounds, level) {
  'use strict';
  return null;
};


/**
 * Options which can be used when creating new layer object.
 * @publish
 *
 * @property {number=} min
 *           The minimum zoom level for which the layer can provide data, default is {@code 0}
 * @property {number=} max
 *           The maximum zoom level for which the layer can provide data, default is {@code 22}
 * @property {boolean=} dark
 *           Indicates whether the content of this layer is mainly dark, default is {@code false}
 *           See also {@link H.Map.Options#autoColor}
 * @property {H.geo.IProjection=} projection
 *           The projection to be used for this layer, default is {@link H.geo.mercator}
 * @property {number=} minWorldSize
 *           The minimal world size at zoom level 0, default is {@code 256}
 *
 * @typedef {{
 *   min: (number|undefined),
 *   max: (number|undefined),
 *   dark: (boolean|undefined),
 *   projection: (H.geo.IProjection|undefined),
 *   minWorldSize: (number|undefined)
 * }}
 */
H.map.layer.Layer.Options;
