goog.provide('H.map.provider.LocalObjectProvider');

goog.require('H.geo.QuadTree');
goog.require('H.map.Group');
goog.require('H.map.Icon');
goog.require('H.map.provider.ObjectProvider');
goog.require('H.math');
goog.require('H.util');

// TODO Store the marker objects within a more efficient geo.Hash instead of geo.QuadTree



//################################################  CONSTRUCTOR  #######################################################
/**
 * A LocalObjectProvider acts as a database for map objects. It provides functionality to fetch visible objects within
 * concrete geographical bounds and zoom levels.
 * All objects are organized within a hierarchical group structure.
 * An object can be added to the provider by adding it to a group within this structure. The root group of the provider
 * can be fetched via {@link H.map.provider.LocalObjectProvider#getRootGroup}.
 * A {@link H.Map} has already its own LocalObjectProvider and provides functionality to add and remove objects.
 * Only in advanced use cases there is a need to create an additional LocalObjectProvider.
*
 * @example
 *   // Create your own LocalObjectProvider and add some hierarchical objects:
 *
 *   myProvider = new H.map.provider.LocalObjectProvider();
 *   myMap.addLayer(new H.map.layer.ObjectLayer(myProvider));

 *   hereDotCom = {lat: 52.5308, lng: 13.3852};
 *   myGroup = new H.map.Group();
 *   myGroup.addObject(new H.map.Circle(hereDotCom, 35));
 *   myProvider.getRootGroup().addObjects([
 *     myGroup,
 *     new H.map.Marker(hereDotCom)
 *   ]);

 *   myMap.setCenter(hereDotCom).setZoom(18);
 *
 * @publish
 * @export
 * @extends {H.map.provider.ObjectProvider}
 * @final
 * @constructor
 * @param {H.map.provider.Provider.Options=} opt_options
 */
H.map.provider.LocalObjectProvider = function(opt_options) {
  'use strict';
  var options = opt_options || {},
      rootGroup = new H.map.Group({
        'provider': this,
        'min': options['min'],
        'max': options['max']
      });

  H.map.provider.LocalObjectProvider.superClass_.constructor.call(this, options);


  /**
   * The root group of this provider.
   * @private
   * @type {H.map.Group}
   */
  this.rootGroup_ = rootGroup;
  rootGroup.setParentEventTarget(this);


  /**
   * The quad tree for all spatial objects
   * @private
   * @type {H.geo.QuadTree}
   */
  this.spatialTree_ = new H.geo.QuadTree();

  /**
   * The quad tree for all Marker map objects
   * @private
   * @type {H.geo.QuadTree}
   */
  this.markerTree_ = new H.geo.QuadTree();

  /**
   * The quad tree for all DomMarker map objects
   * @private
   * @type {H.geo.QuadTree}
   */
  this.domMarkerTree_ = new H.geo.QuadTree();

};
H.lang.inherits(H.map.provider.LocalObjectProvider, H.map.provider.ObjectProvider);


//#################################################  ROOT-GROUP  #######################################################
/**
 * Returns the root group of this provider.
 * @publish
 *
 * @export
 * @return {H.map.Group}
 */
H.map.provider.LocalObjectProvider.prototype.getRootGroup = function() {
  return this.rootGroup_;
};


//###############################################  INVALIDATION  #######################################################
/**
 * @override
 * @export
 */
H.map.provider.LocalObjectProvider.prototype.invalidateObject = function(mapObject, flags) {
  'use strict';
  // If we have spatial invalidation on the object, we need to update the appropriate tree.
  if (flags & H.map.provider.Invalidations.Flag.SPATIAL) {
    // addObject() tries to remove the entry before, therefore no explicit removeObject necessary:
    this.addObject(mapObject);
  }
  H.map.provider.LocalObjectProvider.superClass_.invalidateObject.call(this, mapObject, flags);
};


//#################################################  ADD OBJECT  #######################################################
/**
 * To add an object to this provider's database.
 * Must NOT published, API internally use only!
 * It must not called directly. It is invoked by H.map.Object#setProvider() which checks beforehand,
 * that it is an {@link H.map.Object} instance.
 * @param {!H.map.Object} object The object to add
 */
H.map.provider.LocalObjectProvider.prototype.addObject = function(object) {
  'use strict';
  var entry,
      position;

  H.lang.isInstanceOf(object, H.map.Object, this.addObject, 0);

  // We allow only to add owned objects (Object#setProvider assure the correct ownership)
  if (object.provider !== this) {
    throw new H.lang.IllegalOperationError(this.addObject, 'foreign object');
  }

  // We always try to remove the object beforehand to prevent double adding:
  this.removeObject(object);

  // removeObject() has already checked, that is

  if (H.lang.isInstanceOf(object, H.map.Marker)) {
    position = object.getPosition();
    entry = this.markerTree_.insertPoint(new H.geo.Point(position['lat'], position['lng']));
  } else if (H.lang.isInstanceOf(object, H.map.DomMarker)) {
    position = object.getPosition();
    entry = this.domMarkerTree_.insertPoint(new H.geo.Point(position['lat'], position['lng']));
  } else {
    // Otherwise it must be an instance of H.map.Spatial:
    entry = this.spatialTree_.insertBounds(object.getBounds());
  }

  // Create cross references between the map object and the tree entry:
  entry.$object$ = object;
  object.$entry$ = entry;
};


//###############################################  REMOVE OBJECT  ######################################################
/**
 * To remove an object from this provider's database.
 * It must not called directly. It is invoked by H.map.Group which checks beforehand,
 * that it is an {@link H.map.Object} instance and its provider is this provider.
 * @publish
 *
 * @export
 * @param {!H.map.Object} object The object to remove
 */
H.map.provider.LocalObjectProvider.prototype.removeObject = function(object) {
  'use strict';
  var entry;

  if (object) {
    // We allow only to add owned objects (Object#setProvider assure the correct ownership)
    if (object.provider !== this) {
      if (H.lang.isInstanceOf(object, H.map.Object)) {
        throw new H.lang.IllegalOperationError(this.addObject, 'foreign object');
      }
    } else {
      // We only remove the object if it has an entry because addObject "tries" to remove!
      if (entry = object.$entry$) {
        (H.lang.isInstanceOf(object, H.map.Marker) ?
            this.markerTree_ :
            H.lang.isInstanceOf(object, H.map.DomMarker) ?
            this.domMarkerTree_ :
            this.spatialTree_
        ).remove(entry);
        // Delete cross references between the map object and the tree entry:
        delete entry.$object$;
        delete object.$entry$;
      }
    }
  }
};


//##############################################  REQUEST OBJECT  ######################################################
/**
 * To request objects from the specified geo database (tree)
 * @private
 * @param {H.geo.QuadTree} tree
 * @param {H.geo.Rect} geoRect
 * @param {number} zoom
 * @param {boolean} visiblesOnly
 * @param {boolean} cacheOnly
 * @return {Array.<H.map.Object>}
 */
H.map.provider.LocalObjectProvider.prototype.requestObjects_ = function(
    tree, geoRect, zoom, visiblesOnly, cacheOnly) {
  'use strict';
  var result,
      length,
      i,
      nextIdx,
      object,
      alsoInvisibles,
      noIconTest;
  if (tree.getEntriesCount()) {
    noIconTest = tree !== this.markerTree_;
    alsoInvisibles = !visiblesOnly;
    result = tree.intersectBounds(geoRect);
    nextIdx = 0;
    for (length = result.length, i = 0; i < length; i++) {
      object = result[i].$object$;
      // TODO: To align visibility computation with tile level changes use
      //       Math.round(zoom - (Math.log(3) / Math.LN2 - 1.5))
      //       Off course, we should use a constant for it  ;-)

      // If specified, skip objects which are not visible:
      if (object.isVisibleForZoom(H.math.round(zoom)) && (alsoInvisibles || object.getVisibility(true))) {
        // if map.Marker requested then skip markers whose Icon state is not READY:
        if (noIconTest || object.getIcon().getState() === H.map.Icon.READY) {
          // Replacing the tree entries with their according map objects:
          result[nextIdx++] = object;
        }
      }
    }
    // Caused by the skipping of objects we have to clip the array:
    result.length = nextIdx;
  } else {
    result = H.util.emptyArray;
  }
  return /** @type {Array.<H.map.Object>} */ (result);
};


//#############################################  REQUEST SPATIALS  #####################################################
/**
 * @override
 * @export
 */
H.map.provider.LocalObjectProvider.prototype.providesSpatials = function() {
  return this.spatialTree_.getEntriesCount() > 0;
};


/**
 * @override
 * @export
 */
H.map.provider.LocalObjectProvider.prototype.requestSpatials = function(geoRect, zoom, visiblesOnly, cacheOnly) {
  'use strict';
  return /** @type {Array.<H.map.Polyline|H.map.Polygon>} */ (this.requestObjects_(
      this.spatialTree_, geoRect, zoom, visiblesOnly, cacheOnly));
};


/**
 * @override
 * @export
 */
H.map.provider.LocalObjectProvider.prototype.requestSpatialsByTile = function(tile, visiblesOnly, cacheOnly) {
  'use strict';
  return /** @type {Array.<H.map.Polyline|H.map.Polygon>} */ (this.requestObjects_(
      this.spatialTree_, tile.getGeoClipRect(), tile['z'], visiblesOnly, cacheOnly));
};


//#############################################  REQUEST MARKERS  ######################################################
/**
 * @override
 * @export
 */
H.map.provider.LocalObjectProvider.prototype.providesMarkers = function() {
  return this.markerTree_.getEntriesCount() > 0;
};


/**
 * @override
 * @export
 */
H.map.provider.LocalObjectProvider.prototype.requestMarkers = function(geoRect, zoom, visiblesOnly, cacheOnly) {
  'use strict';
  return /** @type {Array.<H.map.Marker>} */ (this.requestObjects_(
      this.markerTree_, geoRect, zoom, visiblesOnly, cacheOnly));
};


//###########################################  REQUEST DOM-MARKERS  ####################################################
/**
 * @override
 * @export
 */
H.map.provider.LocalObjectProvider.prototype.providesDomMarkers = function() {
  return this.domMarkerTree_.getEntriesCount() > 0;
};


/**
 * @override
 * @export
 */
H.map.provider.LocalObjectProvider.prototype.requestDomMarkers = function(geoRect, zoom, visiblesOnly, cacheOnly) {
  'use strict';
  return /** @type {Array.<H.map.DomMarker>} */ (this.requestObjects_(
      this.domMarkerTree_, geoRect, zoom, visiblesOnly, cacheOnly));
};


/**
 * @override
 */
H.map.provider.LocalObjectProvider.prototype.disposeInternal = function() {
  'use strict';
  this.rootGroup_.removeAll();
  H.map.provider.LocalObjectProvider.superClass_.disposeInternal.call(this);
};
