goog.provide('H.map.render.p2d.MarkerRenderer');

goog.require('H.map.render.p2d.IPickable');
goog.require('H.util');
goog.require('H.util.Disposable');



//###############################################  CONSTRUCTOR  ########################################################
/**
 * MarkerRender is responsible for correctly placing and rendering image markers
 * on the map, fetched from the given layer.
 * @extends {H.util.Disposable}
 * @implements {H.map.render.p2d.IPickable}
 * @abstract
 * @constructor
 * @param {H.map.layer.IMarkerLayer} layer
 * @param {H.map.render.p2d.Surface} surface
 * @param {H.map.render.p2d.MarkerRenderer.Options=} opt_options
 */
H.map.render.p2d.MarkerRenderer = function(layer, surface, opt_options) {
  'use strict';

  /**
   * A reference to the associated layer, initialzed by constructor
   * @protected
   * @type {H.map.layer.IMarkerLayer}
   */
  this.layer = layer;


  /**
   * A reference to the associated surface, initialzed by constructor
   * @protected
   * @type {H.map.render.p2d.Surface}
   */
  this.surface = surface;


  /**
   * Indicates whether subpixel rendering is enabled, initialzed by constructor
   * @protected
   * @type {boolean}
   */
  this.enableSubpixelRendering = !!(opt_options && opt_options['enableSubpixelRendering']);


  /**
   * A reference to the associated DOM document, initialzed by constructor
   * @protected
   * @type {Document}
   */
  this.ownerDocument = surface.getContainer().ownerDocument;


  /**
   * A dictionary (marker ID ==> MarkerData) for all currently rendered markers.
   * @protected
   * @type {!Object.<string, !H.map.render.p2d.MarkerRenderer.MarkerData>}
   */
  this.markerDataDict = {};

  /**
   * A dictionary (marker ID ==> MarkerData) for all previously rendered markers.
   * @protected
   * @type {!Object.<string, H.map.render.p2d.MarkerRenderer.MarkerData>}
   */
  this.prevMarkerDataDict;

};
goog.inherits(H.map.render.p2d.MarkerRenderer, H.util.Disposable);


/**
 * This method returns surface used by the renderer
 * @return {H.map.render.p2d.Surface}
 */
H.map.render.p2d.MarkerRenderer.prototype.getSurface = function() {
  'use strict';
  return this.surface;
};


//#################################################  RENDERING  ########################################################
/**
 * To fetch markers from the layer
 * @abstract
 * @protected
 * @param {H.geo.Rect} bBox
 * @param {number} zoomLevel
 * @param {boolean} cacheOnly
 * @param {H.math.Point} prioCenter
 * @return {H.map.layer.IMarkerLayer.Response}
 */
H.map.render.p2d.MarkerRenderer.prototype.fetchMarkers = goog.abstractMethod;


/**
 * It fetches all markers for the given bounding box and zoom level and render them on the surface
 * @param {H.geo.Rect} bBox The bounding box for which the markers should be rendered
 * @param {H.math.Point} screenCenter The screen center coordinates
 * @param {H.geo.PixelProjection} projection The render engine's pixel projection
 * @param {number} zoomLevel The zoom level for which the markers should be rendered
 * @param {boolean} cacheOnly Indicates whether only cached markers are to be considered
 * @param {H.math.Point} prioCenter The priority center as an offset in screen pixel relative to the center
 * @return {boolean}
 */
H.map.render.p2d.MarkerRenderer.prototype.render = function(bBox, screenCenter, projection,
        zoomLevel, cacheOnly, prioCenter) {
  'use strict';
  var prevMarkerDataDict,
      markerDataDict,
      layer = this.layer,
      response,
      markers,
      length,
      surface,
      i,
      marker,
      invalidations,
      scrCoords,
      worldWidth,
      enableSubpixelRendering = this.enableSubpixelRendering,
      id,
      markerData;

  // Save the previous marker data dictionary and create a new one:
  this.prevMarkerDataDict = prevMarkerDataDict = this.markerDataDict;
  this.markerDataDict = markerDataDict = {};

  if (zoomLevel >= layer['min'] && zoomLevel <= layer['max']) {
    response = this.fetchMarkers(bBox, zoomLevel, cacheOnly, prioCenter);
    markers = response['markers'].sort(H.map.render.p2d.MarkerRenderer.zOrderComparator);
    length = markers.length;
    if (length) {
      surface = this.surface;
      surface.setOpacity(layer['opacity']);
      for (i = 0; i < length; i++) {
        marker = markers[i];

        // Calculate the screen coordinates for this marker:
        scrCoords = projection.geoToPixel(marker.getPosition());

        // TODO Optimize? Adjust it only if the surface width is greater than world width?
        worldWidth = projection['w'];
        // Adjust the x coordinate to set the Marker close to the screen center if world is repeated on x-axis
        scrCoords['x'] -= H.math.round(scrCoords['x'] / worldWidth) * worldWidth;
        scrCoords.add(screenCenter);

        if (!enableSubpixelRendering) {
          scrCoords.round();
        }

        // Try to reuse a previous marker data or create a new one:
        if (markerData = prevMarkerDataDict[id = marker.getId()]) {
          delete prevMarkerDataDict[id];
        } else {
          markerData = /** @type {H.map.render.p2d.MarkerRenderer.MarkerData} */ ({marker: marker});
        }
        markerDataDict[id] = markerData;
        // Render this marker:
        this.renderMarker(
            markerData,
            marker,
            marker.getIcon(),
            surface,
            scrCoords['x'],
            scrCoords['y']);
      }
    }
  }

  // We return true if no markers were requested or if the reponse contained already all markers to render:
  return !response || // no response at all, doesn't provide this zoom level
      // response was tiled handle 'requested' field
      (response['requested'] && (response['requested'] === response['total'])) ||
      // response has only markers for the given bbox
      (!response['requested'] && (markers.length === response['total']));
};


/**
 * To draw a single marker on this renderers surface
 * @abstract
 * @protected
 * @param {H.map.render.p2d.MarkerRenderer.MarkerData} data
 * @param {H.map.Marker|H.map.DomMarker} marker The marker to draw
 * @param {H.map.Icon|H.map.DomIcon} icon The marker's icon
 * @param {H.map.render.p2d.Surface} surface The surface of this renderer
 * @param {number} dx The destination X coordinate
 * @param {number} dy The destination Y coordinate
 */
H.map.render.p2d.MarkerRenderer.prototype.renderMarker = goog.abstractMethod;


//################################################  HIT TESTING  #######################################################
/**
 * Abstract "implementation" of H.map.render.p2d.IPickable#getObjectsAt
 * @override
 */
H.map.render.p2d.MarkerRenderer.prototype.getObjectsAt = goog.abstractMethod;


//################################################  UTILITIES  #########################################################
/**
 * A comparator function to use for z-order sorting of marker objects - see Array.prototype.sort().
 * @protected
 * @param {H.map.AbstractMarker} first
 * @param {H.map.AbstractMarker} second
 * @return {number}
 */
H.map.render.p2d.MarkerRenderer.zOrderComparator = function(first, second) {
  'use strict';
  return first.compareZOrder(second);
};


/**
 * A structure to stores information for rendered markers.
 * @property {H.map.AbstractMarker} marker
 *           A reference to the associated marker object
 * @property {H.map.provider.Invalidations.Mark} invalMark
 *           The invalidation mark of the associated marker when it was rendered
 * @property {*} detail
 *           A property to store renderer class specific data
 *
 * @typedef {{
 *   marker:    (H.map.AbstractMarker),
 *   invalMark: (H.map.provider.Invalidations.Mark),
 *   detail:    (*)
 * }}
 */
H.map.render.p2d.MarkerRenderer.MarkerData;


/**
 * Options which can be used to initialize a MarkerRenderer.
 * @property {boolean} enableSubpixelRendering
 *
 * @typedef {{
 *   enableSubpixelRendering: (boolean)
 * }}
 */
H.map.render.p2d.MarkerRenderer.Options;
