goog.provide('H.map.Rect');

goog.require('H.geo.Rect');
goog.require('H.geo.Strip');
goog.require('H.map.Polygon');



/**
 * A Polygon with a rectangular shape.
 * @publish
 * @export
 * @extends {H.map.Polygon}
 * @final
 * @constructor
 * @param {H.geo.Rect} bounds The geographical bounding box for this rectangle
 * @param {H.map.Spatial.Options=} opt_options
 */
H.map.Rect = function(bounds, opt_options) {
  'use strict';
  // Initialize the rectangle as an Polygon with a null strip:
  H.map.Rect.superClass_.constructor.call(this, null, opt_options);

  this.setBounds(bounds);
};
H.lang.inherits(H.map.Rect, H.map.Polygon);


/**
 * To set the bounds of this rectangle.
 * @publish
 * @export
 * @param {H.geo.Rect} bounds
 */
H.map.Rect.prototype.setBounds = function(bounds) {
  'use strict';
  if (!H.lang.isInstanceOf(bounds, H.geo.Rect)) {
    throw new H.lang.InvalidArgumentError(this.setBounds, 0, bounds);
  }
  if (!bounds.equals(this.bounds)) {
    // We have to store the bounds also in the private bounds_ property because Polygon#setStrip resets the bounds
    this.bounds_ = bounds;
    H.map.Rect.superClass_.setStrip.call(this, null);
    this.bounds = bounds;
    // [MAPSJS-405 FIX] As long as the strip is not computed we have to trigger an invalidation so that the provider
    // is informed about a the new bounds of this rectangle
    //
    if (!this.strip) {
      this.invalidate(H.map.provider.Invalidations.Flag.SPATIAL);
    }
  }
};


/**
 * @override
 * @export
 */
H.map.Rect.prototype.getBounds = function() {
  'use strict';
  return this.bounds_;
};


/**
 * @override
 * @export
 */
H.map.Rect.prototype.setNorthPoleCovering = function() {
  'use strict';
  // We ignore the call. This flag is set in getStrip_
  return this;
};


/**
 * @override
 * @export
 */
H.map.Rect.prototype.setStrip = function() {
  'use strict';
  // We ignore the call. The rect's strip will be defined set in getStrip_ only
  return this;
};


/**
 * @override
 * @export
 */
H.map.Rect.prototype.getStrip = function() {
  'use strict';
  return this.strip || H.map.Rect.superClass_.setStrip.call(this, this.getStrip_()).strip;
};


/**
 * To get the computed strip of the rectangle
 * @private
 * @return {H.geo.Strip}
 */
H.map.Rect.prototype.getStrip_ = function() {
  'use strict';
  var bounds = this.bounds_,
      top,
      left,
      bottom,
      right,
      alt = H.lang.UNDEF,
      latLngAlts = [
        top = bounds.getTop(), left = bounds.getLeft(), alt,
        top, right = bounds.getRight(), alt,
        bottom = bounds.getBottom(), right, alt,
        bottom, left, alt
      ],
      centerLng;
  // If the width of a rectangle is >= 180 we have to insert extra (lat, lng, alt)-entries
  // because a polygon leg is following the "nearest by" rule:
  if (bounds.getWidth() >= 180) {
    latLngAlts.splice(9, 0, bottom, centerLng = bounds.getCenter()['lng'], alt);
    latLngAlts.splice(3, 0, top, centerLng, alt);
  }
  return new H.geo.Strip(latLngAlts);
};

