goog.provide('H.net.Xhr');



/**
 * Sends a XHR request and invokes the provided callback once the request finished.
 * Must not be published, platform-API internal use only!
 * @export
 * @constructor
 * @param {string} url The absolute URL of the XHR service to be called
 * @param {function(string=, string=)} callback The function to be called once the request
 *        finished. It gets the response text and an optional error type as arguments.
 *        Possible error types are:
 *        <ul>
 *          <li>{@code undefined} - if no error occurred </li>
 *          <li>{@code "abort"} - if the request was explicit aborted {@see H.net.Xhr.prototype.abort}}</li>
 *          <li>{@code "timeout"} - if the request timed out {@see H.net.Xhr.Options#timeout} </li>
 *          <li>{@code "network"} - if a {@code NetworkError} occurred </li>
 *          <li>{@code "cors"} - if the cross origin request can not be made</li>
 *        </ul>
 *
 * @param {H.net.Xhr.Options=} opt_options The optional parameters for this XHR request
 */
H.net.Xhr = function(url, callback, opt_options) {
  'use strict';
  var request,
      header,
      headerValue,
      optionsHeaders = opt_options && opt_options['headers'] || {},
      options = {},
      method,
      that = this,
      // Attaches event listeners to the created request object and makes the actual call
      attachEventsAndSend = function() {
        var onfinished = that.onfinished_.bind(that),
            // Default timeout in ms for the request
            requestTimeout = +options['timeout'] || 0;

        // On default browsers of some devices XHR timeouts are not supported (aka Samsung Galaxy Tablet)
        if (H.net.Xhr.XhrTimeoutSupport_) {
          that.addEventListener_('timeout', onfinished);
          request.timeout = requestTimeout;
        } else if (requestTimeout) {
          // Therefore we use custom timeout toe emulate it
          that.xhrTimeout_ = setTimeout(function() {
            request.abort();
            that.onfinished_({'type': 'timeout'});
          }, requestTimeout);
        }

        that.addEventListener_('error', onfinished);
        that.addEventListener_('load', onfinished);

        request.send(options['data']);
      };

  if (!H.lang.isFunction(callback)) {
    throw new H.lang.InvalidArgumentError(H.net.Xhr, 1, 'function required');
  }

  // Make sure that URL is string
  url += '';

  //extend headers
  H.lang.extend(optionsHeaders, H.net.Xhr.defaultOptions_['headers'], optionsHeaders);
  //extend other options
  H.lang.extend(options, H.net.Xhr.defaultOptions_, opt_options || {});
  options['headers'] = optionsHeaders;

  /**
   * A reference to the callback function
   * @private
   * @type {function(?, (string|undefined))}
   */
  this.callback_ = callback;

  /**
   * A reference to the native XHR / XDR object
   * @private
   * @type {XMLHttpRequest|XDomainRequest}
   */
  this.request_ = request = new XMLHttpRequest();

  // HTTP method to use
  method = options['method'];

  // We have to open the request before setting the timeout, otherwise IE9 throws an error:
  try {
    request.open(method, url, true);

    for (header in options['headers']) {
      headerValue = options['headers'][header];
      if (headerValue) {
        request.setRequestHeader(header, headerValue);
      }
    }

    // This is not needed in case of IE9's XDomainRequest
    request['withCredentials'] = !!options['withCredentials'];

    // Attach event listeners to the request
    attachEventsAndSend();
  } catch (xhrEx) { // We've got 'Access Denied' via XHR
    // If exception is thrown fall back to XDR in case if it is supported
    // Keep in mind that in that case we cannot set custom headers
    if (!H.net.Xhr.XhrCorsSupport_) {
      this.request_ = request = new XDomainRequest();

      try {
        request.open(method, url);

        // Attach event listeners to the request
        attachEventsAndSend();
      } catch (xdrEx) { // We've got 'Access Denied' via XDR
        this.onfinished_({'type': 'cors'});
      }
    } else { // XHR failed because CORS is not supported
      this.onfinished_({'type': 'cors'});
    }
  }
};


/**
 * The deafault options to re-use for the constructor
 * @private
 * @type {Object}
 */
H.net.Xhr.defaultOptions_ = {
  'method': 'GET',
  'headers': {},
  'data': null
};


/**
 * A flag to indicate if the XMLHttpRequest supports CORS requests
 * @private
 * @type {boolean}
 */
H.net.Xhr.XhrCorsSupport_ = 'withCredentials' in new XMLHttpRequest;


/**
 * A flag to indicate if the XMLHttpRequest supports timeout property
 * @private
 * @type {boolean}
 */
H.net.Xhr.XhrTimeoutSupport_ = 'timeout' in new XMLHttpRequest;


/**
 * X-browser helper to add an event listener
 * @private
 * @param {string} type
 * @param {function(?):?} listener
 */
H.net.Xhr.prototype.addEventListener_ = function(type, listener) {
  'use strict';
  if (H.net.Xhr.XhrCorsSupport_) {
    this.request_.addEventListener(type, listener);
  } else {
    this.request_['on' + type] = listener;
  }

};


/**
 * X-browser helper to remove an event listener
 * @private
 * @param {string} type
 * @param {function(?):?} listener
 */
H.net.Xhr.prototype.removeEventListener_ = function(type, listener) {
  'use strict';
  if (H.net.Xhr.XhrCorsSupport_) {
    this.request_.removeEventListener(type, listener);
  } else {
    this.request_['on' + type] = null;
  }
};


/**
 * It aborts an ongoing request.
 * If the request is not already finished, the request's callback is invoked with error "abort"
 * @export
 */
H.net.Xhr.prototype.abort = function() {
  'use strict';
  // If the request was not finished yet?
  if (this.request_) {
    this.request_.abort();
    // Some browsers (like IE9) doesn't trigger an "abort" event, we have to do it manually:
    if (this.request_) {
      this.onfinished_({'type': 'abort'});
    }
  }
};


/**
 * A listener for "timeout", "abort", "error" and "load" events
 * @private
 * @param {{type: string}} evt
 */
H.net.Xhr.prototype.onfinished_ = function(evt) {
  'use strict';
  var evtType = evt.type,
      request = this.request_,
      responseText,
      errorType,
      onfinished,
      callback;

  // Clean custom timeout which might have been initialized in constructor
  if (this.xhrTimeout_) {
    clearTimeout(this.xhrTimeout_);
  }

  if (evtType === 'load') {
    if (request instanceof XMLHttpRequest && request.status !== 200) {
      // XHR fires a "load" event, even if the HTTP status is not 200. We treat it as an error type "network":
      errorType = 'network';
    } else {
      responseText = request.responseText;
    }
  } else {
    // Treat this case as security error during CORS request
    // Check for readyState is required for IE9, otherwise it throws "SCRIPT575 c00c023f" error
    if (request.readyState === 4 && request.status === 0 && evtType == 'error') {
      errorType = 'cors';
    } else {
      errorType = evtType != 'error' ? evtType : 'network';
    }
  }

  onfinished = this.onfinished_;
  this.removeEventListener_('timeout', onfinished);
  this.removeEventListener_('error', onfinished);
  this.removeEventListener_('load', onfinished);

  callback = this.callback_;
  delete this.onfinished_;
  delete this.callback_;
  delete this.request_;
  callback(responseText, errorType);
};


/**
 * @property {number=} timeout
 *           The number of milliseconds to wait before the request timed out (0 means no timeout)
 * @property {boolean=} withCredentials
 *           Indicates whether user credentials should be included with the request, defaults to {@code false}
 *           This option is ignored by IE9 because of technical limitations.
 * @property {string=} method
 *           The http request method which should be used. (i.e GET, POST...)
 * @property {*=} data
 *           The data which will be send by the request. For more details see native {@code XMLHttpRequest#send} method.
 *           more details.
 * @property {Object=} headers
 *           Map of request headers and their values which should
 *           be set for the current request
 *
 * @typedef {{
 *   timeout:         (number|undefined),
 *   withCredentials: (boolean|undefined),
 *   method:          (string|undefined),
 *   data:            (*|undefined),
 *   headers:         (Object|undefined)
 * }}
 */
H.net.Xhr.Options;
