goog.provide('H.lang');


goog.require('goog.object');
goog.require('H.global');


/**
 * Contains implementation for the base library definitions and error handling
 * @namespace H.lang
 * @publish
 */


/**
 * This function uses prototype inheritance for provided
 * object constructors. Child constructor receives
 * parent's prototype methods.
 * @method
 * @param {Function} child
 * @param {Function} parent
 *
 * @example
 * function Parent(name){
 *   this.name = name;
 * };
 * Parent.prototype.printName = function(){
 *   console.log(this.name);
 * };
 *
 * function Child(name){
 *   Parent.call(this, name);
 * };
 * H.lang.inherits(Child, Parent);
 *
 * var childInstance = new Child('John');
 * childInstance.printName(); //method is available and will print name
 *
 * @export
 */
H.lang.inherits =
    goog.inherits; // new line, otherwise linter doesn't let it through otherwise :(


/**
 * Extends an object with another object. This operates 'in-place'; it does not create a new Object.
 *
 * Example:
 * var o = {};
 * H.lang.extend(o, {a: 0, b: 1});
 * o; // {a: 0, b: 1}
 * H.lang.extend(o, {c: 2}, {d: 3});
 * o; // {a: 0, b: 1, c: 2, d: 3}
 *
 * @param {Object} target  The object to modify.
 * @param {...Object} var_args The objects from which values will be copied.
 *
 * @method
 * @export
 */
H.lang.extend = goog.object.extend;


/**
 * This function binds provided method to
 * given context. Which means whenever function will
 * be executed 'this' will point to provided context.
 * @method
 * @param {Function} func
 * @param {Object} ctx
 * @param {...*} var_args Additional arguments that are partially
 *     applied to the function.
 * @return {!Function} a new function which is bound to the provided context
 *
 * @export
 */
H.lang.bind = goog.bind;


/**
 * A reference to the global String function, used for code size optimization
 * @type {function(?)}
 */
H.lang.String = String;


/**
 * A helper function to evaluate any kind of code without be boothered by CC
 * @private
 * @param {*=} opt_code
 * @return {*} the evalueated code
 */
H.lang.evaluate_ = function(opt_code) {
  return eval('' + opt_code);
};


//#################################################### CONSTANTS #######################################################


/**
 * The "eval('')" prevents CC to "optimize" references to H.lang.UNDEF with "void 0"
 * @const
 * @type {undefined}
 */
H.lang.UNDEF = /** @type {undefined} */ (H.lang.evaluate_());


/**
 * The "eval(null)" prevents CC to "optimize" references to H.lang.NULL with "null"
 * @const
 * @type {null}
 */
H.lang.NULL = /** @type {null} */ (H.lang.evaluate_(null));


//################################################# CUSTOM TYPE DEFS ###################################################


/**
 * An object which looks like an Array by having a 'length' property and entries accessible by an index.
 * @typedef {string|Array|NodeList|Arguments|{length: number}}
 */
H.lang.ArrayLike;


/**
 * A NumberLike defines an object that can be converted to a number by operating
 * on it using number operators (+, -, etc.).
 * @typedef {number|string}
 */
H.lang.NumberLike;


/**
 * Represents a truthy or falsy value. The value is evaluated by use of double negation {@code !!value}
 *
 * @typedef {?}
 */
H.lang.Booly;


/**
 * Splits the specified string into tokens
 * @export
 * @param {*} val The value to split it's string representation
 * @param {(string|RegExp)=} opt_delimiter The delimiter to use for splitting, if omitted a space is used
 * @return {Array.<string>} the list of tokens
 */
H.lang.tokenize = function(val, opt_delimiter) {
  return H.lang.String(val).split(opt_delimiter !== H.lang.UNDEF ? opt_delimiter : ' ');
};


//############################################# REFLECTION RELATED STUFF ###############################################


/**
 * The names of the fields that are defined on Object.prototype.
 * @const
 * @type {Array.<string>}
 */
H.lang.PROTOTYPEFIELDS = [
  'prototype',
  'constructor',
  'hasOwnProperty',
  'isPrototypeOf',
  'propertyIsEnumerable',
  'toLocaleString',
  'toString',
  'valueOf'
];


/**
 * Checks whether the given value is an instance of the given constructor.
 * If not so and the optional caller is specified, an InvalidArgumentError is thrown
 * @throws {H.lang.InvalidArgumentError}
 * @param {*} value The value to test.
 * @param {!Function} cstr The constructor to test against.
 * @param {Function=} opt_caller The caller to use for the error
 * @param {number=} opt_argNr The argument number to use for the error
 * @param {*=} opt_message The message to use for the error
 * @return {boolean} Whether the value is an instance of the constructor
 */
H.lang.isInstanceOf = function(value, cstr, opt_caller, opt_argNr, opt_message) {
  'use strict';
  var isInstanceOf;
  if (!(isInstanceOf = value instanceof cstr) && opt_caller) {
    throw new H.lang.InvalidArgumentError(
        opt_caller, opt_argNr, H.lang.isDef(opt_message) ? opt_message : value);
  }
  return isInstanceOf;
};


/**
 * Returns true if the value's constructor is identical with the specified constructor
 * @param {*} value The value to test.
 * @param {!Function} cstr The constructor to test against.
 * @return {boolean} Whether the value's constructor is identical
 */
H.lang.isConstructedBy = function(value, cstr) {
  'use strict';
  return H.lang.isInstanceOf(value, cstr) && value.constructor === cstr;
};


/**
 * It checks the constructor of the given instance and the given constructor for identity. If the result of this check
 * not equals the opt_expected value a TypeError with the message "Illegal constructor" and the name of the constructor
 * symbol is thrown.
 *
 * @example
 *   MyAbstractClass = function() {
 *     H.lang.checkIllegalConstructor(this, MyAbstractClass, false);
 *   };
 *   MyFinalClass = function() {
 *     H.lang.checkIllegalConstructor(this, MyFinalClass, true);
 *     MyFinalClass.superClass_.constructor.call(this);
 *   };
 *   goog.inherits(MyFinalClass, MyAbstractClass);
 *   MyClass = function() {
 *     MyClass.superClass_.constructor.call(this);
 *   };
 *   goog.inherits(MyClass, MyFinalClass);
 *
 *   new MyAbstractClass(); // TypeError - You can't instatiate an abstract class
 *   new MyFinalClass();
 *   new MyClass();         // TypeError - You can't extend a final class
 *
 *
 * @throws {TypeError}
 * @param {*} instance The instance to check
 * @param {!Function} constructor The construtor to check against
 * @param {boolean=} opt_expected The expected result of the identity-check, default is {@code false}
 */
H.lang.checkIllegalConstructor = function(instance, constructor, opt_expected) {
  if (H.lang.isConstructedBy(instance, constructor) === !opt_expected) {
    throw new TypeError('Illegal constructor' + ' ' + H.lang.getSymbolPath(constructor));
  }
};


/**
 * Returns true if the specified value is a function
 * @param {*} val The value to test.
 * @return {boolean} Whether variable is a function.
 */
H.lang.isFunction = goog.isFunction;


/**
 * Determines if the given value is an object
 *
 * @param {*} val The value to test
 * @return {boolean}
 */
H.lang.isObject = goog.isObject;


/**
 * Returns true if the specified value is |null|
 * @param {*} val Variable to test.
 * @return {boolean} Whether variable is null.
 */
H.lang.isNull = goog.isNull;


/**
 * Returns true if the object looks like an array. To qualify as array like
 * the value needs to be either a NodeList or an object with a Number length
 * property.
 * @param {*} val The value to test.
 * @return {boolean} Whether variable is an array.
 */
H.lang.isArrayLike = goog.isArrayLike;


/**
 * Checks whether H.lang.typeof() produces for the given value the given type.
 * If not so and the optional caller is specified, an InvalidArgumentError is thrown
 * @throws {H.lang.InvalidArgumentError}
 * @param {*} value The value to test.
 * @param {!string} type The type to test against.
 * @param {Function=} opt_caller The caller to use for the error
 * @param {number=} opt_argNr The argument number to use for the error
 * @param {*=} opt_message The message to use for the error
 * @return {boolean} Whether the value is of the expected type
 */
H.lang.isTypeOf = function(value, type, opt_caller, opt_argNr, opt_message) {
  'use strict';
  var isTypeOf;
  if (!(isTypeOf = H.lang.typeOf(value) === type) && opt_caller) {
    throw new H.lang.InvalidArgumentError(
        opt_caller, opt_argNr, H.lang.isDef(opt_message) ? opt_message : value);
  }
  return isTypeOf;
};


/**
 * Checks whether the given value is of type Array.
 * If not so and the optional caller is specified, an InvalidArgumentError is thrown
 * @throws {H.lang.InvalidArgumentError}
 * @param {*} value The value to test.
 * @param {Function=} opt_caller The caller to use for the error
 * @param {number=} opt_argNr The argument number to use for the error
 * @param {*=} opt_message The message to use for the error
 * @return {boolean} Whether the value is of type Array
 */
H.lang.isArray = function(value, opt_caller, opt_argNr, opt_message) {
  'use strict';
  return H.lang.isTypeOf(value, 'Array', opt_caller, opt_argNr, opt_message);
};


/**
 * Returns true if the specified value is not |undefined|.
 * WARNING: Do not use this to test if an object has a property. Use the in
 * operator instead.
 * @param {*} val The value to test.
 * @return {boolean} Whether variable is defined.
 */
H.lang.isDef = function(val) {
  // We are not using goog.isDef code because it assumes that the global undefined variable has not been redefined
  //return val !== undefined;
  return val !== H.lang.UNDEF;
};


/**
 * Returns true if the specified value is defined and not null
 * @param {*} val The value to test.
 * @return {boolean} Whether variable is defined and not null.
 */
H.lang.isDefAndNotNull = goog.isDefAndNotNull;


/**
 * Returns true if the specified value is a string
 * @param {*} val The value to test.
 * @return {boolean} Whether variable is a string.
 */
H.lang.isString = goog.isString;


/** @ignore */
H.lang.parseInt = parseInt;


/** @ignore */
H.lang.parseFloat = parseFloat;


/** @ignore */
H.lang.isNaN = isNaN;


/**
 * Returns true if the specified value is a number
 * @param {*} val The value to test.
 * @return {boolean} Whether variable is a number.
 */
H.lang.isNumber = function(val) {
  // We are stricter than goog.isNumber(), because we also check !isNaN(val)
  return val === + /** @type {number} */ (val);
};


/**
 * Returns true if the specified value is number like, a type whose valueOf or toString returns a (parseable) number
 * but not NaN. Examples: null, [ ], '4711', '47e11', '0xFF', new Date, '' etc.
 * @param {*} val The value to test.
 * @return {boolean} Whether variable is number like.
 */
H.lang.isNumberLike = function(val) {
  return !H.lang.isNaN(+ /**@type {number} */ (val));
};


/**
 * Determines the type of a value
 *
 * @param {*} val The value to reflect
 * @return {string} The type name of the value
 */
H.lang.typeOf = function(val) {
  'use strict';
  var str = Object[H.lang.PROTOTYPEFIELDS[0]][H.lang.PROTOTYPEFIELDS[6]].call(/** @type {Object} */ (val)),
      match = str.match(/^\[object (\w+)\]$/);
  // Google Chrome v31 produces '[object global]' for window objects:
  return match ? match[1] : typeof val;
};


//######################################### DEBUG RELATED FUNCTIONALITY ################################################


if (goog.DEBUG) {


  /**
   * A flag disbale expensive symbol path detection during {@link H.lang.getSymbolPath} calls.
   * It's automatially set to {@code true} if goog.DEBUG flag is set and jasmine is running.
   * To enable symbol path detection set the value to {@code false}
   * @type {boolean}
   */
  H.lang.NO_SYMBOLS = !!H.global['jasmine'];
  // The try catch is needed for IE9:
  try {
    H.global.console.warn('DEBUG', H.lang.NO_SYMBOLS ? '/ NO_SYMBOLS' : '');
  } catch (e) {}
}


/**
 * Determines for a given object the symbol path within H namespace
 * The optional arguments opt_namespace, opt_path and opt_passed MUST NOT passed in initial call, because they are
 * internally used for recursive calls only!
 *
 * @example
 * H.lang.InvalidArgumentError.getNamespace(H.map.Polyline); // ==> 'H.map.Polyline'
 * H.lang.InvalidArgumentError.getNamespace(function(){});        // ==> '~Function'
 *
 * @param {*} object The object to reflect
 * @param {(Object|Function)=} opt_namespace The namespace to reflect
 * @param {string=} opt_path The path to the namespace
 * @param {(Array.<*>)=} opt_passed The list of already passed namespaces
 * @return {string} The determined symbol path or an empty string if symbol clouldn't found
 */
H.lang.getSymbolPath = function(object, opt_namespace, opt_path, opt_passed) {
  'use strict';
  var path = '',
      initialCall = arguments.length < 2;
  // Initial call? Define the initial optional arguments:
  if (initialCall) {
    // WTF: With "{'H': H}" CC says "incomplete alias created for namespace H"
    opt_namespace = H.lang.NO_SYMBOLS ? {} : {'H': H.global['H']};
    opt_path = '';
    opt_passed = [];
  }
  H.lang.forEachProp(opt_namespace, true, function(obj, key) {
    var val = obj[key];
    // Try to adjust the key to the non obfuscated (exported) key:
    key = H.lang.propertyNameByValue(obj, val);
    // Ignore window objects and DOM nodes:
    if (!H.lang.isWindow(val) && !H.lang.isNode(val)) {
      // Ignore all types other than 'object' and 'function':
      if (H.lang.isObject(val)) {
        // Check if we found the requested object:
        if (val === object) {
          path = opt_path + '.' + key;
          return true; // stops forEachProp_
        }
        // Ignore already passed objects:
        if (opt_passed.indexOf(val) < 0) {
          // Register the value as 'passed' to prevent recursion for circular references:
          opt_passed.push(val);
          // Recursive call with the current property value and extended path:
          path = H.lang.getSymbolPath(object, val,
              opt_path + '.' + key,
              opt_passed);
          if (path) {
            return true; // stops forEachProp_
          }
        }
      }
    }
  });

  if (initialCall) {
    path = path ?
        // Remove the leading '.' and replace 'Foo.prototype.bar' with 'Foo#bar':
        path.substr(1).replace('.' + H.lang.PROTOTYPEFIELDS[0] + '.', '#') :
        '~' + (
            H.lang.isFunction(object) ?
            H.lang.getFunctionName(/** @type {Function} */ (object)) + '()' :
            H.lang.typeOf(object));
  }
  return path;
};


/**
 * Determines if the given value is a Window object
 *
 * @param {*} val The value to reflect
 * @return {boolean}
 */
H.lang.isWindow = function(val) {
  'use strict';
  return H.lang.isObject(val) && val['window'] === val && val['self'] === val;
};


/**
 * Determines if the given value is a Document object
 *
 * @param {*} val The value to reflect
 * @return {boolean}
 */
H.lang.isNode = function(val) {
  'use strict';
  return H.lang.isObject(val) && val['nodeType'] > 0 && H.lang.isFunction(val.cloneNode);
};


/**
 * Detects the identifier of the first property which was found in the given object.
 * It's useful if there is a need to select a property via bracket notation (like myObject[myKey]) but the
 * property identifier was possibly renamed by Closure Compiler.
 * You can also use @link{H.lang.propertyNameByValue} if the property's value is unique within the object and
 * it's whole prototype chain.
 *
 * @example
 * myCar = {color: 'red', doors: 5}; // ==> compiled to e.g. 'x = {m: 'red', o: 5};'
 * colorKey = H.lang.firstOwnPropertyName({color:0}); // ==> 'm'
 * for(key in myObject) {
 *   if(key === colorKey) alert('A ' + myObject[key] + ' Car');  // ==> "A red Car"
 * }
 *
 * @param {!Object} object
 * @return {string|undefined}
 */
H.lang.firstOwnPropertyName = function(object) {
  'use strict';
  var firstPropertyName;
  H.lang.forEachProp(object, true, function(obj, key) {
    firstPropertyName = key;
    return true;
  });
  return firstPropertyName;
};


/**
 * Tries to find the identifier of the property which references an identical value. If more than one property found,
 * the one with the longest name is taken (typically the non obfuscated exported one).
 * Returns undefined if no property was found which references the given value.
 *
 * @example
 * Cat.prototype.hello = function() { this.say_(); }; // ==> 'hello' compiled to 'a', but also exported as 'hello'
 * Cat.prototype.say_ = function() {
 *   alert(H.lang.propertyNameByValue(this, arguments.callee.caller) // ==> 'hello'
 * };
 *
 * @param {Object|Function} object
 * @param {Object|Function} value
 * @param {boolean=} opt_onlyOwnProperties If set to {@code true} prototype properties are ignored
 * @return {string|undefined}
 */
H.lang.propertyNameByValue = function(object, value, opt_onlyOwnProperties) {
  'use strict';
  var keys = [];
  H.lang.forEachProp(object, !!opt_onlyOwnProperties, function(obj, key) {
    if (obj[key] === value) {
      keys.push(key);
    }
  });
  return keys.sort(H.lang.sortByLength)[0];
};


/**
 * To sort a list of array like objects by length in descending order.
 *
 * @param {H.lang.ArrayLike} a
 * @param {H.lang.ArrayLike} b
 * @return {number}
 */
H.lang.sortByLength = function(a, b) {
  'use strict';
  return b.length - a.length;
};


/**
 * Iterates over the object calling the supplied callback once for each property.
 * @export
 * @param {?(Object|undefined)} obj
 * @param {boolean} onlyOwnProperties If set to {@code true} prototype properties are ignored
 * @param {function(Object, string, boolean): ?} callback The function to invoke.
 *      It gets the iterated object, the current property key and an isEnumerable flag as arguments.
 *      If the callback's return value evaluates to {@code true} the iteration stops
 */
H.lang.forEachProp = function(obj, onlyOwnProperties, callback) {
  'use strict';
  var key,
      i;
  if (obj) {
    for (key in obj) {
      if (!onlyOwnProperties || obj[H.lang.PROTOTYPEFIELDS[2]](key)) {
        if (callback(obj, key, true)) {
          return;
        }
      }
    }
    i = H.lang.PROTOTYPEFIELDS.length;
    while (i--) {
      key = H.lang.PROTOTYPEFIELDS[i];
      if (!onlyOwnProperties || obj[H.lang.PROTOTYPEFIELDS[2]](key)) {
        if (callback(obj, key, false)) {
          return;
        }
      }
    }
  }
};


/**
 * Creates a JSDOC like separator symbol for a property
 *
 * @example
 * H.lang.propertyRelation([], 'length'); // '^'  ==> length is a instance property of an Array instance
 * H.lang.propertyRelation([], 'splice'); // '#'  ==> splice is a prototype property of an Array instance
 * H.lang.propertyRelation(RegExp, '$1'); // '.'  ==> $1 is a static property of the RegExp class
 * H.lang.propertyRelation({}, {});       // '~'  ==> no relation between the given objects
 *
 * @param {?Object} object
 * @param {string|undefined} key The key of the property
 * @return {string|undefined} The according property type '^' for instance, '.' for static, '#' for prototype
 *       or {@code undefined} if the property couldn't found
 */
H.lang.propertyRelation = function(object, key) {
  'use strict';
  var cstr;
  return object && key !== undefined && H.lang.isObject(object) && key in object ?
      object[H.lang.PROTOTYPEFIELDS[2]](key) ?
          // The object's constructor is neither Object nor Function:
          (cstr = object[H.lang.PROTOTYPEFIELDS[1]]) !== Object && cstr !== Function ?
              '^' :     // instance property
              '.' :     // static property
          '#' :         // prototype property
      '~';              // no relation (e.g. a function in scope of a class method)
};


//############################################# SOURCE CODE DEBUG HELPERS ##############################################


/**
 * Determines the name of a function
 *
 * @param {Function} func
 * @return {string} The function's name or "anonymous"
 */
H.lang.getFunctionName = function(func) {
  'use strict';
  var matches = /^\s*function ([^\( ]+)/.exec(func);
  return matches ? matches[1] : 'anonymous';
};


/**
 * Removes the comments from a source code
 *
 * @param {*} sourceCode
 * @return {string} The source code without comments
 */
H.lang.dropComments = function(sourceCode) {
  'use strict';
  return ('' + sourceCode).replace(/(\/\*([\s\S]*?)\*\/)|(\/\/(.*)$)/gm, '');
};


/**
 * Renames the given argument names to '#0', '#1', #2' ...
 *
 * @param {string} name
 * @param {number} idx
 * @param {Array.<string>} argumentNames
 */
H.lang.numberedArguments = function(name, idx, argumentNames) {
  'use strict';
  argumentNames[idx] = '#' + idx;
};


/**
 * Determines the argument names of a function.
 *
 * @param {Function} func
 * @return {Array.<string>} the argument names. If COMPILED is true the obfuscated names are replaces by '#0', '#1' ...
 */
H.lang.getArgumentNames = function(func) {
  'use strict';
  var matches = /.*\(([^\)]+)/.exec(H.lang.dropComments(func));
  matches = matches ? H.lang.tokenize(matches[1].replace(/\s+/g, ''), ',') : [];
  if (COMPILED) {
    matches.forEach(H.lang.numberedArguments);
  }
  return matches;
};


/**
 * Function warns the user that a symbol has been
 * deprecated and it will be removed soon.
 * @param {*} symbol symbol function name or object which will be deprecated
 * @param {string=} opt_message optional additional message i.e describing alternative
 */
H.lang.deprecated = function(symbol, opt_message) {
  'use strict';
  var console = H.global['console'],
      message;
  if (console && console['warn']) {
    message = H.lang.getSymbolPath(symbol) + ' is deprecated and will be removed soon. ';
    if (opt_message) {
      message += opt_message;
    }
    console['warn'](message);
  }
};



