goog.provide('H.map.layer.TileLayer');

goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.map.layer.BaseTileLayer');
goog.require('H.map.layer.ITileLayer');
goog.require('H.map.layer.Layer');
goog.require('H.map.provider.ImageTileProvider');
goog.require('H.math.Rect');



/**
 * Tile Layer, represents data shown on map on a tile basis. Can be used
 * to show map tile images or other type of data which is partitioned into tiles.
 *
 * The TileLayer class is an implementation of the ITileLayer interface which
 * uses an ITileProivder as a data source. It delegates tile requests to its
 * provider.
 * @publish
 * @export
 * @final
 *
 * @implements {H.map.layer.ITileLayer}
 * @constructor
 * @extends {H.map.layer.BaseTileLayer}
 * @param {H.map.provider.TileProvider} provider data source for the TileLayer
 * @param {H.map.layer.ITileLayer.Options=} opt_options additional options
 * @throws IllegalArgument if the provider argument is not a TileProvider
 */
H.map.layer.TileLayer = function(provider, opt_options) {
  'use strict';
  var options = opt_options || {};

  /**
   * Layer opacity
   * @type {number}
   * @name {H.map.layer.TileLayer#opacity}
   * @publish
   */
  this['opacity'] = options['opacity'] || 1;

  H.map.layer.BaseTileLayer.call(this, provider, options);
};
goog.inherits(H.map.layer.TileLayer, H.map.layer.BaseTileLayer);


/**
 * @override
 * @export
 */
H.map.layer.TileLayer.prototype.requestTiles = function(boundingRect, zoomLevel, cacheOnly, prioCenter) {
  'use strict';

  if (!H.lang.isInstanceOf(boundingRect, H.geo.Rect)) {
    throw new H.lang.InvalidArgumentError(this.requestTiles, 0, boundingRect);
  }
  if (H.lang.isNaN(zoomLevel = +zoomLevel)) {
    throw new H.lang.InvalidArgumentError(this.requestTiles, 1, zoomLevel);
  }

  var zoomFloored,
      rect,
      tileBounds;

  zoomFloored = H.math.floor(zoomLevel);
  this['pixelProjection'].rescale(zoomFloored);
  rect = this.geoRectToRect(boundingRect);
  tileBounds = this.getTileBounds(rect, zoomFloored);

  // TODO Fix this isCDB comment:
  // override isCDB since we reduced to tile row/column coordinates and
  // original bounds might be CDB and fit into the single tile bound row
  return this.getProviderTiles(tileBounds, rect['left'] > rect['right'], zoomFloored, cacheOnly, prioCenter);
};

