goog.provide('H.map.render.p2d.RenderEngine');


goog.require('H.geo.PixelProjection');
goog.require('H.geo.Point');
goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.map.ViewModel');
goog.require('H.map.render.RenderEngine');
goog.require('H.map.render.p2d.ControlUtils');
goog.require('H.map.render.p2d.InteractionUtils');
goog.require('H.map.render.p2d.canvas.MarkerRenderer');
goog.require('H.map.render.p2d.canvas.Surface');
goog.require('H.map.render.p2d.canvas.TileRenderer');
goog.require('H.map.render.p2d.capabilities');
goog.require('H.map.render.p2d.dom.MarkerRenderer');
goog.require('H.map.render.p2d.dom.Surface');
goog.require('H.math');
goog.require('H.util.animation.Animation');
goog.require('H.util.animation.ease');
goog.require('H.util.kinetics.KineticMove');
goog.require('H.util.shims');



/**
 * Canvas render engine implementation. This engine will present geo
 * position (camera data) provided by the view model on a 2D canvas element,
 * were all layers are rendered into the single canvas element in provided order.
 * This class contains some additional canvas rendering specific functionality,
 * which can be used by the application.
 *
 * @constructor
 * @param {H.map.ViewPort} viewPort
 * @param {H.map.ViewModel} viewModel
 * @param {H.map.DataModel} dataModel
 * @param {H.map.render.p2d.RenderEngine.Options} options
 * @extends {H.map.render.RenderEngine}
 *
 * @export
 * @publish
 */
H.map.render.p2d.RenderEngine = function(viewPort, viewModel, dataModel, options) {
  'use strict';

  var updateEvent = 'update',
      layersContainer,
      viewPortEl,
      option,
      control;

  H.map.render.p2d.RenderEngine.superClass_.constructor.call(this,
      viewPort, viewModel, dataModel, options);

  /**
   * The priority center as an offset in pixel relative to the screen center, scaled by pixelRatio.
   * To modify its coordinates you MUST use Point#set(), because the layers will hold a reference to this point!
   * @private
   */
  this.prioCenter_ = new H.math.Point(0, 0);

  if (option = options['renderBaseBackground']) {
    this.renderBaseBackgroundLower_ = option['lower'];
    this.renderBaseBackgroundHigher_ = option['higher'];
  }
  this.enableSubpixelRendering_ = !!options['enableSubpixelRendering'];

  /**
   * Timeframe after which engine goes to suspended state. Is used when no updates were triggered on the engine.
   * @private
   */
  this.suspendTimeout_ = 1000;

  /**
   * Private zoom level reference for corresponding view's zoom
   * @private
   */
  this.zoom_ = 0;

  /**
   * Holds zoom level that was rendered in previous frame. Is used to determine
   * if engine zooms in or sooms out.
   * @type {number}
   * @private
   */
  this.lastZoom_ = 0;

  /**
   * Flag indicates if geo layers were rendered, used to prevent early removel of
   * scaled down base background
   * @type {boolean}
   * @private
   */
  this.lastGeoRendered_ = false;

  /**
   * Flag indicates if engine is zooming out
   * @type {boolean}
   * @private
   */
  this.zoomingOut_ = false;

  /**
   * Private center (map point) reference for corresponding view's map center
   * @private
   */
  this.mapCenter_ = new H.math.Point(0.5, 0.5);

  /**
   * @private
   */
  this.ratioZoomAdjustment_ = H.math.log(this.pixelRatio) / H.math.LN2;

  /**
   * @private
   */
  this.interaction_ = new H.map.render.p2d.InteractionUtils();

  /**
   * @private
   */
  this.control_ = control = new H.map.render.p2d.ControlUtils();
  this.onAnimationControl_ = H.lang.bind(this.onAnimationControl_, this);
  control.addEventListener(control.EventType.ANIMATIONUPDATE, this.onAnimationControl_);


  /**
   * @private
   */
  this.pixelProjection_ = new H.geo.PixelProjection();

  viewPortEl = viewPort['element'];
  this.layersContainer_ = layersContainer = viewPortEl.ownerDocument.createElement('div');
  layersContainer.style.position = 'absolute';
  layersContainer.style.width = layersContainer.style.height = '100%';
  layersContainer.style.zIndex = 0;

  viewPortEl.insertBefore(layersContainer, viewPortEl.firstChild);

  /**
   * Drawing surface.
   * @private
   */
  this.surface_ = new H.map.render.p2d.canvas.Surface(this.layersContainer_, 1, 1, this.pixelRatio);

  this.initializeRenderers_();

  dataModel.addEventListener(updateEvent, this.onDataUpdate_, false, this);
  dataModel.addEventListener(dataModel.EventType.ADD, this.onLayerAdd_, false, this);
  dataModel.addEventListener(dataModel.EventType.REMOVE, this.onLayerRemove_, false, this);
  dataModel.addEventListener(dataModel.EventType.SET, this.onLayerSet_, false, this);

  this.render_ = H.lang.bind(
      H.map.render.p2d.RenderEngine.prototype.render_, this);

  /**
   * @private
   */
  this.state_ = H.map.render.p2d.RenderEngine.SUSPENDED;
  this.geoRenderDispatched_ = [];
  this.screenRenderDispatched_ = [];
  this.domRenderDispatched_ = [];

  if (goog.DEBUG) {
    this.metrics_ = {
      runs: [],
      launches: 0,
      suspensions: 0
    };
    this.viewUpdates_ = 0;
    this.dataUpdates_ = 0;
  }

  this.resetAnimationDefaults();

  this.zoom_ = this.clampZoomWithRatio_(this.viewModel.getZoom() + this.ratioZoomAdjustment_);
  this.adjustZoomConstraints_();
  this.setMapCenter_(viewModel.getCameraData()['position'], this.zoom_);

  // synchronize engine state with data gathered and adjusted at construction time
  this.synchronize_();

  // Update the surface size to the real size (it triggers also relaunch):
  this.onResize();
};
H.lang.inherits(H.map.render.p2d.RenderEngine,
    H.map.render.RenderEngine);


/**
 * @export
 * @type {H.map.render.RenderEngine.EngineType}
 */
H.map.render.p2d.RenderEngine.prototype.type =
    H.map.render.RenderEngine.EngineType['P2D'];


/**
 * Busy state means that rendering loop is running.
 */
H.map.render.p2d.RenderEngine.BUSY = 1;


/**
 * Suspended state means that rendering loop stopped and engine waits for events from viewModel/dataModel/viewPort
 */
H.map.render.p2d.RenderEngine.SUSPENDED = 2;


/**
 * Shutdown state means that engine terminated rendering loop and detached all listeners, no further interaction
 * with the engine instance is possible
 */
H.map.render.p2d.RenderEngine.SHUTDOWN = 3;


/**
 * Shortcut to capabilities (to enable this.capabilities calls)
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.capabilities_ = H.map.render.p2d.capabilities;


/**
 * Returns current state of the engine. Possible states are:
 * <ul>
 *    <li>{@link H.map.render.p2d.RenderEngine.BUSY}</li>
 *    <li>{@link H.map.render.p2d.RenderEngine.SUSPENDED}</li>
 *    <li>{@link H.map.render.p2d.RenderEngine.SHUTDOWN}</li>
 * </ul>
 * @return {number} state
 * @export
 */
H.map.render.p2d.RenderEngine.prototype.getState = function() {
  'use strict';
  return this.state_;
};


/**
 * This method returns closest pixel point in modulorized world. If path between pixels
 * is more than half of the world it will return closest consecutive worl pixel. If path
 * is less function will return targetPixel
 * @param {number} startX
 * @param {number} destX
 * @return {number}
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.getClosestX_ = function(startX, destX) {
  'use strict';
  var path = destX - startX;
  return (H.math.abs(path) < this.pixelProjection_['w'] / 2) ?
      path : path - this.pixelProjection_['w'] * destX / H.math.abs(destX);
};


/**
 * Callback to react on updates from view and data models.
 * @param {H.map.ViewModel.UpdateEvent} ev event with changed data
 * @protected
 */
H.map.render.p2d.RenderEngine.prototype.onViewUpdate = function(ev) {
  'use strict';
  var that = this,
      destZ,
      deltaZ,
      requested = ev['requested'],
      animated = requested['animate'],
      destPosition = requested['camera']['position'],
      destPixel = that.pixelProjection_.geoToPixel(destPosition),
      destX = destPixel['x'],
      destY = destPixel['y'],
      currentZ = this.zoom_,
      mapCenter = this.mapCenter_,
      lastProgress = 0,
      startPoint,
      startX,
      destPoint,
      adjusted,
      adjustedPos;

  //stop any running animation actions (control, interaction, kinetics, other animation)
  this.stopAnimationActions_();

  this.prioCenter_.set(0, 0);
  adjusted = this.adjustValues_(ev);

  // take adjusted destination zoom level
  destZ = adjusted['zoom'];
  adjustedPos = adjusted['position'];

  if (animated) {
    deltaZ = destZ - currentZ;

    // reference start map point if we change center while zooming
    startPoint = /** @type {H.math.IPoint}*/ ({x: mapCenter['x'],
      y: mapCenter['y']}),
    startX = this.pixelProjection_.pointToPixel(startPoint)['x'],
    // reference destination map point if we change center and zooming in
    destPoint = this.pixelProjection_['projection'].geoToPoint(destPosition);

    //run animation only if destination is different than current
    //TODO: move this check to view model during refarctoring
    //update should not be called if same camera is set twice
    if (destPoint['x'] !== this.mapCenter_['x'] ||
        destPoint['x'] !== this.mapCenter_['x'] || destZ !== this.zoom_) {
      // get closes screen X which cooresponds to map point
      destX = this.getClosestX_(startX, destX);

      this.startAnimation_(function(progress) {
        var progressDelta = progress - lastProgress,
            startPixel;

        if (destZ !== currentZ) {
          // recalculate destination and start pixel coordinates
          destPixel = that.pixelProjection_.pointToPixel(destPoint);
          startPixel = that.pixelProjection_.pointToPixel(startPoint);
          destX = that.getClosestX_(startPixel['x'], destPixel['x']);
          destY = destPixel['y'] - startPixel['y'];
        }

        // memoize progress for progress delta calculation in next frame
        lastProgress = progress;

        that.applyPxOffset_((destX * progressDelta),
            (destY * progressDelta));

        currentZ += (deltaZ * progressDelta);
        that.zoomAt_(currentZ);

        that.synchronize_();
        that.relaunch_();
      }, this.ANIMATION_DURATION_, function() {
        that.setMapCenter_(adjustedPos, destZ);
        that.zoomAt_(destZ);
        that.synchronize_(adjustedPos);
        that.relaunch_();
      }, this.ANIMATION_EASE_);
    }

  } else {
    this.stopAnimation_();
    this.setMapCenter_(adjustedPos, destZ);
    this.zoomAt_(destZ);
    this.synchronize_(adjustedPos);
  }

  if (goog.DEBUG) {
    this.viewUpdates_++;
  }
  this.relaunch_();
};


/**
 * @private
 * @type {number}
 */
H.map.render.p2d.RenderEngine.prototype.ANIMATION_DURATION_ = 300;


/**
 * This method changes animation duration for all engine's animations
 * @param {number} duration
 * @export
 * @publish
 */
H.map.render.p2d.RenderEngine.prototype.setAnimationDuration = function(duration) {
  'use strict';
  var isNumber = H.lang.isNumber(duration);
  if (!isNumber || (isNumber && duration < 0)) {
    throw new H.lang.InvalidArgumentError(this.setAnimationDuration, 1, 'positive number required');
  }
  this.ANIMATION_DURATION_ = duration;
};


/**
 * This method returns current animation duration setting for engine's animations
 * @return {number}
 * @export
 * @publish
 */
H.map.render.p2d.RenderEngine.prototype.getAnimationDuration = function() {
  'use strict';
  return this.ANIMATION_DURATION_;
};


/**
 * @private
 * @type {function(number):number}
 */
H.map.render.p2d.RenderEngine.prototype.ANIMATION_EASE_ = H.util.animation.ease.EASE_OUT_QUAD;


/**
 * This method allows to set ease for all engine's animations.
 * @param {function(number): number} easeFunction function which alters progress.
 * Function will receive progress as parameter which
 * is a values between 0..1 and should return altered values also in 0..1 range.
 * @export
 * @publish
 */
H.map.render.p2d.RenderEngine.prototype.setAnimationEase = function(easeFunction) {
  'use strict';
  if (!H.lang.isFunction(easeFunction)) {
    throw new H.lang.InvalidArgumentError(this.setAnimationEase, 1, 'function required');
  }
  this.ANIMATION_EASE_ = easeFunction;
};


/**
 * This method return current setting for the animation ease.
 * @return {function(number):number}
 * @export
 * @publish
 */
H.map.render.p2d.RenderEngine.prototype.getAnimationEase = function() {
  'use strict';
  return this.ANIMATION_EASE_;
};


/**
 * This method resets animation settings on the engine to defaults. Duration 300ms
 * and ease to {@link H.util.animation.ease.EASE_OUT_QUAD}
 * @export
 * @publish
 */
H.map.render.p2d.RenderEngine.prototype.resetAnimationDefaults = function() {
  'use strict';
  this.ANIMATION_EASE_ = H.util.animation.ease.EASE_OUT_QUAD;
  this.ANIMATION_DURATION_ = 300;
};


/**
 * Update callback for dataModel
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.onDataUpdate_ = function() {
  'use strict';

  if (goog.DEBUG) {
    this.dataUpdates_++;
  }

  this.relaunch_();
};


/**
 * Handles 'add' event on data model. Adds appropriate renderer.
 * @param {H.util.Event} ev
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.onLayerAdd_ = function(ev) {
  'use strict';
  var layer = ev['added'],
      idx = ev['idx'];
  this.insertRenderer_(idx, layer);
  if (idx === 0) {
    this.adjustZoomConstraints_();
    this.manageMinMaxListeners_(layer, true);
  }
  this.resetDispatchedFlags_();
  this.relaunch_();
};


/**
 * Handles 'remove' event on data model. Removes corresponding renderer.
 * @param {H.util.Event} ev
 * @param {boolean=} opt_suppressLaunch launch of engine (used for 'set')
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.onLayerRemove_ = function(ev, opt_suppressLaunch) {
  'use strict';
  var layer = ev['removed'],
      idx = ev['idx'],
      screenRenderer,
      domRenderer,
      geoRenderer;
  this.updateTimestamp_();

  geoRenderer = this.geoSpaceRenderers_.splice(idx, 1)[0];
  if (geoRenderer && geoRenderer.dispose) {
    geoRenderer.dispose();
  }

  screenRenderer = this.screenSpaceRenderers_.splice(idx, 1)[0];
  if (screenRenderer && screenRenderer.dispose) {
    screenRenderer.dispose();
  }

  domRenderer = this.screenDomRenderers_.splice(idx, 1)[0];
  if (domRenderer && domRenderer.dispose) {
    domRenderer.dispose();
  }

  this.layers_ = this.dataModel.asArray();
  if (idx === 0) {
    this.manageMinMaxListeners_(layer, false);
    this.adjustZoomConstraints_();
  }

  this.resetDispatchedFlags_();
  if (!opt_suppressLaunch) {
    this.relaunch_();
  }

};


/**
 * Handles 'set' event on data model. replaces corresponding renderer
 * @private
 * @param {H.util.Event} ev
 */
H.map.render.p2d.RenderEngine.prototype.onLayerSet_ = function(ev) {
  'use strict';
  this.onLayerRemove_(ev, true);
  this.onLayerAdd_(ev);
};


/**
 * Method takes existing layers from data model and instantiates appropriate renderers
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.initializeRenderers_ = function() {
  'use strict';
  var layers = this.dataModel.asArray(),
      i = 0,
      len = layers.length,
      layer;

  /**
   * Sparse array of renderers that belong to geo space.
   * @private
   */
  this.geoSpaceRenderers_ = [];
  this.geoSpaceRenderers_.length = len;

  /**
   * Sparse array of renderers that belong to screen space.
   * @private
   */
  this.screenSpaceRenderers_ = [];
  this.screenSpaceRenderers_.length = len;

  /**
   * Sparse array of renderers that belong to screen DOM space.
   * @private
   */
  this.screenDomRenderers_ = [];
  this.screenDomRenderers_.length = len;

  /**
   * layers array for faster access at rendering time (instead of repetitive
   * this.dataModel.asArray())
   * @private
   */
  this.layers_ = layers;

  for (; i < len; i++) {
    layer = layers[i];
    this.insertRenderer_(i, /** @type {H.map.layer.Layer} */(layer), 1);
    if (i === 0) {
      // add the initial minchange / maxchange listeners to the first layer (aka base map)
      this.manageMinMaxListeners_(/** @type {H.map.layer.Layer} */(layer), true);
    }
  }
  this.adjustZoomConstraints_();
};


/**
 * Manages the adding and removing of "minchange" and "maxchange" listeners to the passed layer
 * @private
 * @param {H.map.layer.Layer} layer
 * @param {boolean} toAdd Indicates whether the listeners should be added - otherwise the listeners should be removed
 */
H.map.render.p2d.RenderEngine.prototype.manageMinMaxListeners_ = function(layer, toAdd) {
  'use strict';
  var method = toAdd ? layer.addEventListener : layer.removeEventListener;
  method.call(layer, layer.EventType.MINCHANGE, this.onMinMaxChabge_, false, this);
  method.call(layer, layer.EventType.MAXCHANGE, this.onMinMaxChabge_, false, this);
};


/**
 * Callback is responsible for adjusting zoom constraints if min/max was changed on the layer in
 * slot 0 on data model.
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.onMinMaxChabge_ = function() {
  'use strict';
  this.adjustZoomConstraints_();
  this.relaunch_();
};


/**
 * Instantiates renderers for layers and inserts them into arrays that are used by rendering loop.
 * @param {number} idx
 * @param {H.map.layer.Layer|H.map.layer.IMarkerLayer} layer
 * @param {number=} opt_howMany
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.insertRenderer_ =
    function(idx, layer, opt_howMany) {
  'use strict';
  var geoRenderer,
      screenRenderer,
      viewportSize,
      domRenderer,
      options;
  if (layer) {
    options = {
      'enableSubpixelRendering': this.enableSubpixelRendering_
    };
    if (layer['requestTiles']) {
      geoRenderer = new H.map.render.p2d.canvas.TileRenderer(
          /** @type {H.map.layer.TileLayer} */(layer), this.surface_, options);
    }
    if (layer['requestMarkers']) {
      screenRenderer = new H.map.render.p2d.canvas.MarkerRenderer(
          /** @type {H.map.layer.IMarkerLayer} */(layer), this.surface_, options);
    }
    if (layer['requestDomMarkers']) {
      // TODO we should reuse dom surface instead of creating for every layer
      // RA: i am not sure since every dom renderer increases zIndex on its own range
      // what if there will be 2 dom marker layers?

      viewportSize = this.getVisibleViewportSize_();
      domRenderer = new H.map.render.p2d.dom.MarkerRenderer(
          /** @type {H.map.layer.IMarkerLayer} */(layer),
          new H.map.render.p2d.dom.Surface(this.layersContainer_,
              viewportSize['w'], viewportSize['h'], this.pixelRatio) , options);
    }
    // the below code is now executed in onLayerAdd/onLayerRemove and in
    // initializeRenderers
    //if (idx === 0) {
    //  this.setZoomConstraints_(layer['min'], layer['max']);
    //}
  }
  opt_howMany = opt_howMany || 0;
  this.geoSpaceRenderers_.splice(idx, opt_howMany, geoRenderer);
  this.screenSpaceRenderers_.splice(idx, opt_howMany, screenRenderer);
  this.screenDomRenderers_.splice(idx, opt_howMany, domRenderer);
  this.layers_ = this.dataModel.asArray();
};


/**
 * Calls setZoomConstraints_ with min and max from layer at index 0.
 * Is bound to engine instance at construction time.
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.adjustZoomConstraints_ = function() {
  'use strict';
  var capabilitiesZoom,
      newZoom;
  if (this.layers_.length > 0) {
    //if we have layers, then we use the first entry (aka base map)...
    this.setZoomConstraints_(this.layers_[0]['min'], this.layers_[0]['max']);
  } else {
    //otherwise we use the engine capabilities
    capabilitiesZoom = this.capabilities_['zoom'];
    this.setZoomConstraints_(capabilitiesZoom['min'], capabilitiesZoom['max']);
  }

  newZoom = this.clampZoom_(this.zoom_);
  if (newZoom !== this.zoom_) {
    this.zoom_ = newZoom;
    this.pixelProjection_.rescale(newZoom);
    this.synchronize_();
  }
};


/**
 * Method sets additional constraints on min/max zoom level (ex. base map layer)
 * @param {number} min
 * @param {number} max
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.setZoomConstraints_ =
    function(min, max) {
  'use strict';
  /**
   * property holds minimum zoom level as defined by base map layer
   * @private_
   */
  this.layerMinZoom_ = min;

  /**
   * property holds maximum zoom level as defined by base map layer
   * @private
   */
  this.layerMaxZoom_ = max;
};


/**
 * Returns true if rendering loop can be launched
 * @private
 * @return {boolean}
 */
H.map.render.p2d.RenderEngine.prototype.canLaunch_ = function() {
  'use strict';
  return this.state_ === H.map.render.p2d.RenderEngine.SUSPENDED;
};


/**
 * Method takes requested data from viewModel, adjusts it to capabilities and synchronizes it back to the viewModel.
 * @param {H.map.ViewModel.UpdateEvent} ev with data to be adjusted {@see H.map.ViewModel}
 * @return {H.map.ViewModel.CameraData} adjusted cameraData object
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.adjustValues_ =
    function(ev) {
  'use strict';
  var requestedData = ev['requested'],
      zoom,
      cameraData,
      cameraPosition,
      newCamera;

  cameraData = requestedData['camera'];
  cameraPosition = cameraData && cameraData['position'];

  //first clamp requested zoom and then clamp with adjustment
  //this fixes issue i.e min is 2 and user sets 1 with + 1 adjustments (pixel ratio 2)
  //as a result level 1 was rendered since 1 + 1 (adjustment) fitted the minimum zoom
  zoom = this.clampZoom_(this.clampZoom_(requestedData['zoom']) + this.ratioZoomAdjustment_);

  newCamera = this.correctCameraAngles_({
    'position': new H.geo.Point(cameraPosition['lat'],
        cameraPosition['lng'])
  });

  // TODO: get rid of this side effect
  // if camera is animated then don't set zoom (it will be set in animation loop)
  this.zoom_ = requestedData['animate'] ? this.zoom_ : zoom;

  newCamera['zoom'] = zoom;
  return newCamera;
};


/**
 * @param {H.geo.Point} point
 * @param {number} zoom
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.setMapCenter_ =
    function(point, zoom) {
  'use strict';
  var pixelProjection = this.pixelProjection_;
  // TODO: why pixelprojection is rescaled while setting center?
  pixelProjection.rescale(zoom);

  // recalculate map center for new geo coordinates and update pixel
  // projection offset
  this.mapCenter_ = pixelProjection['projection'].geoToPoint(point);
  pixelProjection['x'] = pixelProjection['w'] * this.mapCenter_['x'];
  pixelProjection['y'] = pixelProjection['h'] * this.mapCenter_['y'];
};


/**
 * Method adjusts zoom level passed as an argument to engine's capabilities.
 * @private
 * @param {number} zoom zoom level to be adjusted
 * @return {number} adjusted zoom level
 */
H.map.render.p2d.RenderEngine.prototype.clampZoom_ = function(zoom) {
  'use strict';
  var zoomCapabilities = this.capabilities_['zoom'];
  return H.math.clamp(zoom,
      H.math.max(zoomCapabilities['min'], this.layerMinZoom_),
      H.math.min(zoomCapabilities['max'], this.layerMaxZoom_));
};


/**
 * Method sets correct yaw/pitch/roll on the object that's passed as an argument
 * @private
 * @param {H.map.ViewModel.CameraData} obj
 * @return {H.map.ViewModel.CameraData}
 */
H.map.render.p2d.RenderEngine.prototype.correctCameraAngles_ = function(obj) {
  'use strict';
  // TODO: infer from capabilities
  obj['yaw'] = obj['pitch'] = obj['roll'] = 0;
  obj['fov'] = 70;
  return obj;
};


/**
 * @override
 * @export
 */
H.map.render.p2d.RenderEngine.prototype.screenToGeo = function(x, y) {
  'use strict';
  var viewPortCenter = this.viewPort['center'],
      pixelRatio = this.pixelRatio;

  return this.pixelProjection_.xyToGeo(
      (x - viewPortCenter['x']) * pixelRatio,
      (y - viewPortCenter['y']) * pixelRatio
  );
};


/**
 * @override
 * @export
 * @method
 */
H.map.render.p2d.RenderEngine.prototype.setPointer =
    H.util.doNothing;


/**
 * @override
 * @export
 */
H.map.render.p2d.RenderEngine.prototype.screenToCameraData =
    function(x, y) {
  'use strict';
  var position = this.screenToGeo(x, y),
      cameraData = this.viewModel.getCameraData();
  cameraData['position'] = position;
  return cameraData;
};


/**
 * @override
 * @export
 */
H.map.render.p2d.RenderEngine.prototype.geoToScreen = function(geoPoint) {
  'use strict';
  var pixelRatio = this.pixelRatio,
      viewPortCenter = this.viewPort['center'],
      centerX = viewPortCenter['x'] * pixelRatio,
      centerY = viewPortCenter['y'] * pixelRatio,
      result = this.pixelProjection_.geoToPixel(geoPoint),
      w = this.pixelProjection_['w'];

  return new H.math.Point(
      H.math.round(((result['x'] - H.math.round(result['x'] / w) * w) + centerX) / pixelRatio),
      H.math.round((result['y'] + centerY) / pixelRatio)
  );
};


/**
 * @override
 * @export
 */
H.map.render.p2d.RenderEngine.prototype.getBBox = function(type, margin) {
  'use strict';

  var viewPortBounds = this.viewPortBounds[type || H.map.render.RenderEngine.ViewPortBoundsType.PADDED],
      pixelProjection = this.pixelProjection_,
      left = viewPortBounds['left'] - margin,
      topLeft = pixelProjection.xyToGeo(left, viewPortBounds['top'] - margin),
      right = viewPortBounds['right'] + margin,
      bottomRight = pixelProjection.xyToGeo(right, viewPortBounds['bottom'] + margin);

  // Adjust the longitudes to -180...+180 if we have a repeated world on x-axis:
  if (right - left >= pixelProjection['w']) {
    topLeft['lng'] = -(bottomRight['lng'] = 180);
  }
  return H.geo.Rect.fromPoints(topLeft, bottomRight);
};


/**
 * @override
 * @export
 */
H.map.render.p2d.RenderEngine.prototype.getObjectsAt = function(x, y, opt_topMostOnly) {
  'use strict';
  var objects = [],
      i,
      renderer,
      pixelRatio = this.pixelRatio,
      geoSpaceRenderers = this.geoSpaceRenderers_,
      screenSpaceRenderers = this.screenSpaceRenderers_,
      screenDomRenderers = this.screenDomRenderers_;

  x *= pixelRatio;
  y *= pixelRatio;

  objects = this.getObjectsAt_(objects, screenDomRenderers, x, y, opt_topMostOnly);
  if (opt_topMostOnly && objects.length) {
    return objects;
  }

  objects = this.getObjectsAt_(objects, screenSpaceRenderers, x, y, opt_topMostOnly);
  if (opt_topMostOnly && objects.length) {
    return objects;
  }

  return this.getObjectsAt_(objects, geoSpaceRenderers, x, y, opt_topMostOnly);
};


/**
 * Private helper method for getObjectsAt to loop over renderers collections.
 * @private
 * @param {Array} objects objects array to concatanate with
 * @param {Array} renderers renderers collection to loop over
 * @param {number} x x coordinate in screen space to probe renderer
 * @param {number} y y coordinate in screen space to probe renderer
 * @param {boolean=} opt_topMostOnly optional flag, if set method returns array with single element that is located
 * on top
 * @return {Array} array fo objects at given x/y coordinates for the provided renderers collection
 */
H.map.render.p2d.RenderEngine.prototype.getObjectsAt_ = function(objects, renderers, x, y,
    opt_topMostOnly) {
  'use strict';
  var i = renderers.length,
      renderer;
  while (i--) {
    renderer = renderers[i];
    if (renderer && renderer.getObjectsAt) {
      objects = objects.concat(renderer.getObjectsAt(x, y, opt_topMostOnly));
      if (objects.length && opt_topMostOnly) {
        break;
      }
    }
  }

  return objects;
};


/**
 * Property hold new viewport size that must be zynchronized with viewport, after
 * synchronization it's set to null, meaning that everything is in sync.
 * @private
 * @type {H.math.Size}
 */
H.map.render.p2d.RenderEngine.prototype.syncedViewPort_ = H.lang.NULL;


/**
 * This method updates size of currently used dom surfaces
 * @param {number} width
 * @param {number} height
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.updateDomSurfacesSize_ = function(width, height) {
  'use strict';
  var i = this.screenDomRenderers_.length,
      renderer,
      surface;
  while (i--) {
    renderer = this.screenDomRenderers_[i];
    if (renderer) {
      surface = renderer.getSurface();
      surface.setSize(width, height);
    }
  }
};


/**
 * This method returns visible viewport size
 * @private
 * @return {H.math.ISize}
 */
H.map.render.p2d.RenderEngine.prototype.getVisibleViewportSize_ = function() {
  'use strict';
  var bounds = this.viewPortBounds[H.map.render.RenderEngine.ViewPortBoundsType.VISIBLE];
  return /** @type {H.math.ISize} */({
    'w': bounds['right'] - bounds['left'],
    'h': bounds['bottom'] - bounds['top']
  });
};


// TODO: in fact it's onUpdate callback. Rename it.
/**
 * @override
 */
H.map.render.p2d.RenderEngine.prototype.onResize = function() {
  'use strict';
  var viewPortBounds = this.viewPortBounds,
      surfaceSize = this.surface_.getSize(),
      viewportSize,
      previousBounds,
      bounds,
      width,
      height;

  if (!this.viewPort.fixedCenter) {
    // We have to remember the previous margined bounds:
    previousBounds = viewPortBounds[H.map.render.RenderEngine.ViewPortBoundsType.VISIBLE].clone();
  }
  // Call the super which will adjust the viewPortBounds:
  H.map.render.p2d.RenderEngine.superClass_.onResize.call(this);

  // Adjust the surface size:
  viewportSize = this.getVisibleViewportSize_();
  this.prioCenter_.set(0, 0);
  width = viewportSize['w'];
  height = viewportSize['h'];
  if (surfaceSize.width !== width || surfaceSize.height !== height) {
    this.surface_.setSize(width, height);
    this.updateDomSurfacesSize_(width, height);
    this.syncedViewPort_ = new H.math.Size(width, height);
  }

  // If viewPort.fixedCenter is false then previousBounds are defined:
  if (previousBounds) {
    // We have to adjust the center so that the map looks like it would be fixed on top left corner:
    bounds = viewPortBounds[H.map.render.RenderEngine.ViewPortBoundsType.VISIBLE];
    // Unfortunately only the private applyPxOffset_ expects pixelRatio-based coordinates but doesn't call synchronize_
    this.applyPxOffset_(
        previousBounds['left'] - bounds['left'],
        previousBounds['top'] - bounds['top']);
    this.synchronize_();
  }
  this.relaunch_();

};


/**
 * @override
 * @export
 */
H.map.render.p2d.RenderEngine.prototype.getCapabilities = function() {
  'use strict';
  return H.map.render.p2d.capabilities;
};


/**
 * Changes timeStamp that is used by render loop to determine if loop should be stopped bu timeout
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.updateTimestamp_ = function() {
  'use strict';
  /**
   * @private
   */
  this.lastCallTime_ = H.util.shims.now();
};


/**
 * Launches rendering loop.
 * @protected
 */
H.map.render.p2d.RenderEngine.prototype.launch = function() {
  'use strict';
  if (goog.DEBUG) {
    this.frameCounter_ = 0;
    this.metrics_.launches++;
    this.startTime_ = H.util.shims.now();
  }
  this.rendered_ = false;
  this.state_ = H.map.render.p2d.RenderEngine.BUSY;
  this.run_();
};


/**
 * Rendering loop. recursively schedules frame rendering, and is called recursively from rendering logic itself.
 * Manages when to brake the loop.
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.run_ = function() {
  'use strict';
  if ((H.util.shims.now() - this.lastCallTime_) < this.suspendTimeout_ &&
      !this.rendered_) {
    /**
     * Property holds id of the requested frame. Used for cancelling.
     * @private
     */
    this.frameId_ = H.util.shims.requestAnimationFrame(this.render_);
  } else {
    if (goog.DEBUG) {
      var endTime = H.util.shims.now();
      this.metrics_.runs.push({
        frames: this.frameCounter_,
        start: this.startTime_,
        end: endTime,
        fps: this.frameCounter_ / ((endTime - this.startTime_) / 1000),
        layers: this.dataModel.getLength(),
        viewUpdates: this.viewUpdates_,
        dataUpdates: this.dataUpdates_
      });
      this.metrics_.suspensions++;
      this.viewUpdates_ = 0;
      this.dataUpdates_ = 0;
    }
    if (this.rendered_) {
      this.resetDispatchedFlags_();
      this.dispatchRenderEvent_();
    }
    this.state_ = H.map.render.p2d.RenderEngine.SUSPENDED;
  }
};


/**
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.resetDispatchedFlags_ = function() {
  'use strict';
  this.geoRenderDispatched_.length = 0;
  this.screenRenderDispatched_.length = 0;
  this.domRenderDispatched_.length = 0;
};


/**
 * Rendering logic
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.render_ = function() {
  'use strict';
  if (goog.DEBUG) {
    this.frameCounter_++;
  }
  var geoSpaceRenderers = this.geoSpaceRenderers_,
      screenSpaceRenderers = this.screenSpaceRenderers_,
      screenDomRenderers = this.screenDomRenderers_,
      screenCenter = this.viewPort['center'].clone().scale(this.pixelRatio),
      bbox = this.getBBox(H.map.render.RenderEngine.ViewPortBoundsType.MARGINED, 0),
      markersBBox = this.getBBox(H.map.render.RenderEngine.ViewPortBoundsType.MARGINED, 256),
      zoom = this.zoom_, //zoom is already (ratio) adjusted
      nonAdjustedZoom = zoom - this.ratioZoomAdjustment_,
      geoRendered,
      screenRendered,
      domRendered,
      higherLevelBbox,
      bgZoom,
      i,
      pixelProjection = this.pixelProjection_,
      baseBackgroundLower = this.renderBaseBackgroundLower_,
      baseBackgroundHigher = this.renderBaseBackgroundHigher_,
      baseRenderer = geoSpaceRenderers[0],
      prioCenter = this.prioCenter_;

  this.surface_.clear();
  if (baseRenderer) {
    if (baseBackgroundLower) {
      while (baseBackgroundLower) {
        bgZoom = zoom - baseBackgroundLower;
        baseRenderer.render(bbox, screenCenter, pixelProjection,
            bgZoom, true, prioCenter);
        baseBackgroundLower -= 1;
      }
    }

    if (baseBackgroundHigher && this.zoomingOut_) {
      for (i = 1; i <= baseBackgroundHigher; i++) {
        bgZoom = zoom + i;
        // create bbox for higher zoom level that covers only the viewport
        pixelProjection.rescale(bgZoom);
        higherLevelBbox = this.getBBox(
            H.map.render.RenderEngine.ViewPortBoundsType.VISIBLE, 0);
        pixelProjection.rescale(zoom);
        if (baseRenderer.render(higherLevelBbox, screenCenter, pixelProjection,
            bgZoom, true, prioCenter)) {
          break;
        }
      }
    }
  }

  geoRendered = this.renderLayers_(geoSpaceRenderers,
      this.geoRenderDispatched_, bbox, screenCenter, zoom, false, prioCenter);

  // TODO When we introduce other screen space renderes than MarkerRenderer we have NOT to pass the markersBBox because
  //      it is expanded by 256px on each side!
  //request markers using original non adjusted zoom
  screenRendered = this.renderLayers_(screenSpaceRenderers,
      this.screenRenderDispatched_, markersBBox, screenCenter, nonAdjustedZoom, false, prioCenter);

  domRendered = this.renderLayers_(screenDomRenderers,
      this.domRenderDispatched_, markersBBox, screenCenter, nonAdjustedZoom, false, prioCenter);

  this.rendered_ = geoRendered && screenRendered && domRendered;

  if (this.syncedModel_) {
    this.viewModel.synchronize(
        /** @type {H.map.ViewModel.CameraData}*/(this.syncedModel_));
    this.syncedModel_ = null;
  }

  if (this.syncedViewPort_) {
    this.viewPort.synchronize(this.syncedViewPort_);
    this.syncedViewPort_ = H.lang.NULL;
  }

  if (zoom < this.lastZoom_) {
    this.zoomingOut_ = true;
    // set zoomout flag to false only if target level was rendered
  } else if (this.lastGeoRendered_) {
    this.zoomingOut_ = false;
  }
  this.lastZoom_ = zoom;
  this.lastGeoRendered_ = geoRendered;

  this.run_();
};


/**
 * Helper render method, iterates over renderers.
 * @param {Array} renderers array of renderers to iterate over
 * @param {Array} dispatched array that holds indexes of renderers for which "render" event was dispatched
 * @param {H.geo.Rect} bbox
 * @param {H.math.Point} screenCenter
 * @param {number} zoomLevel The zoom level for which the layers should be rendered
 * @param {boolean} cacheOnly Indicates whether only cached resources are to be considered
 * @param {H.math.Point} prioCenter The priority center as an offset in screen pixel relative to the center
 * @private
 * @return {boolean} true if all layers in stack were rendered
 */
H.map.render.p2d.RenderEngine.prototype.renderLayers_ = function(renderers, dispatched, bbox,
        screenCenter, zoomLevel, cacheOnly, prioCenter) {
  'use strict';
  var len = renderers.length,
      layers = this.layers_,
      layerRendered,
      rendered = !!len,
      i = 0;

  for (i = 0; i < len; i++) {
    if (renderers[i]) {
      layerRendered = renderers[i].render(bbox, screenCenter, this.pixelProjection_, zoomLevel, cacheOnly, prioCenter);
      if (layerRendered && !dispatched[i]) {
        this.dispatchRenderEvent_(layers[i]);
        dispatched[i] = true;
      }
      if (!dispatched[i]) {
        rendered = false;
      }
    }
  }


  return rendered;
};


/**
 * This method creates and fires render event
 * @private
 * @fires H.map.render.p2d.RenderEngine#render
 * @param {H.map.layer.Layer=} opt_target
 */
H.map.render.p2d.RenderEngine.prototype.dispatchRenderEvent_ = function(opt_target) {
  'use strict';

  this.dispatchEvent({'type': this.EventType.RENDER, 'target': (opt_target || this)});
};


/**
 * Protected implementation of H.util.Disposable.disposeInternal
 * @protected
 */
H.map.render.p2d.RenderEngine.prototype.disposeInternal = function() {
  'use strict';
  var updateEvent = 'update',
      viewModel = this.viewModel,
      dataModel = this.dataModel,
      baseLayer = this.dataModel.asArray()[0],
      viewPort = this.viewPort,
      geoSpaceRenderers = this.geoSpaceRenderers_,
      screenSpaceRenderers = this.screenSpaceRenderers_,
      domRenderers = this.screenDomRenderers_,
      len;

  H.util.shims.cancelAnimationFrame(this.frameId_);
  this.stopAnimation_(true);

  dataModel.removeEventListener(updateEvent, this.onDataUpdate_, false, this);
  dataModel.removeEventListener(dataModel.EventType.ADD, this.onLayerAdd_, false, this);
  dataModel.removeEventListener(dataModel.EventType.REMOVE, this.onLayerRemove_, false, this);
  dataModel.removeEventListener(dataModel.EventType.SET, this.onLayerSet_, false, this);

  if (baseLayer) {
    this.manageMinMaxListeners_( /** @type {H.map.layer.Layer} */(baseLayer), false);
  }

  this.surface_.dispose();

  len = geoSpaceRenderers.length;
  while (len--) {
    H.util.dispose(geoSpaceRenderers[len]);
  }

  len = screenSpaceRenderers.length;
  while (len--) {
    H.util.dispose(screenSpaceRenderers[len]);
  }

  len = domRenderers.length;
  while (len--) {
    H.util.dispose(domRenderers[len]);
  }

  viewPort['element'] && viewPort['element'].removeChild(this.layersContainer_);
  this.layersContainer_ = null;
  this.state_ = H.map.render.p2d.RenderEngine.SHUTDOWN;

  H.map.render.p2d.RenderEngine.superClass_.disposeInternal.call(this);
};


/**
 * Function relaunches engine if it can
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.relaunch_ = function() {
  'use strict';
  this.updateTimestamp_();
  if (this.canLaunch_()) {
    this.launch();
  }
};


/**
 * Function returns current camera which is or will be rendered in the
 * next frame.
 * @return {H.map.ViewModel.CameraData} current camera values
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.getCameraData_ = function() {
  'use strict';
  var that = this,
      position,
      camera;

  return this.correctCameraAngles_({
    'position': this.pixelProjection_['projection'].pointToGeo(this.mapCenter_)
  });
};


/**
 * Function synchronizes current engine values with the view model
 * @param {H.geo.Point=} opt_geopoint
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.synchronize_ = function(opt_geopoint) {
  'use strict';
  var that = this,
      camera = this.getCameraData_();

  if (opt_geopoint) {
    camera['position'] = opt_geopoint;
  }
  this.syncedModel_ = camera;

  this.syncedModel_['zoom'] = that.zoom_ - that.ratioZoomAdjustment_;
};


/**
 * Function applies x,y to the map center and the projection
 * @param {number} x offset to move on the x axis
 * @param {number} y offset to move on the y axis
 * @param {boolean=} opt_preventSync ifset to true prevents synchronization with view model
 */
H.map.render.p2d.RenderEngine.prototype.applyPxOffset =
    function(x, y, opt_preventSync) {
  'use strict';
  this.applyPxOffset_(x * this.pixelRatio, y * this.pixelRatio);

  this.prioCenter_.set(0, 0);
  if (!opt_preventSync) {
    this.synchronize_();
    this.relaunch_();
  }
};


/**
 * Function applies offset to map projection and modifies the center. It
 * does not take pixelRatio into account. (all public functions should)
 * @param {number} x
 * @param {number} y
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.applyPxOffset_ =
    function(x, y) {
  'use strict';
  var pixelProjection = this.pixelProjection_,
      mapCenter = this.mapCenter_;
  pixelProjection['x'] = H.math.modulo(pixelProjection['x'] + x, pixelProjection['w']);
  pixelProjection['y'] = H.math.max(0, H.math.min(pixelProjection['w'], pixelProjection['y'] + y));
  mapCenter['x'] = pixelProjection['x'] / pixelProjection['w'];
  mapCenter['y'] = pixelProjection['y'] / pixelProjection['h'];
};


/**
 * Function changes the zoom at provided screen coordinates. It
 * incorporates the zoom ratio adjustment
 * @param {number} zoom desired zoom level
 * @param {number=} opt_atX x coordinate to zoom to in screen space
 * @param {number=} opt_atY y coordinate to zoom to in screen space
 * @export
 */
H.map.render.p2d.RenderEngine.prototype.zoomAt = function(zoom, opt_atX, opt_atY) {
  'use strict';
  zoom = this.clampZoom_(this.clampZoom_(zoom) + this.ratioZoomAdjustment_);
  this.zoomAt_(zoom, opt_atX, opt_atY);
};


/**
 * Function changes the zoom at provided screen coordinates.
 * @param {number} zoom desired zoom level
 * @param {number=} opt_atX x coordinate to zoom to in screen space
 * @param {number=} opt_atY y coordinate to zoom to in screen space
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.zoomAt_ = function(zoom, opt_atX, opt_atY) {
  'use strict';
  var screenOffsetX,
      screenOffsetY,
      pixelRatio = this.pixelRatio,
      screenCenter = this.viewPort['center'];

  if (opt_atX === +opt_atX && opt_atY === +opt_atY) {
    screenOffsetX = (opt_atX - screenCenter['x']) * pixelRatio;
    screenOffsetY = (opt_atY - screenCenter['y']) * pixelRatio;
    this.prioCenter_.set(screenOffsetX, screenOffsetY);
    this.applyPxOffset_(screenOffsetX, screenOffsetY);
    this.pixelProjection_.rescale(zoom);
    this.applyPxOffset_(-screenOffsetX, -screenOffsetY);
  } else {
    this.prioCenter_.set(0, 0);
    this.pixelProjection_.rescale(zoom);
  }

  this.zoom_ = zoom;
  this.synchronize_();
  this.relaunch_();
};


/**
 * @override
 * @export
 */
H.map.render.p2d.RenderEngine.prototype.getCamForBounds = function(bounds, keepCenter) {
  'use strict';
  var zoom = this.getCapabilities()['zoom']['max'],
      center = bounds.getCenter(),
      pixelProjection,
      pxTopLeft,
      pxBottomRight,
      worldSize,
      pxCenter,
      pxWidth,
      pxHeight,
      viewPortBounds,
      viewPortWidth,
      viewPortHeight;

  if (!bounds.isEmpty()) {
    // We need a huge PixelProjection to prevent rounding issues
    pixelProjection = new H.geo.PixelProjection(this.pixelProjection_['projection']);
    pixelProjection.rescale(zoom);
    // Project the bounds:
    pxTopLeft = pixelProjection.geoToPixel(bounds.getTopLeft()).round();
    pxBottomRight = pixelProjection.geoToPixel(bounds.getBottomRight()).round();

    worldSize = pixelProjection['w'];
    if (bounds.isCDB()) {
      // Adjust the left edge of the projected rectangle if bounds crossing the date border:
      pxTopLeft['x'] -= worldSize;
    }
    if (keepCenter) {
      // Detect the pixel position of the given bounds center within the pixel rectangle:
      pxCenter = pixelProjection.geoToPixel(center);
      // Align the bounds center and the projected rectangle center by expanding the rectangle
      pxWidth = H.math.max(pxCenter['x'] - pxTopLeft['x'], pxBottomRight['x'] - pxCenter['x']) * 2;
      pxHeight = H.math.max(pxCenter['y'] - pxTopLeft['y'], pxBottomRight['y'] - pxCenter['y']) * 2;
    } else {
      pxWidth = pxBottomRight['x'] - pxTopLeft['x'];
      pxHeight = pxBottomRight['y'] - pxTopLeft['y'];
      // Adjust the resulting center to the center of the projected rectangle
      center = pixelProjection.pixelToGeo(pxTopLeft.add(pxBottomRight).scale(.5));
    }
    // Calculate the view port dimension to use (the padded viewport bounds):
    viewPortBounds = this.viewPortBounds[H.map.render.RenderEngine.ViewPortBoundsType.PADDED];
    viewPortWidth = viewPortBounds['right'] - viewPortBounds['left'];
    viewPortHeight = viewPortBounds['bottom'] - viewPortBounds['top'];

    // Compute the highest zoom level where the projected rectangle would fit into the view port (padded):
    zoom = -8 + H.math.min(
        H.math.log(worldSize * (viewPortWidth / pxWidth)) / H.math.LN2,
        H.math.log(worldSize * (viewPortHeight / pxHeight)) / H.math.LN2) - this.ratioZoomAdjustment_;
  }
  return /** @type {H.map.ViewModel.CameraData} */({
    'zoom': this.clampZoom_(zoom),
    'position': center
  });
};


/**
 * This method stops current control, interation and animation if running
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.stopAnimationActions_ = function() {
  'use strict';
  this.stopAnimation_(true);
  if (this.control_.isRunning()) {
    this.endControl(true);
  }
  if (this.interaction_.isRunning()) {
    this.endInteraction(true);
  }
  this.stopAnimation_(true);
};


/**
 * @override
 * @export
 */
H.map.render.p2d.RenderEngine.prototype.startInteraction = function(modifiers, opt_kinetics) {
  'use strict';
  this.stopAnimationActions_();
  this.kinetics_ = opt_kinetics;
  this.interaction_.start(modifiers, !!opt_kinetics);
};


/**
 * This is mad! get user zoom (by substracting ratio adjustment) clamp
 * and to clamped zoom (in user range) add ratio again
 * and clamp again to preserve layer range for pixel ratio.
 * Works for now.
 * TODO: figure better way to handle zoom levels in different ratios/ranges
 * @param {number} zoom
 * @return {number}
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.clampZoomWithRatio_ = function(zoom) {
  'use strict';
  return this.clampZoom_(this.clampZoom_(zoom - this.ratioZoomAdjustment_) +
      this.ratioZoomAdjustment_);
};


/**
 * @override
 * @export
 */
H.map.render.p2d.RenderEngine.prototype.interaction =
    function(x, y, opt_bx, opt_by, opt_timestamp) {
  'use strict';
  var interactionMods = H.map.render.RenderEngine.InteractionModifiers,
      vecXY,
      newZoom,
      midPoint,
      relaunch = false;


  if (this.interaction_.isRunning()) {
    this.interaction_.store(x, y, opt_bx, opt_by, opt_timestamp);

    if (this.interaction_.has(interactionMods['COORD'])) {
      vecXY = this.interaction_.getMoveVector();
      if (vecXY.magnitude() > 0) {
        this.applyPxOffset(-vecXY['x'], -vecXY['y']);
        relaunch = true;
      }
    }

    if (this.interaction_.has(interactionMods['ZOOM']) && this.isBifocalInteraction(x, y, opt_bx, opt_by)) {
      midPoint = this.interaction_.getBifocalCenter();
      newZoom = this.interaction_.getBifocalZoom(this.zoom_);
      if (newZoom !== this.zoom_) {
        newZoom = this.clampZoomWithRatio_(newZoom);
        this.zoomAt_(newZoom, midPoint['x'], midPoint['y']);
        relaunch = true;
      }

    }

    relaunch && this.relaunch_();
  }

};


/**
 * @override
 * @export
 */
H.map.render.p2d.RenderEngine.prototype.endInteraction = function(opt_preventKinetics) {
  'use strict';
  var that = this,
      interactionMods = H.map.render.RenderEngine.InteractionModifiers,
      initialSpeed,
      currentSpeed,
      deceleration,
      kineticMove,
      direction,
      duration;


  if (this.interaction_.isRunning() && !opt_preventKinetics) {
    if (this.kinetics_) {

      initialSpeed = this.interaction_.getSpeed();
      direction = this.interaction_.getDirection();

      duration = this.kinetics_.duration;

      currentSpeed = (this.kinetics_.power || 1) * initialSpeed;
      deceleration = -(currentSpeed / duration);
      kineticMove = new H.util.kinetics.KineticMove(deceleration, currentSpeed);

      if (!H.lang.isNaN(direction['x']) && !H.lang.isNaN(direction['y']) && currentSpeed > 0 &&
          that.interaction_.has(interactionMods['COORD'])) {

        this.startAnimation_(function(progress) {
          currentSpeed = kineticMove.getCurrentSpeed(progress * duration);
          if (!H.lang.isNaN(currentSpeed)) {
            that.applyPxOffset(
                H.math.round(-direction['x'] * currentSpeed),
                H.math.round(-direction['y'] * currentSpeed));
          }
        }, duration, undefined, this.kinetics_.ease);

      }

    }

    this.kinetics_ = null;
    this.interaction_.clear();
  }

};


/**
 * @override
 * @export
 */
H.map.render.p2d.RenderEngine.prototype.startControl =
    function(opt_kinetics, opt_atX, opt_atY) {
  'use strict';
  this.stopAnimationActions_();
  this.control_.start(opt_atX, opt_atY);
  this.kinetics_ = opt_kinetics;
};


/**
 * @override
 * @export
 */
H.map.render.p2d.RenderEngine.prototype.control =
    function(moveX, moveY, moveZ, angleX, angleY, angleZ, zoom) {
  'use strict';

  if (H.lang.isNaN(moveZ = +zoom || +moveZ)) {
    throw new H.lang.InvalidArgumentError(this.control, 2, moveZ);
  }

  if (H.lang.isNaN(moveX)) {
    throw new H.lang.InvalidArgumentError(this.control, 0, moveX);
  }

  if (H.lang.isNaN(moveY)) {
    throw new H.lang.InvalidArgumentError(this.control, 2, moveY);
  }

  if (this.control_.isRunning()) {
    this.control_.set(moveX, moveY, moveZ);
  }
};


/**
 * @override
 * @export
 */
H.map.render.p2d.RenderEngine.prototype.endControl =
    function(opt_preventKinetics, opt_adjustView) {
  'use strict';
  var that = this,
      timeFromLastFrame,
      controlEndTs,
      offset,
      initialSpeed,
      currentSpeed,
      deceleration,
      kineticMove,
      direction,
      duration,
      requestedData,
      zoomChange;

  if (this.control_.isRunning()) {
    this.stopAnimation_(true);
    controlEndTs = H.util.shims.now();
    timeFromLastFrame = controlEndTs - this.controlFrameTs_;

    if (timeFromLastFrame >= 1) {
      offset = this.control_.getOffsetValues();
      this.applyControl_(offset, timeFromLastFrame);
    }

    if (this.kinetics_ && !opt_preventKinetics && !opt_adjustView) {
      initialSpeed = this.control_.getSpeed();
      direction = this.control_.getDirection();

      duration = this.kinetics_.duration;
      currentSpeed = (this.kinetics_.power || 1) * initialSpeed;
      deceleration = -(currentSpeed / duration);

      kineticMove = new H.util.kinetics.KineticMove(deceleration, currentSpeed);

      if (!H.lang.isNaN(direction['x']) && !H.lang.isNaN(direction['y']) && currentSpeed > 0) {
        this.startAnimation_(function(progress, timeFromStart, frameTime, timeStamp) {
          if (controlEndTs) {
            //adjust frameTime for first frame
            frameTime = timeStamp - controlEndTs;
            controlEndTs = 0;
          }
          currentSpeed = kineticMove.getCurrentSpeed(progress * duration) * frameTime;
          if (!H.lang.isNaN(currentSpeed)) {
            zoomChange = direction['z'] * currentSpeed;
            if (zoomChange) {
              that.zoomAt_(that.clampZoomWithRatio_(that.zoom_ + zoomChange),
                  that.control_['atX'], that.control_['atY']);
            }

            that.applyPxOffset(
                H.math.round(direction['x'] * currentSpeed),
                H.math.round(direction['y'] * currentSpeed));

          }
        }, duration, undefined, this.kinetics_.ease);

      }
    } else if (typeof(opt_adjustView) === 'function') {

      requestedData = {
        'camera': this.getCameraData_(),
        'zoom': this.zoom_ - this.ratioZoomAdjustment_
      };
      opt_adjustView(requestedData);
      requestedData.zoom += this.ratioZoomAdjustment_;
      this.applyAdjustedView_(requestedData);
    }

    this.control_.reset();
  }
};


/**
 * This method moves map in decelerated move to target adjusted view
 * @param {Object} requestedData
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.applyAdjustedView_ = function(requestedData) {
  'use strict';
  var that = this,
      mapCenter = this.mapCenter_,
      destPoint = this.pixelProjection_['projection'].geoToPoint(requestedData['camera']['position']),
      destPosition = this.pixelProjection_.pointToPixel(destPoint),
      destY = destPosition['y'],
      destX = destPosition['x'],
      destZ = requestedData['zoom'],
      startPoint = this.pixelProjection_.pointToPixel(mapCenter),
      startY = startPoint['y'],
      startX = startPoint['x'],
      startZ = this.zoom_,
      speed = this.control_.getSpeed(),
      atX = this.control_['atX'],
      atY = this.control_['atY'],
      duration,
      lastPath = 0,
      deceleration,
      distance,
      direction,
      dx,
      dy,
      dz;

  destX = this.getClosestX_(startX, destX);
  dx = destX - startX;
  dy = destY - startY;
  dz = destZ - startZ;
  distance = H.math.sqrt(dx * dx + dy * dy + dz * dz);
  if (speed === 0 || distance === 0) {
    //either we are already at target (when distance is 0) or we are not moving at all (when speed is 0), do noting
    return;
  }
  //normalized direction vector
  direction = {
    x: dx / distance,
    y: dy / distance,
    z: dz / distance
  };
  duration = (2 * distance) / speed;
  deceleration = speed * speed / (2 * distance);
  this.startAnimation_(function(progress, timeFromStart, frameTime, timeStamp) {
    var path,
        deltaPath,
        newZoom,
        zoomChange;
    if (progress !== 1) {
      //use path delta (decelerated move for nice effect of slowing down)
      //and direction vector, to modify zoom and center
      path = (speed * timeFromStart - deceleration * timeFromStart * timeFromStart / 2);
      deltaPath = path - lastPath;
      zoomChange = direction.z * deltaPath;
      if (zoomChange) {
        newZoom = that.clampZoomWithRatio_(that.zoom_ + zoomChange);
        that.zoomAt_(that.clampZoomWithRatio_(newZoom), atX, atY);
      }

      that.applyPxOffset(
          H.math.round(direction.x * deltaPath),
          H.math.round(direction.y * deltaPath));
      lastPath = path;
    } else {
      //progress === 1 means we have reached the last frame of animation
      //correct position to destination coords and render
      that.mapCenter_ = destPoint;
      that.zoomAt_(that.clampZoomWithRatio_(destZ), atX, atY);
      that.synchronize_();
      that.relaunch_();
    }
    //we are not using ease since decelerated move does that already
  }, duration);
};


/**
 * Function listenes to control value changes and updates current animation
 * @param {H.util.Event} evt
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.onAnimationControl_ = function(evt) {
  'use strict';
  var that = this,
      newZoom,
      offset = evt['offset'];

  this.prioCenter_.set(0, 0);
  if (offset['x'] || offset['y'] || offset['z']) {
    this.controlFrameTs_ = H.util.shims.now();
    this.startAnimation_(function(progress, timeFromStart, frameTime, timeStamp) {
      that.controlFrameTs_ = timeStamp;
      that.applyControl_(offset, frameTime);
    }, Number.POSITIVE_INFINITY);
  } else {
    //if all values are 0 we can stop animation (no move)
    this.stopAnimation_(true);
  }
};


/**
 * This method applies the control offset
 * @param {H.map.render.p2d.ControlUtils.ControlOffset} offset
 * @param {number=} opt_frameTime
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.applyControl_ = function(offset, opt_frameTime) {
  'use strict';
  var frameTime = opt_frameTime || 0;
  //console.log(offset);
  if (offset['z']) {
    this.zoomAt_(this.clampZoomWithRatio_(this.zoom_ + offset['z'] * frameTime),
        this.control_['atX'], this.control_['atY']);
  }
  this.applyPxOffset(offset['x'] * frameTime, offset['y'] * frameTime);
};


/**
 * Function starts internal animation
 * @param {function(number, number, number, number)} onFrame
 * @param {number} duration
 * @param {function()=} opt_onFinish
 * @param {function(number):number=} opt_ease
 * @private
 */
H.map.render.p2d.RenderEngine.prototype.startAnimation_ =
    function(onFrame, duration, opt_onFinish, opt_ease) {
  'use strict';
  if (this.animation_ && this.animation_.isRunning()) {
    this.animation_.stop(true);
  }

  this.animation_ = new H.util.animation.Animation(onFrame, duration,
      opt_onFinish, opt_ease);
  this.animation_.start();
};


/**
 * Function stops running animation
 * @private
 * @param {boolean=} opt_suppressCallback
 */
H.map.render.p2d.RenderEngine.prototype.stopAnimation_ = function(opt_suppressCallback) {
  'use strict';
  if (this.animation_ && this.animation_.isRunning()) {
    this.animation_.stop(opt_suppressCallback);
  }
};


/**
 * @override
 * @export
 * @method
 */
H.map.render.p2d.RenderEngine.prototype.enter = H.util.doNothing;


/**
 * @override
 * @export
 */
H.map.render.p2d.RenderEngine.prototype.exit = function(callback) {
  'use strict';
  // TODO: hackish way to realign 2d to panorama should there be more generic
  // way to do that?
  var cameraData = this.viewModel.getCameraData();
  this.onViewUpdate(/** @type {H.map.ViewModel.UpdateEvent} */({
    'requested': {
      'camera': cameraData,
      'zoom': cameraData['zoom'],
      'animate': true
    }
  }));
  this.fadeout(this.layersContainer_, callback);
};


/**
 * @override
 */
H.map.render.p2d.RenderEngine.prototype.capture = function(x1, y1, x2, y2) {
  'use strict';
  var surfaceCanvas = this.surface_.getElement(),
      captureCanvas = /** @type {HTMLCanvasElement}*/(surfaceCanvas.ownerDocument.createElement('canvas')),
      ctx = captureCanvas.getContext('2d'),
      pixelRatio = this.pixelRatio;

  captureCanvas.width = (x2 - x1) * pixelRatio;
  captureCanvas.height = (y2 - y1) * pixelRatio;

  // Draw surface canvas into capturing canvas by shifting position accordingly
  ctx.drawImage(surfaceCanvas, -x1 * pixelRatio, -y1 * pixelRatio);

  return captureCanvas;
};


/**
 * Capabilities object, describes values that can be rendered by RenderEngine ex.:
 * {
 * 'zoom': {
 *   'min': -8,
 *   'max': 22
 * },
 * 'camera': {
 *   'pitch' : {'min': 0, 'max': 0},
 *   'roll' : {'min': 0, 'max': 0},
 *   'yaw' : {'min': 0, 'max': 0},
 *   'fov' : {'min': 70, 'max': 70}
 * }
 * }
 *
 */
H.map.render.p2d.RenderEngine.Capabilities;


// TODO: add option that supplies default projection to the engine
/**
 * @typedef {{
 *   renderBaseBackground: (Object|undefined),
 *   pixelRatio: (number),
 *   enableSubpixelRendering: (boolean|undefined)
 * }}
 * @property {Object=} renderBaseBackground Object describes how many cached zoom levels should be used as a base map
 *    background while base map tiles are
 * @property {number} pixelRatio The pixelRatio to use for over-sampling in cases of high-resolution displays
 * @property {boolean=} enableSubpixelRendering
 * @publish
 */
H.map.render.p2d.RenderEngine.Options;

