goog.provide('H.util.AsyncQueue');

goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.lang.OutOfRangeError');
goog.require('H.util.ICancelable');



/**
 * The AsyncQueue represents a queue of task items that are processed
 * asynchronously. Items can be added and cancelled before they are
 * processed by the queue.
 *
 * The AsyncQueue expects the implementation of processing a work item to be
 * passed as a function. This function MUST call task#done with no arguments
 * when it finishes a task. If an error occurs during the processing the
 * task's done function MUST be called with an error message.
 *
 * @example
 * //---------------------------------
 * //a simple example
 * //---------------------------------
 * var additionQueue = new H.util.AsyncQueue(3, function(task, data) {
 *   //process data
 *   var operandA = data.operandA,
 *       operandB = data.operandB,
 *       result = operandA + operandB;
 *   //call task.done
 *   task.done(result);
 * });
 *
 * //push tasks with data to process
 * additionQueue.push({
 *    operandA: 1,
 *    operandB: 1
 * }, function(task, result, opt_error) { console.log(result); });
 *
 * //---------------------------------
 * //an example with an error
 * //---------------------------------
 * var divisionQueue = new H.util.AsyncQueue(3, function(task, data) {
 *   //process data
 *   var operandA = data.operandA,
 *       operandB = data.operandB;
 *   if(operandB === 0) {
 *     //call task.done with an error message
 *     task.done(null, 'Division by zero!');
 *   }
 *   else {
 *     result = operandA / operandB;
 *     //call task.done
 *     task.done(result);
 *   }
 * });
 * //push tasks with data to process
 * divisionQueue.push({
 *    operandA: 1,
 *    operandB: 0
 * }, function(task, result, opt_error) {
 *   if(opt_error) console.log("Oh noes: ", opt_error);
 *   else console.log("Yay: ", result);
 * });
 *
 * @export
 * @constructor
 * @param {H.util.AsyncQueue.Mode} mode the processing mode for this queue
 * @param {function(H.util.AsyncQueue.Task, *)} processFn the function implementing the actual processing of the
 *        task item (it receives the the task item and the task data).
 */
H.util.AsyncQueue = function(mode, processFn) {
  'use strict';
  var that = this;

  if (!H.lang.isFunction(processFn)) {
    throw new H.lang.InvalidArgumentError(H.map.provider.ImageTileProvider, 1, processFn);
  }

  if (!H.lang.isFunction(mode['next'] || H.lang.isFunction(mode['cycle']))) {
    throw new H.lang.InvalidArgumentError(H.util.AsyncQueue, 2,
        'Argument "mode" must implement H.util.AsyncQueue.Mode');
  }

  this.queue_ = [];
  this.processing_ = [];
  this.mode_ = mode;

  this.process = processFn;

  //binding process queue to THIS
  this.processQueueBound_ = function() {
    //console.log("process async");
    that.processQueue_();
  };
  //thresholding of processQueue calls to avoid unnecessary timer installs
  this.processQueueHandle_ = null;
  this.scheduleProcessQueue_ = function() {
    if (!that.processQueueHandle_) {
      that.processQueueHandle_ = H.global.setTimeout(that.processQueueBound_, 1);
    }
  };
};


/**
 * This method pushes a task item to the queue for processing.
 * @publish
 * @export
 * @param {*} data the task data to be passed to the process function once the taks is being processed.
 * @param {function(H.util.AsyncQueue.Task, *, ?)} onDone a function to be called once the task finishes
 *        processing (an optional error string is passed in case of an error or in case the task was cancelled).
 * @return {H.util.AsyncQueue.Task} the new task object
 */
H.util.AsyncQueue.prototype.push = function(data, onDone) {
  'use strict';
  if (!H.lang.isFunction(onDone)) {
    throw new H.lang.InvalidArgumentError(this.push, 1, onDone);
  }

  var that = this,
      task = this.popTask_().init_(
          data,
          this.scheduleProcessQueue_,
          onDone);

  this.queue_.push(task);
  this.scheduleProcessQueue_();
  return task;
};


/**
 * Returns the mode object this AsyncQueue uses.
 * @return {H.util.AsyncQueue.Mode} the mode used by this AsyncQueue
 */
H.util.AsyncQueue.prototype.getMode = function() {
  return this.mode_;
};


/**
 * Immediately triggers the queue to process items.
 */
H.util.AsyncQueue.prototype.processSync = function() {
  this.processQueue_();
};


/**
 * This method takes items from the queue and calls the process function if the task was not already cancelled.
 * @private
 */
H.util.AsyncQueue.prototype.processQueue_ = function() {
  'use strict';
  var next,
      processing = this.processing_,
      queue = this.queue_,
      i = 0,
      doneTask,
      mode = this.mode_;
  mode.cycle();

  //we reset the processQueueHandle_ here in order to be able
  //to schedule another run from within this function. This is
  //indirectly called from task#done (to be seen further down);
  this.processQueueHandle_ = null;

  //now we go over all the processing items and remove the ones which are
  //done.
  while (i < processing.length) {
    next = processing[i];
    if (next.isDone_) {
      processing.splice(i, 1);
    } else {
      i++;
    }
  }

  //now we take items from the queue until our processing array is as large
  //as maxConcurrent prescribes
  while ((next = mode.next(this.queue_, this.processing_))) {
    //next will always have a value (since we checked for this.queue_.length)
    //next = this.queue_.shift();
    if (next['cancelled']) {
      //this will trigger another process cycle
      next.done(null, 'cancelled');
    } else {
      processing.push(next);
      this.process(next, next['data']);
    }
  }
};


/**
 * This function tries to retrieve a previously used task instance from the task
 * pool. If no free tasks are available a new one is created.
 * @private
 * @return {H.util.AsyncQueue.Task} a free task instance
 */
H.util.AsyncQueue.prototype.popTask_ = function() {
  //greedy / no clean-up
  return new H.util.AsyncQueue.Task();
};



/**
 * The task represents a cancelable work item managed by the AsyncQueue.
 * @implements {H.util.ICancelable}
 * @publish
 * @export
 * @constructor
 */
H.util.AsyncQueue.Task = function() {
  this.init_(null, null, null);
};


/**
 * This method populates a task with new data. As a side-effect the tasks 'isDone_' flag is reset.
 * @private
 * @param {*} data The data required by the process function
 * @param {?function()} onDoneForQueue A callback for the queue to be informed when the task has finished execution.
 * @param {?function(H.util.AsyncQueue.Task, *, ?)} onDoneForTask A callback executed for each task
 *        individually when the task finished execution.
 * @return {H.util.AsyncQueue.Task} the task instance
 */
H.util.AsyncQueue.Task.prototype.init_ = function(data, onDoneForQueue, onDoneForTask) {
  'use strict';

  /**
   * This property holds the Task's data object.
   * @type {TData}
   */
  this['data'] = data;

  /**
   * @type {boolean}
   * @private
   */
  this['cancelled'] = false;

  /**
   * @type {boolean}
   */
  this.isDone_ = false;

  /**
   * @type {?function()}
   * @private
   */
  this.onDoneForQueue_ = onDoneForQueue;

  /**
   * @type {?function(H.util.AsyncQueue.Task, *, ?)}
   * @private
   */
  this.onDoneForTask_ = onDoneForTask;

  /**
   * Reset the custom cancel function
   */
  this.cancel_ = null;

  return this;
};


/**
 * This method signals that a task is done processing. It must be called from
 * the process function to inform the task queue that new tasks can be processed.
 * @publish
 * @export
 * @param {*} result the result of the process operation on this task
 * @param {?} error an optional error message in case the processing of the task encountered an error.
 */
H.util.AsyncQueue.Task.prototype.done = function(result, error) {
  'use strict';
  this.isDone_ = true;
  this.onDoneForQueue_();
  this.onDoneForTask_(this, result, error);
};


/**
 * The default implementation of cancelling a task. It only has an effect if
 * the task is cancelled before processing. To implement task specific
 * cancelling use the setCancelFn method on this object once it is being
 * processed.
 * @publish
 * @export
 */
H.util.AsyncQueue.Task.prototype.cancel = function() {
  'use strict';
  this.cancel_ && this.cancel_();
  this['cancelled'] = true;
};


/**
 * This method sets the implementation of a task specific cancel operations to
 * allow for ad-hoc cancelling during the processing of the task.
 * @publish
 * @export
 * @param {function()} cancelFn the cancel function
 * @return {H.util.AsyncQueue.Task} the instance itself
 */
H.util.AsyncQueue.Task.prototype.setCancelFn = function(cancelFn) {
  'use strict';
  this.cancel_ = cancelFn;
  return this;
};



/**
 * This interface defines a processing mode object which is used by an asynchronous queue to
 * determine how many items to process in one cycle.
 * @interface
 */
H.util.AsyncQueue.Mode = function() {};


/**
 * Requests the next item from the queued items for processing in the current cycle.
 * @param {Array.<H.util.AsyncQueue.Task>} queuedItems the currently queued tasks
 * @param {Array.<H.util.AsyncQueue.Task>} processingItems the tasks currently processing
 * @return {?H.util.AsyncQueue.Task} either the next task for processing or null if no tasks are to be
 *         processed in this cycle.
 */
H.util.AsyncQueue.Mode.prototype.next = function(queuedItems, processingItems) {};


/**
 * Informs the processing mode that a new processing cycle has started.
 */
H.util.AsyncQueue.Mode.prototype.cycle = function() {};
