goog.provide('H.map.Circle');

goog.require('H.geo.Point');
goog.require('H.geo.Strip');
goog.require('H.lang');
goog.require('H.map.Polygon');
goog.require('H.math');



/**
 * A Polygon with a circular shape.
 * @publish
 * @export
 * @extends {H.map.Polygon}
 * @final
 * @constructor
 * @param {H.geo.IPoint} center The geographical coordinates of the circle's center
 * @param {number} radius The radius of the circle in meters
 * @param {H.map.Circle.Options=} opt_options
 *    An object that specifies circle options and their initial values (among these,
 *    <code>precision</code> has a significant impact on the shape of the circle - please
 *    see <code>{@link H.map.Circle.Options}</code>
 */
H.map.Circle = function(center, radius, opt_options) {
  'use strict';
  // Initialize the circle as an Polygon with a null-strip:
  H.map.Circle.superClass_.constructor.call(this, null, opt_options);
  // Set the circle specific properties:
  this.setCenter(center);
  this.setRadius(radius);
  if (opt_options && 'precision' in opt_options) {
    this.setPrecision(opt_options['precision']);
  }
};
H.lang.inherits(H.map.Circle, H.map.Polygon);


/**
 * To set the geographical center point of this circle.
 * If the specified center is an instance of H.geo.Point you must not modify this Point instance without calling
 * setCenter immediately afterwards.
 *
 * @param {H.geo.IPoint} center

 * @publish
 * @export
 */
H.map.Circle.prototype.setCenter = function(center) {
  'use strict';
  if (!(center = H.geo.Point.fromIPoint(center)).equals(this.center_)) {
    this.center_ = center;
    H.map.Circle.superClass_.setStrip.call(this, null);
  }
};


/**
 * To get the center point of this circle
 * You must not modify the returned Point instance without calling setCenter immediately afterwards.
 *
 * @return {H.geo.Point}

 * @publish
 * @export
 */
H.map.Circle.prototype.getCenter = function() {
  'use strict';
  return this.center_;
};


/**
 * To set the length of the radius of the circle in meters.
 * The value is clamped to the of {@code[0 ... 20015089.27787877]}  (half WGS84 mean circumference)
 *
 * @param {number} radius
 *
 * @publish
 * @export
 */
H.map.Circle.prototype.setRadius = function(radius) {
  'use strict';
  // The maximum radius is a quarter of the earth's circumference, don't worry, CC will fold the constants
  if (H.lang.isNaN((radius = H.math.clamp(radius, 0, H.math.PI * H.geo.WGS84_MEAN_RADIUS / 2)))) {
    throw new H.lang.InvalidArgumentError(this.setRadius, 0, radius);
  }
  if (this.radius_ !== radius) {
    this.radius_ = radius;
    H.map.Circle.superClass_.setStrip.call(this, null);
  }
};


/**
 * To get the length of the radius of the circle in meters.
 *
 * @return {number}
 *
 * @publish
 * @export
 */
H.map.Circle.prototype.getRadius = function() {
  'use strict';
  return this.radius_;
};


/**
 * The precision of the circle.
 * @private
 * @type {number}
 */
H.map.Circle.prototype.precision_ = 60;


/**
 * To set the precision of this circle {@see H.map.Circle.Options#precision}
 *
 * @param {number} precision
 *
 * @publish
 * @export
 */
H.map.Circle.prototype.setPrecision = function(precision) {
  'use strict';
  if (!H.lang.isNumber(precision)) {
    throw new H.lang.InvalidArgumentError(this.setPrecision, 0, precision);
  }
  if (this.precision_ !== (precision = H.math.clamp(H.math.round(precision), 4, 360))) {
    this.precision_ = precision;
    H.map.Circle.superClass_.setStrip.call(this, null);
  }
};


/**
 * The key of the precision property. Not tagged as constants to reduce code size!
 * @private
 * @type {string}
 */
H.map.Circle.prototype.precisionKey_ = 'precision';


/**
 * To get the precision value of this circle
 *
 * @return {number}
 *
 * @publish
 * @export
 */
H.map.Circle.prototype.getPrecision = function() {
  'use strict';
  return this.precision_;
};


