/**
 * @override
 * @export
 */
H.map.Circle.prototype.setNorthPoleCovering = function() {
  'use strict';
  // We ignore the call. This flag is set in getStrip_
  return this;
};


/**
 * @override
 * @export
 */
H.map.Circle.prototype.setStrip = function() {
  'use strict';
  // We ignore the call. The circle's strip will be defined in getStrip_ only
  return this;
};


/**
 * @override
 * @export
 */
H.map.Circle.prototype.getStrip = function() {
  'use strict';
  return this.strip || H.map.Circle.superClass_.setStrip.call(this, this.getStrip_()).strip;
};


/**
 * To get the computed strip of the circle
 * @private
 * @return {H.geo.Strip}
 */
H.map.Circle.prototype.getStrip_ = function() {
  'use strict';
  var center = this.center_,
      lat = center['lat'],
      radius = this.radius_,
      i = 0,
      end = 360,
      strip = new H.geo.Strip,
      precision,
      step;

  // Circle close to pole became an open polygon - some wave around the globe, than we need proper filling
  this.isCoveringNorthPole = lat > 0;

  if (H.math.abs(lat) === 90) {
    // Center is exact at a pole, so we just add points with same latitude but different longitudes
    lat = center.walk(0, radius, true)['lat'];
    for (; i < end; i += 120) {
      strip.pushLatLngAlt(lat, i - 180, H.lang.UNDEF);
    }
  } else {
    precision = this.precision_;
    step = 360 / precision;
    // We drop the last point (360) because Circle is a Polygon which doesn't need the closing leg
    for (; i < end; i += step) {
      // Uses walk() over Great Circle!
      strip.pushPoint(center.walk(i, radius, true));
    }
  }
  return strip;
};


/**
 * @publish
 * @property {H.map.SpatialStyle=} style the style to be used when tracing the polyline
 * @property {boolean=} visibility An optional boolean value indicating whether this
 *      map object is visible, default is {@code true}
 * @property {number=} precision The precision of a circle as a number of segments to be
 *      used when rendering the circle. The value is clamped to the range between [4 ... 360],
 *      where 60 is the default. Note that the lower the value the more angular and
 *      the less circle-like the shape appears and, conversely, the higher the value the
 *      smoother and more rounded the result. Thus, starting at the extreme low end of the
 *      possible values, 4 produces a square, 6 a hexagon, while 30 results in a
 *      circle-like shape, although it appears increasingly angular as the zoom level
 *      increases (as you zoom in), and finally 360 produces a smooth circle.
 * @property {number=} zIndex
 *      The z-index value of the circle, default is {@code 0}
 * @property {number=} min
 *      The minimum zoom level for which the circle is visible, default is {@code -Infinity}
 * @property {number=} max
 *      The maximum zoom level for which the circle is visible, default is {@code Infinity}
 * @property {(H.map.provider.Provider|null)=} provider
 *           The provider of this object. This property is only needed if a customized Implementation of
 *           ObjectProvider wants to instantiate an object.
 *
 * @typedef {{
 *   style: H.map.SpatialStyle,
 *   visibility: boolean,
 *   precision: number,
 *   zIndex: (number|undefined),
 *   min: (number|undefined),
 *   max: (number|undefined),
 *   provider: (H.map.provider.ObjectProvider|null|undefined)
* }}
 */
H.map.Circle.Options;
