goog.provide('H.util.shims');

goog.require('H.lang');


/**
 * Shim for requestAnimationFrame
 */
H.util.shims.requestAnimationFrame;


/**
 * Shim for cancelAnimationFrame
 */
H.util.shims.cancelAnimationFrame = (function() {
  'use strict';
  var win = H.global,
      hasNativeImpl,
      nativeRequest,
      nativeCancel,
      ANIMATION_FRAME = 'AnimationFrame',
      // The prefixes in ascending priority, last token is an empty string "" to try W3C's "cancelAnimationFrame" first
      prefixes = H.lang.tokenize('o ms moz webkit '),
      prefix,
      i = 5,
      doNothing = function() {};

  while (i-- && !hasNativeImpl) {
    prefix = prefixes[i];
    hasNativeImpl = (nativeRequest = win[prefix + (prefix ? 'R' : 'r') + 'equest' + ANIMATION_FRAME]) &&
        !H.lang.isNaN(nativeRequest.call(window, doNothing)) &&
        (nativeCancel = win[prefix + (prefix ? 'C' : 'c') + 'ancel' + ANIMATION_FRAME]);
  }

  H.util.shims.requestAnimationFrame = hasNativeImpl ?
      function(callback) {return nativeRequest.call(win, callback);} :
      function(callback) {return win.setTimeout(callback, 25);};

  return hasNativeImpl ?
      function(handle) {return nativeCancel.call(win, handle);} :
      function(handle) {return win.clearTimeout(handle);};
})();


/**
 * Shim for performance.now
 */
H.util.shims.now = (function() {
  'use strict';
  return (H.global.performance && H.global.performance.now) ? function() {
    return H.global.performance.now();
  } : function() {
    return Date.now();
  };
}());
