goog.provide('H.util.events');
goog.require('goog.events');


/**
 * Namespace contains events related functions
 * @namespace H.util.events
 * @export
 */


/**
 * Alias for listenOnce static function
 * @export
 * @method
 * @param {goog.events.ListenableType} src The node to listen to
 *     events on.
 * @param {string|Array.<string>} type Event type or array of event types.
 * @param {Function|Object} listener Callback method.
 * @param {boolean=} opt_capt Fire in capture phase?.
 * @param {Object=} opt_handler Element in whose scope to call the listener.
 * @return {goog.events.Key} Unique key for the listener.
 */
//@borrows goog.events.listenOnce as H.util.events.listenOnce
H.util.events.listenOnce = goog.events.listenOnce;



