//######################################################################################################################
//############################################### eventPhase Patch #####################################################
//######################################################################################################################


/**
 * A reference to the original H.util.events.fireListener function (taken from goog.events)
 * @private
 * @param {goog.events.Listener} listener The listener object to call.
 * @param {Object} eventObject The event object to pass to the listener.
 * @return {boolean} Result of listener.
 * @method
 */
H.util.events.fireListenerOriginal_ = goog.events.fireListener;


/**
 * @ignore
 * @suppress {duplicate}
 *
 * @param {goog.events.Listener} listener
 * @param {Object} eventObject
 * @return {boolean}
 */
goog.events.fireListener = function(listener, eventObject) {
  // A modified verson of goog.events.fireListener to update the eventPhase property of the event before a listener is
  // invoked. It delegates the call to the original H.util.events.fireListener function.
  // A second patch: If it is in bubbling phase and the event type is either "pointerenter" or "pointerleave" we don't
  // call the listener.
  'use strict';
  var eventPhase,
      isBubbling,
      eventType,
      returnValue = true;

  // Detect the event phase:
  eventPhase = eventObject[
      eventObject.currentTarget === eventObject.target ? 'AT_TARGET' :
      listener.capture ? 'CAPTURING_PHASE' :
      (isBubbling = true, 'BUBBLING_PHASE')];

  // PATCH 1: Skip the listener if it is BUBBLING_PHASE and the type is either "pointerenter" or "pointerleave"
  if (!isBubbling || (eventType = eventObject.type) !== 'pointerenter' && eventType !== 'pointerleave') {
    // PATCH 2a: Set the event's eventPhase property:
    eventObject['eventPhase'] = eventPhase;
    returnValue = H.util.events.fireListenerOriginal_(listener, eventObject);
    // PATCH 2b: Reset the event's eventPhase property at the end of the bubbling phase:

    if (isBubbling && listener.src.getParentEventTarget && listener.src.getParentEventTarget() === null) {
      delete eventObject['eventPhase'];
    }
  }

  return returnValue;
};
