goog.provide('H.math.Size');

goog.require('H.math.ISize');



/**
 * Class for representing sizes consisting of a width and height.
 * @publish
 * @export
 * @final
 * @implements {H.math.ISize}
 * @constructor
 * @param {number} width Width.
 * @param {number} height Height.
 */
H.math.Size = function(width, height) {
  'use strict';

  this['w'] = +width;
  this['h'] = +height;

};


/**
 * The size's width value
 * @publish
 *
 * @type {number}
 */
H.math.Size.prototype.w;


/**
 * The size's height value
 * @publish
 *
 * @type {number}
 */
H.math.Size.prototype.h;
