goog.provide('H.map.render.p2d.canvas.Surface');

goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.map.render.p2d.Surface');



//###############################################  CONSTRUCTOR  ########################################################
/**
 * Class represents a drawing surface based on canvas.
 *
 * @extends {H.map.render.p2d.Surface}
 * @constructor
 * @param {Element} container
 * @param {number} width The width of the surface in pixels (already multiplied by pixelRatio)
 * @param {number} height The height of the surface in pixels (already multiplied by pixelRatio)
 * @param {number} pixelRatio
 */
H.map.render.p2d.canvas.Surface = function(container, width, height, pixelRatio) {
  'use strict';
  H.map.render.p2d.canvas.Surface.superClass_.constructor.apply(this, arguments);

  /**
   * The drawing context of the canvas element
   * @private
   * @type {CanvasRenderingContext2D}
   */
  this.ctx_ = this.element.getContext('2d');

};
H.lang.inherits(H.map.render.p2d.canvas.Surface, H.map.render.p2d.Surface);


//###########################################  CONTAINER + ELEMENT  ####################################################
/**
 * Concrete implementation of {@link H.map.render.p2d.Surface.prototype.createElement}
 * @override
 */
H.map.render.p2d.canvas.Surface.prototype.createElement = function(ownerDoc) {
  'use strict';
  var element = ownerDoc.createElement('CANVAS'),
      style = element.style;
  style.position = 'absolute';
  style.top = style.left = 0;
  return element;
};


//####################################################  SIZE  ##########################################################
/**
 * Concrete implementaion of {@link H.map.render.p2d.Surface.prototype.setSizeInternal}
 * @override
 */
H.map.render.p2d.canvas.Surface.prototype.setSizeInternal = function(element, style, width, height, pixelRatio) {
  'use strict';
  // We have to assure that we have a minimum size of 1 x 1 pixel to avoid errors when drawing on a canvas:
  element.width = H.math.max(width, 1);
  element.height = H.math.max(height, 1);
  // Scale the canvas according to the pixel ratio:
  style.width = H.math.round(width / pixelRatio) + 'px';
  style.height = H.math.round(height / pixelRatio) + 'px';
};


//################################################  OPACITY  ###########################################################
/**
 * Concrete implementaion of {@link H.map.render.p2d.Surface.prototype.setOpacityInternal}
 * @override
 */
H.map.render.p2d.canvas.Surface.prototype.setOpacityInternal = function(opacity) {
  'use strict';
  this.ctx_.globalAlpha = opacity;
};


//#################################################  DRAW  #############################################################
/**
 * Concrete implementaion of {@link H.map.render.p2d.Surface.prototype.draw}
 * This implementation excepts only a HTMLCanvasElement, HTMLImageElement or HTMLVideoElement as el argument
 * @override
 */
H.map.render.p2d.canvas.Surface.prototype.draw = function(el, dx, dy, dw, dh) {
  'use strict';
  this.ctx_.drawImage(/** @type {(HTMLCanvasElement|HTMLImageElement|HTMLVideoElement)} */(el), dx, dy, dw, dh);
};


//#################################################  CLEAR  ############################################################
/**
 * Concrete implementaion of {@link H.map.render.p2d.Surface.prototype.clear}
 * The requested type for the opt_target argument is {@link H.math.Rect}
 * @override
 */
H.map.render.p2d.canvas.Surface.prototype.clear = function(opt_target) {
  'use strict';
  var left,
      top;

  // Satisfy CC, will be dropped by compiler:
  opt_target = /** @type {H.math.Rect} */ (opt_target);

  if (opt_target) {
    // TODO: UNITTEST
    this.ctx_.clearRect(
        left = opt_target['left'],
        top = opt_target['top'],
        opt_target['right'] - left,
        opt_target['bottom'] - top);
  } else {
    this.element.width = this.width;
  }
};


//################################################  DISPOSABLE  ########################################################
/**
 * Overrides {@link H.map.render.p2d.Surface.prototype.disposeInternal}
 * @override
 */
H.map.render.p2d.canvas.Surface.prototype.disposeInternal = function() {
  'use strict';
  this.element.width = this.element.height = 0;
  H.map.render.p2d.canvas.Surface.superClass_.disposeInternal.call(this);
};
