goog.provide('H.map.provider.SpatialTile');

goog.require('H.geo.Rect');
goog.require('H.map.Object');
goog.require('H.map.Spatial');
goog.require('H.map.provider.Tile');
goog.require('H.math');
goog.require('H.math.Point');
goog.require('H.math.clipping');
goog.require('H.math.simplifyDP');



//###################################################  CONSTRUCTOR  ####################################################
/**
 * SpatialTile represents part of the spatial data (i.e polyline, polygon)
 * clipped to area which fits into a single map tile. The data is clipped
 * according to tile's current x,y position and zoom level (z).
 * @export
 * @extends {H.map.provider.Tile}
 * @final
 * @constructor
 * @param {string} key the tile key
 * @param {number} x the column number of this tile
 * @param {number} y the row number of this tile
 * @param {number} z the zoom level of this tile
 * @param {number} tileSize the size of this spatial tile
 * @param {number} pixelRatio The pixelRatio to use for over-sampling in cases of high-resolution displays
 * @param {H.map.provider.ObjectProvider} objectProvider the object provider
 *  from which this tile will take it's information
 */
H.map.provider.SpatialTile = function(key, x, y, z, tileSize, pixelRatio, objectProvider) {
  'use strict';

  H.map.provider.SpatialTile.superClass_.constructor.call(this, x, y, z, null);

  this['key'] = key;

  this.objectProvider_ = objectProvider;
  this.tileSize_ = tileSize;
  if (pixelRatio !== 1) {
    this.pixelRatio_ = pixelRatio;
  }

  //to store the map objects that touch the tile
  this.objects_ = null;
  //to store the projected clipped data for fast repainting and hit detection
  this.renderData_ = {};
};
H.lang.inherits(H.map.provider.SpatialTile, H.map.provider.Tile);


//###############################################  CLIPPING BOUNDARIES  ################################################

// TODO We shouldn't store on every tile of the same provider an equal math.Rect instances for pxClipRect_ .
//      Instead the provider should get a getTilePxClipRect() method so that we can delegate the getPxClipRect() calls!


/**
 * The projected rectangle on pixel base which represents the area to clip objects
 * @private
 * @type {H.math.Rect}
 */
H.map.provider.SpatialTile.prototype.pxClipRect_;


/**
 * Returns a projected rectangle on pixel base which represents the area to clip objects.
 * The origin (0, 0) is at the top-left corner of the tile
 * @export
 * @return {H.math.Rect}
 */
H.map.provider.SpatialTile.prototype.getPxClipRect = function() {
  'use strict';
  var pxClipRect = this.pxClipRect_,
      negMargin,
      rightBottom;
  if (!pxClipRect) {
    negMargin = this['z'] > 0 ? -H.map.Spatial.prototype.PX_CLIP_MARGIN : 0;
    rightBottom = this.tileSize_ - negMargin;
    pxClipRect =
        this.pxClipRect_ =
        new H.math.Rect(negMargin, negMargin, rightBottom, rightBottom);
  }
  return pxClipRect;
};


/**
 * The geographical rectangle which represents the area to query intersecting objects
 * @private
 * @type {H.geo.Rect}
 */
H.map.provider.SpatialTile.prototype.geoClipRect_;


/**
 * Returns the geographical rectangle which represents the area to query intersecting objects
 * @export
 * @return {H.geo.Rect}
 */
H.map.provider.SpatialTile.prototype.getGeoClipRect = function() {
  'use strict';
  var geoClipRect = this.geoClipRect_,
      pxProj,
      pxClipRect;
  if (!geoClipRect) {
    pxProj = this.getPxProj();
    pxClipRect = this.getPxClipRect();
    geoClipRect =
        this.geoClipRect_ =
        H.geo.Rect.fromPoints(
        pxProj.pixelToGeo(new H.math.Point(pxClipRect['left'], pxClipRect['top'])),
        pxProj.pixelToGeo(new H.math.Point(pxClipRect['right'], pxClipRect['bottom'])));
  }
  return geoClipRect;
};


//################################################  PIXEL PROJECTION  ##################################################
/**
 * The pixel projection for this tile. Origin (0, 0) is at the top-left corner of the tile
 * @private
 * @type {H.geo.PixelProjection}
 */
H.map.provider.SpatialTile.prototype.pxProj_;


/**
 * Returns the pixel projection for this tile. The x and y coordinates defines the top-left corner of the tile
 * @export
 * @return {H.geo.PixelProjection}
 */
H.map.provider.SpatialTile.prototype.getPxProj = function() {
  'use strict';
  var pxProj = this.pxProj_,
      tileSize;
  if (!pxProj) {
    tileSize = this.tileSize_;
    pxProj =
        this.pxProj_ =
        new H.geo.PixelProjection(H.lang.UNDEF, tileSize);
    pxProj.rescale(this['z']);
    pxProj.x = this['x'] * tileSize;
    pxProj.y = this['y'] * tileSize;
  }
  return pxProj;
};


//###############################################  PIXEL RATIO  ########################################################
/**
 * The pixel ratio of this tile
 * @private
 * @type {number}
 */
H.map.provider.SpatialTile.prototype.pixelRatio_ = 1;


/**
 * Returns the pixel ratio of this tile
 * @export
 * @return {number}
 */
H.map.provider.SpatialTile.prototype.getPixelRatio = function() {
  return this.pixelRatio_;
};


//######################################################################################################################
/**
 * A stateless comparator function to use for z-order sorting of spatial objects - see Array.prototype.sort().
 * @private
 * @param {H.map.Spatial} first
 * @param {H.map.Spatial} second
 * @return {number}
 */
H.map.provider.SpatialTile.zOrderComparator_ = function(first, second) {
  return H.map.Object.compareZInfo(first.getZInfo(), second.getZInfo(), false);
};


/**
 * This method returns the objects that may draw on this tile in correct z-order
 * @return {Array.<H.map.Spatial>} the spatial objects
 * @private
 */
H.map.provider.SpatialTile.prototype.requestObjects_ = function() {
  'use strict';
  if (!this.objects_) {
    this.objects_ = this.objectProvider_['requestSpatialsByTile'](this, false, false).
        sort(H.map.provider.SpatialTile.zOrderComparator_);
  } else {
    if (this.getProviderInvalidations_().isZOrder(this.renderedAt_)) {
      // so we already have the valid object list BUT the order inside has changed
      // then we can safely sort the objects
      this.objects_.sort(H.map.provider.SpatialTile.zOrderComparator_);
    }
  }
  return this.objects_;
};


//#############################################  TILE INVALIDATION  ####################################################
/**
 * Returns the invalidations for all spatial objects of the provider
 * @private
 * @return {H.map.provider.Invalidations}
 */
H.map.provider.SpatialTile.prototype.getProviderInvalidations_ = function() {
  return this.objectProvider_['getInvalidations'](H.map.Object.Type['SPATIAL']);
};


/**
 * This method checks invalidations on the object provider and invalidates
 * this tile's information. In certain circumstances we can keep information
 * on the tile that is expensive to retrieve. If only style information on the
 * objects changes we can keep the spatially projected and clipped data for
 * faster redrawing of the tile.
 * @private
 */
H.map.provider.SpatialTile.prototype.checkInvalidations_ = function() {
  'use strict';

  // FIXME: Sometimes objects on the tile are null
  if (goog.DEBUG) {
    if (this.objects_ === null) {
      H.global.console.error('FIXME: checkInvalidations_ this.objects_ is NULL!');
    }
  }

  var renderedAt = this.renderedAt_ || H.map.provider.Invalidations.MARK_INITIAL,
      invalidations = this.getProviderInvalidations_(),
      objects = this.objects_ || this.requestObjects_(),
      queriedObjects,
      j,
      object,
      i = objects.length;

  // Let's assume canvas and data are still valid:
  this.isCanvasValid_ = true;
  this.isDataValid_ = true;

  // Is there any kind of invalidation since last rendering??
  if (invalidations.isAny(renderedAt)) {
    // Do we have ADD or REMOVE invalidations?
    if (invalidations.isAdd(renderedAt) || invalidations.isRemove(renderedAt)) {
      // If any objects have been added or removed we invalidate the canvas and the data (just to be save):
      // TODO check precisely what's invalid and what's not:
      this.isCanvasValid_ = this.isDataValid_ = false;
    } else {
      // Is there a "spatial" invalidation?
      if (invalidations.isSpatial(renderedAt) || invalidations.isVisual(renderedAt)) {
        if (invalidations.isSpatial(renderedAt)) {
          // If we have spatial invalidations, we have to query again all potentially objects for the tile:
          queriedObjects = this.objectProvider_['requestSpatialsByTile'](this, false, false);
          j = queriedObjects.length;
          if (j !== i) {
            // Fail fast: If the current object list has a different length than the queried list -> data is invalid
            // TODO Don't invalidate all data if just some are invalid
            this.isDataValid_ = false;
          } else {
            // Otherwise, we check all queried objects:
            while (j-- && this.isDataValid_) {
              object = queriedObjects[j];
              // If the object is already in the render data and it has spatial invalidation -> data is invalid
              if (this.renderData_[object.getId()]) {
                if (object.getInvalidations().isSpatial(renderedAt)) {
                  this.isDataValid_ = false;
                }
              } else {
                // If the object is not already in the render data it is new to the tile and we also need to re-render
                this.isDataValid_ = false;
              }
            }
          }
          // If the data is invalid, then the canvas is also invalid
          this.isCanvasValid_ &= this.isDataValid_;
        }
        // Is there a "visual" invalidation?
        if (invalidations.isVisual(renderedAt)) {
          // If we only visual invalidations then we check if any of our own objects have such invalidations
          // OH-OH! this could cause performance problems for many objects!
          while (i-- && this.isCanvasValid_) {
            invalidations = objects[i].getInvalidations();
            // Any invalidations on the object?
            if (invalidations.isAny(renderedAt) && invalidations.isVisual(renderedAt)) {
              // Visual? Then canvas is invalid
              this.isCanvasValid_ = false;
            }
          }
        }
      } else {
        if (invalidations.isZOrder(renderedAt)) {
          // Only z-ordering has changed but data are valid.
          // We invalidate the canvas to force a repainting of the objects in the correct new order:
          this.isCanvasValid_ = false;
        }
      }
    }
  }
};


/**
 * This method checks if this spatial tile's canvas element is still valid and returns
 * a boolean value indicating whether the canvas element can still be used
 * for tile placement or whether it needs to be redrawn.
 * @export
 * @return {boolean} true if the canvas element is still valid, otherwise false
 */
H.map.provider.SpatialTile.prototype.isCanvasValid = function() {
  'use strict';
  //if the canvas claims to be valid, we do a check on the renderAt mark
  //TODO: We should also compare with a last checked invalidation mark
  if (this.isCanvasValid_) {
    this.checkInvalidations_();
  }
  //otherwise we already know it is invalid
  return this.isCanvasValid_;
};


// TODO: the sad truth behind this hack is that we ended up with subclass - SpatialTile
// that diverged from the base class - Tile. SpatialTile doesn't care about 'valid'
// flag introduced on the base class, but TileProvider knows nothing about canvas or data, and doesn't
// care about these annoyances it just wants to know if tile fetched from cache is valid or not.
// It's getting even worse - 'valid' is property, so there's no way just to overload this function
// and be done with it. Long story short: align base and child classes, and decide how to handle
// two level caching on TileProvider.
/**
 * Custom getter, to shadow parent's class valid property
 */
Object.defineProperty(H.map.provider.SpatialTile.prototype, 'valid', {
  // Delegate this call to isCanvasValid
  /** @this {H.map.provider.SpatialTile} */
  get: function() {
    return this.isCanvasValid();
  },

  // Disallow externally setting of valid/invalid
  set: H.util.doNothing
});


/**
 * This method checks if this spatial tile's render data is still valid and returns a
 * boolean value indicating whether the projected, clipped and simplified spatial
 * information can still be used.
 * @return {boolean} true if the rendering data is still valid, otherwise false
 */
H.map.provider.SpatialTile.prototype.isDataValid = function() {
  'use strict';
  // If the data claims to be valid, we do a check on the renderAt mark
  // TODO: We should also compare with a last checked invalidation mark
  if (this.isDataValid_) {
    this.checkInvalidations_();
  }
  // otherwise we already know it is invalid
  return this.isDataValid_;
};


/**
 * This method releases this tile's canvas element and returns it for pooling
 * @export
 * @return {HTMLCanvasElement} the canvas element
 */
H.map.provider.SpatialTile.prototype.releaseCanvas = function() {
  'use strict';
  var canvas = this['data'];
  this['data'] = null;
  return canvas;
};


/**
 * Releases this tile's rendering data.
 * @export
 * @return {Object}
 */
H.map.provider.SpatialTile.prototype.releaseData = function() {
  'use strict';
  this.renderData_ = {};
  this.objects_ = null;
  return this.renderData_;
};


//##############################################  OBJECT PICKING  ######################################################
/**
 * Returns the spatial objects considered by this tile. The array is sorted according to their drawing
 * order, the topmost object has an index #0 in returned array.
 * This function is a temporarly solution to be able to iterate over the objects of a tile,
 * needed for remote objects like metaInfo and venue to retain and release objects!
 * @export
 * @return {Array.<H.map.Spatial>}
 */
H.map.provider.SpatialTile.prototype.getObjects = function() {
  'use strict';
  return this.objects_;
};


/**
 * Returns visible objects under the given tile x/y coordinates. Array is sorted according to their drawing
 * order, topmost object has an index #0 in returned array.
 * @param {number} x x pixel coordinate on the tile
 * @param {number} y y pixel coordinate on the tile
 * @param {boolean=} opt_topMostOnly optional flag, if set method returns array with single element that is located
 * on top
 * @return {Array.<H.map.Spatial>} array of map objects
 */
H.map.provider.SpatialTile.prototype.getObjectsAt = function(x, y, opt_topMostOnly) {
  'use strict';

  // FIXME: sometimes objects on the tile are null
  // sometimes this happens when is called by events
  // on every mouse move we pick objects from the map to dispatch events on them
  if (this.objects_ === null) {
    if (goog.DEBUG) {
      H.global.console.error('FIXME: getObjectsAt this.objects_ is NULL!');
    }
    return /** @type {Array.<H.map.Spatial>} */ (H.util.emptyArray);
  }

  var objects = this.objects_,
      i = objects.length,  //@FIXME: check above sometimes this is null!
      j,
      id,
      object,
      style,
      lineWidth,
      renderData = this.renderData_,
      clippedData,
      coverageType,
      isObjectClosed,
      isGeometryClosed,
      isFilled,
      xyCoords = [],
      result = [],
      hybridIdx,
      CoverType = H.math.CoverType,
      k,
      length;

  while (i--) {
    object = objects[i];
    hybridIdx = object['getHybridIdx'](this);
    if (object.getVisibility(true)) {
      style = object.style || object.getStyle();
      lineWidth = style['strokeColor'] !== 'none' ? style['lineWidth'] : 0;
      isObjectClosed = object.isClosed(),
      isFilled = isObjectClosed && style['fillColor'] !== 'none';
      id = object.getId();
      clippedData = renderData[id][1];

      if (clippedData === object.FULLY_COVERED) {
        if (isFilled) {
          result.push(object);
        }
      } else {
        if (clippedData !== object.NOT_COVERED) {
          j = clippedData.length;
          while (j--) {
            // TODO: here we flatten array of math.Points. we must either change isCoveredBy to work with
            // array of points or change clipping to work with flat array
            xyCoords.length = 0;
            length = clippedData[j].length;
            for (k = 0; k < length; k++) {
              xyCoords.push(clippedData[j][k]['x'], clippedData[j][k]['y']);
            }

            // Don't check hitting on tessellated polygons if not isFilled
            if (j >= hybridIdx || isFilled) {
              // For tessellated spatials polygons are closed and polylines are not,
              // otherwise the same as for Spatial itself
              isGeometryClosed = (hybridIdx !== -1) ? (j < hybridIdx) : isObjectClosed;
              coverageType = H.math.isCoveredBy(x, y, xyCoords, lineWidth, isGeometryClosed);

              // Do we have a VERTEX or EDGE hit or do we have a SURFACE hit and the object is filled?
              if ((coverageType !== CoverType.NONE) && (coverageType !== CoverType.SURFACE || isFilled)) {
                result.push(object);
                break;
              }
            }
          }
        }
      }
      if (opt_topMostOnly && result.length) {
        break;
      }
    }
  }

  return result;
};


//#################################################  RENDERING  ########################################################
/**
 * This method renders the object data on to this tile and returns a boolean value whether
 * anything was rendered on the provided canvas.
 * @export
 * @param {HTMLCanvasElement} canvas the canvas on which this tile will paint
 * @return {boolean} true if the any object was rendered, false otherwise
 */
H.map.provider.SpatialTile.prototype.render = function(canvas) {
  'use strict';
  var ctx = /** @type {CanvasRenderingContext2D} */ (canvas.getContext('2d')),
      objects = this.requestObjects_(),
      length = objects.length,
      i,
      object,
      style;

  canvas.width = canvas.height = this.tileSize_;
  ctx.clearRect(0, 0, this.tileSize_, this.tileSize_);

  if (length) {
    for (i = 0; i < length; i++) {
      object = objects[i];
      style = object.style || object.getStyle();
      (object.isClosed() ? this.renderPolygon_ : this.renderPolyline_).call(this, object, ctx, style);
    }
    this['data'] = canvas;

    //########################################### Code for Visual Debugging ############################################
    if (goog.DEBUG) {
      var pixelRatio = this.pixelRatio_;
      ctx.save();
      ctx.fillStyle = 'rgba(0, 0, 0, 127)';
      ctx.font = (pixelRatio * 11) + 'px sans-serif';
      ctx.fillText(this['z'] + '/' + this['x'] + '/' + this['y'], pixelRatio * 11, pixelRatio * 11);
      ctx.strokeStyle = 'rgba(0, 0, 0, 127)';
      ctx.lineWidth = 1;
      ctx.strokeRect(0, 0, 2 * this.tileSize_, 2 * this.tileSize_);
      ctx.strokeRect((this.tileSize_ / 2) - 1, (this.tileSize_ / 2) - 1, 2, 2);
      ctx.restore();
      // Force to return true:
      length = 1;
    }
    //##################################################################################################################

  }
  this.isCanvasValid_ = this.isDataValid_ = true;
  this.renderedAt_ = this.getProviderInvalidations_().getMark();
  return length > 0;
};


//#############################################  POLYLINE RENDERING  ###################################################
/**
 * This method renders a not closed spatial object (polyline).
 * @private
 * @param {H.map.Spatial} object the polyline
 * @param {CanvasRenderingContext2D} ctx the target context
 * @param {H.map.SpatialStyle} style The style to apply
 */
H.map.provider.SpatialTile.prototype.renderPolyline_ = function(object, ctx, style) {
  'use strict';

  var renderData = this.renderData_,
      renderDataEntry = renderData && renderData[object.getId()],
      data = renderDataEntry && renderDataEntry[1],
      arrows;

  // If we do not have any renderable data yet, so we have to create it:
  if (!data) {
    data = object['getGeometry'](this);

    // TODO: We should simplify the WHOLE geometry only ONCE instead of simplify parts again and again!
    // Simplify the geometry with Douglas-Peucker algorithm:
    data = H.map.provider.SpatialTile.simplify_(data);
  }

  // We store the data ALLWAYS, even if the object will not be rendered for now:
  this.renderData_[object.getId()] = [object, data];

  // But we render ONLY if the polyline is visible and has a style which is effectively stroking:
  if (object.getVisibility(true)) {
    // ... and is effectively stroked:
    if (H.map.provider.SpatialTile.setStrokeStyle_(ctx, style, this.pixelRatio_)) {
      H.map.provider.SpatialTile.drawPaths_(data, ctx, false, true, false);
    }
    // Additionally we render the arrows if the strokeColor of the polyline is not "none" and arrows are defined ...
    if (style['fillColor'] !== 'none') {
      if (arrows = object.arrows) {
        // ... and the arrows are effectively filled (type cast to deactivate CC type check):
        if (H.map.provider.SpatialTile.setFillStyle_(ctx, /** @type {?} */(arrows))) {
          this.renderArrows_(ctx, data, arrows, style['lineWidth']);
        }
      }
    }
  }
};


//#############################################  POLYGON RENDERING  ####################################################
/**
 * This method renders a polygon onto this tile.
 * @private
 * @param {H.map.Spatial} object the polygon to render
 * @param {CanvasRenderingContext2D} ctx the canvas context on which to render
 * @param {H.map.SpatialStyle} style The style to apply
 */
H.map.provider.SpatialTile.prototype.renderPolygon_ = function(object, ctx, style) {
  'use strict';
  var renderData = this.renderData_,
      renderDataEntry = renderData && renderData[object.getId()],
      data = renderDataEntry && renderDataEntry[1],
      SpatialTile = H.map.provider.SpatialTile,
      hybridIdx,
      isFilled,
      isStroked;

  //This is the step which prepares the render data for this object
  if (!data) {
    data = object['getGeometry'](this);

    // TODO: We should simplify the WHOLE geometry only ONCE instead of simplify parts again and again!
    // Simplify the geometry with Douglas-Peucker algorithm:
    data = SpatialTile.simplify_(data);

  }

  // We do want to keep the rendering data ALLWAYS:
  this.renderData_[object.getId()] = [object, data];

  // But we render ONLY if the polygon is visible and is effectively stroked or filled:
  if (object.getVisibility(true)) {
    // If fully covered and filled, we just draw a filled rectangle and ignore the stroke:
    if (data === object.FULLY_COVERED && SpatialTile.setFillStyle_(ctx, style)) {
      ctx.fillRect(0, 0, this.tileSize_, this.tileSize_);
    } else {
      if (data !== object.NOT_COVERED) {
        isStroked = SpatialTile.setStrokeStyle_(ctx, style, this.pixelRatio_);
        isFilled = SpatialTile.setFillStyle_(ctx, style);
        // If we have clipped data, then we need to paint the path if it is stroked and/or filled:
        hybridIdx = object['getHybridIdx'](this);

        if (hybridIdx >= 0) {
          // Geometries from 0 to "hybridIdx" should be rendered as polygons without a stroke
          SpatialTile.drawPaths_(data, ctx, /*isClosed*/ true, false, isFilled, 0, hybridIdx);
          // Geometries "hybridIdx" should be rendered as polylines without a fill
          SpatialTile.drawPaths_(data, ctx, false, isStroked, false, hybridIdx);
        } else {
          SpatialTile.drawPaths_(data, ctx, true, isStroked, isFilled);
        }
      }
    }
  }
};


//######################################################################################################################
/**
 * The epsilon value to use for Douglas-Peucker simplification {@see H.math.simplifyDP}
 * @private
 * @const
 * @type {number}
 */
H.map.provider.SpatialTile.simplifyEpsilon_ = 0.6;
// TODO Determine the best epsilon value, 0.6 is just a guess


/**
 * Simplifies an arbitrary number of x,y strips using Douglas-Peucker line
 * simplification algorithm.
 * @private
 * @param {Array.<Array.<H.math.Point>>} data a list of alternating x,y arrays
 * @return {Array.<Array.<H.math.Point>>} the simplified list
 */
H.map.provider.SpatialTile.simplify_ = function(data) {
  'use strict';
  var i = data.length;
  while (i--) {
    data[i] = H.math.simplifyDP(data[i], H.map.provider.SpatialTile.simplifyEpsilon_, 2);
  }
  return data;
};


/**
 * This method applies the stroking related properties of a spatial style to the a canvas context
 * @private
 * @param {CanvasRenderingContext2D} ctx The rendering context
 * @param {H.map.SpatialStyle} style The style to apply
 * @param {number} pixelRatio
 * @return {boolean} whether the style is effectively stroking and was applied to the given context
 */
H.map.provider.SpatialTile.setStrokeStyle_ = function(ctx, style, pixelRatio) {
  'use strict';
  var isStroking = style.isStroking,
      lineDash;

  if (isStroking) {
    ctx.strokeStyle = style['strokeColor'];
    ctx.lineWidth = style['lineWidth'] * pixelRatio;
    ctx.lineCap = style['lineCap'];
    ctx.lineJoin = style['lineJoin'];
    ctx.miterLimit = style['miterLimit'];
    // IE supports dashed line only since IE11:
    // Bracket notation used because the Closure Compiler doesn't know this method and property and would obfusecate it:
    if (ctx['setLineDash'] && (lineDash = style['lineDash']) !== H.util.emptyArray) {
      ctx['setLineDash'](style['lineDash']);
      ctx['lineDashOffset'] = style['lineDashOffset'];
    }
  }
  return isStroking;
};


/**
 * This method applies the filling related properties of a spatial style to the a canvas context
 * @private
 * @param {CanvasRenderingContext2D} ctx The rendering context
 * @param {H.map.SpatialStyle} style The style to apply
 * @return {boolean} whether the style is effectively filling and was applied to the given context
 */
H.map.provider.SpatialTile.setFillStyle_ = function(ctx, style) {
  'use strict';
  var isFilling = style.isFilling;
  if (isFilling) {
    ctx.fillStyle = style['fillColor'];
  }
  return isFilling;
};


/**
 * This method draws a list of x,y array as paths on a rendering context.
 * Using opt_start and opt_end you can filter out which geometries you want to skip
 * @param {Array.<Array.<number>>} data the x,y arrays to render
 * @param {CanvasRenderingContext2D} ctx The rendering context
 * @param {boolean} isClosed Whether to close and fill each path
 * @param {boolean} isStroked Whether to stroke each path
 * @param {boolean} isFilled Whether to fill each path
 * @param {number=} opt_start Inclusive first geometry index to draw
 * @param {number=} opt_end Exclusive last geometry index to draw
 * @private
 */
H.map.provider.SpatialTile.drawPaths_ = function(data, ctx, isClosed, isStroked, isFilled, opt_start, opt_end) {
  'use strict';
  var strip,
      stripLength,
      i,
      j;

  if (isStroked || isFilled) {
    opt_start = opt_start || 0;
    if (opt_end === undefined) {
      opt_end = data.length;
    }
    for (i = opt_start; i < opt_end; i++) {
      strip = data[i];
      stripLength = strip.length;
      if (stripLength) { // TODO: Empty sub-strips shouldn't be stored at all
        ctx.beginPath();
        ctx.moveTo(strip[0].x, strip[0].y);
        for (j = 1; j < stripLength; j++) {
          ctx.lineTo(strip[j].x, strip[j].y);
        }
        if (isClosed) {
          ctx.closePath();
          if (isFilled) {
            ctx.fill();
          }
        }
        if (isStroked) {
          ctx.stroke();
        }
      }
    }
  }
};


//#############################################  ARROWS RENDERING  #####################################################
/**
 * The method will compute the length of a strip in pixel, used by renderArrows_() for each strip
 * @private
 * @param {Array.<H.math.Point>} strip The strip to compute the length for.
 * @return {number} the length of the strip in pixel.
 */
H.map.provider.SpatialTile.getStripLength_ = function(strip) {
  var i = strip.length,
      length = 0;
  while (--i) {
    length += (strip[i]).distance(strip[i - 1]);
  }
  return length;
};


/**
 * The method will draw arrows on top of a polyline to visualize the direction (e.g. for routes).
 * It's triggered by renderPolyline_()
 * @private
 * @param {CanvasRenderingContext2D} ctx The target context
 * @param {Array.<Array.<H.math.Point>>} strips The strips used to draw arrows.
 * @param {H.map.ArrowStyle} arrows The style definition for the arrows
 * @param {number} lineWidth The line width of the polyline
 */
H.map.provider.SpatialTile.prototype.renderArrows_ = function(ctx, strips, arrows, lineWidth) {
  'use strict';
  var arrowWidthHalf = lineWidth * arrows['width'] / 2,
      arrowLengthHalf = lineWidth * arrows['length'] / 2,
      arrowLengthQuarter = arrowLengthHalf / 2,
      arrowFrequency = arrowLengthHalf * 2 * arrows['frequency'],
      arrowRadius = H.math.sqrt(H.math.pow(arrowLengthHalf, 2) + H.math.pow(arrowWidthHalf, 2)),
      reducedTileSize = this.tileSize_ - arrowRadius,
      // A rectangle with the size of the tile, reduced by arrow radius on all sides to prevent arrows cutted
      // on tile border
      arrowsArea = new H.math.Rect(arrowRadius, arrowRadius, reducedTileSize, reducedTileSize),
      i,
      strip,
      j,
      stripLength,
      arrowCount,
      arrowOffset,
      nextArrowAt,
      cursor,
      legLength,
      pointA,
      pointB,
      legRatio,
      x,
      y,
      PI_OVER_2 = H.math.PI / 2;

  i = strips.length;
  // Iterate over all arrow strips
  while (i--) {
    cursor = 0;
    strip = strips[i];
    // Iterate over all legs of the strip
    if (j = strip.length) {
      // Is the whole strip long enough to place at least 1 arrow?
      if ((stripLength = H.map.provider.SpatialTile.getStripLength_(strip)) >= arrowFrequency) {
        // Define the number of arrows on the strip, restricted to 1000 arrows to prevent heavy CPU load:
        arrowCount = H.math.min(H.math.floor(stripLength / arrowFrequency), 1e3);
        // Define the arrow frequency for this strip, slightly increased, because arrowCount is an integer:
        arrowOffset = stripLength / (arrowCount + 1);
        // Compute the position of the first arrow after half frequency.
        // An ideal arrow distribution over tile borders looks like:  --->---|--->------>------>---|--->---
        nextArrowAt = arrowOffset / 2;
        // Iterate over all legs of the strip
        while (--j) {
          legLength = (pointA = strip[j]).distance(pointB = strip[j - 1]);
          // Draw as many arrows as fit on this leg:
          while (nextArrowAt <= cursor + legLength) {
            // Compute the coordinate for the arrow:
            x = pointA.x + (pointB.x - pointA.x) * (legRatio = (nextArrowAt - cursor) / legLength),
            y = pointA.y + (pointB.y - pointA.y) * legRatio;
            // Check if the distance between arrow center and tile border is >= arrow radius:
            if (arrowsArea.containsXY(x, y)) {
              // Save the IDL state, draw the arrow, restore the IDL state:
              ctx.save();
              ctx.translate(x, y);
              // TODO Can we optimize this formula?
              ctx.rotate(-H.math.atan((pointB.y - pointA.y) / (pointA.x - pointB.x)) +
                  (pointA.x < pointB.x ? PI_OVER_2 : -PI_OVER_2));
              ctx.beginPath();
              ctx.moveTo(-arrowWidthHalf, -arrowLengthHalf);
              ctx.lineTo(0, arrowLengthHalf);
              ctx.lineTo(arrowWidthHalf, -arrowLengthHalf);
              ctx.lineTo(0, -arrowLengthQuarter);
              ctx.closePath();
              ctx.fill();
              ctx.restore();
            }
            // Compute the px position of the next arrow at this leg
            nextArrowAt += arrowOffset;
          }
          // Set the cursor to the start point of the next leg:
          cursor += legLength;
        }
      }
    }
  }
};

