goog.provide('H.net.ImageLoader');


goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.util');
goog.require('H.util.ParallelQueue');



/**
 * The image loader class encapsulates image requests sent by the browser
 * and adds functionality for request queuing and cancelling running image
 * requests.
 *
 * The maxConcurrent argument to the constructor specifies the number of image
 * requests the loader will attempt to load in parallel.
 * The useFrames argument to the constructor specifies whether the loader will
 * use IFrames to load images.
 *
 * In some browser implementations image requests generated by and &lt;img&gt; tag
 * cannot be cancelled once the request was started (e.g. Safari). The only way
 * to safely cancel an image request in such browsers IFrames in conjunction
 * with the window.stop() function can be used. The number specified in
 * maxConcurrent will also define the maximum number of IFrames generated. All
 * loader frames are placed within a single IFrame that is appended to the host
 * document's body.
 *
 * A default instance of the ImageLoader class is provided as
 * {@link H.net.images}.
 *
 * @final
 * @export
 * @param {number=} opt_maxConcurrent the amount of concurrently running image requests, default is {@code 6}
 * @param {boolean=} opt_useFrames Indicating whether images are loaded in an IFrame, default is {@code false}
 * @param {HTMLDocument=} opt_doc an optional host document from which image elements are created
 *
 * @constructor
 */
H.net.ImageLoader = function(opt_maxConcurrent, opt_useFrames, opt_doc) {
  'use strict';
  var that = this;
  /**
   * @type {H.util.AsyncQueue}
   * @private
   */
  this.requestQueue_ = new H.util.ParallelQueue((opt_maxConcurrent || 6), function(task) { that.process_(task); });

  /**
   * @type {HTMLDocument}
   */
  this.doc = opt_doc || document;

  this.onImageLoad_ = H.lang.bind(this.onImageLoad_, this);

  /**
   * @type {H.net.FrameLoader_}
   * @private
   */
  this.frameLoader_ = null;

  if (opt_useFrames) {
    this.newImage_ = this.newImageFramed_;
  }
};


/**
 * This method enqueues and image request for further processing.
 * @throws {Error} This method throws an error if the url parameter is not specified or
 *                 is not a string
 * @throws {Error} This method throws an error if the callback parameter is not specified or
 *                 is not a function
 * @throws {Error} This method throws an error if the optianl error callback parameter is specified but
 *                 is not a function
 * @param {string} url the URL of the image to load
 * @param {function(HTMLImageElement)} callback a callback function to be
 *  invoked once the image was successfully loaded
 * @param {function(string)=} opt_errback a callback function to be invoked if
 *  the image could not be loaded
 * @param {string=} opt_crossOrigin optional cross-origin information to be
 *  sent to the server
 *
 * @return {H.util.AsyncQueue.Task} the task object which encapsulates the image
 *  request. It exposes a cancel function to cancel the loading task.
 *
 * @export
 */
H.net.ImageLoader.prototype.load = function(url, callback, opt_errback, opt_crossOrigin) {
  if (!url || !H.lang.isString(url)) {
    throw new H.lang.InvalidArgumentError(H.net.ImageLoader.prototype.load, 0, url);
  }
  if (!H.lang.isFunction(callback)) {
    throw new H.lang.InvalidArgumentError(H.net.ImageLoader.prototype.load, 1, callback);
  }
  if (opt_errback && !H.lang.isFunction(opt_errback)) {
    throw new H.lang.InvalidArgumentError(H.net.ImageLoader.prototype.load, 2, opt_errback);
  }

  var task = this.requestQueue_.push({
    url: url,
    callback: callback,
    errback: opt_errback,
    crossOrigin: opt_crossOrigin
  }, this.onImageLoad_);

  return task;
};


/**
 * This callback is called when the async queue has processed a task.
 * @param {H.util.AsyncQueue.Task} task the completed task
 * @param {*} result the result image element or null in case of an error
 * @param {*=} opt_failure the error message or undefined in the case of success
 * @private
 */
H.net.ImageLoader.prototype.onImageLoad_ = function(task, result, opt_failure) {
  var isDataUrl;

  if (result) {
    isDataUrl = H.net.ImageLoader.dataUrlRegExp_.test(result.src);

    // Call callback if IMG source is not a data URL, or it is but image is already loaded
    if (!isDataUrl || (isDataUrl && H.util.isImageLoaded(/** @type {HTMLImageElement}*/ (result)))) {
      task.data.callback(result);
    }
    // Otherwise, if it is data URL and is not loaded
    else if (isDataUrl) {
      // Bug fix for MAPSJS-60: IE11 triggers onload but throws an error if the img is blitted immediately to an canvas.
      //                        After a timeout it works:
      setTimeout(H.lang.bind(this.onImageLoad_, this, task, result, opt_failure), 0);
    }
  } else if (task.data.errback) {
    task.data.errback(opt_failure || 'failed');
  }
};


/**
 * Regular expression for checking whether image source is a data URL
 * @private
 * @type {RegExp}
 */
H.net.ImageLoader.dataUrlRegExp_ = /^data:[^:]+/;


/**
 * The process function for the async queue.
 * @param {H.util.AsyncQueue.Task} task the task to be executed
 * @private
 */
H.net.ImageLoader.prototype.process_ = function(task) {
  var data = task.data,
      image,
      that = this;

  image = this.newImage_(
      function() {
        var element = image.element;
        if (element.ownerDocument !== that.doc) {
          image.element = element = that.doc.importNode(element, true);
        }
        task.done(element, null);
      },
      function(failure) {
        task.done(null, failure);
      });

  task.setCancelFn(image.cancel);
  //check if we need to set a cross-origin attribute
  if (data.crossOrigin) {
    image.element.crossOrigin = data.crossOrigin;
  }
  //Finally: set the source to trigger the request
  image.element.src = data.url;
};


/**
 * This function acts as a factory for an image tag with onload and onerror
 * attached.
 * @param {function()} onload the onload handler
 * @param {function((Event|string|null))} onerror the onerror handler
 *
 * @return {Object} the image wrap
 *
 * @private
 */
H.net.ImageLoader.prototype.newImage_ = function(onload, onerror) {
  'use strict';

  //create an image element from this.doc (for unit-testing)
  var img = this.doc.createElement('img');
  img.onload = onload;
  img.onerror = onerror;

  //return the request image wrapper
  //note that there is a new cancel function
  return {
    element: img,
    'cancel': function() {
      //console.log('loading cancel');
      img.onerror = img.onload = null;
      img.removeAttribute('src');
      onerror('cancelled');
    }
  };
};


/**
 * This function acts as a factory for an image tag that resides inside a
 * loader frame with onload and onerror attached.
 * @param {function()} onload the onload handler
 * @param {function((Event|string|null))} onerror the onerror handler
 *
 * @return {Object} the image wrap
 *
 * @private
 */
H.net.ImageLoader.prototype.newImageFramed_ = function(onload, onerror) {
  'use strict';

  if (!this.frameLoader_) {
    this.frameLoader_ = new H.net.FrameLoader_(this.doc);
  }
  return this.frameLoader_.newImage_(onload, onerror);
};



/**
 * The frame loader encapsulates image tags in IFrames
 * @param {HTMLDocument} doc the host document
 *
 * @constructor
 * @private
 */
H.net.FrameLoader_ = function(doc) {
  'use strict';

  this.frame = doc.createElement('iframe');
  this.frame.style.display = 'none';

  this.subFrames = [];
  this.doc = doc;

  doc.body.appendChild(this.frame);
};


/**
 * The image tag factory
 * @param {function()} onload the onload handler
 * @param {function((Event|string|null))} onerror the onerror handler
 *
 * @return {Object} an image wrap
 *
 * @private
 */
H.net.FrameLoader_.prototype.newImage_ = function(onload, onerror) {
  'use strict';

  var that = this,
      //get a free frame or create one
      subFrame = this.subFrames.pop() || this.frame.contentDocument.createElement('iframe'),
      img,
      innerOnload,
      innerOnerror;

  innerOnload = function() {
    //when the frame's image loads...
    img.onload = img.onerror = null;
    //we free the frame (since it is done loading)
    that.subFrames.push(subFrame);
    // we need to import it to the host document
    //var imported = that.doc.createElement('img');
    //imported.src = img.src;
    //and we finally call onload with the imported image
    onload();
  };
  innerOnerror = function(failure) {
    img.onload = img.onerror = null;
    //we free the frame
    that.subFrames.push(subFrame);
    //and call on error
    onerror(failure);
  };


  //was the subFrame attached before?
  if (!subFrame.parentNode) this.frame.contentDocument.body.appendChild(subFrame);

  //create a new image
  img = new subFrame.contentWindow.Image();
  // attach onload and onerror
  img.onload = innerOnload;
  img.onerror = innerOnerror;

  //return an image wrap
  return {
    element: img,
    'cancel': function() {
      //stop the frame
      subFrame.contentWindow.stop();
      innerOnerror('cancelled');
    }
  };
};


/**
 * This object is the default instance of an image loader. It is automatically initialized when the API is loaded
 * with parameters depending on the host environment. This object can be replaced by a different image loader in
 * case the behavior needs to be changed.
 * @export
 * @type {H.net.ImageLoader}
 */
H.net.images = new H.net.ImageLoader();
