goog.provide('H.map.render.p2d.Surface');

goog.require('H.math');
goog.require('H.util.Disposable');



//###############################################  CONSTRUCTOR  ########################################################
/**
 * Class represents a drawing surface for a renderer.
 * @extends {H.util.Disposable}
 * @abstract
 * @constructor
 * @param {Element} container The container where the surface element must be appended
 * @param {number} width The width of the surface in pixels
 * @param {number} height The height of the surface in pixels
 * @param {number} pixelRatio The pixel ratio of the surface
 */
H.map.render.p2d.Surface = function(container, width, height, pixelRatio) {
  'use strict';
  H.map.render.p2d.Surface.superClass_.constructor.call(this);

  /**
   * Reference to this surface container element
   * @protected
   * @type {Element}
   */
  this.container = container;

  /**
   * The pixel ratio of this surface
   * @protected
   * @type {number}
   */
  this.pixelRatio = pixelRatio;

  /**
   * Reference to this surface's element
   * @protected
   * @type {Element}
   */
  this.element = /** @type {Element} */(container.appendChild(this.createElement(container.ownerDocument)));

  this.setSize(width, height);

};
H.lang.inherits(H.map.render.p2d.Surface, H.util.Disposable);


//###########################################  CONTAINER + ELEMENT  ####################################################
/**
 * To create the element of this surface
 * @protected
 * @abstract
 * @param {Document} ownerDoc The document to use when creating the element
 * @return {!Element}
 */
H.map.render.p2d.Surface.prototype.createElement = goog.abstractMethod;


/**
 * This method returns the surface's container element.
 * @return {Element} the container element
 */
H.map.render.p2d.Surface.prototype.getContainer = function() {
  'use strict';
  return this.container;
};


/**
 * This method returns the surface element.
 * @return {Element}
 */
H.map.render.p2d.Surface.prototype.getElement = function() {
  'use strict';
  return this.element;
};


//####################################################  SIZE  ##########################################################
/**
 * The width of this surface in CSS pixels
 * @protected
 * @type {number}
 */
H.map.render.p2d.Surface.prototype.width;


/**
 * The height of this surface in CSS pixels
 * @protected
 * @type {number}
 */
H.map.render.p2d.Surface.prototype.height;


/**
 * To set the size of this surface
 * @param {number} width The width of the surface in pixels, minimum is 1 pixel
 * @param {number} height The height of the surface in pixels, minimum is 1 pixel
 */
H.map.render.p2d.Surface.prototype.setSize = function(width, height) {
  'use strict';
  this.setSizeInternal(
      this.element,
      this.element.style,
      this.width = width,
      this.height = height,
      this.pixelRatio);
};


/**
 * To get the size and pixelRatio of this surface
 * @return {H.map.render.p2d.Surface.Size}
 */
H.map.render.p2d.Surface.prototype.getSize = function() {
  'use strict';
  return {width: this.width, height: this.height, pixelRatio: this.pixelRatio};
};


/**
 * To adjust the size of this surface
 * @protected
 * @abstract
 * @param {Element} element The element of this surface
 * @param {CSSStyleDeclaration} style The element's style
 * @param {number} width The width of the surface in pixels, minimum is 1 pixel
 * @param {number} height The height of the surface in pixels, minimum is 1 pixel
 * @param {number} pixelRatio The pixelRatio to use, minimum is 1/4, maximum
 */
H.map.render.p2d.Surface.prototype.setSizeInternal = goog.abstractMethod;


/**
 * Data represents size of the surface, and its pixel ratio
 * @typedef {{
 *   width: (number),
 *   height: (number),
 *   pixelRatio: (number)
 * }} H.map.render.p2d.Surface.Size
 */
H.map.render.p2d.Surface.Size;


//################################################  OPACITY  ###########################################################
/**
 * The opacity of this surface, default is {@code 1}
 * @protected
 * @type {number}
 */
H.map.render.p2d.Surface.prototype.opacity = 1;


/**
 * To set the opacity of this surface.
 * @param {number} opacity The opacity value in range {@code [0...1]}
 */
H.map.render.p2d.Surface.prototype.setOpacity = function(opacity) {
  'use strict';
  if (this.opacity !== opacity) {
    this.setOpacityInternal(opacity);
    this.opacity = opacity;
  }
};


/**
 * Returns the current opacity of this surface
 * @return {number} opacity
 */
H.map.render.p2d.Surface.prototype.getOpacity = function() {
  'use strict';
  return this.opacity;
};


/**
 * To set the opacity of this surface.
 * @protected
 * @abstract
 * @param {number} opacity The opacity value in range {@code [0...1]}
 */
H.map.render.p2d.Surface.prototype.setOpacityInternal = goog.abstractMethod;


//#################################################  DRAW  #############################################################
/**
 * To draw an element onto this surface
 * @param {Element} el The element to draw, derived sub classes can restrict the type
 * @param {number} dx The destination x coordinate
 * @param {number} dy The destination y coordinate
 * @param {number} dw The destination width
 * @param {number} dh The destination height
 */
H.map.render.p2d.Surface.prototype.draw = goog.abstractMethod;
// TODO add optional arguments [sx, sy[, sw, sh]] to support sprites


//#################################################  CLEAR  ############################################################
/**
 * To clear the surface. If no opt_target argument is specified the whole surface is cleared.
 * Some Surfaces need a clear() before drawing new content (e.g. Canvas based) others needs a clear(target) afterwards
 * (e.g. DOM based)
 * @param {*=} opt_target The target to clear
 *      The type of the target is further specified in concrete implementations classes
 */
H.map.render.p2d.Surface.prototype.clear = goog.abstractMethod;


//################################################  DISPOSABLE  ########################################################
/**
 * Protected implementation of H.util.Disposable.disposeInternal
 * @protected
 */
H.map.render.p2d.Surface.prototype.disposeInternal = function() {
  'use strict';
  this.clear();
  this.container.removeChild(this.element);
  this.setSize(0, 0);
  this.element = this.container = null;
  H.map.render.p2d.Surface.superClass_.disposeInternal.call(this);
};
