goog.provide('H.lang.OutOfRangeError');

goog.require('H.lang');



/**
 * An error to throw if an value is out of range
 *
 * @example
 * H.Foo.prototype.bar = function(array, idx) {
 *   if(idx < 0) throw new H.lang.OutOfRangeError(
 *     H.Foo.prototype.bar, 2, 0, array.length - 1);
 * }
 * new H.Foo().bar([0, 1], -4); // ==> 'OutOfrange: H.Foo#bar (-4 out of [0...1])
 *
 * @publish
 * @export
 * @extends {Error}
 *
 * @constructor
 * @param {Function=} opt_caller The calling function
 * @param {number=} opt_val
 * @param {Array.<number>=} opt_range The range's values as [min, max]
 */
H.lang.OutOfRangeError = function(opt_caller, opt_val, opt_range) {
  'use strict';
  var vals = [];
  arguments.length > 1 && vals.push(opt_val);
  arguments.length > 2 && vals.push('out of [' + opt_range[0] + '...' + opt_range[1] + ']');

  this['message'] =
      (opt_caller ? H.lang.getSymbolPath(opt_caller) : '') +
      (vals.length ? ' (' + vals.join(' ') + ')' : '');
};


/** @override */
H.lang.OutOfRangeError.prototype = Error();


/** @override */
H.lang.OutOfRangeError.prototype['name'] = 'OutOfRangeError';
