goog.provide('H.map.AbstractMarker');

goog.require('H.geo.Point');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.map.HitArea');
goog.require('H.map.Object');
goog.require('H.math.Point');
goog.require('H.math.Size');



//################################################  CONSTRUCTOR  #######################################################
/**
 * This class represents marker, which offers a means of identifying a location on the map with an icon.
 *
 * A marker consists of a geographic location and a visual representation in the form of a bitmap or a DOM element.
 * Markers are rendered in screen space, i.e. while they are always fixed to their geographic location their icon is
 * always rendered independently of the map's zoom level.
 * @abstract
 *
 * @extends {H.map.Object}
 * @publish
 * @export
 * @constructor
 * @param {H.geo.IPoint} position The location of this marker
 * @param {H.map.AbstractMarker.Options=} opt_options The values to initialize this marker
 */
H.map.AbstractMarker = function(position, opt_options) {
  'use strict';
  var key,
      val;

  H.lang.checkIllegalConstructor(this, H.map.AbstractMarker, false);
  H.map.AbstractMarker.superClass_.constructor.call(this, opt_options);

  this.setPosition(position);

  // Process the options:
  if (opt_options) {
    key = 'icon';
    if (key in opt_options) {
      val = opt_options[key];
    }
  }
  this.setIcon(val || this.getDefaultIcon_());

};
H.lang.inherits(H.map.AbstractMarker, H.map.Object);


//##################################################  Z-ORDER  #########################################################
/**
 * @override
 */
H.map.AbstractMarker.prototype.compareZOrder = function(other) {
  'use strict';
  var thisZInfo,
      otherZInfo,
      result = this.zOrderIdx - other.zOrderIdx;

  // The other object has exactly the same zOrderIndex (either booth are Marker or booth are DomMarker)?
  if (!result) {
    result =
        // Either compare the zInfos without sibling-index (onlyZIndex = true):
        H.map.Object.compareZInfo(thisZInfo = this.getZInfo(), otherZInfo = other.getZInfo(), true) ||
        // or compare the latitudes
        /** @type {H.map.AbstractMarker} */(other).getPosition()['lat'] - this.getPosition()['lat'] ||
        // or (in very rare cases) we compare the zInfos with sibling-index (for always consistent z-order):
        H.map.Object.compareZInfo(thisZInfo, otherZInfo, false);
  }
  return result;
};


//#################################################  POSITION  #########################################################
/**
 * Keeps a reference to the marker's current position
 * @private
 * @type {H.geo.Point}
 */
H.map.AbstractMarker.prototype.position_;


/**
 * This method returns this marker's current position.
 * @return {H.geo.Point} current marker geo position
 * @export
 * @publish
 */
H.map.AbstractMarker.prototype.getPosition = function() {
  'use strict';
  return this.position_;
};


/**
 * This method sets the marker's current position.
 * @export
 * @publish
 * @throws {H.lang.InvalidArgumentError}
 * @param {H.geo.IPoint} position
 * @return {H.map.AbstractMarker} the marker itself
 */
H.map.AbstractMarker.prototype.setPosition = function(position) {
  'use strict';
  var currentPosition = this.position_,
      isInitialCall = !currentPosition;
  if (isInitialCall || !currentPosition.equals(position)) {
    H.geo.Point.validate(position, isInitialCall ? H.map.AbstractMarker : this.setPosition, 0);
    this.position_ = H.geo.Point.fromIPoint(position);
    // We DON'T trigger the invalidate() for the initial position (set by constructor)
    if (!isInitialCall) {
      this.invalidate(H.map.provider.Invalidations.Flag.SPATIAL);
    }
  }
  return this;
};


//###################################################  ICON  ###########################################################
/**
 * The constructor for the icon. It is used for instanceof checks and must be defined in su classes
 * @abstract
 * @protected
 * @type {!Function}
 */
H.map.AbstractMarker.prototype.iconConstructor = goog.abstractMethod;


/**
 * The the marker's current icon.
 *
 * @protected
 * @type {!(H.map.Icon|H.map.DomIcon)}
 */
H.map.AbstractMarker.prototype.icon;


/**
 * Returns this marker's current icon.
 * @publish
 *
 * @export
 * @return {!(H.map.Icon|H.map.DomIcon)}
 */
H.map.AbstractMarker.prototype.getIcon = function() {
  'use strict';
  return this.icon;
};


/**
 * Sets the marker's current icon.
 * @export
 * @publish
 *
 * @throws H.lang.InvalidArgumentError
 * @param {!(H.map.Icon|H.map.DomIcon)} icon The new marker icon
 * @return {H.map.AbstractMarker} the marker itself
 */
H.map.AbstractMarker.prototype.setIcon = function(icon) {
  'use strict';
  var currentIcon = this.icon,
      isInitialCall = !currentIcon,
      error;
  if (currentIcon !== icon) {
    if (!H.lang.isInstanceOf(icon, this.iconConstructor)) {
      H.lang.InvalidArgumentError.apply(
          error = new H.lang.InvalidArgumentError(),
          isInitialCall ? [this.constructor, 1, 'icon'] : [this.setPosition, 0, icon]);
      throw error;
    }
    this.icon = icon;
    // We DON'T trigger the invalidate() for the initial icon (set by constructor)
    if (!isInitialCall) {
      this.invalidate(H.map.provider.Invalidations.Flag.VISUAL);
    }
  }
  return this;
};


//###############################################  DEFAULT ICON  #######################################################
/**
 * Creates and returns a default icon
 * @abstract
 * @protected
 * @param {H.map.AbstractMarker.DefaultIconOptions} options
 * @return {!(H.map.Icon|H.map.DomIcon)}
 */
H.map.AbstractMarker.prototype.createDefaultIcon = goog.abstractMethod;


/**
 * Keeps the default icon
 * @private
 * @type {!(H.map.Icon|H.map.DomIcon)}
 */
H.map.AbstractMarker.prototype.defaultIcon_;


/**
 * Returns the default icon
 * @private
 * @return {!(H.map.Icon|H.map.DomIcon)}
 */
H.map.AbstractMarker.prototype.getDefaultIcon_ = function() {
  'use strict';
  var defaultIcon = this.defaultIcon_;

  if (!defaultIcon) {
    defaultIcon =
        // We have to store it on the constructor's (Marker or DomMarker) prototype:
        this.constructor.prototype.defaultIcon_ =
        this.createDefaultIcon(H.map.AbstractMarker.defaultIconOptions_);
  }
  return defaultIcon;
};


//############################################  DEFAULT ICON OPTIONS  ##################################################
/**
 * A record to store options to create a default icon
 * @protected
 *
 * @property {string} svg The icon's SVG markup
 * @property {H.math.Size} size The icon's size
 * @property {H.math.Point} anchor The anchor to apply to the icon
 * @property {H.map.HitArea} hitArea The icon's area for hit detection
 *
 * @typedef {{
 *   svg: string,
 *   size: H.math.Size,
 *   anchor: H.math.Point,
 *   hitArea: H.map.HitArea
 * }}
 */
H.map.AbstractMarker.DefaultIconOptions;


/**
 * Keeps the options for the default icon
 * @private
 * @type {H.map.AbstractMarker.DefaultIconOptions}
 */
H.map.AbstractMarker.defaultIconOptions_ = {
  svg:
      '<svg xmlns="http://www.w3.org/2000/svg" width="28px" height="36px">' +
      '<path d="M 19 31 C 19 32.7 16.3 34 13 34 C 9.7 34 7 32.7 7 31 C 7 29.3 9.7 28 13 28 C 16.3 28 19' +
      ' 29.3 19 31 Z" fill="#000" fill-opacity=".2"/>' +
      '<path d="M 13 0 C 9.5 0 6.3 1.3 3.8 3.8 C 1.4 7.8 0 9.4 0 12.8 C 0 16.3 1.4 19.5 3.8 21.9 L 13 31 L 22.2' +
      ' 21.9 C 24.6 19.5 25.9 16.3 25.9 12.8 C 25.9 9.4 24.6 6.1 22.1 3.8 C 19.7 1.3 16.5 0 13 0 Z" fill="#fff"/>' +
      '<path d="M 13 2.2 C 6 2.2 2.3 7.2 2.1 12.8 C 2.1 16.1 3.1 18.4 5.2 20.5 L 13 28.2 L 20.8 20.5 C' +
      ' 22.9 18.4 23.8 16.2 23.8 12.8 C 23.6 7.07 20 2.2 13 2.2 Z" fill="#18d"/>' +
      //'<text transform="matrix( 1 0 0 1 13 18 )" x="0" y="0" fill="#fff" text-anchor="middle"' +
      //' font-weight="bold" font-size="13.3px" font-family="arial"></text>' +
      '</svg>',
  'size': new H.math.Size(28, 36),
  'anchor': new H.math.Point(14, 32),
  'hitArea': new H.map.HitArea(
      H.map.HitArea.ShapeType.POLYGON, [0, 16, 0, 7, 8, 0, 18, 0, 26, 7, 26, 16, 18, 34, 8, 34])
};


//################################################  OPTIONS  ###########################################################
/**
 * Options used to initialize a AbstractMarker
 * @publish
 *
 * @property {number=} min
 *           The minimum zoom level for which the object is visible, default is {@code -Infinity}
 * @property {number=} max
 *           The maximum zoom level for which the object is visible, default is {@code Infinity}
 * @property {boolean=} visibility
 *           Indicates whether the map object is visible at all, default is {@code true}.
 * @property {number=} zIndex
 *           The z-index value of the map object, default is {@code 0}
 * @property {(H.map.provider.Provider|null)=} provider
 *           The provider of this object. This property is only needed if a customized Implementation of ObjectProvider
 *           wants to instantiate an object.
 * @property {(H.map.Icon|H.map.DomIcon)=} icon
 *           The icon to use for the visual representation, if omitted a default icon is used.
 *
 * @typedef {{
 *   min: (number|undefined),
 *   max: (number|undefined),
 *   visibility: (boolean|undefined),
 *   zIndex: (number|undefined),
 *   provider: (H.map.provider.ObjectProvider|null|undefined),
 *   icon: (H.map.Icon|H.map.DomIcon|undefined)
 * }}
 */
H.map.AbstractMarker.Options;
