goog.provide('H.lang.InvalidArgumentError');

goog.require('H.lang');



/**
 * An error to throw if an argument is invalid
 *
 * @example
 * H.Foo.prototype.bar = function(array, idx) {
 *   if(idx < 0) throw new H.lang.InvalidArgumentError(
 *     H.Foo.prototype.bar, 2, 'less then  0');
 * }
 * new H.Foo().bar([], -4711); // ==> 'Invalid Argument: H.Foo#bar "idx" (-4711) less then 0
 *
 * @publish
 * @export
 * @extends {Error}
 *
 * @constructor
 * @param {Function=} opt_caller The calling function
 * @param {?number=} opt_argNr The number of the invalid argument (zero based)
 * @param {*=} opt_message
 */
H.lang.InvalidArgumentError = function(opt_caller, opt_argNr, opt_message) {
  'use strict';
  var argsLength = arguments.length,
      argName,
      msg;
  opt_argNr = argsLength > 1 ? +opt_argNr : opt_argNr;
  msg = argsLength > 2 ? H.lang.String(opt_message) : '';
  if (opt_argNr !== H.lang.UNDEF) {
    argName = H.lang.getArgumentNames(/** @type {Function} */ (opt_caller))[opt_argNr] || ('#' + opt_argNr);
  }
  this['message'] =
      (opt_caller ? H.lang.getSymbolPath(opt_caller) : '') +
      (argName || msg ? ' (' : '') +
      (argName ? 'Argument ' + argName : '') +
      (msg ? (argName ? ' ' : '') + msg : '') +
      (argName || msg ? ')' : '');
};


/** @override */
H.lang.InvalidArgumentError.prototype = Error();


/** @override */
H.lang.InvalidArgumentError.prototype['name'] = 'InvalidArgumentError';
