goog.provide('H.util.Cache');

goog.require('H.lang');



/**
 * The cache represents a in-memory LRU-cache with a fixed size. It stores any
 * data that is added until the cache's content exceeds a maximum size. Once
 * the size of all content elements exceeds the maximum size the cache will
 * drop the least recently retrieved elements until the size of the cache is
 * within the bounds of its maximum size. Data elements are always associated
 * with an identifier that allow to retrieve them at a later stage and their
 * content size.
 *
 * @export
 * @final
 * @constructor
 * @throws {Error} The constructor throws an error if the 'maxSize' argument is not a positive number, the opt_onDrop
 *         argument was set but is not a function or if the opt_filter argument was set but is not a function.
 * @param {number} maxSize the maximum size of the cache
 * @param {function(string, ?, number)=} opt_onDrop A callback to be invoked when
 *     a data element is dropped from the cache
 * @param {(function(string, ?, number):boolean)=} opt_filter A function to filter
 *     data elements that are not to be cached
 */
H.util.Cache = function(maxSize, opt_onDrop, opt_filter) {
  'use strict';
  if (!(maxSize >= 0)) {
    throw new H.lang.InvalidArgumentError(H.util.Cache, 0, maxSize);
  }
  if (opt_onDrop && !H.lang.isFunction(opt_onDrop)) {
    throw new H.lang.InvalidArgumentError(H.util.Cache, 1, opt_onDrop);
  }
  if (opt_filter && !H.lang.isFunction(opt_filter)) {
    throw new H.lang.InvalidArgumentError(H.util.Cache, 2, opt_filter);
  }

  this.maxSize = +maxSize;
  this.currentSize = 0;
  this.onDrop = opt_onDrop;
  this.filter = opt_filter;

  this.entries_ = {};

  this.firstEntry_ = null;
  this.lastEntry_ = null;
};


/**
 * This method adds a data element to the cache and marks it as most recently
 * used. The data element must have an identifier that allows for retrieving
 * the object at a later stage and a size which will count towards the cache's
 * maximum size.
 * @publish
 * @export
 * @throws {Error} Throws an error if the 'size' argument is not a positive number.
 * @param {*} id The identifier of this data element, the value is converted to a string.
 * @param {*} data the actual data to be stored
 * @param {number} size the size of the data element
 * @return {boolean} a boolean value indicating whether the data was added
 */
H.util.Cache.prototype.add = function(id, data, size) {
  'use strict';
  var entry,
      filterResult;

  if (size != +size || size < 0) {
    throw new H.lang.InvalidArgumentError(this.add, 2, size);
  }

  id = /** @type {string} */ (String(id));
  entry = this.entries_[id];
  filterResult = true;

  if (this.filter) {
    filterResult = this.filter(id, data, size);
  }

  if (entry) {
    //if we already have an entry
    if (filterResult) {
      //if the filter allows adding/updating this entry

      //update the entry
      this.currentSize += (size - entry.size);
      entry.size = size;
      entry.data = data;

      //move it to the top
      this.moveToLRU_(entry);
    } else {
      //if the filter disallows adding/updating the entry
      //remove it
      this.remove_(entry, true);
    }
  } else {
    // if we do not yet have an an entry
    if (filterResult) {
      //if the filter allows adding this entry

      //add it to the front
      this.entries_[id] = this.insertBefore_({
        id: id,
        data: data,
        size: size,
        next_: null,
        prev_: null
      }, this.firstEntry_);
    }
  }

  this.adjustToMaxSize_();

  return filterResult;
};


/**
 * This method retrieves an element from the cache and marks it as 'most recently used'.
 * @param {string} id the ID of the data element to be retrieved.
 * @param {boolean=} opt_noUpdate and optional flag to indicate that the
 *   retrieved object should not be marked as 'most recently used'.
 *
 * @return {*} returns the data associated with the ID or undefined if the
 *  data element is not currently in the cache.
 * @publish
 * @export
 */
H.util.Cache.prototype.get = function(id, opt_noUpdate) {
  'use strict';

  var entry = (opt_noUpdate ? this.entries_[id] : this.moveToLRU_(this.entries_[id]));
  return entry && entry.data;
};


/**
 * This method drops an item from the cache explicitly.
 * @param {*} id the id of the item to drop
 * @publish
 * @export
 */
H.util.Cache.prototype.drop = function(id) {
  'use strict';
  var entry;
  if (entry = this.entries_[id]) {
    this.remove_(entry, true);
  }
};


/**
 * This method will execute the provided callback function on each of the
 * cache's entries. If the optional match predicate is passed to this method
 * the callback will only be executed on those entries for which the predicated
 * returns true.
 * @param {function(string, ?, number)} callback the callback to be invoked for
 *  each entry
 * @param {Object=} opt_ctx an optional context object to be used as this within
 *  the callback
 * @param {(function(string, ?, number):boolean)=} opt_matcher an optional match
 *  predicate to customize on which entries the callback will be called
 */
H.util.Cache.prototype.forEach = function(callback, opt_ctx, opt_matcher) {
  'use strict';

  var entry,
      key;
  for (key in this.entries_) {
    entry = this.entries_[key];
    if (opt_matcher ? opt_matcher(key, entry.data, entry.size) : true) {
      callback.call(opt_ctx, key, entry.data, entry.size);
    }
  }
};


/**
 * This method removes all data elements from the cache. If the optional match predicate is
 * passed to this method only those data elements will be removed for which the
 * predicate return true.
 * @param {(function(string, ?, number):boolean)=} opt_matcher an optional
 *  function that receives an entries id, data and size and may return true
 *  or false to either remove it or leave the entry in the cache respectively
 * @publish
 * @export
 */
H.util.Cache.prototype.removeAll = function(opt_matcher) {
  'use strict';

  var entry,
      key;
  for (key in this.entries_) {
    entry = this.entries_[key];
    if (opt_matcher ? opt_matcher(key, entry.data, entry.size) : true) {
      this.remove_(this.entries_[key], true);
    }
  }
};


/**
 * This method sets this cache's maximum size to a new size. If the cache's contents
 * exceed the new size, least recently used data elements will be dropped.
 * @publish
 *
 * @param {number} maxSize the new maximum size of this cache.
 * @return {H.util.Cache} this cache
 * @export
 */
H.util.Cache.prototype.setMaxSize = function(maxSize) {
  'use strict';

  this.maxSize = maxSize;
  this.adjustToMaxSize_();

  return this;
};


/**
 * This method returns the maximum size of this cache.
 * @return {number} the maximum size of the cache
 * @publish
 * @export
 */
H.util.Cache.prototype.getMaxSize = function() {
  'use strict';

  return this.maxSize;
};


/**
 * This method returns the current size of this cache.
 * @return {number} the current size of the cache
 * @publish
 * @export
 */
H.util.Cache.prototype.getCurrentSize = function() {
  'use strict';

  return this.currentSize;
};


/**
 * Marks an entry as 'most recently used'.
 * @param {H.util.Cache.Entry_} entry the entry to be marked
 *
 * @return {H.util.Cache.Entry_} the entry that was marked
 *
 * @private
 */
H.util.Cache.prototype.moveToLRU_ = function(entry) {
  'use strict';

  if (entry) {
    this.firstEntry_ = this.insertBefore_(entry, this.firstEntry_);
  }
  return entry;
};


/**
 * This method drops entries from this cache until the size is within the boundaries
 * of this cache's maximum size.
 *
 * @private
 */
H.util.Cache.prototype.adjustToMaxSize_ = function() {
  'use strict';

  //drop entries until the current size is not bigger than the maximum size
  while (this.currentSize > this.maxSize && this.lastEntry_) {
    this.remove_(this.lastEntry_, true);
  }
};


/**
 * Inserts an entry into the cache by prepending it to the specified next
 * entry.
 * @param {H.util.Cache.Entry_} entry the entry to be inserted
 * @param {?H.util.Cache.Entry_} next the entry before which the new entry
 *   is to be inserted
 * @return {H.util.Cache.Entry_} the inserted entry
 *
 * @private
 */
H.util.Cache.prototype.insertBefore_ = function(entry, next) {
  'use strict';

  //if the entry is the same
  if (next !== entry) {
    //if the entry is already in the list we need to remove it first
    if (entry.next_ || entry.prev_) {
      this.remove_(entry);
    }

    //set the entry's next to the next entry
    entry.next_ = next;
    if (next) {
      //set the entry's previous the next's previous
      entry.prev_ = next.prev_;
      //set the next's previous to the entry'
      next.prev_ = entry;
    }


    //if the entry has no previous entry it is the first entry
    if (!entry.prev_) {
      this.firstEntry_ = entry;
    }
    //if the entry has no next entry it is the last entry
    if (!entry.next_) {
      this.lastEntry_ = entry;
    }

    this.currentSize += entry.size;
  }
  return entry;
};


/**
 * Removes an entry from the cache.
 * @private
 * @param {H.util.Cache.Entry_} entry the entry to be removed
 * @param {boolean=} opt_isCacheDrop a boolean value indicating whether onDrop should be called
 * @return {H.util.Cache.Entry_} the removed entry
 */
H.util.Cache.prototype.remove_ = function(entry, opt_isCacheDrop) {
  'use strict';

  //get the entries previous and next
  var prev = entry.prev_,
      next = entry.next_;

  //if the entry is actually part of the list
  if (prev || next || (entry == this.firstEntry_ && entry == this.lastEntry_)) {
    //update the links
    if (prev) {
      prev.next_ = next;
    } else {
      this.firstEntry_ = next;
    }

    if (next) {
      next.prev_ = prev;
    } else {
      this.lastEntry_ = prev;
    }

    //update the cache's size
    this.currentSize -= entry.size;

    if (opt_isCacheDrop) {
      //call onDrop if it exists
      this.onDrop && this.onDrop(entry.id, entry.data, entry.size);
      //remove the entry from the lookup list
      delete this.entries_[entry.id];
    }

  }
  //when detaching we need to null this entry's links
  entry.next_ = entry.prev_ = null;

  return entry;
};


/**
 * This object definition represents a data element in the cache.
 * @private
 * @property {string} id the ID of this data element (note, the data type will be converted to a string)
 * @property {*} data the data contained within this element
 * @property {number} size the size of the data within this element
 * @property {H.util.Cache.Entry_} prev_ the elements predecessor in the linked list
 * @property {H.util.Cache.Entry_} next_ the elements successor in the linked list
 *
 * @typedef {{
 *   id: string,
 *   data: *,
 *   size: number,
 *   prev_: (H.util.Cache.Entry_|undefined),
 *   next_: (H.util.Cache.Entry_|undefined)
 * }}
 */
H.util.Cache.Entry_;
