goog.provide('H.geo.Point');


goog.require('H.geo');
goog.require('H.geo.IPoint');
goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.math');



/**
 * Class represents a geographical point, which is defined by the latitude, longitude and
 * optional altitude.
 *
 * @example
 * var geoPoint = new H.geo.Point(1, 51);
 *
 * @publish
 * @export
 * @implements {H.geo.IPoint}
 *
 * @constructor
 * @param {H.geo.Latitude} lat The latitude coordinate
 * @param {H.geo.Longitude} lng The longitude coordinate
 * @param {H.geo.Altitude=} opt_alt The altitude coordinate
 * @param {H.geo.AltitudeContext=} opt_ctx The altitude context
 */
H.geo.Point = function(lat, lng, opt_alt, opt_ctx) {
  'use strict';

  /**
   * @type {H.geo.Latitude}
   */
  this['lat'] = H.geo.validateLat(lat, H.geo.Point, 0);

  /**
   * @type {H.geo.Longitude}
   */
  this['lng'] = H.geo.validateLng(lng, H.geo.Point, 1);

  if (opt_alt !== H.lang.UNDEF) {
    /**
     * @type {H.geo.Altitude}
     */
    this['alt'] = H.geo.validateAlt(opt_alt, H.geo.Point, 2);
  }

  if (opt_ctx !== H.lang.UNDEF && H.geo.validateCtx(opt_ctx, H.geo.Point, 3)) {
    /**
     * @type {H.geo.AltitudeContext}
     */
    this['ctx'] = opt_ctx;
  }

};


/**
 * @override
 */
H.geo.Point.prototype.lat;


/**
 * @override
 */
H.geo.Point.prototype.lng;


/**
 * @override
 */
H.geo.Point.prototype.alt = H.lang.UNDEF;


/**
 * @override
 */
H.geo.Point.prototype.ctx = H.lang.UNDEF;


/**
 * To compare this point with a supplied other point for equality. Two points are considered equal if they
 * have the same {@code lat} and {@code lng} as well as equivalent values for {@code alt} and {@code ctx}.
 *
 * @publish
 * @export
 *
 * @param {H.geo.IPoint} other
 * @return {boolean} Whether the two points are equal.
 */
H.geo.Point.prototype.equals = function(other) {
  'use strict';

  return this === other ||
      !!other &&
      this['lat'] === other['lat'] &&
      this['lng'] === other['lng'] &&
      H.geo.AltitudeContext[this['ctx']] === H.geo.AltitudeContext[other['ctx']] &&
      (this['alt'] || 0) === (other['alt'] || 0);
};


/**
 * To calculate the distance between this point and the supplied other point.
 * The method uses the Haversine formula. The altitude is not considered.
 *
 * @publish
 * @export
 *
 * @param {H.geo.IPoint} other
 * @return {number} The distance between the given location and the location supplied by the caller
 *    in meters
 */
H.geo.Point.prototype.distance = function(other) {
  'use strict';
  // TODO
  // Add Support for distance in 3D space - see 2.5 version!
  // @param {Boolean} [straight] An flag to compute distance in 3D space, not shortest distance on sphere.
  var distance,
      thisLatRad,
      otherLatRad;

  if (this === other || this['lat'] === other['lat'] && this['lng'] === other['lng']) {
    distance = 0;
  } else {
    thisLatRad = this['lat'] * H.math.PI_OVER_180;
    otherLatRad = other['lat'] * H.math.PI_OVER_180;
    distance = H.geo.WGS84_MEAN_RADIUS * 2 * H.math.asin(H.math.min(1, H.math.sqrt(
        H.math.pow(H.math.sin((thisLatRad - otherLatRad) / 2), 2) +
        H.math.cos(thisLatRad) * H.math.cos(otherLatRad) *
        H.math.pow(H.math.sin((this['lng'] * H.math.PI_OVER_180 -
            other['lng'] * H.math.PI_OVER_180) / 2), 2))));

  }
  return distance;
};


/**
 * This method calculates the geographic point of a destination point using the distance and bearing specified
 * by the caller. The altitude is ignored, instead the WGS84 Mean Radius is taken.
 *
 * @publish
 * @export
 *
 * @param {number} bearing The bearing to use in the calculation in degrees
 * @param {number} distance The distance to the destination in meters
 * @param {boolean=} opt_overGreatCircle If true the computation uses the
 *         "Great Circle" otherwise "Rhumb Line".
 * @return {H.geo.Point} The calculated point
 */
H.geo.Point.prototype.walk = function(bearing, distance, opt_overGreatCircle) {
  'use strict';

  var lng2,
      lat1,
      lng1,
      lat2,
      dLat,
      dPhi,
      q,
      dLon;
  if (opt_overGreatCircle) {
    return this.walkGC_(bearing, distance);
  }
  // Adjust bearing to a value 0 (inclusive) ... 360 (exclusive)
  bearing = (bearing % 360 + 360) % 360;
  // Is bearing 90 or 270 ?
  if (((bearing + 90) % 180) === 0) {
    lng2 = this['lng'] + (distance / (H.math.TAU * (H.math.cos(this['lat'] *
        H.math.PI_OVER_180) * H.geo.WGS84_MEAN_RADIUS))) *
        (bearing === 270 ? -360 : 360);
    return new H.geo.Point(
        this['lat'],
        (lng2 + 540) % 360 - 180
    );
  }

  // translate into angular distance covered on earth's surface
  if (distance /= H.geo.WGS84_MEAN_RADIUS) {
    bearing *= H.math.PI_OVER_180;
    lat1 = this['lat'] * H.math.PI_OVER_180;
    lng1 = this['lng'] * H.math.PI_OVER_180;
    lat2 = lat1 + distance * H.math.cos(bearing);
    dLat = lat2 - lat1;

    dPhi = H.math.log(
        H.math.tan(lat2 / 2 + H.math.PI_OVER_4) / H.math.tan(lat1 / 2 + H.math.PI_OVER_4)
        );

    q = (!H.lang.isNaN(dLat / dPhi)) ? dLat / dPhi : H.math.cos(lat1); // E-W line gives dPhi=0
    dLon = distance * H.math.sin(bearing) / q;
    // Check for passing the pole
    if (H.math.abs(lat2) > H.math.PI_OVER_2) {
      lat2 = lat2 > 0 ? H.math.PI - lat2 : -(H.math.PI + lat2);
    }


  }

  return distance ?
      new H.geo.Point(
          lat2 * H.math._180_OVER_PI,
          (((lng1 + dLon + H.math.PI3 +
              (H.math.abs(lat1 + dLat) > H.math.PI_OVER_2 ? H.math.PI : 0)
          ) % H.math.TAU - H.math.PI)) * H.math._180_OVER_PI
      ) :
      this;
};


/**
 * This method calculates the geo points of a destination point using the distance and bearing specified by the caller.
 * The algorithm based on Great Circle.
 *
 * @private
 *
 * @param {number} bearing The bearing to use in the calculation in degrees
 * @param {number} distance The distance to the destination in meters
 * @return {H.geo.Point} The calculated target as a location object containing geo points
 */
H.geo.Point.prototype.walkGC_ = function(bearing, distance) {
  'use strict';

  if (distance /= H.geo.WGS84_MEAN_RADIUS) {
    bearing *= H.math.PI_OVER_180; // to radian

    var angleA = H.math.PI_OVER_180 * this['lat'], // radians
        sinAngleA = H.math.sin(angleA),
        sinDist = H.math.sin(distance),
        cosDist = H.math.cos(distance),
        angleB = H.math.PI_OVER_180 * this['lng'], // radians
        cosAngleA = H.math.cos(angleA),
        y = H.math.asin(cosAngleA * H.math.cos(bearing) * sinDist + sinAngleA * cosDist),
        dLng = H.math.atan2(H.math.sin(bearing) * cosAngleA * sinDist,
            cosDist - sinAngleA * H.math.sin(y)),
        x = ((angleB + dLng + H.math.PI) % (2 * H.math.PI)) - H.math.PI, // modulorize function
        lng = x * H.math._180_OVER_PI;
  }
  return distance ?
      new H.geo.Point(y * H.math._180_OVER_PI, (lng + 540) % 360 - 180) :
      this;
};

if (goog.DEBUG) {
  /**
   * Returns a nice string representing the geographic point.
   * @return {string} In the form (lat, lng[, alt][, ctx]).
   * @override
   */
  H.geo.Point.prototype.toString = function() {
    'use strict';

    return '(' +
        this['lat'] + '/' + this['lng'] + (
            this['alt'] || this['ctx'] ?
            ' ' + (this['alt'] || 0) + 'm ' + H.geo.AltitudeContext[this['ctx']] :
            ''
        ) + ')';
  };
}


/**
 * This method validates the given IPoint.
 * It checks, if  {@code lat}, {@code lng}, {@code alt} and {@code ctx} have valid types.
 * Additionally the value of the {@code lat} property is clamped into a range of {@code -90 ... +90} and
 * the value of the {@code lng} property is modulo into a range of {@code -180 ... +180} plus validates the values of
 * the {@code alt} and {@code ctx} properties
 *
 * @publish
 * @export
 *
 * @this {H.geo.Point}
 * @param {H.geo.IPoint} point The point to validate
 * @param {Function=} opt_caller The caller to use for InvalidArgumentError. If omitted no error is thrown
 * @param {number=} opt_argNr The argument number to use for InvalidArgumentError.
 * @return {boolean} if the given point could validate
 */
H.geo.Point.validate = function(point, opt_caller, opt_argNr) {
  'use strict';
  var couldValidate = !!point && !(
      H.lang.isNaN(point['lat'] = H.geo.validateLat(point['lat'])) ||
      H.lang.isNaN(point['lng'] = H.geo.validateLng(point['lng'])) ||
      point['alt'] !== H.lang.UNDEF && H.lang.isNaN(point['alt'] = H.geo.validateAlt(point['alt'])) ||
      point['ctx'] !== H.lang.UNDEF && !H.geo.validateCtx(point['ctx']));
  if (!couldValidate && opt_caller) {
    throw new H.lang.InvalidArgumentError(opt_caller, opt_argNr, point);
  }
  return couldValidate;
};


/**
 * This method creates a Point instance from a given IPoint object.
 *
 * @publish
 * @export
 *
 * @param {H.geo.IPoint} iPoint The IPoint object to use
 * @return {H.geo.Point} the created Point instance
 */
H.geo.Point.fromIPoint = function(iPoint) {
  'use strict';
  if (!iPoint) {
    throw new H.lang.InvalidArgumentError(H.geo.Point.fromIPoint, 0, iPoint);
  }
  return H.lang.isInstanceOf(iPoint, H.geo.Point) ?
      /** @type {H.geo.Point} */ (iPoint) :
      new H.geo.Point(iPoint['lat'], iPoint['lng'], iPoint['alt'], iPoint['ctx']);
};
