goog.provide('H.util.animation.Animation');

goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.util.animation');
goog.require('H.util.animation.ease');
goog.require('H.util.shims');



/**
 * This class represents animation. It defines animation by the transform function
 * which is fired on every requested animation frame, duration, and the easing function
 * (optionally). Transform function on every animation frame receives as arguments:
 * - progress - value in range of 0..1 indicating the progress of the animation where 1 is finished
 * - tileElapsed - time in milisecond measured from the start of the animation
 * - frameTime - time in miliseconds elapsed from the last frame
 * - tiemStamp - current timestamp
 *
 * @export
 * @final
 * @constructor
 * @param {function(number, number, number, number)} transform function which makes transformation during single fram
 * @param {number} duration duration in miliseconds
 * @param {function()=} opt_onAnimationStop function which is called when animation is ended
 * @param {function(number)=} opt_ease easing function which modifies the progress value
 */
H.util.animation.Animation = function(transform, duration, opt_onAnimationStop, opt_ease) {
  'use strict';

  if (!H.lang.isFunction(this.transform_ = transform)) {
    throw new H.lang.InvalidArgumentError(H.util.animation.Animation, 0, transform);
  }
  if (H.lang.isNaN(this.duration_ = +duration)) {
    throw new H.lang.InvalidArgumentError(H.util.animation.Animation, 1, duration);
  }
  if (!H.lang.isFunction(this.onAnimationStop_ = opt_onAnimationStop) &&
      H.lang.isDefAndNotNull(opt_onAnimationStop)) {
    throw new H.lang.InvalidArgumentError(H.util.animation.Animation, 2, opt_onAnimationStop);
  }
  if (!H.lang.isFunction(this.ease_ = opt_ease) && H.lang.isDefAndNotNull(opt_ease)) {
    throw new H.lang.InvalidArgumentError(H.util.animation.Animation, 3, opt_ease);
  }

  this.id = H.util.getUid();

  this.isRunning_ = false;
};


/**
 * Stores duration in milisecond for the animation object
 * @type {number}
 * @private
 */
H.util.animation.Animation.prototype.duration_;


/**
 * Function which is called when animation finishes (unless it is suprpressed by the stop)
 * @type {(?function(H.util.animation.Animation)|undefined)}
 * @private
 */
H.util.animation.Animation.prototype.onAnimationStop_;


/**
 * Function which is used to ease animation
 * @type {(?function(number)|undefined)}
 * @private
 */
H.util.animation.Animation.prototype.ease_;


/**
 * Flag which indicates if the animation is running
 * @type {boolean}
 * @private
 */
H.util.animation.Animation.prototype.isRunning_;


/**
 * Stores transform function which is the function which is called every frame
 * @type {function(number, number, number, number)}
 * @private
 */
H.util.animation.Animation.prototype.transform_;


/**
 * This method starts the animation.
 * @export
 * @publish
 */
H.util.animation.Animation.prototype.start = function() {
  'use strict';
  var that = this,
      lastFrame,
      requestAnimationFrame = H.util.shims.requestAnimationFrame,
      transformWrapper;

  transformWrapper = function() {
    var timestamp = H.util.shims.now(),
        timeElapsed = timestamp - that.startTime_,
        progress = timeElapsed / that.duration_;

    progress = progress > 1 ? 1 : progress;
    if (that.ease_) {
      progress = that.ease_(progress);
    }

    that.transform_(progress, timeElapsed, timestamp - lastFrame, timestamp);

    lastFrame = timestamp;

    progress >= 1 ? that.stop() : that.animationHandle_ = requestAnimationFrame(transformWrapper);
  };

  that.startTime_ = H.util.shims.now();
  lastFrame = that.startTime_;

  that.isRunning_ = true;
  that.animationHandle_ = requestAnimationFrame(transformWrapper);
};


/**
 * This method stops animation. Method can also surpess the
 * animation end callback.
 * @param {boolean=} opt_suppressCallback if set to true animation stop callback is not fired
 * @export
 */
H.util.animation.Animation.prototype.stop = function(opt_suppressCallback) {
  'use strict';
  H.util.shims.cancelAnimationFrame(this.animationHandle_);
  this.isRunning_ = false;
  opt_suppressCallback || this.onAnimationStop_ && this.onAnimationStop_(this);
};


/**
 * This method indicates if the animation is currently running.
 * @return {boolean}
 * @export
 */
H.util.animation.Animation.prototype.isRunning = function() {
  'use strict';
  return this.isRunning_;
};

