goog.provide('H.util.ChangeEvent');

goog.require('H.util.Event');



/**
 * This event indicates a change. It contains the old and the new value.
 * @publish
 * @export
 * @extends {H.util.Event}
 * @constructor
 * @param {string} type The type of the event
 * @param {*} newValue The new value of the property
 * @param {*} oldValue The previous value of the property
 */
H.util.ChangeEvent = function(type, newValue, oldValue) {
  'use strict';

  H.util.ChangeEvent.superClass_.constructor.call(this, type);

  /**
   * This property holds the value of the property before it changed.
   * @type {*}
   */
  this['oldValue'] = oldValue;

  /**
   * This property holds the value of the property after it changed.
   * @type {*}
   */
  this['newValue'] = newValue;

};
H.lang.inherits(H.util.ChangeEvent, H.util.Event);
