goog.provide('H.map.Imprint');

goog.require('H.map.ICopyright');
goog.require('H.util.Disposable');
goog.require('H.util.ICapturable');



/**
 * This class encapsulates the brand, copyright and terms of use elements on the map.
 * @publish
 * @export
 * @extends {H.util.Disposable}
 * @implements {H.util.ICapturable}
 * @final
 * @constructor
 * @param {H.Map} map The map where the imprint is attached to
 * @param {H.map.Imprint.Options=} opt_options The options to style the imprint
 *
 */
H.map.Imprint = function(map, opt_options) {
  'use strict';

  var DIV = 'DIV',
      el = map.getElement(),
      doc = el.ownerDocument,
      span = 'SPAN';

  //console.log(H.map.Imprint.superClass_);
  H.map.Imprint.superClass_.constructor.call(this);

  /**
   * @private
   * @type {H.Map}
   */
  this.map_ = map;

  // Imprint element:

  /**
   * @type {Element}
   * @private
   */
  this.element_ = el = /** @type {Element} */ (el.appendChild(H.map.Imprint.createEl_(doc, DIV,
      'position:absolute;bottom:4em;left:0.5em;z-index:1;height:0px')));

  /**
   * @private
   * @type {Element}
   */
  this.brandElement_ = H.map.Imprint.createEl_(doc, DIV, 'position:absolute;top:0px;pointer-events:none;',
      H.map.Imprint.BRAND_SVG_);

  /**
   * The style of the brand's SVG path
   * @private
   * @type {CSSStyleDeclaration}
   */
  this.brandPathStyle_ =
      el.appendChild(this.brandElement_)
      .firstChild.firstChild.style;

  // The info element:
  el = el.appendChild(H.map.Imprint.createEl_(doc, DIV, '-webkit-touch-callout: none;-webkit-user-select:none;' +
      '-khtml-user-select: none;-moz-user-select: none;-ms-user-select: none;user-select:none;' +
      'whiteSpace:nowrap;position:relative;top:2.2em;'));

  /**
   * @private
   * @type {Element}
   */
  this.copyrightElement_ = /** @type {Element} */ (el.appendChild(H.map.Imprint.createEl_(doc, span)));
  this.copyrightElement_.setAttribute('unselectable', 'on'); //for IE 10 were poitner-events do not work
  /**
   * Terms of use element
   * @private
   * @type {Element}
   */
  this.termsOfUseElement_ = /** @type {Element} */ (el.appendChild(H.map.Imprint.createEl_(doc, 'A',
      'color:inherit', 'Terms of use')));
  this.termsOfUseElement_.target = '_blank';

  // add separator
  el = el.appendChild(H.map.Imprint.createEl_(doc, span, 'color:inherit', ' | '));

  /**
   * Container that hold report image link and separator
   * @private
   * @type {Element}
   */
  this.reportImageContainer_ = /** @type {Element} */ (el);

  /**
   * Report Image link for street level
   * @private
   * @type {Element}
   */
  this.reportImageElement_ = /** @type {Element} */ (el.appendChild(function() {
    var el = H.map.Imprint.createEl_(doc, 'A', 'color:inherit', 'Report image');
    el.target = '_blank';
    return el;
  }()));


  /**
   * @private
   * @type {Array.<string>}
   */
  this.lastOptionValues_ = H.map.Imprint.defaultOptionValues_.slice();

  this.setOptions(opt_options);

  this.updateCopyright_ = H.lang.bind(this.updateCopyright_, this);
  this.onLayerChange_ = H.lang.bind(this.onLayerChange_, this);
  this.map_.addEventListener(this.map_.EventType.MAPVIEWCHANGEEND, this.updateCopyright_);
  //this.map_.addEventListener(this.map_.EventType.BASELAYERCHANGE, this.onBaseLayerChange_);
  this.map_.getLayers().addEventListener('add', this.onLayerChange_);
  this.map_.getLayers().addEventListener('remove', this.onLayerChange_);
  this.map_.getLayers().addEventListener('set', this.onLayerChange_);
  this.onLayerChange_();
};
H.lang.inherits(H.map.Imprint, H.util.Disposable);


/**
 * @private
 * @type {Array.<string>}
 */
H.map.Imprint.optionNames_ = H.lang.tokenize('font href invert reportHref');


/**
 * Default base url for terms of use and report image
 * @private
 * @type {string}
 */
H.map.Imprint.defaultURL_ = 'http://here.com/';


/**
 * @private
 * @type {Array.<string>}
 */
H.map.Imprint.defaultOptionValues_ =
    H.lang.tokenize('11px Arial,sans-serif;' +
        H.map.Imprint.defaultURL_ + 'terms;;' +
        H.map.Imprint.defaultURL_, ';');


/**
 * To set the imprint options.
 * If no opt_options argument is defined then all values are reset to their defaults.
 * @publish
 * @export
 * @param {H.map.Imprint.Options=} opt_options The options to style the imprint
 */
H.map.Imprint.prototype.setOptions = function(opt_options) {
  'use strict';
  var i = H.map.Imprint.optionNames_.length,
      key,
      value,
      lastOptionValues;
  if (!opt_options) {
    // Rest all last option values to their defaults:
    this.lastOptionValues_ = H.map.Imprint.defaultOptionValues_.slice();
  }
  lastOptionValues = this.lastOptionValues_;
  while (i--) {
    key = H.map.Imprint.optionNames_[i];
    value = opt_options && key in opt_options ?
        // Update the last option value.
        lastOptionValues[i] = opt_options[key] :
        lastOptionValues[i];
    if (i === 2 /*invert*/) {
      this.brandPathStyle_.fill = value ? '#FFF' : '#1B468D';
      this.element_.style.color = value ? '#FFF' : '#333';
    } else if (i === 1 /*href*/) {
      this.termsOfUseElement_[key] = value;
    } else {
      this.element_.style[key] = value;
    }
  }
};


/**
 * Helper to create an element
 *
 * @private
 * @param {Document} doc The owner document of the element
 * @param {string} name The node name of the element
 * @param {*=} opt_cssText The textContent to set for the "cssText" attribute
 * @param {*=} opt_innerHTML The markup to set for the "innerHTML" property
 * @return {Element}
 */
H.map.Imprint.createEl_ = function(doc, name, opt_cssText, opt_innerHTML) {
  'use strict';
  // TODO: Should be moved to a generic dom library
  var el = /** @type {Element} */ (doc.createElement(name));
  if (opt_cssText) {
    el.style.cssText = /** @type {string} */ (opt_cssText);
  }
  if (opt_innerHTML) {
    el.innerHTML = opt_innerHTML;
  }
  return el;
};


/**
 * Methos returns imprint root element
 * @export
 * @return {Element}
 */
H.map.Imprint.prototype.getElement = function() {
  'use strict';
  return this.element_;
};


/**
 * Methos returns imprint brand element
 * Do not publish! Internal API use only!
 * @return {Element}
 */
H.map.Imprint.prototype.getBrandImage = function() {
  'use strict';
  return this.brandElement_.childNodes[0];
};


/**
 * To update the copyrights
 * @private
 */
H.map.Imprint.prototype.updateCopyright_ = function() {
  'use strict';
  var reportHref = H.map.Imprint.defaultOptionValues_[3],
      map = this.map_,
      reportImageElement = this.reportImageElement_,
      reportImageContainer = this.reportImageContainer_,
      cameraData,
      position;

  if (map.getEngine().type === H.Map.EngineType.P2D) {
    reportImageContainer.style.display = 'none';
  } else {
    cameraData = map.getViewModel().getCameraData();
    position = cameraData.position;
    reportImageContainer.style.display = 'inline';
    reportImageElement.href = reportHref + [position['lat'], position['lng'],
      22, cameraData['yaw'], 90 + cameraData['pitch']].join(',') + ',panorama.day,report';
  }

  this.copyrightElement_.innerHTML = '&copy; ' + this.getCopyrights() + ' | ';
};


/**
 * A callback to be invoked when the base layer changes
 * @private
 */
H.map.Imprint.prototype.onLayerChange_ = function() {
  this.updateCopyright_();
  // In case copyrights are still loading, we will check back in 5 seconds
  setTimeout(this.updateCopyright_, 5000);
};


