/**
 * Stores the current year for automatic update of copyright info.
 * @type {number}
 * @private
 */
H.map.Imprint.CURRENT_YEAR_ = H.math.max(2014, (new Date().getFullYear()));


/**
 * Stores the default copyright for the map (HERE).
 * @type {H.map.ICopyright}
 * @private
 */
H.map.Imprint.DEFAULT_COPYRIGHT_ = /** @type {H.map.ICopyright} */ ({
  label: '1987&ndash;' + H.map.Imprint.CURRENT_YEAR_ + ' HERE',
  alt: 'copyright 1987&ndash;' + H.map.Imprint.CURRENT_YEAR_ + ' HERE All Rights Reserved'
});


/**
 * This method retrieves the copyright string for the current view of the map.
 *
 * @return {string} The copyright string for the current map view
 * @publish
 * @export
 */
H.map.Imprint.prototype.getCopyrights = function() {
  'use strict';
  var map = this.map_,
      level = map.getZoom(),
      bounds = map.getViewBounds(),
      layers = map.getLayers(),
      length = layers.getLength(),
      lookup = {},
      i = 0,
      j,
      copyrights,
      label,
      labels = [H.map.Imprint.DEFAULT_COPYRIGHT_.label];
  if (bounds) {
    for (; i < length; i++) {
      if (copyrights = layers.get(i)['getCopyrights'](bounds, level)) {
        for (j = 0; j < copyrights.length; j++) {
          if (!lookup[label = copyrights[j].label]) {
            labels.push(label);
            lookup[label] = 1;
          }
        }
      }
    }
  }
  return labels.join(', ');
};


/**
 * To get the CSS style declaration of the imprint DOM element
 * @publish
 *
 * @export
 * @return {CSSStyleDeclaration}
 */
H.map.Imprint.prototype.getStyle = function() {
  return this.element_.style;
};


/**
 * @override
 */
H.map.Imprint.prototype.disposeInternal = function() {
  //cleaning up after ourselves
  this.map_.removeEventListener(this.map_.EventType.MAPVIEWCHANGEEND, this.updateCopyright_);
  //this.map_.removeEventListener(this.map_.EventType.BASELAYERCHANGE, this.updateCopyright_);
  this.map_.getLayers().removeEventListener('add', this.onLayerChange_);
  this.map_.getLayers().removeEventListener('remove', this.onLayerChange_);
  this.map_.getLayers().removeEventListener('set', this.onLayerChange_);

  if (this.element_.parentNode) {
    this.element_.parentNode.removeChild(this.element_);
  }
};


/**
 * Imprint margin in pixels relative to the bottom corner of the drawing canvas when capturing.
 * Note that it does not take into account pixel ratio!
 * @private
 * @type {Object}
 */
H.map.Imprint.imprintMargin_ = {
  left: 5,
  bottom: 5
};


/**
 * @override
 * @export
 */
H.map.Imprint.prototype.capture = function(canvas, pixelRatio, callback, opt_errback) {
  var ctx = canvas.getContext('2d'),
      style = window.getComputedStyle(this.copyrightElement_),
      fontSize = H.lang.parseFloat(style.getPropertyValue('font-size')),
      text,
      brandImage = this.getBrandImage(),
      brandImageDataUrl,
      brandImageClone,
      tmpNode,
      brandImageHeight = this.brandElement_.offsetHeight,
      doc = this.element_.ownerDocument,
      // Compute imprint margins
      marginLeft = H.map.Imprint.imprintMargin_.left,
      marginBottom = H.map.Imprint.imprintMargin_.bottom,
      copyrightHeight = this.copyrightElement_.offsetHeight,
      canvasHeight = +canvas.height,
      // Get real size of the brand image in pixels. The method check is to bypass unit test failor for phantomjs
      brandImageSize = brandImage.getBoundingClientRect ? brandImage.getBoundingClientRect() : {},
      // Adjust according to pixel ratio
      scaledWidth = brandImageSize.width * pixelRatio,
      scaledHeight = brandImageSize.height * pixelRatio,
      // 3 is for having slight margin between copyright text and brand image
      imrpintOffsetTop = canvasHeight - (brandImageHeight + copyrightHeight + 3) * pixelRatio;

  // Add copyright icon in front and replace "long" dashes with their char code
  text = String.fromCharCode(169) + ' ' +
      this.getCopyrights().replace(/&ndash;/g, String.fromCharCode(8211));

  // Copy basic styling attributes from copyright DOM element into canvas
  ctx.font = (fontSize * pixelRatio) + 'px ' + style.getPropertyValue('font-family');
  ctx.fillStyle = style.getPropertyValue('color');
  ctx.fillText(text, marginLeft * pixelRatio, canvasHeight - marginBottom * pixelRatio);

  // Draw brand image into the canvas
  // First we clone brand image node and apply styling to it. As a result SVG element will
  // get all its actual styling as an inline style
  brandImageClone = H.util.cloneNode(brandImage, document, false);

  // Set size of the brand image taking into account pixel ratio
  brandImageClone.setAttribute('width', scaledWidth);
  brandImageClone.setAttribute('height', scaledHeight);

  // Bug fix for FF when outerHTML of an SVG node is not available
  tmpNode = doc.createElement('div');
  tmpNode.appendChild(brandImageClone);

  // Convert SVG markup into data URL
  brandImageDataUrl = H.util.svgToDataUrl(tmpNode.innerHTML);

  // Load an image with computed data URL
  H.net.images.load(brandImageDataUrl, function(img) {
    // Once image is loaded, draw it to canvas
    ctx.drawImage(img, marginLeft * pixelRatio, imrpintOffsetTop);

    // Pass result to the callback
    callback(canvas);
  }, opt_errback);
};


/**
 * Options to style an imprint
 * @publish
 * @typedef {{
 *   invert: (boolean|undefined),
 *   font: (string|undefined),
 *   href: (string|undefined)
 * }}
 * @property {boolean=} invert
 *           Indicates whether the logo is inverted.
 *           If omitted the current value remains, default is {@code false}.
 * @property {string=} font
 *           The font of the text.
 *           If omitted the current value remains, default is {@code "11px Arial,sans-serif"}.
 * @property {string=} href
 *           The URL of the "Terms of use" link.
 *           If omitted the current value remains, default is "http://here.com/terms".
 */
H.map.Imprint.Options;
