goog.provide('H.util.BuildInfo');



/**
 * This class encapsulates build information of modules. It holds the name of the module, it build version and
 * the revision from which it was built.
 *
 * @constructor
 * @param {string} name the name of the module
 * @param {string} version the version of the module
 * @param {string} revision the revision from which the module was built
 * @export
 */
H.util.BuildInfo = function(name, version, revision) {
  'use strict';
  if (!name || !version || !revision) {
    throw new H.lang.InvalidArgumentError(
        H.util.BuildInfo,
        null,
        'Must specify name, version and revision parameter');
  }

  /**
   * @type {string}
   */
  this['name'] = name;
  /**
   * @type {string}
   */
  this['version'] = version;
  /**
   * @type {string}
   */
  this['revision'] = revision;
};


/**
 * @override
 */
H.util.BuildInfo.prototype.toString = function() {
  'use strict';
  return this['name'] + ';' + this['version'] + ';' + this['revision'];
};


/**
 * Holds build infos, which have been requested
 * @private
 * @dict
 */
H.util.BuildInfo.infos_ = {};


/**
 * Lazily creates or returns a previously created build info
 * @param {string} name the name of the module
 * @param {string} version the version of the module
 * @param {string} revision the revision from which the module was built
 * @return {H.util.BuildInfo}
 * @export
 */
H.util.BuildInfo.get = function(name, version, revision) {
  'use strict';
  if (!name) {
    throw new H.lang.InvalidArgumentError(
        H.util.BuildInfo.get,
        1,
        'Must specify a name parameter');
  }
  return H.util.BuildInfo.infos_[name] ||
         (H.util.BuildInfo.infos_[name] = new H.util.BuildInfo(name, version, revision));
};
