goog.provide('H.util.OList');
goog.provide('H.util.OListEvent');

goog.require('H.lang');
goog.require('H.lang.OutOfRangeError');
goog.require('H.math');
goog.require('H.util.Event');
goog.require('H.util.EventTarget');



/**
 * This class represents an list of ordered entries which dispatches events
 * when the list is modified.
 * @publish
 * @export
 * @extends {H.util.EventTarget}
 * @constructor
 */
H.util.OList = function() {
  'use strict';
  /**
   * The internal list of all entries
   * @type {Array.<*>}
   * @private
   */
  this.entries_ = [];
  H.util.OList.superClass_.constructor.call(this);
};
H.lang.inherits(H.util.OList, H.util.EventTarget);


/**
 * Helper method to adjust a given index
 * @private
 * @throws OutOfRangeError if the given index is out of bounds
 * @param {number|undefined} idx The index to adjust
 * @param {boolean} setToLength To signal that the index value should be the list length
 * @return {number} the adjusted index
 */
H.util.OList.prototype.adjustIndex_ = function(idx, setToLength) {
  'use strict';
  var length = this.entries_.length,
      adjIdx;
  if (setToLength) {
    adjIdx = length;
  } else {
    // if index is < 0 treat it as relative from the end of the list and assure it will be not negative:
    if (H.lang.isNaN(
        adjIdx = (adjIdx = +idx) < 0 ? H.math.max(0, length + adjIdx) :
            // if index is >= 0 assure that it is not greater than the length of the entries:
            H.math.min(length, adjIdx))) {
      throw new H.lang.OutOfRangeError(H.util.OList, idx, [0, length - 1]);
    }
  }
  return adjIdx;
};


/**
 * Internal method to insert an entry at a given index.
 * @private
 * @param {?} entry The entry to add
 * @param {number} idx The index where the entry should be inserted.
 *    The caller must validate that the idx is &gt;=0 and &lt;=length beforehand!
 * @param {boolean=} opt_suppressEvent To signal that NO event should be dispatched
 * @fires H.util.OList#add
 */
H.util.OList.prototype.add_ = function(entry, idx, opt_suppressEvent) {
  'use strict';
  this.entries_.splice(idx, 0, entry);
  this.dispatchEvent(new H.util.OList.Event(this, this.EventType.ADD, idx, entry, null, null));
};


/**
 * This method inserts an entry to the list. Optionally it can
 * place new entry at provided index.
 * @export
 *
 * @param {?} entry The entry to insert
 * @param {number=} opt_idx The index where the new entry should be inserted;
 *    if omitted or greater then the current size of the list,
 *    the entry is added at the end of the list;
 *    a negative index is treated as being relative from the end of the list
 * @fires H.util.OList#add
 * @publish
 */
H.util.OList.prototype.add = function(entry, opt_idx) {
  'use strict';
  this.add_(entry, this.adjustIndex_(opt_idx, H.lang.isNaN(opt_idx)));
};


/**
 * This method removes an entry from the list at the given index
 * The caller must validate that the idx to be &gt;=0 and &lt;length beforehand!
 * index of the entry to remove.
 * @private
 * @fires H.util.OList#remove
 * @param {number} idx The index of the entry which should be removed
 * @param {boolean=} opt_suppressEvent To signal that NO event should be dispatched
 * @return {?} the removed entry
 */
H.util.OList.prototype.remove_ = function(idx, opt_suppressEvent) {
  'use strict';
  var entry = this.entries_.splice(idx, 1)[0];
  this.dispatchEvent(new H.util.OList.Event(this, this.EventType.REMOVE, idx, null, entry, null));
  return entry;
};


/**
 * This method removes an entry by a given index from the list.
 * @export
 *
 * @param {number} idx The index of the entry which should be removed;
 *    a negative index is treated as being relative from the end of the list
 * @throws IllegalArgument if the given index is out of bounds
 * @return {?} The removed entry
 * @publish
 */
H.util.OList.prototype.removeAt = function(idx) {
  'use strict';
  this.get(idx);
  return this.remove_(idx);
};


/**
 * This method removes the the first entry which is identical with the given entry.
 * @export
 *
 * @param {?} entry The entry to remove
 * @fires H.util.OList#remove
 * @return {boolean} signals if the entry could be found in the list and is removed
 * @publish
 */
H.util.OList.prototype.remove = function(entry) {
  'use strict';
  var idx = this.indexOf(entry);
  return idx >= 0 ? (this.remove_(idx), true) : false;
};


/**
 * This method replaces an entry at the given index with the given entry.
 * @export
 *
 * @param {number} idx The index of the entry which should be replaced;
 *    a negative index is treated as being relative from the end of the list
 * @param {?} entry The entry which replaces the existing one
 * @fires H.util.OList#set
 * @throws IllegalArgument if the given index is out of bounds
 * @return {?} The replaced entry
 * @publish
 */
H.util.OList.prototype.set = function(idx, entry) {
  'use strict';
  var replacedElement = this.get(idx);
  idx = this.adjustIndex_(idx, false);
  replacedElement = this.entries_[idx];
  this.entries_[idx] = entry;
  this.dispatchEvent(new H.util.OList.Event(this, this.EventType.SET, idx,
      entry, replacedElement, null));
  return replacedElement;
};


/**
 * This method retrieves the index of the first object in this list that is identical with the
 * object supplied by the caller.
 * @export
 *
 * @param {?} entry The entry for which to return the index.
 * @return {number} The index of the first matching entry in this list
 *    or -1 if the entry provided by the caller is not found in the list
 * @publish
 */
H.util.OList.prototype.indexOf = function(entry) {
  'use strict';
  return this.entries_.indexOf(entry);
};


/**
 * To get the entry at the specified index.
 * @export
 * @publish
 *
 * @throws OutOfRangeError if the given index is out of bounds
 * @param {number} idx The index of the entry to get
 *    a negative index is treated as being relative from the end of the list
 * @return {?} The element at the given index
 */
H.util.OList.prototype.get = function(idx) {
  'use strict';
  idx = this.adjustIndex_(idx, false);
  if ((idx = this.adjustIndex_(idx, false)) >= this.entries_.length) {
    throw new H.lang.OutOfRangeError(this.get, idx, [0, this.entries_.length - 1]);
  }
  return this.entries_[idx];
};


/**
 * This method returns the length of the list.
 * @export
 *
 * @return {number}
 * @publish
 */
H.util.OList.prototype.getLength = function() {
  'use strict';
  return this.entries_.length;
};


/**
 * This method returns all list's entries as an array.
 * @export
 * @return {Array.<*>} The list as an array
 * @publish
 */
H.util.OList.prototype.asArray = function() {
  'use strict';
  return [].concat(this.entries_);
};


/**
 * This method removes all entries from the list.
 * @export
 * @publish
 */
H.util.OList.prototype.flush = function() {
  'use strict';
  var i = this.getLength();
  while (i--) {
    this.remove_(i);
  }
};


/**
 * Specific implementation for H.util.Disposable#disposeInternal, it will remove all entries of the list
 * @override
 * @protected
 */
H.util.OList.prototype.disposeInternal = function() {
  'use strict';
  this.flush();
  H.util.OList.superClass_.disposeInternal.call(this);
};


//#################################################  EVENTS  ###########################################################
/**
 * Events fired by OList.
 * @enum {string}
 */
H.util.OList.prototype.EventType = {
  ADD: 'add',
  REMOVE: 'remove',
  SET: 'set',
  MOVE: 'move'
};


/**
 * Fired when an entry was added to the list.
 * @publish
 * @event H.util.OList#add
 * @type {H.util.OList.Event}
 */


/**
 * Fired when an entry was removed from the list.
 * @publish
 * @event H.util.OList#remove
 * @type {H.util.OList.Event}
 */


/**
 * Fired when an entry was set in the list.
 * @publish
 * @event H.util.OList#set
 * @type {H.util.OList.Event}
 */


/**
 * Fired when an entry was moved within the list.
 * @publish
 * @event H.util.OList#move
 * @type {H.util.OList.Event}
 */



//##############################################  OList.Event  #########################################################
/**
 * The event class for events that are dispatched by OList
 * @publish
 * @constructor
 * @extends {H.util.Event}
 * @param {H.util.OList} list The OList instance which is emitting the event
 * @param {H.util.OList.prototype.EventType} type The type of the event
 * @param {number} idx The affected index within this list
 * @param {?*} added The value of the entry which was added or set
 * @param {?*} removed The value of the entry which was removed or replaced
 * @param {?*} moved The value of the entry which was moved
 */
H.util.OList.Event = function(list, type, idx, added, removed, moved) {
  'use strict';

  // Invoke the H.util.Event constructor first:
  H.util.OList.Event.superClass_.constructor.call(this, type, list);

  /**
   * The list index where the entry was added, removed, set or moved to.
   * @type {number}
   */
  this['idx'] = idx;

  /**
   * If the event type is [add]{@link H.util.OList#add} or [set]{@link H.util.OList#set}
   * this property references the entry which was added to the list
   * @type {?*}
   */
  this['added'] = added;

  /**
   * If the event type is [remove]{@link H.util.OList#remove} or
   * [set]{@link H.util.OList#set} this property references the entry which was removed from the list
   * @type {?*}
   */
  this['removed'] = removed;

  /**
   * If the event type is ['move']{@link H.util.OList#move} this property
   * references the entry which was moved.
   * @type {?*}
   */
  this['moved'] = moved;
};
H.lang.inherits(H.util.OList.Event, H.util.Event);
