goog.provide('H.map.render.p2d.canvas.MarkerRenderer');

goog.require('H.map.render.p2d.MarkerRenderer');
goog.require('H.math.Rect');
goog.require('H.util');



//###############################################  CONSTRUCTOR  ########################################################
/**
 * This renderer fetches Marker objects from the associated layer and render them on a Canvas Surface.
 * @constructor
 * @extends {H.map.render.p2d.MarkerRenderer}
 * @param {H.map.layer.IMarkerLayer} layer
 * @param {H.map.render.p2d.canvas.Surface} surface
 * @param {H.map.render.p2d.MarkerRenderer.Options=} opt_options
 */
H.map.render.p2d.canvas.MarkerRenderer = function(layer, surface, opt_options) {
  'use strict';

  // Initial marker data list, because otherwise it is first set in render() and getObjectsAt would fail:
  this.markerDataList_ = [];

  H.map.render.p2d.canvas.MarkerRenderer.superClass_.constructor.apply(this, arguments);
};
goog.inherits(H.map.render.p2d.canvas.MarkerRenderer, H.map.render.p2d.MarkerRenderer);


//#################################################  RENDERING  ########################################################
/**
 * Concrete implementation of H.map.render.p2d.MarkerRenderer.prototype.fetchMarkers
 * @override
 */
H.map.render.p2d.canvas.MarkerRenderer.prototype.fetchMarkers = function() {
  'use strict';
  return this.layer.requestMarkers.apply(this.layer, arguments);
};


/**
 * A list of MarkerData for all currently rendered markers in ascending z-order.
 * It's needed for hit testing and is reset at the begin of each render() call
 * @private
 * @type {Array.<H.map.render.p2d.MarkerRenderer.MarkerData>}
 */
H.map.render.p2d.canvas.MarkerRenderer.prototype.markerDataList_;


/**
 * Override the super's render() to reset the markerDataList_
 * @override
 */
H.map.render.p2d.canvas.MarkerRenderer.prototype.render = function() {
  'use strict';
  // Reset the list of rendered markers:
  this.markerDataList_ = [];
  return H.map.render.p2d.canvas.MarkerRenderer.superClass_.render.apply(this, arguments);
};


/**
 * Concrete implementation of H.map.render.p2d.MarkerRenderer.prototype.renderMarker
 * @override
 */
H.map.render.p2d.canvas.MarkerRenderer.prototype.renderMarker = function(data, marker, icon, surface, dx, dy) {
  'use strict';
  // Just to satisfy CC's type checks, code will be dropped by compiler:
  marker = /** type {H.map.Marker} */ (marker);
  icon = /** type {H.map.Icon} */ (icon);
  surface = /** @type {H.map.render.p2d.canvas.Surface} */(surface);

  var size = icon.getSize(),
      enableSubpixelRendering = this.enableSubpixelRendering,
      dw = size['w'],
      dh = size['h'],
      anchor = icon.getAnchor();

  dx -= enableSubpixelRendering ? anchor['x'] : H.math.round(anchor['x']);
  dy -= enableSubpixelRendering ? anchor['y'] : H.math.round(anchor['y']);
  surface.draw(icon.getBitmap(), dx, dy, dw, dh);
  // If we have already an old screen rectangle we just update it, otherwise we have to instantiate a new one
  if (data.details) {
    data.details.set(dx, dy, dx + dw, dy + dh);
  } else {
    data.details = new H.math.Rect(dx, dy, dx + dw, dy + dh);
  }
  this.markerDataList_.push(data);
};


//################################################  HIT TESTING  #######################################################
/**
 * Concrete implementation of H.map.render.p2d.IPickable#getObjectsAt
 * @override
 */
H.map.render.p2d.canvas.MarkerRenderer.prototype.getObjectsAt = function(x, y, opt_topMostOnly) {
  'use strict';
  var markerDataList = this.markerDataList_,
      i = markerDataList.length,
      markerData,
      scrRect,
      marker,
      hitArea,
      objects;

  while (i--) {
    markerData = markerDataList[i];
    scrRect = /** @type {H.math.Rect} */ (markerData.details);
    if (scrRect.containsXY(x, y)) {
      marker = markerData.marker;
      // Marker has no hitArea or x/y is within the hitArea?
      if (!(hitArea = marker.getIcon().getHitArea()) || hitArea.hitTest(x - scrRect['left'], y - scrRect['top'], 0)) {
        if (objects) {
          objects.push(marker);
        } else {
          // It's the first object:
          objects = [marker];
          if (opt_topMostOnly) {
            break;
          }
        }
      }
    }
  }
  return objects || H.util.emptyArray;
};


//################################################  DISPOSABLE  ########################################################
/**
 * @override
 */
H.map.render.p2d.canvas.MarkerRenderer.prototype.disposeInternal = function() {
  'use strict';
  this.markerDataList_ = null;
  H.map.render.p2d.canvas.MarkerRenderer.superClass_.disposeInternal.call(this);
};
