goog.provide('H.map.DomIcon');



//################################################  CONSTRUCTOR  #######################################################
/**
 * A visual representation of the {@link H.map.DomMarker}. DomIcon needs to be created with a dom element.
 * Dom element works as a template for visual representation of the marker therefore
 * single DomIcon can be reused across multiple DomMarkers. Provided dom node gets cloned and
 * rendered every time when marker reaches (is visible within) the map view port therefore
 * all previously attached event listeners will not execute. To handle cases where listeners
 * for the dom node are in needed, user needs to add them within <i>onAttach</i> callback
 * were currently displayed clone reference is available. Within <i>onDetach</i> callback,
 * cloned node listener can be removed.
 *
 * The <i>onAttach</i> and <i>onDetach</i> callbacks can be provided to the icon by using
 * {@link H.map.DomIcon.Options}
 *
 * @example
 * var domElement = document.createElement('div');
 * domElement.style.width = '20px';
 * domElement.style.height = '20px';
 * domElement.style.backgroundColor = 'blue';
 *
 * function changeOpacity(evt) {
 *   evt.target.style.opacity = 0.8;
 * };
 *
 * var domIcon = new H.map.DomIcon(domElement, {
 *   onAttach: function(clonedElement, domIcon, domMarker) {
 *     clonedElement.addEventListener('mouseover', changeOpacity);
 *   },
 *   onDetach: function(clonedElement, domIcon, domMarker) {
 *     clonedElement.removeEventListener('mouseover', changeOpacity);
 *   }
 * });
 *
 *
 * @publish
 * @export
 * @final
 * @constructor
 * @throws {H.lang.InvalidArgumentError}
 * @param {!(Element|string)} element The element or markup to use for this icon
 * @param {H.map.DomIcon.Options=} opt_options
 */
H.map.DomIcon = function(element, opt_options) {
  'use strict';
  var key,
      val = element;

  if (!H.lang.isInstanceOf(val, Element)) {
    H.map.DomIcon.parserElement_.innerHTML = val;
    val = H.map.DomIcon.parserElement_.firstElementChild;
    if (!val) {
      throw new H.lang.InvalidArgumentError(H.map.DomIcon, 0, 'No element data');
    }

    // This is a IE bugfix: Without removing the element before reset innerHTML the elment will not be proper rendered:
    H.map.DomIcon.parserElement_.removeChild(val);

    // Set a flag to prevent unnecessary initial cloning:
    key = true;
    H.map.DomIcon.parserElement_.innerHTML = '';
  }
  this.containsCanvasses_ = H.util.getContainedCanvasses(/** @type {!Element} */ (val)).length !== 0;
  this.elem = key ?
      // It's a fresh parsed markup, no cloning is needed:
      /** @type {!Element} */ (val) :
      // Yes, here we want to use the API's document as clone target!
      /** @type {!Element} */ (H.util.cloneNode(/** @type {!Element} */ (val), H.global.document,
      this.containsCanvasses_));

  if (opt_options) {
    key = 'onAttach';
    if (key in opt_options && H.lang.isTypeOf(val = opt_options[key], 'Function', H.map.DomIcon, 1, key)) {
      this.onAttach = val;
    }
    key = 'onDetach';
    if (key in opt_options && H.lang.isTypeOf(val = opt_options[key], 'Function', H.map.DomIcon, 1, key)) {
      this.onDetach_ = val;
    }
  }

};


//##################################################  ELEMENT  #########################################################
/**
 * The DOM element of this icon
 * Must NOT be published, it's used API interally only!
 * @type {!Element}
 */
H.map.DomIcon.prototype.elem;


/**
 * A container element used to parse markup
 * @private
 * @type {Element}
 */
H.map.DomIcon.parserElement_ = document.createElement('DIV');


//##################################################  CLONING  #########################################################
/**
 * The customized callback to invoke when a clone is appended to a DOM tree
 * Must NOT be published, it's used API interally only by rendering!
 * The renderer has to assure that the callback is called in the correct moment.
 *
 * @type {?function(!Element, !H.map.DomIcon, !H.map.DomMarker)}
 */
H.map.DomIcon.prototype.onAttach = null;


/**
 * The customized callback to invoke when a clone is removed from a DOM tree
 * @private
 * @type {?function(!Element, !H.map.DomIcon, !H.map.DomMarker)}
 */
H.map.DomIcon.prototype.onDetach_ = null;


/**
 * Returns a clone of this icon's element
 * Must NOT be published, it's used API interally only by rendering!
 *
 * @param {!H.map.DomMarker} marker The DomMarker instance for which this clone is requested
 * @param {Document} targetDocument The target document for the clone
 * @return {!Element}
 */
H.map.DomIcon.prototype.getClone = function(marker, targetDocument) {
  'use strict';
  return /** @type {!Element} */ (H.util.cloneNode(this.elem, targetDocument, this.containsCanvasses_));
};


/**
 * To dispose a no longer used clone of this icon's element
 * Must NOT be published, it's used API interally only by rendering!
 *
 * @param {!Element} clone
 * @param {!H.map.DomMarker} marker The DomMarker instance for which this clone was requested
 */
H.map.DomIcon.prototype.disposeClone = function(clone, marker) {
  'use strict';
  // TODO store the disposed clone into an icon cache
  if (this.onDetach_) {
    this.onDetach_(clone, this, marker);
  }
};


/**
 * Indicates whether the element contains one or more canvas elements
 * @private
 * @type {boolean}
 */
H.map.DomIcon.prototype.containsCanvasses_ = false;


//##################################################  OPTIONS  #########################################################
/**
 * Options used to initialize a DomIcon
 * @publish
 *
 * @property {function(Element, H.map.DomIcon, H.map.DomMarker)=} onAttach
 *           A callback which is invoked before a clone of the icon's element is appended and displayed on the map.
 *           This callback can be used to setup the clone.
 * @property {function(Element, H.map.DomIcon, H.map.DomMarker)=} onDetach
 *           A callback which is invoked after a clone of the icon's element is removed from the map.
 *           This callback can be used to clean up the clone.
 *
 * @typedef {{
 *   onAttach: (function(Element, H.map.DomIcon, H.map.DomMarker)|undefined),
 *   onDetach: (function(Element, H.map.DomIcon, H.map.DomMarker)|undefined)
 * }}
 */
H.map.DomIcon.Options;
