goog.provide('H.map.layer.MarkerTileLayer');

goog.require('H.map.layer.BaseTileLayer');
goog.require('H.map.provider.MarkerTileProvider');



/**
 * ObjectTileLayer represents map objects which are requested on a tile basis
 *
 * @constructor
 * @extends {H.map.layer.BaseTileLayer}
 * @implements {H.map.layer.IMarkerLayer}
 * @param {H.map.provider.MarkerTileProvider} provider
 * @param {H.map.layer.ITileLayer.Options=} opt_options
 * @export
 * @publish
 */
H.map.layer.MarkerTileLayer = function(provider, opt_options) {
  'use strict';
  H.map.layer.MarkerTileLayer.superClass_.constructor.call(this, provider, opt_options);
};
H.lang.inherits(H.map.layer.MarkerTileLayer, H.map.layer.BaseTileLayer);


/**
 * This method calcualtes the tile grid bounds
 * @param {H.geo.Rect} geoRect
 * @param {number} zoom
 * @return {H.math.Rect}
 * @private
 */
H.map.layer.MarkerTileLayer.prototype.calculateTileGrid_ = function(geoRect, zoom) {
  'use strict';

  var zoomFloored,
      rect;

  zoomFloored = H.math.floor(zoom);
  this['pixelProjection'].rescale(zoomFloored);
  rect = this.geoRectToRect(geoRect);
  return this.getTileBounds(rect, zoomFloored);
};


/**
 * This method merges markers from tile layer response to
 * single set of markers
 *
 * @param {H.map.layer.ITileLayer.Response} markerTiles
 * @return {H.map.layer.IMarkerLayer.TiledResponse}
 *
 * @private
 */
H.map.layer.MarkerTileLayer.prototype.mergeMarkerTiles_ = function(markerTiles) {
  'use strict';
  var tiles = markerTiles['tiles'],
      requested = markerTiles['total'],
      l = tiles.length,
      markers = [],
      tile,
      j,
      i;

  for (i = 0; i < l; i++) {
    tile = tiles[i];
    j = tile['data'].length;
    while (j--) {
      markers.push(tile['data'][j]);
    }
  }

  return {
    'markers': markers,
    'total': tiles.length,
    'requested': requested
  };

};


/**
 * @const
 * @private
 * @type {H.map.layer.IMarkerLayer.TiledResponse}
 */
H.map.layer.MarkerTileLayer.EMPTY_RESPONSE_ = {
  'markers': [],
  'total': 0,
  'requested': 0
};


/**
 * This method requests marker tiles from the provider
 * @param {H.geo.Rect} geoRect
 * @param {number} zoom
 * @param {boolean} cacheOnly
 * @param {H.math.Point} prioCenter
 * @return {H.map.layer.IMarkerLayer.TiledResponse}
 *
 * @private
 */
H.map.layer.MarkerTileLayer.prototype.requestTiledMarkers_ = function(geoRect, zoom, cacheOnly, prioCenter) {
  'use strict';
  var tileBounds,
      markerTiles;
  tileBounds = this.calculateTileGrid_(geoRect, zoom);
  markerTiles = this.getProviderTiles(tileBounds, tileBounds['left'] > tileBounds['right'],
      ~~zoom, cacheOnly, prioCenter);
  return this.mergeMarkerTiles_(markerTiles);
};


/**
 * @export
 * @override
 */
H.map.layer.MarkerTileLayer.prototype.requestMarkers = function(geoRect, zoom, cacheOnly, prioCenter) {
  'use strict';
  if (!this.provider['providesDomMarkers']()) {
    return this.requestTiledMarkers_(geoRect, zoom, cacheOnly, prioCenter);
  } else {
    return H.map.layer.MarkerTileLayer.EMPTY_RESPONSE_;
  }
};


/**
 * @export
 * @override
 */
H.map.layer.MarkerTileLayer.prototype.requestDomMarkers = function(geoRect, zoom, cacheOnly, prioCenter) {
  'use strict';
  if (this.provider['providesDomMarkers']()) {
    return this.requestTiledMarkers_(geoRect, zoom, cacheOnly, prioCenter);
  } else {
    return H.map.layer.MarkerTileLayer.EMPTY_RESPONSE_;
  }
};


