goog.provide('H.map.Marker');

goog.require('H.map.AbstractMarker');
goog.require('H.map.Icon');
goog.require('H.map.provider.Invalidations');
goog.require('H.util.events');



//################################################  CONSTRUCTOR  #######################################################
/**
 * A marker with a visual representation in the form of a bitmap icon.
 * Marker are predestinated if large amounts of markers with static icons should be displayed om the map.
 * @publish
 * @export
 * @extends {H.map.AbstractMarker}
 * @final
 * @constructor
 * @param {H.geo.IPoint} position The location of this marker
 * @param {H.map.Marker.Options=} opt_options The values to initialize this marker
 */
H.map.Marker = function(position, opt_options) {
  'use strict';
  H.map.Marker.superClass_.constructor.call(this, position, opt_options);
};
H.lang.inherits(H.map.Marker, H.map.AbstractMarker);


/**
 * @override
 */
H.map.Marker.prototype.type = H.map.Object.Type['MARKER'];


//#################################################  Z-ORDER  ##########################################################
/**
 * @override
 */
H.map.Marker.prototype.zOrderIdx = 1;


//###############################################  INVALIDATION  #######################################################
/**
 * @override
 */
H.map.Marker.prototype.invalidate = function(flags) {
  'use strict';
  var retVal;
  // We block VISUAL invalidations as long as neither the previous nor the current icon state is READY
  // to prevent unnecessary rendering:
  if (flags !== H.map.provider.Invalidations.Flag.VISUAL ||
      this.previousIconState_ === H.map.Icon.READY || this.icon.getState() === H.map.Icon.READY) {
    retVal = H.map.Marker.superClass_.invalidate.call(this, flags);
  }
  return retVal || false;
};


/**
 * Keeps the state of the previous icon, see invalidate()
 * @private
 * @type {H.map.Icon.prototype.State}
 */
H.map.Marker.prototype.previousIconState_ = H.map.Icon.LOADING;


//###################################################  ICON  ###########################################################
/**
 * @override
 */
H.map.Marker.prototype.iconConstructor = H.map.Icon;


/**
 * Indicates whether this marker is currently listening to icon's "statechange" event
 * @private
 * @type {boolean}
 */
H.map.Marker.prototype.isListeningForStateChange_ = false;


/**
 * The listener for the "statechange" event
 * @private
 */
H.map.Marker.prototype.onIconStateChange_ = function() {
  this.invalidate(H.map.provider.Invalidations.Flag.VISUAL);
};


/**
 * @override
 * @export
 */
H.map.Marker.prototype.setIcon = function(icon) {
  'use strict';
  var STATECHANGE = 'statechange',
      current = this.icon;

  // Remove the listener from the old icon if necessary:
  if (this.isListeningForStateChange_) {
    current.removeEventListener(STATECHANGE, this.onIconStateChange_, false, this);
    delete this.isListeningForStateChange_;
  }
  // Save the state of the current icon - see invalidate()
  if (current) {
    this.previousIconState_ = current.getState();
  }

  H.map.Marker.superClass_.setIcon.call(this, icon);

  // Add a listener to the new icon if necessary:
  if (icon.getState() === H.map.Icon.LOADING) {
    this.isListeningForStateChange_ = true;
    H.util.events.listenOnce(/** @type {H.map.Icon} */ (icon),
        STATECHANGE, this.onIconStateChange_, false, this);
  }
  return this;
};


//###############################################  DEFAULT ICON  #######################################################
/**
 * @override
 */
H.map.Marker.prototype.createDefaultIcon = function(options) {
  'use strict';
  return new H.map.Icon(options.svg, /** @type {H.map.Icon.Options} */ (options));
};


//################################################  OPTIONS  ###########################################################
/**
 * Options used to initialize a Marker
 * @publish
 *
 * @property {number=} min
 *           The minimum zoom level for which the object is visible, default is {@code -Infinity}
 * @property {number=} max
 *           The maximum zoom level for which the object is visible, default is {@code Infinity}
 * @property {boolean=} visibility
 *           Indicates whether the map object is visible at all, default is {@code true}.
 * @property {number=} zIndex
 *           The z-index value of the map object, default is {@code 0}
 * @property {(H.map.provider.Provider|null)=} provider
 *           The provider of this object. This property is only needed if a customized Implementation of ObjectProvider
 *           wants to instantiate an object.
 * @property {H.map.Icon=} icon
 *           The icon to use for the visual representation, if omitted a default icon is used.
 *
 * @typedef {{
 *   min: (number|undefined),
 *   max: (number|undefined),
 *   visibility: (boolean|undefined),
 *   zIndex: (number|undefined),
 *   provider: (H.map.provider.ObjectProvider|null|undefined),
 *   icon: (H.map.Icon|undefined)
 * }}
 */
H.map.Marker.Options;
