goog.provide('H.map.Icon');


goog.require('H.lang.InvalidArgumentError');
goog.require('H.math.Point');
goog.require('H.math.Size');
goog.require('H.net.ImageLoader');
goog.require('H.util');
goog.require('H.util.Event');
goog.require('H.util.EventTarget');



//################################################  CONSTRUCTOR  #######################################################
/**
 * A visual representation of the {@link H.map.Marker}.
 * @publish
 * @export
 * @extends {H.util.EventTarget}
 * @final
 * @constructor
 * @throws {H.lang.InvalidArgumentError} if size, anchor or hitArea is specified with invalid type or values
 *      or the bitmap is a HTMLImageElement which is not succesfully loaded
 * @param {!(string|HTMLImageElement|HTMLCanvasElement)} bitmap Either an image URL, a SVG markup, an image or a canvas.
 * @param {H.map.Icon.Options=} opt_options
 */
H.map.Icon = function(bitmap, opt_options) {
  'use strict';
  var key,
      val;

  // TODO Add sprite support (defined by options sourceSize, sourceOffset)

  H.map.Icon.superClass_.constructor.call(this);

  // Evaluate the options:
  if (opt_options) {
    key = 'size';
    if (key in opt_options) {
      val = opt_options[key];
      if (!val || isNaN(val['w']) || isNaN(val['h'])) {
        throw new H.lang.InvalidArgumentError(H.map.Icon, 1, key);
      }
      this.size_ = new H.math.Size(val['w'], val['h']);
    }
    key = 'anchor';
    if (key in opt_options) {
      val = opt_options[key];
      if (!val || isNaN(val['x']) || isNaN(val['y'])) {
        throw new H.lang.InvalidArgumentError(H.map.Icon, 1, key);
      }
      this.anchor_ = new H.math.Point(val['x'], val['y']);
    }
    key = 'hitArea';
    if (key in opt_options) {
      val = opt_options[key];
      H.lang.isInstanceOf(val, H.map.HitArea, H.map.Icon, 1);
      this.hitArea_ = val = opt_options[key];
    }
    key = 'asCanvas';
    if (key in opt_options) {
      val = opt_options[key];
      this.asCanvas_ = !!(val === H.lang.UNDEF || opt_options[key]);
    }
  }

  this.processBitmap_(bitmap);

};
H.lang.inherits(H.map.Icon, H.util.EventTarget);


//###################################################  STATE  ##########################################################


/**
 * The state types of an Icon
 * @publish
 * @export
 * @enum {number}
 */
H.map.Icon.prototype.State = {
  'ERROR': -1,
  'LOADING': 0,
  'READY': 1
};


/**
 * @const
 * @type {H.map.Icon.prototype.State}
 */
H.map.Icon.ERROR = /** @type {H.map.Icon.prototype.State} */ (-1);


/**
 * @const
 * @type {H.map.Icon.prototype.State}
 */
H.map.Icon.LOADING = /** @type {H.map.Icon.prototype.State} */ (0);


/**
 * @const
 * @type {H.map.Icon.prototype.State}
 */
H.map.Icon.READY = /** @type {H.map.Icon.prototype.State} */ (1);


/**
 * The state of this Icon
 * @private
 * @type {H.map.Icon.prototype.State}
 */
H.map.Icon.prototype.state_ = H.map.Icon.LOADING;


/**
 * Returns the icon's bitmap loading state
 * @publish
 *
 * @export
 * @return {H.map.Icon.prototype.State}
 */
H.map.Icon.prototype.getState = function() {
  'use strict';
  return this.state_;
};


/**
 * Events fired by Icon
 * @enum {string}
 */
H.map.Icon.prototype.EventType = {
  STATECHANGE: 'statechange'
};


/**
 * Fired when the icon's bitmap loading state changes, see {@link H.map.Icon.prototype.getState}
 * @publish
 * @event H.map.Icon#statechange
 * @type {H.util.Event}
 */


/**
 * Sets the state of this icon and dispatch a "statechange" event
 * @private
 * @param {H.map.Icon.prototype.State} state
 */
H.map.Icon.prototype.setState_ = function(state) {
  'use strict';
  this.state_ = state;
  this.dispatchEvent(this.EventType.STATECHANGE);
};


//##################################################  BITMAP  ##########################################################
/**
 * Indicates whether the bitmap should be converted into a canvas, default is {@code true}
 * @private
 * @type {boolean}
 */
H.map.Icon.prototype.asCanvas_ = true;


/**
 * Converts an HTMLImageElement into a HTMLCanvasElement. It expects that the icon's size_ property is already set!
 * @private
 * @param {HTMLImageElement} img
 * @return {HTMLCanvasElement}
 */
H.map.Icon.prototype.imgToCanvas_ = function(img) {
  'use strict';
  var canvas = /** @type {HTMLCanvasElement} */(document.createElement('CANVAS'));
  canvas.getContext('2d').drawImage(img, 0, 0,
      // Attention: Canvas gets cleared because of the width and height assignement!
      canvas.width = this.size_['w'],
      canvas.height = this.size_['h']);
  return canvas;
};


/**
 * Processes the constructor's bitmap argument
 * @private
 * @param {!(string|HTMLImageElement|HTMLCanvasElement)} bitmap Either an image URL, a SVG markup, an image or a canvas.
 */
H.map.Icon.prototype.processBitmap_ = function(bitmap) {
  'use strict';

  switch (H.map.Icon.BmpType_[H.lang.typeOf(bitmap)]) {
    case 2: // HTMLCanvasElement
      // Just to satisfy CC's type checking, will be dropped by compiler:
      bitmap = /** @type {!HTMLCanvasElement} */ (bitmap);
      this.adjustSizeAndAnchor_(bitmap, false);
      this.bitmap_ = bitmap;
      this.state_ = H.map.Icon.READY;
      break;
    case 1: // HTMLImageElement
      // Just to satisfy CC's type checking, will be dropped by compiler:
      bitmap = /** @type {!HTMLImageElement} */ (bitmap);
      if (H.util.isImageLoaded(bitmap)) {
        this.adjustSizeAndAnchor_(bitmap, true);
        this.bitmap_ = this.asCanvas_ ? this.imgToCanvas_(bitmap) : bitmap;
        this.state_ = H.map.Icon.READY;
        break;
      }
      // The HTMLImageElement is not complete, we modify the bitmap to treat its "src" attribute as a URL or SVG string
      bitmap = bitmap.src;
    case 0: // String
      if (bitmap) {
        // Just to satisfy CC's type checking, will be dropped by compiler:
        // If it is SVG markup ("<svg ...") we translate it into an encoded svg+xml data URI:
        bitmap = H.util.svgToDataUrl(/** @type {string} */(bitmap));

        // Let's utilize the ImageLoader to load the image:
        this.state_ = H.map.Icon.LOADING;
        this.imageLoaderTask_ = H.net.images.load(bitmap,
            H.lang.bind(this.onImageOK_, this),
            H.lang.bind(this.onImageError_, this));
        break;
      }
    default:
      this.state_ = H.map.Icon.ERROR;
      throw new H.lang.InvalidArgumentError(H.map.Icon, 0, 'No image data');
  }
};


/**
 * The bitmap of this icon
 * @private
 * @type {?(HTMLImageElement|HTMLCanvasElement)}
 */
H.map.Icon.prototype.bitmap_ = H.lang.NULL;


/**
 * Returns the bitmap of this icon or {@code null} if the bitmap is not ready yet
 * (see {@link H.map.Icon.prototype.getState})
 * @publish
 *
 * @export
 * @return {?(HTMLImageElement|HTMLCanvasElement)}
 */
H.map.Icon.prototype.getBitmap = function() {
  'use strict';
  return this.bitmap_;
};


/**
 * The type of the bitmap
 * @private
 * @enum {number}
 */
H.map.Icon.BmpType_ = {
  'String': 0,
  'HTMLImageElement': 1,
  'HTMLCanvasElement': 2
};


//###################################################  SIZE  ###########################################################
/**
 * The size of this icon
 * @private
 * @type {?H.math.Size}
 */
H.map.Icon.prototype.size_ = H.lang.NULL;


/**
 * Returns the size of this icon or {@code null} if a size wasn't specified in the constructor's options and the state
 * of this icon is not {@link H.map.Icon.READY}
 * @publish
 * @export
 * @return {?H.math.Size}
 */
H.map.Icon.prototype.getSize = function() {
  'use strict';
  return this.size_;
};


//##################################################  ANCHOR  ##########################################################
/**
 * The anchor of this icon or {@code null} if an anchor wasn't specified in the
 * constructor's options and the state of this
 * icon is not {@link H.map.Icon.READY}
 * @private
 * @type {?H.math.Point}
 */
H.map.Icon.prototype.anchor_ = H.lang.NULL;


/**
 * Returns the anchor point of this icon or {@code null} if an anchor wasn't specified in the constructor's options
 * and the state of this icon is not {@link H.map.Icon.READY}.
 * @publish
 * @export
 * @return {?H.math.Point}
 */
H.map.Icon.prototype.getAnchor = function() {
  'use strict';
  return this.anchor_;
};


/**
 * Adjusts the size_ and anchor_ properties if not already defined
 * @private
 * @param {!(HTMLImageElement|HTMLCanvasElement)} bitmap
 * @param {boolean} isImage Indicates whether the bitmap is an HTMLImageElement
 */
H.map.Icon.prototype.adjustSizeAndAnchor_ = function(bitmap, isImage) {
  'use strict';
  var size,
      width,
      height,
      isAppended,
      origParent,
      origSibling;

  if (size = this.size_) {
    width = size['w'];
    height = size['h'];
  } else {
    // Bug fix for MAPSJS-60: IE11 doesn't set the widt/height of an IMG if its src is a URL to a SVG document
    // Solution: Image's size is set when the IMG is (temporarly) appended to the document:
    width = bitmap.naturalWidth || bitmap.width;
    if (width === 0 && isImage && H.util.IS_MSIE) {
      isAppended = true;
      if (origParent = bitmap.parentNode) {
        origSibling = bitmap.nextSibling;
      }
      document.body.appendChild(bitmap);
    }
    this.size_ = size = new H.math.Size(
        width = bitmap.naturalWidth || bitmap.width,
        height = bitmap.naturalHeight || bitmap.height);
    if (isAppended) {
      if (origParent) {
        origParent.insertBefore(bitmap, /** @type {(Node|null)} */(origSibling));
      } else {
        document.body.removeChild(bitmap);
      }
    }
  }
  if (!this.anchor_) {
    this.anchor_ = new H.math.Point(width / 2 | 0, height);
  }
};


//##################################################  HITAREA  #########################################################
/**
 * The hit area of this icon or {@code null} if a hit area wasn't specified in the
 * constructor's options
 * @private
 * @type {H.map.HitArea}
 */
H.map.Icon.prototype.hitArea_ = H.lang.NULL;


/**
 * Returns the hit area of the icon.
 * @publish
 * @export
 * @return {?H.map.HitArea} icon's anchor point
 */
H.map.Icon.prototype.getHitArea = function() {
  'use strict';
  return this.hitArea_;
};


//###############################################  IMAGE LOADING  ######################################################
/**
 * Keeps the task for the ImageLoader (if the bitmap was specified as URL)
 * @private
 * @type {H.util.AsyncQueue.Task}
 */
H.map.Icon.prototype.imageLoaderTask_;


/**
 * A callback to use for the ImageLoader in the case where an image could be loaded.
 * @private
 * @param {!HTMLImageElement} img
 */
H.map.Icon.prototype.onImageOK_ = function(img) {
  'use strict';
  delete this.imageLoaderTask_;
  this.adjustSizeAndAnchor_(img, true);
  this.bitmap_ = this.asCanvas_ ? this.imgToCanvas_(img) : img;
  this.setState_(H.map.Icon.READY);
};


/**
 * A callback to use for the ImageLoader in the case where an image could be NOT loaded.
 * @private
 * @param {string} reason
 */
H.map.Icon.prototype.onImageError_ = function(reason) {
  'use strict';
  this.setState_(H.map.Icon.ERROR);
  delete this.imageLoaderTask_;
};


//##################################################  DISPOSE  #########################################################
/**
 * @override
 */
H.map.Icon.prototype.disposeInternal = function() {
  'use strict';

  H.map.Icon.superClass_.disposeInternal.call(this);
  if (this.bitmap_) {
    this.bitmap_.removeAttribute('src');
    delete this.bitmap_;
  } else if (this.imageLoaderTask_) {
    this.imageLoaderTask_.cancel();
  }
};


//##################################################  OPTIONS  ############################################
/**
 * Options used to initialize a Icon
 * @publish
 *
 * @property {H.math.ISize=} size
 *           The icon's size in pixel, default is the bitmap's natural size
 * @property {H.math.IPoint=} anchor
 *           The anchorage point in pixel, default is {@code bottom-center}
 * @property {H.map.HitArea=} hitArea
 *           The area to use for hit detection, default is the whole rectangular area
 * @property {H.map.HitArea=} asCanvas
 *           Indicates whether a non canvas bitmap is converted into a canvas, default is {@code true}.
 *           The conversion improves the rendering performance but it could also cause a higher memory consumption.
 *
 * @typedef {{
 *   size: (H.math.ISize|undefined),
 *   anchor: (H.math.IPoint|undefined),
 *   hitArea: (H.map.HitArea|undefined)
 * }}
 */
H.map.Icon.Options;

