goog.provide('H.map.provider.RemoteTileProvider');

goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.map.provider.Tile');
goog.require('H.map.provider.TileProvider');
goog.require('H.util.Cache');
goog.require('H.util.ICancelable');



/**
 * RemoteTileProvider is an abstract class which should be used by classes
 * implementing data provision on a tile basis. Every child class needs to
 * implement 'requestInternal' (to request remote tile) and 'getCache' (to
 * provide configured cache object were tiled data is being cached)
 *
 * @publish
 * @export
 * @abstract
 * @extends {H.map.provider.TileProvider}
 * @constructor
 * @param {H.map.provider.TileProvider.Options} options The options to instantiate this TileProvider
 */
H.map.provider.RemoteTileProvider = function(options) {
  'use strict';
  H.lang.checkIllegalConstructor(this, H.map.provider.RemoteTileProvider, false);
  H.map.provider.RemoteTileProvider.superClass_.constructor.call(this, options);

  /**
   * A map of currently requested tiles (by tile key)
   * @private
   * @type {Object.<string, H.util.ICancelable>}
   */
  this.pending_ = {};

  /**
   * Size of the provider's tile that it occupies in cache.
   * @protected
   * @type {number}
   */
  this['entryWeight'] = 1;

  /**
   * Tile cache.
   * @type {H.util.Cache}
   * @private
   */
  this.cache_ = this['getCache']();

  this.cacheFilter = H.lang.bind(this.cacheFilter, this);
};
H.lang.inherits(H.map.provider.RemoteTileProvider, H.map.provider.TileProvider);


/**
 * RemoteTileProvider cache instance, shared with all remote tile providers
 * @type {H.util.Cache}
 * @export
 */
//TODO: The proper cache size can be an issue. If a browser decides to start swapping
// images from GPU to RAM then drawing performance is massively impacted. How do we
// make sure this doesn't happen. I am guessing it's a matter of what machine with
// how much VRAM you're running on...'
H.map.provider.RemoteTileProvider.cache = new H.util.Cache(256 * 256);


/**
 * This method returns cache which should be used to store tiles
 * @return {H.util.Cache} cache
 * @export
 * @publish
 */
H.map.provider.RemoteTileProvider.prototype.getCache = function() {
  'use strict';
  return H.map.provider.RemoteTileProvider.cache;
};


/**
 * This method is used to remove all cache entries for this particular provider.
 * It is passed as filter to cache removeAll method.
 * @param {string} entryKey tile key corresponding to a single tile coordinates
 * @return {boolean} indicating if the element should be removed
 * @export
 */
H.map.provider.RemoteTileProvider.prototype.cacheFilter = function(entryKey) {
  'use strict';
  return +H.lang.tokenize(entryKey, '_')[0] === +this['uid'];
};


/**
 * This method request tile from remote service
 * @publish
 * @export
 * @method
 * @abstract
 * @param {number} x The row number of the tile
 * @param {number} y The column number of the tile
 * @param {number} z The zoom level for which the tile is requested
 * @param {function ((Array.<H.map.Object>|HTMLImageElement|HTMLCanvasElement|null),*=)} onResponse
 *    function which is called after response arrives
 * @param {function (string=)} onError function which is called in case of communication error
 * @return {H.util.ICancelable}
 */
H.map.provider.RemoteTileProvider.prototype.requestInternal = goog.abstractMethod;


/**
 * @override
 * @export
 */
H.map.provider.RemoteTileProvider.prototype.createTileInternal = function(x, y, z, data, opt_options) {
  'use strict';
  return new H.map.provider.Tile(x, y, z, data);
};


/**
 * Dummy cancelable object used in case if requestInternal onSuccess was called synchronously
 * @private
 * @type {Object}
 */
H.map.provider.RemoteTileProvider.prototype.dummyCancelable_ = {cancel: function() {}};


/**
 * @override
 * @export
 */
H.map.provider.RemoteTileProvider.prototype.requestTile = function(x, y, z, cacheOnly) {
  'use strict';
  var that = this,
      tileKey,
      tile,
      cache = this.cache_,
      errMsg = 'tile coordinates must be numbers',
      cancelable,
      pending = this.pending_,
      url;

  if (H.lang.isNaN(x = +x)) throw new H.lang.InvalidArgumentError(this.requestTile, 0, x);
  if (H.lang.isNaN(y = +y)) throw new H.lang.InvalidArgumentError(this.requestTile, 0, y);
  if (H.lang.isNaN(z = +z)) throw new H.lang.InvalidArgumentError(this.requestTile, 0, z);

  z = ~~z; // floor

  tileKey = this['getTileKey'](x, y, z);
  tile = /** @type {H.map.provider.Tile} */ (cache.get(tileKey));

  if ((!tile || !tile['valid']) && !cacheOnly && !this.pending_[tileKey]) {
    // create pending entry with dummy cancelable object
    pending[tileKey] = /** @type {H.util.ICancelable}*/ (this.dummyCancelable_);
    cancelable = this['requestInternal'](
        x, y, z,
        function(data, options) {
          delete that.pending_[tileKey];
          tile = that['createTileInternal'](x, y, z, data, options);
          tile.key = tileKey;
          cache.add(tileKey, tile, that['entryWeight']);
          that.dispatchUpdate();
        },
        function() {
          delete that.pending_[tileKey];
        });
    // check that entry still exists, if success was called synchronously entry was deleted
    if (pending[tileKey] && cancelable) {
      pending[tileKey] = cancelable;
    }
  }

  return tile;
};


/**
 * @override
 * @export
 */
H.map.provider.RemoteTileProvider.prototype.cancelTileByKey = function(tileKey) {
  'use strict';
  var request = this.pending_[tileKey];
  if (request) {
    request['cancel'](); // cancel calls 'errback' with 'canceled' as argument
    delete this.pending_[tileKey];
  }
};


/**
 * @override
 * @export
 */
H.map.provider.RemoteTileProvider.prototype.cancelTile = function(x, y, z) {
  'use strict';
  // We have to access the exported method name to make extened Provider possible
  this['cancelTileByKey'](this['getTileKey'](x, y, z));
};


/**
 * This method instructs the provider to reload data from it's source.
 *
 * Two reload modes are possible:
 *
 * - {@code hard}: A hard immediately removes the cached tiles from
 *   the tile cache and forces the provider to re-fetch the tiles. Currently
 *   cached tiles will not be used for rendering.
 * - {@code soft}: A soft invalidation only marks tiles as invalid and requests
 *   the tiles to be re-fetched at the earliest convenience. The provider does
 *   not invalidate the cache immediately in this case. This means that cached
 *   tiles can still be rendered while the updated tiles are fetched.
 *
 * @param {boolean} hard a boolean flag indicating whether to invalidate in
 *  {@code hard} mode ({@code true}) or in {@code soft} mode ({@code false});
 *
 * @export
 * @publish
 */
H.map.provider.RemoteTileProvider.prototype.reload = function(hard) {
  'use strict';
  var key;
  if (hard) {
    // in the hard case we only need to remove all cache items from this provider
    this.cache_.removeAll(this.cacheFilter);
  } else {
    // otherwise we mark all cache items as invalid so that the next call to
    // request will update the tile in the cache 'in-place'
    this.cache_.forEach(
        this.invalidateEach,
        this,
        this.cacheFilter);
  }

  // cancel all currently ongoing requests
  // they are rescheduled by the requestTile function
  for (key in this.pending_) {
    this.pending_[key]['cancel']();
  }

  // reset pending list after cancelling all tiles
  this.pending_ = {};
  this.dispatchUpdate();
};


/**
 * This method sets the 'valid' flag of each cached tile it is called on to
 * {@code false}.
 * @param {string} entryKey tile key corresponding to a single tile coordinates
 * @param {H.map.provider.Tile} data the tile
 */
H.map.provider.RemoteTileProvider.prototype.invalidateEach = function(entryKey, data) {
  'use strict';
  data['valid'] = false;
};


/**
 * This method disposes the RemoteTileProvider
 * @protected
 */
H.map.provider.RemoteTileProvider.prototype.disposeInternal = function() {
  'use strict';
  var key;

  this.cache_.removeAll(this.cacheFilter);

  // cancel all pending requests
  for (key in this.pending_) {
    this.pending_[key]['cancel']();
    delete this.pending_[key];
  }
};


/**
 * Options which are used to initialize the RemoteTileProvider object.
 * @typedef {{
 *   min: (number|undefined),
 *   max: (number|undefined)
 * }}
 * @publish
 * @property {number=} min The minimal supported zoom level, default is {@code 0}
 * @property {number=} max The max maximal supported zoom level, default is {@code 22}
 */
H.map.provider.RemoteTileProvider.Options;
