goog.provide('H.map.DataModel');


goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.map.layer.Layer');
goog.require('H.util.EventTarget');
goog.require('H.util.OList');



/**
 * The class represents data model of the map. It holds list of layers that are rendered
 * by map's RenderEngine. The class listens to 'update' events from layers and dispatches
 * them to the RenderEngine.
 * @publish
 * @export
 * @final
 * @extends {H.util.OList}
 * @constructor
 * @param {Array=} opt_layers array of layers to be added to the data model
 */
H.map.DataModel = function(opt_layers) {
  'use strict';
  H.map.DataModel.superClass_.constructor.call(this);

  this.dispatchEvent = H.lang.bind(this.dispatchEvent, this);

  if (opt_layers) {
    var i = 0,
        len = opt_layers.length;
    for (; i < len; i++) {
      this.add(opt_layers[i]);
    }
  }
};
H.lang.inherits(H.map.DataModel, H.util.OList);


/**
 * To register an "update" event listener
 * @private
 * @param {H.map.layer.Layer} layer The layer to register
 */
H.map.DataModel.prototype.registerLayer_ = function(layer) {
  'use strict';
  var that = this;
  layer.addEventListener(layer.EventType.UPDATE, this.dispatchEvent, false);
};


/**
 * To deregister an "update" listeners
 * @private
 * @param {H.map.layer.Layer} layer The layer to register
 */
H.map.DataModel.prototype.deregisterLayer_ = function(layer) {
  'use strict';
  layer.removeEventListener(layer.EventType.UPDATE, this.dispatchEvent, false);
};


/**
 * This method adds layer to the data model, which will result in displaying the layer
 * on the map respecting the order in which they were added.
 * @param {H.map.layer.Layer} layer The Layer to add
 * @param {number=} opt_idx index where the new element should be inserted
 * @throws InvalidArgument if the given layer doesn't inherit H.map.layer.Layer
 * @publish
 * @export
 */
H.map.DataModel.prototype.add = function(layer, opt_idx) {
  'use strict';

  H.lang.isInstanceOf(layer, H.map.layer.Layer, this.add, 0);
  H.map.DataModel.superClass_.add.call(this, layer, opt_idx);
  this.registerLayer_(layer);
};


/**
 * @param {number} idx
 * @param {H.map.layer.Layer} layer
 * @throws InvalidArgument if the given layer doesn't inherit H.map.layer.Layer
 * @return {H.map.layer.Layer}
 * @publish
 * @export
 */
H.map.DataModel.prototype.set = function(idx, layer) {
  'use strict';
  var replacedLayer;

  H.lang.isInstanceOf(layer, H.map.layer.Layer, this.set, 0);
  replacedLayer = /** @type {H.map.layer.Layer} */ (H.map.DataModel.superClass_.set.call(this, idx, layer));
  this.registerLayer_(layer);
  this.deregisterLayer_(replacedLayer);
  return replacedLayer;
};


/**
 * This method removes layer with the given index. This will effectively remove
 * the layer from the map.
 * @param {number} idx index of the layer to be removed
 * @throws IllegalArgument if the given index is out of bounds
 * @return {H.map.layer.Layer}
 * @publish
 * @export
 */
H.map.DataModel.prototype.removeAt = function(idx) {
  'use strict';
  var layer;

  if (layer = /** @type {H.map.layer.Layer} */ (H.map.DataModel.superClass_.removeAt.call(this, idx))) {
    this.deregisterLayer_(layer);
  }
  return layer;
};


/**
 * This method removes a given layer. This will effectively remove the layer from the map.
 * @publish
 * @export
 * @param {H.map.layer.Layer} layer The layer to be removed
 * @return {boolean} signals if the layer could be found in the list and is removed
 */
H.map.DataModel.prototype.remove = function(layer) {
  'use strict';
  var wasFound;

  if (wasFound = H.map.DataModel.superClass_.remove.call(this, layer)) {
    this.deregisterLayer_(layer);
  }
  return wasFound;
};


/**
 * This method removes all layers from data model.
 * @publish
 * @export
 */
H.map.DataModel.prototype.flush = function() {
  'use strict';
  var i = this.getLength();
  while (i--) {
    this.removeAt(i);
  }
  H.map.DataModel.superClass_.flush.call(this);
};

