goog.provide('H.util');

goog.require('H.lang');



/**
 * This namespace contains utility implementations.
 * @namespace H.util
 * @publish
 * @export
 */


//##################################################  CSS COLOR  #######################################################
/**
 * A RegExp to isolate the alpha value of a "rgba(...)" or "hsla(...)" CSS color
 * @private
 * @type {RegExp}
 */
H.util.alphaChannelRegExp_ = /^\s*(rgb|hsl)a\s*\(.+,([^,]+)\)\s*$/;


/**
 * To check if the given CCS color is non-transparent (neither "transparent" nor if rgba or hsla value with alpha 0)
 * DON'T publish, API internally used only in SpatialStyle and ArrowStyle!
 * @param {string} color The CSS color string to check
 * @return {boolean} whether the color is transparent
 */
H.util.isNonTransparent = function(color) {
  'use strict';
  return color !== 'transparent' && color !== 'none' &&
      // "RegExp.$2 >= 2e-3" is a shorter simplification of: Math.round(255 * RegExp.$2) > 0
      (!H.util.alphaChannelRegExp_.test(color) || H.global['RegExp'].$2 >= 2e-3);
};


//################################################  UNSORTED STUFF  ####################################################
/**
 * Indicates whether the browser is a Micosoft Internet Explorer
 * @type {boolean}
 */
H.util.IS_MSIE = !!H.global['VBArray'];


/**
 * Returns the super class prototype of the passed constructor or {@code undefined} if the constructor
 * doesn't inherits another one.
 * @export
 * @param {!Function} constructor
 * @return {?}
 */
// We need this functionality (at least in unit tests) to get access to a super class!
H.util.getSuperProto = function(constructor) {
  return constructor.superClass_;
};


/**
 * Method evals string in core module scope
 * @param {string} str string to be evaled
 * @export
 * @return {Object}
 */
H.util.eval = function(str) {
  // just eval, it's meant to be like that
  return /**@type {Object}*/(eval(str));
};


/**
 * Returns the given value if !== undefined - otherwise the given defaultValue
 * @export
 * @param {?} value The value to use
 * @param {?} defaultVal The default value to use
 * @return {?}
 */
H.util.getValue = function(value, defaultVal) {
  return value !== H.lang.UNDEF ? value : defaultVal;
};


/**
 * Returns data URL from a given SVG markup. If markup is not a valid SVG returns it as is.
 * @export
 * @param {string} svgMarkup The SVG markup to convert
 * @return {string} Data URL
 */
H.util.svgToDataUrl = function(svgMarkup) {
  // If it is SVG markup ("<svg ...") we translate it into an encoded svg+xml data URI:
  return /^<svg/.test(svgMarkup) ?
      'data:image/svg+xml;charset=utf-8,' + encodeURIComponent(svgMarkup) : svgMarkup;
};


/**
 * Returns data URL from a given SVG markup. If markup is not a valid SVG returns it as is.
 * @param {Element} from
 * @param {Element} to
 * @return {Element} Passed element with the applied styling
 */
H.util.copyStyle = function(from, to) {
  var cs = window.getComputedStyle(from),
      prop, val;

  // Filtering default style properties did not make much difference, so it was removed.
  // And even more, in order to obtain default styling temporary node has to be inserted into document body
  // However at the moment of calling this function it might not be fully loaded
  for (prop in cs) {
    val = cs[prop];
    // Omit numeric properties
    if (typeof prop === 'string' && typeof val === 'string' && val !== '') {
      to.style[prop] = val;
    }
  }

  return to;
};


//#############################################  MAPSJS WIDE UNIQUE ID  ################################################
/**
 * Counter for session wide unique identifier
 * @type {number}
 * @private
 */
H.util.nextUid_ = 0;


/**
 * Creates a unique ID. The uniqueness is restricted to the session's life time.
 * @return {number}
 */
H.util.getUid = function() {
  'use strict';
  return H.util.nextUid_++;
};


//###############################################  PREDEFINED OBJECTS  #################################################
/**
 * A function which returns always null
 * @param {...*} var_args
 * @return {null}
 */
H.util.returnNull = function(var_args) {
  'use strict';
  return null;
};


/**
 * A function which returns always true
 * @param {...*} var_args
 * @return {boolean}
 */
H.util.returnTrue = function(var_args) {
  'use strict';
  return true;
};


/**
 * A function which returns always false
 * @param {...*} var_args
 * @return {boolean}
 */
H.util.returnFalse = function(var_args) {
  'use strict';
  return false;
};


/**
 * A function which does nothing
 * @param {...*} var_args
 */
H.util.doNothing = function(var_args) {
  'use strict';
};


/**
 * An empty Array
 * @type {Array.<*>}
 */
H.util.emptyArray = [];


//##############################################  DOM NODE CLONING  ####################################################
/**
 * Returns all HTMLCanvasElement nodes on the descendant-or-self axis of the passed element.
 *
 * @param {!Element} element
 * @return {H.lang.ArrayLike.<HTMLCanvasElement>}
 */
H.util.getContainedCanvasses = function(element) {
  'use strict';
  return element.tagName === 'CANVAS' ? [element] : element.getElementsByTagName('CANVAS');
};


/**
 * Clones a node (if owner- and targetDocument are equal) or imports a node into the targetDocument
 * @export
 * @param {Node} node
 * @param {Document} targetDocument
 * @param {boolean} containsCanvas Indicates that the node is a HTMLCanvasElement or contains at least one of it.
 *      If so, the method blitts also the ImageData of all canvas elements into the according clones
 * @return {Node}
 */
H.util.cloneNode = function(node, targetDocument, containsCanvas) {
  'use strict';
  var clone,
      originCanvasses,
      clonedCanvasses,
      i;
  clone = node.ownerDocument === targetDocument ?
      node.cloneNode(true) :
      targetDocument.importNode(node, true);
  if (containsCanvas) {
    originCanvasses = H.util.getContainedCanvasses(/** @type {!Element} */ (node));
    clonedCanvasses = H.util.getContainedCanvasses(/** @type {!Element} */ (clone));
    i = clonedCanvasses.length;
    while (i--) {
      clonedCanvasses[i].getContext('2d').drawImage(originCanvasses[i], 0, 0);
    }
  }
  return clone;
};


//################################################  IMAGE UTILS  #######################################################
/**
 * A 1x1 px canvas context used by util.isImageLoaded()
 * @private
 * @type {CanvasRenderingContext2D}
 */
H.util.dummyCanvasContext_ = (function() {
  'use strict';
  var canvas = document.createElement('CANVAS');
  canvas.width = canvas.height = 1;
  return canvas.getContext('2d');
})();


/**
 * Indicates whether the passed image is complete loaded
 * @param {HTMLImageElement} img
 * @return {boolean}
 */
H.util.isImageLoaded = function(img) {
  'use strict';
  var isComplete = img.complete,
      hasSize = img.naturalWidth > 0;
  // MSIE is not setting the natural size if the src is a SVG (file or data URI):
  if (isComplete && !hasSize && H.util.IS_MSIE) {
    try {
      // This hack works only in IE: drawImage() throws an exception if the image couldn't be rednered before:
      H.util.dummyCanvasContext_.drawImage(img, 0, 0);
      // If no exception was thrown we can assume the image has a natural size:
      hasSize = true;
    } catch (e) {
      // Nothing to do here, hasSize is already false
    }
  }
  return isComplete && hasSize;
};
