goog.provide('H.map.provider.TileProvider');



/**
 * TileProvider is an abstract class to provide data on a tile basis
 * @publish
 * @export
 * @abstract
 * @extends {H.map.provider.Provider}
 * @constructor
 * @param {H.map.provider.TileProvider.Options} options The options to instantiate this TileProvider
 */
H.map.provider.TileProvider = function(options) {
  'use strict';
  var key,
      val;
  H.lang.checkIllegalConstructor(this, H.map.provider.TileProvider, false);
  H.map.provider.TileProvider.superClass_.constructor.call(this, options);

  key = 'tileSize';
  if (key in options) {
    val = H.math.log(+options[key]) / H.math.LN2;
    if (isNaN(val) || val % 1 || val < 0 || val > 30) {
      throw new H.lang.InvalidArgumentError(H.map.provider.TileProvider, 0, key + ': ' + options[key]);
    }
    this[key] = +options[key];
  }

};
goog.inherits(H.map.provider.TileProvider, H.map.provider.Provider);


/**
 * Request data on a tile basis
 * @publish
 * @abstract
 * @param {number} x The row number of the tile
 * @param {number} y The column number of the tile
 * @param {number} z The zoom level for which the tile is requested
 * @param {boolean} cacheOnly Indicates whether only cached tiles are to be considered
 * @return {H.map.provider.Tile|undefined} tile object
 */
H.map.provider.TileProvider.prototype.requestTile = goog.abstractMethod; // function(x, y, z, cacheOnly) {};


/**
 * Cancels tile from being requested using x, y, z coordinates (column, row, zoom)
 * @publish
 * @abstract
 * @param {number} x x tile coordinate (row)
 * @param {number} y y tile coordinate (column)
 * @param {number} z tile coordinate (zoom)
 */
H.map.provider.TileProvider.prototype.cancelTile = goog.abstractMethod; // function(x, y, z) {};


/**
 * Cancels tile from being requested using a tile-key
 * @publish
 * @abstract
 * @param {string} tileKey The key of the tile
 */
H.map.provider.TileProvider.prototype.cancelTileByKey = goog.abstractMethod; // function(tileKey) {};


/**
 * This method creates a tile object with given parameters
 * @param {number} x x tile coordinate (row)
 * @param {number} y y tile coordinate (column)
 * @param {number} z tile coordinate (zoom)
 * @param {HTMLImageElement|HTMLCanvasElement} data data for the tile
 * @param {Object.<string,*>=} opt_options free form options object. These options are meant to
 *   be used in tile specific rendering cases
 * @return {H.map.provider.Tile}
 * @method
 * @publish
 * @abstract
 * @export
 */
H.map.provider.TileProvider.prototype.createTileInternal = goog.abstractMethod;


/**
 * This method creates a tile key consisting of the provider's uid, and the tile's x, y and z coordinates, seperated by
 * underscores e.g.: "4711_7_42_23"
 * @publish
 * @export
 * @param {number} x The x tile coordinate (row)
 * @param {number} y The y tile coordinate (column)
 * @param {number} z The z tile coordinate (zoom level)
 * @return {string} string
 */
H.map.provider.TileProvider.prototype.getTileKey = function(x, y, z) {
  'use strict';
  var sep = '_';
  return this['uid'].concat(sep, x, sep, y, sep, z);
};


/**
 * The size of a tile as edge length in pixels. It must be 2^n where n is in range {@code [0 ... 30],
 * default is {@code 256}
 * @publish
 * @type {number}
 */
H.map.provider.TileProvider.prototype['tileSize'] = 256;


/**
 * Options which are used to initialize the TilePovider
 * @typedef {{
 *   tileSize: (number|undefined),
 *   min: (number|undefined),
 *   max: (number|undefined)
 * }}
 * @property {number=} tileSize The size of a tile as edge length in pixels.
 *      It must be 2^n where n is in range {@code [0 ... 30]}, default is {@code 256}
 * @property {number=} min The minimal supported zoom level, default is {@code 0}
 * @property {number=} max The max maximal supported zoom level, default is {@code 22}
 * @publish
 */
H.map.provider.TileProvider.Options;

