goog.provide('H.util.ICapturable');



/**
 * An interface for capturable elements
 * @interface
 * @export
 */
H.util.ICapturable = function() {};


/**
 * This method is used to capture the element view
 * @export
 * @param {HTMLCanvasElement} canvas HTML Canvas element to draw the view of the capturable element
 * @param {number} pixelRatio The pixelRatio to use for over-sampling in cases of high-resolution displays,
 *                                default is {@code 1}
 * @param {function(HTMLCanvasElement=)} callback Callback function to call once result of
 *                                                the capturing is ready
 * @param {function(string)=} opt_errback Callback function to call if error occurerd during capturing
 */
H.util.ICapturable.prototype.capture = function(canvas, pixelRatio, callback, opt_errback) {};
