goog.provide('H.geo.mercator');

goog.require('H.geo.IProjection');
goog.require('H.geo.Point');
goog.require('H.math.Point');


/**
 * Spherical mercator projection. This projection treats earth as a sphere and
 * maps geo coordinates to a cooresponding 2 dimensional space.
 * Map uses this projection by default.
 * @namespace H.geo.mercator
 * @implements {H.geo.IProjection}
 * @export
 * @publish
 */
H.geo.mercator = {};

//####################################################################################################################
//                                             geo to mercator
//####################################################################################################################


/**
 * This function transforms geographical latitude coordinate to a projected y coordinate in range {@code 0...1}
 * @private
 * @param {number} lat
 * @return {number}
 */
H.geo.mercator.latToY_ = function(lat) {
  'use strict';
  return H.math.min(1,
      H.math.max(0, 0.5 - (H.math.log(
          H.math.tan(H.math.PI_OVER_4 + H.math.PI_OVER_2 * lat / 180)) / H.math.PI) / 2)
  );
};


/**
 * This function transforms geographical longitude coordinate to a projected x coordinate in range {@code 0...1}
 * @private
 * @param {number} lng
 * @return {number}
 */
H.geo.mercator.lngToX_ = function(lng) {
  'use strict';
  return lng / 360 + 0.5;
};


/**
 * @override
 * @export
 */
H.geo.mercator.latLngToPoint = function(lat, lng, opt_out) {
  'use strict';
  if (opt_out) {
    opt_out['x'] = H.geo.mercator.lngToX_(lng);
    opt_out['y'] = H.geo.mercator.latToY_(lat);
  } else {
    opt_out = new H.math.Point(H.geo.mercator.lngToX_(lng), H.geo.mercator.latToY_(lat));
  }
  return opt_out;
};


/**
 * @override
 * @export
 */
H.geo.mercator.geoToPoint = function(geoPoint, opt_out) {
  'use strict';
  return H.geo.mercator.latLngToPoint(geoPoint['lat'], geoPoint['lng'], opt_out);
};


//####################################################################################################################
//                                             mercator to geo
//####################################################################################################################


/**
 * This function transforms a projected y coordinate in range {@code 0...1} to geographical latitude coordinate
 * @private
 * @param {number} y
 * @return {number}
 */
H.geo.mercator.yToLat_ = function(y) {
  'use strict';
  return y <= 0 ?
      90 :
      y >= 1 ?
          -90 :
          H.math._180_OVER_PI *
          (2 * H.math.atan(H.math.exp(H.math.PI * (1 - 2 * y))) -
          H.math.PI_OVER_2);
};


/**
 * This function transforms a projected x coordinate in range {@code 0...1} to geographical longitude coordinate
 * @private
 * @param {number} x
 * @return {number}
 */
H.geo.mercator.xToLng_ = function(x) {
  'use strict';
  return (x === 1 ? 1 : H.math.modulo(x, 1)) * 360 - 180;
};


/**
 * @override
 * @export
 */
H.geo.mercator.xyToGeo = function(x, y, opt_out) {
  'use strict';
  if (opt_out) {
    opt_out['lat'] = H.geo.mercator.yToLat_(y);
    opt_out['lng'] = H.geo.mercator.xToLng_(x);
  } else {
    opt_out = new H.geo.Point(H.geo.mercator.yToLat_(y), H.geo.mercator.xToLng_(x));
  }
  return opt_out;
};


/**
 * @override
 * @export
 */
H.geo.mercator.pointToGeo = function(point, opt_out) {
  'use strict';
  return H.geo.mercator.xyToGeo(point['x'], point['y'], opt_out);
};

