goog.provide('H.map.HitArea');


goog.require('H.lang');
goog.require('H.math');



/**
 * This class represents an area that objects, like a marker, occupies in the screen space, meaning that
 * object can be probed and returned by {@link H.Map@getObjectsAt} method.
 * @publish
 * @export
 * @final
 * @constructor
 * @param {H.map.HitArea.ShapeType} shapeType type of HitArea's shape {@link H.map.HitArea.ShapeType}
 * @param {Array.<number>} values the type-dependent values to define the shape of the hit area. The format
 * for the different types are: H.map.HitArea.ShapeType.RECT: [left, top, right, bottom],
 * H.map.HitArea.ShapeType.CIRCLE: [centerX, centerY, radius],
 * H.map.HitArea.ShapeType.POLYGON: [x1, y1, x2, y2 &#x2026; xN,yN]
 */
H.map.HitArea = function(shapeType, values) {
  'use strict';
  var len,
      i,
      r,
      x,
      y,
      deg,
      rad,
      type = shapeType,
      types = [H.map.HitArea.ShapeType['RECT'],
        H.map.HitArea.ShapeType['CIRCLE'],
        H.map.HitArea.ShapeType['POLYGON']];
  values = [].concat(values);
  len = i = values.length;
  if (types.indexOf(type) === -1) {
    throw new H.lang.InvalidArgumentError(H.map.HitArea, 0, shapeType);
  }
  if (!values) {
    throw new H.lang.InvalidArgumentError(H.map.HitArea, 1, values);
  }
  while (i--) {
    if (H.lang.isNaN(values[i])) {
      throw new H.lang.InvalidArgumentError(H.map.HitArea, 1, values);
    }
  }

  // rectangle to polygon
  if (type === H.map.HitArea.ShapeType['RECT'] && len > 3) {
    values = [
      values[0], values[1], // left, top
      values[2], values[1], // right, top
      values[2], values[3], // right, bottom
      values[0], values[3] // left, bottom
    ];
    type = H.map.HitArea.ShapeType['POLYGON'];
  }

  // circle to polygon
  if (type === H.map.HitArea.ShapeType['CIRCLE'] && len > 2) {
    // positive radius?
    r = values[2];
    if (r > 0) {
      x = values[0];
      y = values[1];
      i = H.math.min(H.math.ceil(r) * 2, 40);
      deg = 360 / i;
      values = [];
      while (i--) {
        rad = deg * i * H.math.PI_OVER_180;
        values.push(x + H.math.sin(rad) * r, y + H.math.cos(rad) * r);
      }
      type = H.map.HitArea.ShapeType['POLYGON'];
    }
  }

  if (type === H.map.HitArea.ShapeType['POLYGON'] && len && !(values.length % 2)) {
    this.polygon_ = values;
  } else {
    throw new H.lang.InvalidArgumentError(H.map.HitArea, 1, values);
  }
};


/**
 * This method tests if HitArea covers the given coordinate.
 * @param {number} x x pixel coordinate to test
 * @param {number} y y pixel coordinate to test
 * @param {number} tolerance A numeric value indicating the tolerance to use for
 * hit testing
 * @return {H.math.CoverType} returns type of coverage
 */
H.map.HitArea.prototype.hitTest = function(x, y, tolerance) {
  'use strict';
  return H.math.isCoveredBy(x, y, this.polygon_, tolerance, true);
};


/**
 * Enumeration represents possible shape types that HitArea can have.
 * @enum {number}
 * @export
 * @publish
 */
H.map.HitArea.ShapeType = {
  'RECT': 0,
  'CIRCLE': 1,
  'POLYGON': 2
};
