goog.provide('H.map.Spatial');

goog.require('H.geo.Rect');
goog.require('H.map.Object');
goog.require('H.map.SpatialStyle');
goog.require('H.map.provider.Invalidations');
goog.require('H.util.ChangeEvent'); // TODO "change" event deprecated since 3.0.7.0



//###############################################  CONSTRUCTOR  ########################################################
/**
 * This class represents a spatial map object which provides its projected geometry.
 * @publish
 * @export
 * @extends {H.map.Object}
 * @abstract
 * @constructor
 * @param {boolean} isClosed Indicates whether this spatial object represents a closed shape
 * @param {H.map.Spatial.Options=} opt_options The options to apply
 */
H.map.Spatial = function(isClosed, opt_options) {
  'use strict';

  if (opt_options) {
    this.setStyle(opt_options['style']);
  }
  if (isClosed) {
    this.isClosed_ = true;
  }
  H.map.Spatial.superClass_.constructor.call(this, opt_options);

};
H.lang.inherits(H.map.Spatial, H.map.Object);


//##################################################  TYPE  ############################################################
/**
 * @override
 */
H.map.Spatial.prototype.type = H.map.Object.Type['SPATIAL'];


//#################################################  Z-ORDER  ##########################################################
/**
 * @override
 */
H.map.Spatial.prototype.zOrderIdx = 0;


//#################################################  EVENTS  ###########################################################
/**
 * Fired when this spatial's style changes.
 * @publish
 * @deprecated since 3.0.7.0
 * @event H.map.Spatial#stylechange
 * @type {H.util.ChangeEvent}
 */


//################################################  STYLE  #############################################################
/**
 * The current style of this spatial object. If it is not defined you have to call getStyle() to create a default style.
 * Must NOT published, API internally used only by SpatialTile to prevent unnecessary cloning.
 * @type {H.map.SpatialStyle}
 */
H.map.Spatial.prototype.style = H.map.SpatialStyle.DEFAULT_STYLE;


/**
 * To get the drawing style of this object. The returned style is treated as immutable and must not be modified
 * afterwards to prevent inconsistancies!
 * @export
 * @publish
 * @return {H.map.SpatialStyle}
 */
H.map.Spatial.prototype.getStyle = function() {
  'use strict';
  return this.style;
};


/**
 * To set the drawing style of this object.
 * If the passed opt_style argument is an instance of {@link H.map.SpatialStyle} it is treated as immutable and
 * must not be modified afterwards to prevent inconsistancies!
 * .
 * @export
 * @publish
 * @param {(H.map.SpatialStyle|H.map.SpatialStyle.Options)=} opt_style The style to set.
 *        If it evaluates to a {@code falsy} the {@link H.map.SpatialStyle.DEFAULT_STYLE} is used.
 * @return {H.map.Spatial} the Spatial instance itself
 */
H.map.Spatial.prototype.setStyle = function(opt_style) {
  'use strict';
  var currentStyle = this.style,
      hasChanged = true;

  if (opt_style) {
    this.style = H.lang.isInstanceOf(opt_style, H.map.SpatialStyle) ?
        /** @type {H.map.SpatialStyle} */ (opt_style) :
        new H.map.SpatialStyle(opt_style);
  } else {
    if (this.style) {
      // Delete the instance property to expose the prototypic style:
      delete this.style;
    } else {
      hasChanged = false;
    }
  }
  if (hasChanged) {
    // TODO "stylechange" event deprecated since 3.0.7.0
    this.dispatchEvent(new H.util.ChangeEvent('stylechange', this.style, currentStyle));
    this.invalidate(H.map.provider.Invalidations.Flag.VISUAL);
  }
  return this;
};


//##########################################  GEOMETRY & PROJECTION  ###################################################
/**
 * The clip margin in pixel to use when clipping a projected geometry in pixel space
 * DON'T publish it, API internal use only
 * @type {number}
 */
H.map.Spatial.prototype.PX_CLIP_MARGIN = H.math.round(H.map.SpatialStyle['MAX_LINE_WIDTH'] / 2) + 1;


/**
 * The constant to signalize that the geometry of the spatial object is covering the requested area fully
 * DON'T publish it, API internally use only
 * @type {Array}
 */
H.map.Spatial.prototype.FULLY_COVERED = [];


/**
 * The constant to signalize that the geometry of the spatial object is not covering the requested area at all
 * DON'T publish it, API internally use only
 * @type {Array}
 */
H.map.Spatial.prototype.NOT_COVERED = [];


/**
 * Indicates whether this spatial object represents a closed shape
 * @private
 * @type {boolean}
 */
H.map.Spatial.prototype.isClosed_ = false;


/**
 * Indicates whether this spatial object represents a closed shape
 * @publish
 * @export
 * @return {boolean}
 */
H.map.Spatial.prototype.isClosed = function() {
  return this.isClosed_;
};


/**
 * Returns the projected geometry of this spatial object for a given spatial tile. Tile's coordinate
 * system origin is top left corner.
 * @publish
 * @abstract
 * @export
 * @method
 * @param {H.map.provider.SpatialTile} tile The tile for which the geometry is requested
 * @return {Array.<Array.<H.math.Point>>} an array of arrays containing alternating x, y coordinate pairs
 */
H.map.Spatial.prototype.getGeometry = goog.abstractMethod;


/**
 * The index of the geometry where the polyline geometries start. If the value is {@code -1} the geometry is not a
 * hybrid geometrys.
 * DON'T publish! It's just a work arround to be able to represent spatial objects which are a composit of
 * tessellated polygons (as surface area) and polylines (as outline) like the Meta-Info-Buildings.
 * The provided geometry begins with the shapes for the tessellated polygons. Starting at hybrid index the pathes for
 * the outlines are added. The hybrid index is taken in to account during rendering and hit testing.
 * It is only considered if the isClosed flag is true and the hybrid index is {@code >= 0}.
 * The default implementation returns always {@code -1}. Concrete Spatial implementation have to override it.
 * @export
 * @param {H.map.provider.SpatialTile} tile The tile for which the geometry is requested
 * @return {number} the index where outline geometries are starting, 0 means no hybrid geometry at all
 */
H.map.Spatial.prototype.getHybridIdx = function(tile) {
  return -1;
};


//################################################  OPTIONS  ###########################################################
/**
 * Options which are used to initialize spatial object object
 * @publish
 * @typedef {{
 *   style: (H.map.SpatialStyle|H.map.SpatialStyle.Options|undefined),
 *   visibility: (boolean|undefined),
 *   zIndex: (number|undefined),
 *   min: (number|undefined),
 *   max: (number|undefined),
 *   provider: (H.map.provider.ObjectProvider|null|undefined)
 * }}
 * @property {(H.map.SpatialStyle|H.map.SpatialStyle.Options)=} style the
 *           style to be used when tracing the spatial object
 * @property {boolean=} visibility An optional boolean value indicating whether this
 *           map object is visible, default is {@code true}
 * @property {number=} zIndex The z-index value of the map object, default is {@code 0}
 * @property {number=} min
 *           The minimum zoom level for which the object is visible, default is {@code -Infinity}
 * @property {number=} max
 *           The maximum zoom level for which the object is visible, default is {@code Infinity}
 * @property {(H.map.provider.Provider|null)=} provider
 *           The provider of this object. This property is only needed if a customized Implementation of
 *           ObjectProvider wants to instantiate an object.
 */
H.map.Spatial.Options;
