goog.provide('H.map.layer.ObjectLayer');

goog.require('H.map.layer.IMarkerLayer');
goog.require('H.map.layer.ITileLayer');
goog.require('H.map.layer.Layer');
goog.require('H.map.layer.TileLayer');
goog.require('H.map.provider.SpatialTileProvider');
goog.require('H.util');



/**
 * This class represents a layer which renders map objects. Spatial objects like
 * polygons and polylines a rendered to tiles before being passed to the enigne.
 * Point objects like markers are provided as objects given an rectangular area.
 * @publish
 * @final
 * @export
 * @implements {H.map.layer.ITileLayer}
 * @implements {H.map.layer.IMarkerLayer}
 * @extends {H.map.layer.Layer}
 * @constructor
 * @param {H.map.provider.ObjectProvider} provider the ObjectProvider which
 *  provides the map objects to this object layer.
 * @param {H.map.layer.ObjectLayer.Options=} opt_options The options for this layer
 */
H.map.layer.ObjectLayer = function(provider, opt_options) {
  'use strict';
  var options = opt_options || {},
      that = this;
  //defaults
  options['tileSize'] = options['tileSize'] || 256;
  options['pixelRatio'] = options['pixelRatio'] || 1;

  this.spatialProvider_ = new H.map.provider.SpatialTileProvider(provider,
      /** @type {H.map.provider.SpatialTileProvider.Options} */ (options));
  this.spatialTileLayer_ = new H.map.layer.TileLayer(this.spatialProvider_, { 'opacity': 1 });
  options['minWorldSize'] = this.spatialProvider_['tileSize'];
  H.map.layer.ObjectLayer.superClass_.constructor.call(this,
      /** @type {H.map.layer.Layer.Options} */ (options));
  this['opacity'] = this.spatialTileLayer_['opacity'];
  this['tileSize'] = this.spatialTileLayer_['tileSize'];
  //this['placeAtOnce'] = true;
  this.provider_ = provider;

  this.onProvidersUpdate_ = this.onProvidersUpdate_.bind(this);

  // We listen to the SpatialTileProvider's "update" event directly instead of going the "long" way by delegate
  // the event from SpatialTileProvider to SpatialTileLayer to this ObjectLayer:
  this.spatialProvider_.addEventListener(this.spatialProvider_.EventType.UPDATE, this.onProvidersUpdate_);
  // We can't use "this.provider_.EventType.UPDATE" because it is differently obfuscated, as long as we not compile all
  // modules in one go:
  this.provider_.addEventListener('update', this.onProvidersUpdate_);
};
H.lang.inherits(H.map.layer.ObjectLayer, H.map.layer.Layer);


/**
 * A listener for update events from ObjectProvider and SpatialTileProvider
 * @private
 * @param {H.util.Event} evt
 */
H.map.layer.ObjectLayer.prototype.onProvidersUpdate_ = function(evt) {
  // We dispatch an update event only, if the event was fired by the ObjectProvider and its target is not an object
  // of type SPATIAL or if the event was fired by the SpatialTileProvider.
  // spatial invalidations if none of the currently requested tiles are invalidated
  if (evt.currentTarget === this.spatialProvider_ || evt.target.type !== H.map.Object.Type['SPATIAL']) {
    this.dispatchUpdate();
  }
};


/**
 * This method returns current ObjectLayer's data provider
 * @return {H.map.provider.ObjectProvider}
 * @export
 * @publish
 */
H.map.layer.ObjectLayer.prototype.getProvider = function() {
  'use strict';
  return this.provider_;
};


//######################################################################################################################
//                                     H.map.layer.IMarkerLayer IMPLEMENTATION
//######################################################################################################################


/**
 * @private
 * @const
 * @type {H.map.layer.IMarkerLayer.Response}
 */
H.map.layer.ObjectLayer.EMPTY_MARKER_LAYER_RESPONSE_ = {
  'markers': /** @type {Array.<H.map.AbstractMarker>} */ (H.util.emptyArray),
  'total': 0
};


/**
 * @override
 * @export
 */
H.map.layer.ObjectLayer.prototype.requestMarkers = function(geoRect, zoom, cacheOnly, prioCenter) {
  'use strict';
  // If the provider has currently no markers then there is no need to do the whole requesting:
  var provider = this.provider_,
      markers;
  return /** @type {H.map.layer.IMarkerLayer.Response} */ (
      provider['providesMarkers']() && (markers = provider['requestMarkers'](geoRect, zoom, true, cacheOnly)).length ?
      {'markers': markers, 'total': markers.length} : // TODO How do we get the REAL total number of markers?
      H.map.layer.ObjectLayer.EMPTY_MARKER_LAYER_RESPONSE_
  );
};


/**
 * @override
 * @export
 */
H.map.layer.ObjectLayer.prototype.requestDomMarkers = function(geoRect, zoom, cacheOnly, prioCenter) {
  'use strict';
  // If the provider has currently no markers then there is no need to do the whole requesting:
  var provider = this.provider_,
      markers;
  return /** @type {H.map.layer.IMarkerLayer.Response} */ (
      provider['providesDomMarkers']() &&
          (markers = provider['requestDomMarkers'](geoRect, zoom, true, cacheOnly)).length ?
      {'markers': markers, 'total': markers.length} : // TODO How do we get the REAL total number of markers?
      H.map.layer.ObjectLayer.EMPTY_MARKER_LAYER_RESPONSE_
  );
};


//######################################################################################################################
//                                      H.map.layer.ITileLayer IMPLEMENTATION
//######################################################################################################################


/**
 * @private
 * @const
 * @type {H.map.layer.ITileLayer.Response}
 */
H.map.layer.ObjectLayer.EMPTY_TILE_LAYER_RESPONSE_ = {
  'tiles': /** @type {Array.<H.map.provider.Tile>} */ (H.util.emptyArray),
  'total': 0
};


/**
 * Implementation of {@link H.map.layer.ITileLayer#requestTiles}
 * @export
 * @override
 */
H.map.layer.ObjectLayer.prototype.requestTiles = function(geoRect, zoomLevel, cacheOnly, prioCenter) {
  'use strict';
  // If the provider has currently no spatials then there is no need to do the whole tiling:
  var provider = this.provider_,
      result;
  if (provider['providesSpatials']()) {
    result = this.spatialTileLayer_.requestTiles(geoRect, zoomLevel, cacheOnly, prioCenter);
    if (!cacheOnly) {
      this.spatialProvider_.processSync();
      result = this.spatialTileLayer_.requestTiles(geoRect, zoomLevel, true, prioCenter);
    }
  } else {
    result = H.map.layer.ObjectLayer.EMPTY_TILE_LAYER_RESPONSE_;
  }
  return result;
};


/**
 * Implementation of {@link H.map.layer.ITileLayer#requestTile}
 * @override
 * @export
 */
H.map.layer.ObjectLayer.prototype.requestTile = function(x, y, z, cacheOnly) {
  'use strict';
  return this.spatialTileLayer_.requestTile(x, y, z, cacheOnly);
};


/**
 * Implementation of {@link H.map.layer.ITileLayer#cancelTile}
 * @override
 * @export
 */
H.map.layer.ObjectLayer.prototype.cancelTile = function(x, y, z) {
  'use strict';
  this.spatialTileLayer_.cancelTile(x, y, z);
};


/**
 * @override
 */
// Implementation of {@link H.util.Disposable#disposeInternal}
H.map.layer.ObjectLayer.prototype.disposeInternal = function() {
  'use strict';
  this.spatialProvider_.dispose();
  this.spatialTileLayer_.dispose();
  this.provider_['dispose']();
  H.map.layer.ObjectLayer.superClass_.disposeInternal.call(this);
};


/**
 * Configuration object which can be use to initialize the ObjectLayer.
 * @typedef {{
 *   tileSize: (number|undefined),
 *   tileCacheSize: (number|undefined),
 *   dataCacheSize: (number|undefined),
 *   pixelRatio: (number|undefined)
 * }}
 * @property {number=} tileSize the size of the tiles rendered by this layer
 *  for polylines and polygons (must be power of 2, default is {@code 256})
 * @property {number=} tileCacheSize the number of fully rendered spatial tiles
 *  that are cached for immediate reuse, default is {@code 32}
 * @property {number=} dataCacheSize the number of tiles to cache which have
 *  render data only, default is {@code 512}
 * @property {number=} pixelRatio The pixelRatio to use for over-sampling in cases of high-resolution displays
 * @publish
 */
H.map.layer.ObjectLayer.Options;
