goog.provide('H.map.render.p2d.dom.Surface');

goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.map.render.p2d.Surface');



//###############################################  CONSTRUCTOR  ########################################################
/**
 * Class represents a drawing surface based on DOM.
 * In distinction from {@link H.map.render.p2d.Surface#draw} the method
 * {@link H.map.render.p2d.dom.Surface#draw} excepts only a blittable HTMLImageElement or HTMLCanvasElement!
 *
 * @extends {H.map.render.p2d.Surface}
 * @constructor
 * @param {Element} container
 * @param {number} width The width of the surface in pixels (already multiplied by pixelRatio)
 * @param {number} height The height of the surface in pixels (already multiplied by pixelRatio)
 * @param {number} pixelRatio
 */
H.map.render.p2d.dom.Surface = function(container, width, height, pixelRatio) {
  'use strict';
  H.map.render.p2d.dom.Surface.superClass_.constructor.apply(this, arguments);
};
H.lang.inherits(H.map.render.p2d.dom.Surface, H.map.render.p2d.Surface);


//###########################################  CONTAINER + ELEMENT  ####################################################
/**
 * Concrete implementation of {@link H.map.render.p2d.Surface.prototype.createElement}
 * @override
 */
H.map.render.p2d.dom.Surface.prototype.createElement = function(ownerDoc) {
  'use strict';
  var element = ownerDoc.createElement('DIV');
  element.style.position = 'absolute';
  return element;
};


//####################################################  SIZE  ##########################################################
/**
 * Concrete implementaion of {@link H.map.render.p2d.Surface.prototype.setSizeInternal}
 * @override
 */
H.map.render.p2d.dom.Surface.prototype.setSizeInternal = function(element, style, width, height, pixelRatio) {
  'use strict';
  style.width = width + 'px';
  style.height = height + 'px';
};


//################################################  OPACITY  ###########################################################
/**
 * Concrete implementaion of {@link H.map.render.p2d.Surface.prototype.setOpacityInternal}
 * @override
 */
H.map.render.p2d.dom.Surface.prototype.setOpacityInternal = function(opacity) {
  'use strict';
  this.element.style.opacity = opacity;
};


//#################################################  DRAW  #############################################################
/**
 * Concrete implementaion of {@link H.map.render.p2d.Surface.prototype.draw}
 * @override
 */
H.map.render.p2d.dom.Surface.prototype.draw = function(el, dx, dy, dw, dh) {
  'use strict';
  var pixelRatio = this.pixelRatio,
      style = el.style;

  // TODO Use a DOM library to set a transform which sets only the necessary style attributes!
  style.webkitTransform =
      style.mozTransform =
      style.oTransform =
      style.msTransform =
      style.transform = 'matrix(' + [1, 0, 0, 1, dx / pixelRatio, dy / pixelRatio].join(',') + ')';

  if (el.parentNode !== this.element) {
    style.position = 'absolute';
    this.element.appendChild(el);
  }
};


//#################################################  CLEAR  ############################################################
/**
 * Concrete implementaion of {@link H.map.render.p2d.Surface.prototype.clear}
 * The requested type for the opt_target argument is DOM Node
 * @override
 */
H.map.render.p2d.dom.Surface.prototype.clear = function(opt_target) {
  'use strict';
  // Satisfy CC, will be dropped by compiler:
  opt_target = /** @type {Node} */ (opt_target);

  if (opt_target) {
    this.element.removeChild(opt_target);
  } else {
    this.element.innerHTML = '';
  }
};
