goog.provide('H.map.Polygon');

goog.require('H.geo.Rect');
goog.require('H.geo.Strip');
goog.require('H.map.GeoShape');
goog.require('H.math');



/**
 * This class represents a polygon in geo-space. It is defined by a strip
 * containing the vertices of a geo shape object (lat, lng, alt values) and a pen
 * to use when rendering the polyline.
 * Polygon represents a closed plane defined by the list of verticies, projected
 * on the map display. List of vericies which define the polygon are is a list
 * of geo coordinates encapsulated by the strip object {@link H.geo.Strip}
 * @publish
 * @export
 * @extends {H.map.GeoShape}
 * @constructor
 * @param {H.geo.Strip} strip the strip describing this polygon's vertices
 * @param {H.map.Spatial.Options=} opt_options optional initialization parameters
 *
 */
H.map.Polygon = function(strip, opt_options) {
  'use strict';
  H.map.Polygon.superClass_.constructor.call(this, true, strip,
      /** @type {H.map.Spatial.Options} */ (opt_options));
};
H.lang.inherits(H.map.Polygon, H.map.GeoShape);


/**
 * @override
 * @export
 */
H.map.Polygon.prototype.getBounds = function() {
  'use strict';
  var bounds,
      strip,
      dbcs,
      count;
  // Has this polygon no valid bounds?
  if (!(bounds = this.bounds)) {
    // Has this polygon a valid strip?
    if (strip = this.getStrip()) {
      // If the strip has bounds we recalculate the polygon bounds out of the strip bounds (polygon specific algorithm):
      if (bounds = strip.getBounds()) {
        // TODO We should move the bounds computing code into Strip.js like Strip.getBounds(opt_isClosed)
        // Does this polygon cross the data-border?
        if ((dbcs = strip.getDBCs(true)) && (count = strip.getPointCount())) {
          // Merge the computed box with the box of the closing leg:
          bounds = bounds.mergeRect(H.geo.Rect.coverPoints(
              [strip.extractPoint(0), strip.extractPoint(count - 1)], true), true);
        }
        // Add south or north pole if the polygon goes around the world and the number of data border crossings is odd
        // (it means is open and looks like a polyline). By default we add south pole:
        if (bounds.getWidth() === 360 && dbcs % 2 === 1) {
          bounds = bounds.mergeLatLng(this.isCoveringNorthPole ? 90 : -90, 0);
        }
      }
    }
    // Store the calculated bounds:
    this.bounds = bounds || null;
  }
  return bounds;
};


//###########################################  CLIPPING & PROJECTION  ##################################################
/**
 * @override
 * @export
 */
H.map.Polygon.prototype.getGeometry = function(tile) {
  'use strict';
  var data,
      pxProj = tile.getPxProj(),
      bounds,
      dbcs,
      northwards,
      pxClipRect,
      left,
      top,
      right,
      bottom,
      isLeftTopInside,
      clippedData,
      clippingPolygon,
      i,
      clipResult,
      strip;

  // TODO Check, why do we had the arguments before (does it come from 2.5 ?)
  data = this.preparedStripForDateBorder_(/*tile.getGeoClipRect(), tile.getPxProj()*/);

  // Project the geo-based strips:
  data = this.projectGeoStrips(data, true, pxProj);

  bounds = this.getBounds();
  dbcs = this.getStrip().getDBCs(true),
  northwards = this.getNorthPoleCovering();

  // Check if it is an "open polygon"
  // (A polygon which covers the north or south pole by having an "open" strip along the whole longitude axis).
  if (bounds.getWidth() === 360 && (dbcs % 2) == 1) {
    data = this.closeOpenPolygon_(data, northwards, pxProj);
  }

  pxClipRect = tile.getPxClipRect();
  left = pxClipRect['left'];
  top = pxClipRect['top'];
  right = pxClipRect['right'];
  bottom = pxClipRect['bottom'];


  // Check if the top-left corner of the clip-rectangle is inside the polygon:
  isLeftTopInside = this.coversCoords_(left, top, data[0], northwards);
  // If we have a second shape then we have to check again for this one
  if (data[1]) {
    // It's only true if the "isLeftTopInside" value for the first and second shape are different:
    isLeftTopInside ^= this.coversCoords_(left, top, data[1], northwards);
  }

  // The following is a bit of a trick to spare unnecesary computing effort if no legs intersecting the rectangle:
  // TODO: Pass a "testOnly" argument to clipStrips() to stop clipping after a the first intersecting leg was found
  // TODO: Why not using a quadtree to determine if there are any intersecting legs?
  if (!H.math.clipping.clipStrips(data, left, top, right, bottom).length) {
    data = isLeftTopInside ?
        this.FULLY_COVERED :   // The polygon covers the whole rectangle
        this.NOT_COVERED;     // The polygon doesn't cover the rectangle at all
  } else {
    // TODO We have to do the following check BEFORE we do some clipping (see above)!
    // Only if the rectangle DOESN'T contain the polygon completely there is a need to clip the polygon:
    if (!tile.getGeoClipRect().containsRect(bounds)) {
      clippedData = [];
      clippingPolygon = [
        new H.math.Point(left, top),
        new H.math.Point(right, top),
        new H.math.Point(right, bottom),
        new H.math.Point(left, bottom)
      ];
      i = data.length;
      while (i--) {
        clipResult = H.math.clipping.clipPolygon(clippingPolygon, data[i]);
        if (clipResult) {
          strip = this.getStripFromResult_(clipResult);
          clippedData.push(strip);
          while (clipResult.nextPoly) {
            clipResult = clipResult.nextPoly;
            strip = this.getStripFromResult_(clipResult);
            clippedData.push(strip);
          }
        }
      }
      data = clippedData;
    }
  }
  return data;
};


/**
 * This method closes an "open polygon" by multiplying the strip across the x-axis and adding the closing legs north- or
 * southwards (depending on the "northwards" argument).
 * @private
 * @param {Array.<Array.<H.math.Point>>} data The strip list
 * @param {boolean} northwards A flag to indicate whether to close the polygon north- or southwards
 * @param {H.geo.PixelProjection} pxProj The pixel projection to use
 * @return {Array.<Array.<H.math.Point>>} the modified strip list
 */
H.map.Polygon.prototype.closeOpenPolygon_ = function(data, northwards, pxProj) {
  'use strict';
  var i,
      length,
      newArray,
      point,
      Point = H.math.Point,
      // TODO Why do we add 1 to the PX_CLIP_MARGIN? It's already round(MAX_LINE_WIDTH / 2) + 1
      PX_CLIP_MARGIN = this.PX_CLIP_MARGIN + 1,
      p1,
      p2;

  // This is the weirdest part of the clipping algorithm. After multiplying the
  // shape we add two points north of the open polygon to close it over the pole
  // (if we ray cast from south). If we put these closing points on -51 sometimes
  // the clipping algorithm clips away everything and the shape is not painted
  // on some tiles (trust me on SOMETIMES, it's different tiles for different
  // rendering cycles...)
  // To avoid the clipping algorithm to behave this way we put the northern
  // closing leg one more pixel up. Then the tile is fully covered and the
  // clipping algorithm is never triggered. This also saves memory ;)

  // Upcoming code needs open polygon in direction from left to right.
  // Reversing the order of points if they are in wrong way:
  if (data[0][1].x > data[0][data[0].length - 1].x) {
    data[0] = data[0].reverse();
  }

  // Strip multiplication:
  // This is the weakest part of clipping, not sure if it works 100% times.
  // We need to multiply the shape because one shape fit the window and we need at least two other
  // (left and right) to fill window+padding.But because we don't know nothing about shape direction
  // so to be safe we multiply it twice to both direction (then it will be clipped).
  // TODO: JH - why do we multiply 5 times? Shouldn't 3 be enough???
  newArray = [];
  length = data[0].length - 1;
  for (i = 0; i < length; i++) {
    point = data[0][i];
    newArray[i] = new Point(point['x'] - 2 * pxProj['w'], point['y']);
    newArray[i + length] = new Point(point['x'] - pxProj['w'], point['y']);
    newArray[i + 2 * length] = new Point(point['x'], point['y']);
    newArray[i + 3 * length] = new Point(point['x'] + pxProj['w'], point['y']);
    newArray[i + 4 * length] = new Point(point['x'] + 2 * pxProj['w'], point['y']);
  }

  data[0] = newArray;

  // Closes the shape over the pole so it is like closed shape but closing legs wouldn't be never
  // rendered because they are made in pixel projection:
  p1 = pxProj.latLngToPixel(northwards ? 90 : -90, 0);
  // Maximum line width is 100. So we have to be sure that this virtual line will not be visible:
  p1.y = northwards ? p1.y - (PX_CLIP_MARGIN) : p1.y + (PX_CLIP_MARGIN);
  p2 = p1.clone();
  p1.x = data[0][data[0].length - 1].x + 1;
  p2.x = data[0][0].x - 1;
  data[0].push(p1, p2);

  return data;
};


/**
 * This method tests if a given point is within a 2D polygon area.
 * @private
 * @param {number} x The x-component of the coordinate to test
 * @param {number} y The y-component of the coordinate to test
 * @param {Array.<H.math.Point>} pixelStrip the strip describing the polygon
 * @param {boolean} rayCastFromSouthPole a boolean value indicating whether to
 *  check by ray casting from south (true) or north (false)
 * @return {boolean} true if the point is within the polygon, false otherwise
 */
H.map.Polygon.prototype.coversCoords_ = function(x, y, pixelStrip, rayCastFromSouthPole) {
  'use strict';
  var length = pixelStrip.length,
      i = 1,
      count = 0,
      coord,
      point1 = pixelStrip[0],
      point2,
      tmp;

  for (; i < length; i++) {
    point2 = pixelStrip[i];
    // Now polygons have their closing legs, but in future it is not needed,
    // than this line will better works than the line above
    // for (; i <= length; i++) {
    // point2 = i <length ? pixelStrip[i] : pixelStrip[0];


    tmp = point1.y + (point2.y - point1.y) * ((x - point1.x) / (point2.x - point1.x));
    // test x is between points and than bellow/up the line made
    // from these points last check with tmp is in condition because if we
    // raycast from north pole (as default) we count all lines above given
    // point, when we are raycasting from south pole we count lines bellow
    if (H.math.min(point1.x, point2.x) <= x && H.math.max(point1.x, point2.x) >= x &&
        (rayCastFromSouthPole ? tmp > y : tmp < y)) {
      count++;
    }
    point1 = point2;
  }
  return count % 2 > 0;
};


/**
 * This method converts a clipping result back to an x,y array
 * @private
 * @param {Object} result the clipping result
 * @return {Array.<number>} the x,y array
 */
H.map.Polygon.prototype.getStripFromResult_ = function(result) {
  'use strict';
  var strip = [],
      point,
      aux = result,
      x,
      y,
      round = H.math.round,
      sqrt = H.math.sqrt,
      pow = H.math.pow;
  do {
    x = round((aux.origx ? aux.origx : aux.x) * 100) / 100;
    y = round((aux.origy ? aux.origy : aux.y) * 100) / 100;

    if (!point || sqrt(pow(point.x - x, 2) + pow(point.y - y, 2)) > 1e-2) {
      point = new H.math.Point(x, y);
      strip.push(point);
    }

    aux = aux.next;
  } while (aux && aux !== result);

  return strip;
};


//######################################################################################################################
/**
 * The cached bounds of the polygon
 * @protected
 * @type {?H.geo.Rect}
 */
H.map.Polygon.prototype.bounds = null;


/**
 * @override
 * @export
 */
H.map.Polygon.prototype.setStrip = function(strip) {
  'use strict';

  // Only for sub classes (e.g. Circle and Rect) we allow to set null as "strip" parameter
  // and let them calculate the strip on demand later on in getStrip()
  if (!H.lang.isInstanceOf(strip, H.geo.Strip) &&
      (H.lang.isConstructedBy(this, H.map.Polygon) || strip !== null)) {
    throw new H.lang.InvalidArgumentError(H.map.Polygon.prototype.setStrip, 0, strip);
  }
  this.bounds = null;
  return H.map.Polygon.superClass_.setStrip.call(this, strip);
};


/**
 * See {@link H.map.Polygon.prototype.setNorthPoleCovering}
 * IMPORTANT:
 * If a sub-classes doesn't calculate the strip during construction time (still null), they must adjust this property
 * during the lazy strip calculation when getStrip() is called.
 * @protected
 * @type {boolean}
 */
H.map.Polygon.prototype.isCoveringNorthPole = true;


/**
 * To set the indicator whether this polygon covers the north pole.
 * It's needed for Polygons whose strip is defined as lines arround the world on longitude axis
 * (for example a circle whose center is one of the poles).
 * In this case a additional information is needed to know if the southern or northern part of the world should be
 * covered by the poygon.
 * @publish
 * @export
 * @param {boolean} flag A value of {@code true} means it covers the north pole, {@code false} means south pole
 * @return {H.map.Polygon} the Polygon instance itself
 */
H.map.Polygon.prototype.setNorthPoleCovering = function(flag) {
  'use strict';
  if (this.isCoveringNorthPole !== flag) {
    this.isCoveringNorthPole = flag;
    this.bounds = null;
    this.invalidate(H.map.provider.Invalidations.Flag.SPATIAL);
  }
  return this;
};


/**
 * See {@link H.map.Polygon.prototype.setNorthPoleCovering}
 * @publish
 * @export
 * @return {boolean}
 */
H.map.Polygon.prototype.getNorthPoleCovering = function() {
  'use strict';
  return this.isCoveringNorthPole;
};


/**
 * This method clips this polygon against a rectangular area. This method will
 * take polygons crossing the date border into account duplicate the shape in
 * the returned list of intersecting strips.
 * @private
 * @return {Array.<Array.<number>>} a list of arrays with alternating lat, lng values.
 */
// TODO seems it was a "left over", booth arguments not effectively used at all
// @param {H.geo.Rect} geoRect the rectangular area against which to clip
// @param {H.geo.PixelProjection} projection a projection to use for bounding box padding
///*function(geoRect, projection)*/
H.map.Polygon.prototype.preparedStripForDateBorder_ = function() {
  'use strict';

  // See "Efficient Clipping of Arbitrary Polygons" of Gnther Greiner and Kai Hormann.
  var strips,
      coords,
      //geoCenter = geoRect.getCenter(),
      isLeftTopInside = false, // will be computed bellow
      length = 0,
      i,
      j,
      shapeBounds,
      shapeBoundsWidth,
      shapeBoundsExt,
      shapeCenterLng,
      coord1,
      coord2;

  // There are terms "real" and "virtual" words used quite often in comments. That is because our "real"
  // world is covered only by coordinates with longitudes in range [-180 ... 180]. But when the polygon is crossing
  // the date border we have to draw that. It means that for algorithms the crossing leg is going from "real" world to
  // "virtual", e.g. leg [0, 170],[0,-170] is for algorithm [0, 170],[0,190] or [0, -190],[0,-170]. This
  // unfolding means that projection into pixels project these coordinates on right side of tile.
  //
  // The clipping of polygons involves some preperation magic.
  // In general we cannot split polygons into parts which are crossing date border like for polylines.
  // So there are two steps needed:
  //  - If the polygon touches date border (like: {lat: 53, lng: 180}) we have to add also an additional point
  //    (like {lat: 53, lng: -180}) to render it also on the "other side of word" because of the line width
  //  - If the polygon crosses the date border we have to doublicate the whole polygon to get once in the "real world",
  //    and a second one in the "virtual word". That ensures that tiles on both sides of date border are covered.
  //
  // Clipping polygons is expensive because we must work with all coordinates in every tile which is covered
  // and because we must do computation twice for date border crossing polygons.

  strips = [[]];
  // Gets filtered array of lat,lng for bounding box computation:
  // The sanitize function creates a copy of the array so that we do not modify the
  // original coords of the polygon
  coords = this.sanitizePolygonPoints_(this.getStrip().getLatLngAltArray());

  // We must compute own bounding box from filtered coordinates, not use the one from shape
  // because originally date border crossing shape shouldn't cross date border after coordinate
  // filtering process
  // TODO: CHECK CODE VS. COMMENTS
  shapeBounds = this.getBounds();
  shapeBoundsWidth = shapeBounds.getWidth();

  // There is first trick for shapes which only touch the date-border.
  // Here we need the proper rotation of virtual shape so we change 180 to -180 and otherwise
  // this is corresponding with adding virtual points to coordsArray
  if (shapeBoundsWidth < 360 && shapeBounds.getLeft() == -180) {
    shapeBounds = new H.geo.Rect(shapeBounds.getTop(), 180, shapeBounds.getBottom(), shapeBounds.getRight());
  }
  if (shapeBoundsWidth < 360 && shapeBounds.getRight() == 180) {
    shapeBounds = new H.geo.Rect(shapeBounds.getTop(), shapeBounds.getLeft(), shapeBounds.getBottom(), -180);
  }

  // Second trick for shapes close to date border which goes over it just because of line thickness.
  // Extending BBox with thickness of maximum line width:
  // Before let's assign the original shapeBounds to have a valid value later on

  //TODO: Maybe we won't need this, since we are already passing in a rectangle which
  // is padded by maxLineWidth / 2 + 1
  /*shapeBoundsExt = shapeBounds;

  if (shapeBoundsWidth < 360) {
    // As mentioned above, this magic is not needed on zoom level 0 where all shapes are on one tile:
    shapeCenterLng = shapeBounds.getCenter()['lng'];
    coord1 = projection.geoToPixel(shapeBounds.getTopLeft());
    coord1.x -= maxLineWidth;
    coord1.y -= maxLineWidth;
    coord1 = projection.pixelToGeo(coord1);

    coord2 = projection.geoToPixel(shapeBBox.getBottomRight);
    coord2.x += maxLineWidth;
    coord1.y += maxLineWidth;
    coord2 = projection.pixelToGeo(coord2);

    shapeBoundsExt = H.geo.Rect.coverLatLngAlts([
      coord1['lat'], coord1['lng'], undefined,
      coord1['lat'], shapeCenterLng, undefined,
      coord1['lat'],  coord2['lng'], undefined,
      coord2['lat'],  coord2['lng'], undefined,
      coord2['lat'],  shapeCenterLng, undefined,
      coord2['lat'],  coord1['lng'], undefined
    ]);
  }*/

  if (shapeBounds.isCDB()) {
    coords = this.addDateBorderPoint_(coords);
  }

  // Reorganizes (rotates) points to start from most left one
  strips[0] = this.rotateStripCoords_([].concat(coords), shapeBounds.getLeft());

  // Tests if shape crosses date border, if so than we need to create virtual shape on other side (virtual)
  // of world and do all computations again. It is computed from filtered coordinates not from original shape
  // because original one should cross date border and filtered not.
  //TODO: because it is repeating code above, it should be redesign as sub function.
  if (shapeBounds.getWidth() < 360 && shapeBounds.containsPoint(new H.geo.Point(shapeBounds.getTop(), 180))) {
    //first condition repair 360 width rectangle on zoom 2 and more

    //Rotates strip to start on other side:
    coords = this.rotateStripCoords_([].concat(coords), shapeBounds.getRight());

    // joins the virtual and the real world polygon
    strips.push(coords);
  }

  return strips;
};


/**
 * This method clips this polygon against a rectangular area. This method will
 * take polygons crossing the date border into account duplicate the shape in
 * the returned list of intersecting strips.
 * @deprecated since 3.0.5.1
 * @publish
 * @export
 * @param {H.geo.Rect} geoRect the rectangular area against which to clip
 * @param {H.geo.PixelProjection} projection a projection to use for bounding box padding
 * @return {Array.<Array.<number>>} a list of arrays with alternating lat, lng values.
 */
H.map.Polygon.prototype.clip = function(geoRect, projection) {
  // The "clip" function doesn't clip, we keep it for backward comaptibility but it's now marked as @deprecated
  H.lang.deprecated(H.map.Polygon.prototype.clip);
  return this.preparedStripForDateBorder_(/*geoRect, projection*/);
};


/**
 * This method sanitizes a polygon strip by adding additional points if the
 * shape touches the date border. It returns a new array with lat, lng values.
 * @private
 * @param {Array.<number>} latLngAltArray the lat,lng,alt array from the poylgon's strip.
 * @return {Array.<number>} the santized lat,lng values from the strip.
 */
H.map.Polygon.prototype.sanitizePolygonPoints_ = function(latLngAltArray) {
  'use strict';
  var coordArray = [],
      length = latLngAltArray.length,
      c,
      lat,
      lng,
      virtualPointAdded = false;

  for (c = 0; c < length; c += 3) {
    lat = latLngAltArray[c];
    lng = latLngAltArray[c + 1];
    //no alt, since we do not care

    // If we have shape which has vertical line laying on date border
    // (two points have the same longitude +-180) we must add some virtual
    // points to be sure that the shape "cross" the DB.
    // It is due to rotation of shape. But we cannot change the direction so
    // we add "normal", "virtual", and then is the normal one. All these three
    // points are on same place.
    // This fixes shape made from these coordinates:
    //       (83, 165),(-83, 165),(-83, 180),(83, 180)
    if (!virtualPointAdded && H.math.abs(lng) == 180) {
      coordArray.push(lat);
      coordArray.push(lng);
      coordArray.push(lat);
      coordArray.push(-lng);
      virtualPointAdded = true;
    }

    coordArray.push(lat);
    coordArray.push(lng);
  }
  return coordArray;
};


/**
 * Algorithm adds two points laying on date border if polygon strip crosses it.
 * This operation is needed to rotate strip correctly (see also
 * {@link H.map.Polygon#_rotateStripCoords}) if that polygon is "open"
 * (covering a pole).
 * This method is destructive, meaning that it modifies the data passed in
 * rather than copying the array.
 * @private
 * @param {Array.<number>} coordArray an array of alternating lat,lng values
 * @return {Array.<number>} the modified array
 */
H.map.Polygon.prototype.addDateBorderPoint_ = function(coordArray) {
  'use strict';
  // In general this code finds a leg crossing date border and splits this leg
  // exactly on the date border. It adds two points with longitude +-180.
  var length = coordArray.length,
      i,
      tmp1,
      tmp2,
      lat1, lat2,
      lng1, lng2,
      newLat,
      newLng,
      delta,
      splitIndex = -1,
      isDBC = H.geo.Strip.isDBC,
      abs = H.math.abs;

  // go through the array finding the first DBC leg
  for (i = 0; i < length - 2; i += 2) {
    if (isDBC(coordArray[i + 1], coordArray[i + 3])) {
      splitIndex = i + 2;
      break;
    }
  }

  // do not forget to check the closing leg (but only if we haven't found
  // a DBC leg before)
  if (splitIndex === -1) {
    if (isDBC(coordArray[length - 1], coordArray[1])) {
      splitIndex = length;
    }
  }

  //if we did find a splitIndex
  if (splitIndex !== -1) {
    lat1 = coordArray[splitIndex - 2];
    lng1 = coordArray[splitIndex - 1];
    // because it may be that our split index is length the next array index
    // can be at the start of the array (using modulo here)
    lat2 = coordArray[(splitIndex) % length];
    lng2 = coordArray[(splitIndex + 1) % length];

    if (abs(lat1) !== 180 && abs(lat2) !== 180) {
      // we now calculate the new lat,lng to insert
      delta = abs(lat2 - lat1);
      tmp1 = abs(lng2 - lng1);
      tmp1 += tmp1 > 180 ? -360 : 0;
      tmp2 = abs(180 - lng2);
      tmp2 += tmp2 > 180 ? -360 : 0;
      delta = delta / (abs(tmp1) / abs(tmp2));
      newLat = lat2 + (lat2 > lat1 ? -delta : delta);
      newLng = lng1 > 0 ? 180 : -180;

      coordArray.splice(splitIndex, 0, newLat, newLng, newLat, -newLng);
    }
  }

  return coordArray;
};


/**
 * Rotate the array of coordinates to start with point which has longitude most
 * similar to given longitude. In general passed coordArray is spliced and
 * concatenated to start with that coordinate.
 * @private
 * @param {Array.<number>} coordArray Array of coords lat, lng, lat,...
 * @param {number} longitude
 * @return {Array.<number>} Array of coords lat, lng, lat,...
 */
H.map.Polygon.prototype.rotateStripCoords_ = function(coordArray, longitude) {
  'use strict';
  var coordArrayTmp = [],
      c,
      delta = Number.POSITIVE_INFINITY,
      index = -1,
      tmpLngDelta,
      length = coordArray.length,
      abs = H.math.abs;

  // strict comparation
  /*for (c = 0; c < coordArray.length; c+=2) {
    if (coordArray[c+1] == longitude) {
      coordArrayTmp = coordArray.splice(c, coordArray.length - c);
      return coordArrayTmp.concat(coordArray);
    }
  }*/

  // fuzzy comparison, because not all the time longitude parameter exactly
  // match any longitudes in array
  for (c = 0; c < length; c += 2) {
    tmpLngDelta = abs(coordArray[c + 1] - longitude);
    tmpLngDelta += tmpLngDelta > 180 ? 360 - tmpLngDelta : tmpLngDelta;
    if (tmpLngDelta < delta) {
      delta = tmpLngDelta;
      index = c;
    }
  }

  if (c > 0) {
    coordArrayTmp = coordArray.splice(index, coordArray.length - index);
    return coordArrayTmp.concat(coordArray);
  }

  return coordArray;
};
