goog.provide('H.map.layer.BaseTileLayer');

goog.require('H.map.layer.Layer');
goog.require('H.map.provider.Provider');



/**
 * BaseTileLayer encapsulates funcitonailty that is common to all layers that deliver tiles, such
 * as {@link H.map.layer.TileLayer}. The functionality includes geo bounding box to grid
 * calculation, tile request management.
 *
 * @constructor
 * @publish
 * @export
 * @extends {H.map.layer.Layer}
 * @param {H.map.provider.TileProvider} provider data source for the TileLayer
 * @param {H.map.layer.ITileLayer.Options=} opt_options additional options
 * @throws IllegalArgument if the provider argument is not a TileProvider
 */
H.map.layer.BaseTileLayer = function(provider, opt_options) {
  'use strict';
  var options = opt_options || {};

  if (!provider) {
    throw new H.lang.InvalidArgumentError(H.map.layer.TileLayer, 0, provider);
  }

  this.dispatchUpdate = H.lang.bind(this.dispatchUpdate, this);
  this.provider = provider;
  this.provider.addEventListener(H.map.provider.Provider.prototype.EventType.UPDATE,
      this.dispatchUpdate, false, this);

  /**
   * Size of a tile image rendered by the tile layer
   * @type {number}
   * @name {H.map.layer.TileLayer#tileSize}
   * @publish
   */
  this['tileSize'] = provider['tileSize'];
  this.tilesRequested_ = {};

  H.map.layer.Layer.call(this, {
    'min': this.provider['min'],
    'max': this.provider['max'],
    'minWorldSize': this['tileSize'],
    'projection': options.projection,
    'dark': options['dark']
  });
};
goog.inherits(H.map.layer.BaseTileLayer, H.map.layer.Layer);


/**
 * Instance of image tile provider
 * @protected
 * @type {H.map.provider.TileProvider}
 */
H.map.layer.BaseTileLayer.prototype.provider;


/**
 * Map of previously requested tile (used to filter out ongoing and current requests)
 * @private
 * @type {Object}
 */
H.map.layer.BaseTileLayer.prototype.tileRequested_;


/**
 * This method returns the provider which feeds this layer with data.
 * @return {H.map.provider.TileProvider} this layer's provider
 * @publish
 * @export
 */
H.map.layer.BaseTileLayer.prototype.getProvider = function() {
  return this.provider;
};


/**
 * This method transforms a geo-rectangle to geometrical projected rectangle at
 * the current projection zoom level or at provided zoom level
 * @param {H.geo.Rect} geoRect geo bounds to be projected
 * @param {number=} opt_zoom overrides current projection zoom level
 * @return {H.math.Rect} projected 2d space rect cooresponding to provided geo bounds
 * @publish
 * @export
 */
H.map.layer.BaseTileLayer.prototype.geoRectToRect = function(geoRect, opt_zoom) {
  'use strict';
  var previousZoom,
      pixelProjection = this['pixelProjection'],
      projectedRect;

  if (!H.lang.isInstanceOf(geoRect, H.geo.Rect)) {
    throw new H.lang.InvalidArgumentError(this.geoRectToRect, 0, geoRect);
  }

  //zoom level forced
  if (!H.lang.isNaN(opt_zoom)) {
    previousZoom = pixelProjection.getZoomScale();
    pixelProjection.rescale(opt_zoom);
  }

  projectedRect = H.math.Rect.fromPoints(
      pixelProjection.geoToPixel(geoRect.getTopLeft()),
      pixelProjection.geoToPixel(geoRect.getBottomRight()));

  //rescale back to previous zoom in case it was forced
  !H.lang.isNaN(previousZoom) && pixelProjection.rescale(previousZoom);

  return projectedRect;
};


/**
 * This method returns tile grid for a bounding rectangle
 * @param {H.math.Rect} rectBounds projected rect bounds which coorespond to geo bounding box
 * @param {number} zoom current zoom level
 * @return {H.math.Rect} rectangle which represents the tile grid bounds
 * @publish
 * @export
 */
H.map.layer.BaseTileLayer.prototype.getTileBounds = function(rectBounds, zoom) {
  'use strict';

  if (!H.lang.isInstanceOf(rectBounds, H.math.Rect)) {
    throw new H.lang.InvalidArgumentError(this.getTileBounds, 0, rectBounds);
  }
  if (H.lang.isNaN(zoom = +zoom)) {
    throw new H.lang.InvalidArgumentError(this.getTileBounds, 1, zoom);
  }

  var tiles = H.math.pow(2, H.math.floor(zoom)),
      tileSize = this['tileSize'],
      tileLeft,
      tileRight,
      tileBounds;

  tileLeft = H.math.floor(rectBounds['left'] / tileSize);
  if (tileLeft === tiles) {
    tileLeft -= 1;
  }

  tileRight = H.math.floor(rectBounds['right'] / tileSize);
  if (tileRight === tiles) {
    tileRight -= 1;
  }

  tileBounds = new H.math.Rect(
      tileLeft,
      H.math.clamp(H.math.floor(rectBounds['top'] / tileSize), 0, tiles - 1),
      tileRight,
      H.math.clamp(H.math.floor(rectBounds['bottom'] / tileSize), 0, tiles - 1));
  return tileBounds;
};


/**
 * This method requests a single tile according to tile coordinates. It returns
 * either a Tile object if it is already loaded or undefined and starts loading
 * the tile
 * @param {number} x tile row position
 * @param {number} y tile column position
 * @param {number} z The zoom level for which the tile is requested
 * @param {boolean} cacheOnly indicates whether only cached tiles are to be considered
 * @return {H.map.provider.Tile|undefined} tile object corresponding to requested coordinates
 * @publish
 */
H.map.layer.BaseTileLayer.prototype.requestTile = function(x, y, z, cacheOnly) {
  'use strict';
  // We have to access the exported method name to make extended Provider possible
  return this.provider['requestTile'](x, y, z, cacheOnly);
};


/**
 * This method cancels a previously requested tile.
 * @param {number} x tile row position
 * @param {number} y tile column position
 * @param {number} z zoom level
 * @publish
 * @export
 */
H.map.layer.BaseTileLayer.prototype.cancelTile = function(x, y, z) {
  'use strict';
  // We have to access the exported method name to make extened Provider possible
  this.provider['cancelTile'](x, y, z);
};


/**
 * @override
 * @export
 */
H.map.layer.BaseTileLayer.prototype.getCopyrights = function(bounds, level) {
  'use strict';
  return this.provider['getCopyrights'] ? this.provider['getCopyrights'](bounds, level) : null;
};


/**
 * This method cancels previously requested tiles
 * which are out of current request range.
 * @param {Object} currentlyRequested map of currently requested tiles
 * @private
 */
H.map.layer.BaseTileLayer.prototype.cancelPreviouslyRequested_ = function(currentlyRequested) {
  'use strict';
  var key;
  for (key in this.tilesRequested_) {
    if (this.tilesRequested_.hasOwnProperty(key) && this.tilesRequested_[key] && !currentlyRequested[key]) {
      // We have to access the exported method name to make extened Provider possible
      this.provider['cancelTileByKey'](key);
    }
  }

  this.tilesRequested_ = currentlyRequested;
};


/**
 * This method requests tiles from the data source (provider). It can return a set of
 * tiles which are currently loaded. All tiles which are not yet loaded will
 * be included in response as soon as they will be available during subsequent calls.
 * @param {H.math.Rect} tileBounds bounds in tile grid
 * @param {boolean} isCDB
 * @param {number} zoomLevel The zoom level for which the objects are requested
 * @param {boolean} cacheOnly Indicates whether only cached objects are to be considered
 * @param {H.math.Point} prioCenter The priority center as an offset in screen pixel relative to the center
 * @return {H.map.layer.ITileLayer.Response} returns an array if tile which are already loaded
 * @publish
 * @export
 */
H.map.layer.BaseTileLayer.prototype.getProviderTiles = function(tileBounds, isCDB, zoomLevel,
    cacheOnly, prioCenter) {
  'use strict';
  // TODO: fix argument "isCDB" description

  if (!H.lang.isInstanceOf(tileBounds, H.math.Rect)) {
    throw new H.lang.InvalidArgumentError(this.getProviderTiles, 0, tileBounds);
  }
  if (H.lang.isNaN(zoomLevel = +zoomLevel)) {
    throw new H.lang.InvalidArgumentError(this.getProviderTiles, 1, zoomLevel);
  }

  var zoomFloored = H.math.floor(zoomLevel),
      tilesNum = H.math.pow(2, zoomFloored),
      tiles = [],
      total = 0,
      currentlyRequested = {},
      x = tileBounds['left'],
      y = tileBounds['top'],
      tileBoundsRight = tileBounds['right'],
      xMax = tileBoundsRight + (isCDB ? tilesNum + (tileBoundsRight === tileBounds['left'] ? 0 : 1) : 1),
      yMax = tileBounds['bottom'],
      xmod,
      tile,
      requestInfos = [],
      i,
      provider = this.provider,
      requestTile = provider['requestTile'],
      prioX = x + (1 + tileBoundsRight - x) / 2 + prioCenter['x'] / this['tileSize'] - 0.5,
      prioY = y + (1 + yMax - y) / 2 + prioCenter['y'] / this['tileSize'] - 0.5;

  // Collect all requests infos:
  while (x < xMax) {
    y = tileBounds['top'];
    while (y <= yMax) {
      xmod = x % tilesNum;
      currentlyRequested[provider.getTileKey(xmod, y, zoomFloored)] = true;
      requestInfos.push([
        xmod, y, zoomFloored, cacheOnly,
        // Store a priority value (sum of the x and y distances between priority center and tile center)
        H.math.abs(prioX - x) + H.math.abs(prioY - y)
      ]);
      y++;
      total++;
    }
    x++;
  }
  if (total) {
    // Sort the request-infos by priority and request the tiles from the provider (in reverse order!):
    if (!cacheOnly) {
      requestInfos.sort(H.map.layer.BaseTileLayer.sortByPriority_);
    }
    i = total;
    while (i--) {
      if (tile = requestTile.apply(provider, requestInfos[i])) {
        tiles.push(tile);
      }
    }
  }

  !cacheOnly && this.cancelPreviouslyRequested_(currentlyRequested);
  return {'total': total, 'tiles': tiles};
};


/**
 * A comparator to sort a list of RequestInfo_ by priority in descending order
 * @private
 * @param {H.map.layer.BaseTileLayer.RequestInfo_} first The first request info
 * @param {H.map.layer.BaseTileLayer.RequestInfo_} second The second request info
 * @return {number}
 */
H.map.layer.BaseTileLayer.sortByPriority_ = function(first, second) {
  'use strict';
  return second[4] - first[4];
};


/**
 * An array to store request information for a tile, where the entries are:
 *   0: {number} The tile's number on x-axis
 *   1: {number} The tile's number on y-axis
 *   2: {number} The tile's zoom level
 *   3: {boolean} The cacheOnly flag
 *   4: {number} The priority value
 * @private
 * @typedef {Array.<number|boolean>}
 */
H.map.layer.BaseTileLayer.RequestInfo_;
