goog.provide('H.map.Object');

goog.require('H.lang.IllegalOperationError');
goog.require('H.map.provider.Invalidations');
goog.require('H.math');
goog.require('H.util');
goog.require('H.util.ChangeEvent');
goog.require('H.util.EventTarget');



//###############################################  CONSTRUCTOR  ########################################################
/**
 * This class represents the abstract base class for map objects such as
 * polylines, polygons, markers, groups etc.
 * @publish
 * @export
 * @abstract
 * @extends {H.util.EventTarget}
 * @constructor
 * @param {H.map.Object.Options=} opt_options The values to initialize this object
 */
H.map.Object = function(opt_options) {
  'use strict';
  var key;

  H.lang.checkIllegalConstructor(this, H.map.Object, false);
  H.map.Object.superClass_.constructor.call(this);

  this.id_ = H.map.Object.nextObjectId_++;

  if (opt_options) {
    key = 'min';
    if (H.lang.isNumber(opt_options[key])) {
      this.min_ = opt_options[key];
    }
    key = 'max';
    if (H.lang.isNumber(opt_options[key])) {
      this.max_ = opt_options[key];
    }
    key = 'visibility';
    if (key in opt_options) {
      this.visibility_ = !!opt_options[key];
    }
    key = 'zIndex';
    if (key in opt_options) {
      this.zIndex_ = +opt_options[key] || 0;
    }
    key = 'provider';
    if (key in opt_options) {
      // We must NOT use setProvider() because it is only used by Group for LocalObjectProvider content
      this.provider = opt_options[key];
    }
    key = 'data';
    if (key in opt_options) {
      this.setData(opt_options[key]);
    }
  }

};
H.lang.inherits(H.map.Object, H.util.EventTarget);


//###################################################  TYPE  ###########################################################
/**
 * The major types of map objects
 * @publish
 * @export
 * @enum {number}
 */
H.map.Object.Type = {
  /** spatial object */
  'ANY': 0,
  /** spatial object */
  'SPATIAL': 1,
  /** Marker object */
  'MARKER': 2,
  /** DomMarker object */
  'DOM_MARKER': 3,
  /** DomMarker object */
  'GROUP': 4
};


/**
 * The major type of this object. Any concrete implementation has to define the type!
 * Must NOT be published, it's used API internally only!
 *
 * @type {H.map.Object.Type}
 */
H.map.Object.prototype.type;


//##################################################  EVENTS  ##########################################################
/**
 * Events fired by Object
 * @enum {string}
 */
H.map.Object.prototype.EventType = {
  VISIBILITYCHANGE: 'visibilitychange',
  ZINDEXCHANGE: 'zindexchange'
};


/**
 * Fired when this object's visibility changes
 * @publish
 * @deprecated since 3.0.7.0
 * @event H.map.Object#visibilitychange
 * @type {H.util.ChangeEvent}
 */


/**
 * Fired when this object's z-index changes
 * @publish
 * @deprecated since 3.0.7.0
 * @event H.map.Object#zindexchange
 * @type {H.util.ChangeEvent}
 */


//##################################################  ID  ##############################################################
/**
 * A counter to create unique IDs for all map.Object instances
 * @private
 * @type {number}
 */
H.map.Object.nextObjectId_ = H.math.MIN_IDX;


/**
 * The ID of this object
 * @private
 * @type {number}
 */
H.map.Object.prototype.id_;


/**
 * Returns the ID of this object.
 * @publish
 *
 * @export
 * @return {number}
 */
H.map.Object.prototype.getId = function() {
  'use strict';
  return this.id_;
};


//################################################  VISIBILITY  ########################################################
/**
 * The minimum zoom level for which the object is visible, default is {@code -Infinity}
 * @private
 * @type {number}
 */
H.map.Object.prototype.min_ = -1 / 0;


/**
 * The maximum zoom level for which the object is visible, default is {@code Infinity}
 * @private
 * @type {number}
 */
H.map.Object.prototype.max_ = 1 / 0;


/**
 * Checks if the given zoom level is in the visibility range of this object
 * Must NOT be published, it's used API interally only by ObjectProvider
 *
 * @param {number} zoomLevel The zoom level to check against
 * @return {boolean}
 */
H.map.Object.prototype.isVisibleForZoom = function(zoomLevel) {
  'use strict';
  var object = this,
      visibility;
  while ((visibility = (object.min_ <= zoomLevel && object.max_ >= zoomLevel)) && (object = object.parentGroup)) {
    // The body is here to satisfy CC and gjslint
  }
  return visibility;
};


/**
 * The visibility flag of this Object, default is {@code true}
 * @private
 * @type {boolean}
 */
H.map.Object.prototype.visibility_ = true;


/**
 * Sets the visibility of this object.
 * @publish
 *
 * @export
 * @param {boolean=} opt_visibility Indicates whether this map object should be visible.
 * @return {H.map.Object} returns this object
 */
H.map.Object.prototype.setVisibility = function(opt_visibility) {
  'use strict';
  var currentVisibility = this.visibility_;
  if ((opt_visibility = !!opt_visibility) ^ currentVisibility) {
    this.visibility_ = opt_visibility;
    this.invalidate(H.map.provider.Invalidations.Flag.VISUAL);
    // TODO "visibilitychange" event deprecated since 3.0.7.0
    this.dispatchEvent(new H.util.ChangeEvent(
        this.EventType.VISIBILITYCHANGE, opt_visibility, currentVisibility));
  }
  return this;
};


/**
 * Returns the visibility of this object.
 * @publish
 *
 * @export
 * @param {boolean=} opt_effective Indicates that the effective visibility is requested. In this case the visibility of
 *        all possible ancestor groups is also taken into account
 * @return {boolean}
 */
H.map.Object.prototype.getVisibility = function(opt_effective) {
  'use strict';
  var object = this,
      visibility;
  while ((visibility = object.visibility_) && opt_effective && (object = object.parentGroup)) {
    // The body is here to satisfy CC and gjslint
  }
  return visibility;
};


//################################################  Z-INDEX  ###########################################################
/**
 * The z-index of this object, default is {@code 0}
 * @private
 *
 * @type {number|undefined}
 */
H.map.Object.prototype.zIndex_ = H.lang.UNDEF;


/**
 * Returns the z-index of this object.
 * @publish
 *
 * @export
 * @return {number|undefined}
 */
H.map.Object.prototype.getZIndex = function() {
  'use strict';
  return this.zIndex_;
};


/**
 * To set the-index of this object.
 * @publish
 *
 * @export
 * @param {number|undefined} zIndex
 * @return {H.map.Object} returns this object
 */
H.map.Object.prototype.setZIndex = function(zIndex) {
  'use strict';
  var currentZIndex = this.zIndex_;
  if (zIndex !== currentZIndex) {
    this.resetZInfo();
    this.zIndex_ = zIndex;
    this.invalidate(H.map.provider.Invalidations.Flag.Z_ORDER);
    // TODO "zindexchange" event deprecated since 3.0.7.0
    this.dispatchEvent(new H.util.ChangeEvent(
        this.EventType.ZINDEXCHANGE, zIndex, currentZIndex));
  }
  return this;
};


//#################################################  Z-INFO  ###########################################################
/**
 * An Array that collects information for z-ordering.
 * At index 0 it stores a boolean flag to indicate whether at least one of the objects on ancestor-or-self axis
 * has a defined zIndex {@code 1} - otherwise {@code 0}.
 * At index [1 ... n] it stores alternating the zIndex and the siblingIndex of each object on ancestor-or-self axis,
 * starting with the root ancestor
 * Must NOT be published, it's used API interally only by Group#resetZInfo!
 *
 * @protected
 * @type {Array.<number>|undefined}
 */
H.map.Object.prototype.zInfo = H.lang.UNDEF;


/**
 * Returns the z-info of this object.
 * Must NOT be published, it's used API interally only!
 *
 * @return {Array.<number>} see H.map.Object.prototype.zInfo for more informations
 */
H.map.Object.prototype.getZInfo = function() {
  'use strict';
  var zInfo = this.zInfo,
      parentGroup,
      isZIndexed,
      zIndex,
      siblingIndex;
  if (!zInfo) {
    isZIndexed = (zIndex = this.zIndex_) !== H.lang.UNDEF;
    if (parentGroup = this.parentGroup) {
      zInfo = parentGroup.getZInfo().slice();
      zInfo[0] |= isZIndexed;
    } else {
      zInfo = [isZIndexed | 0];
    }
    zInfo.push(
        zIndex || 0,
        // If siblingIndex is undefined (root node) we use the ID as a fall back
        (siblingIndex = this.siblingIndex) !== H.lang.UNDEF ? siblingIndex : this.id_
    );
    this.zInfo = zInfo;
  }
  return zInfo;
};


/**
 * Resets the z-info for this object. We need a method, because Group will override the behavior
 * Must NOT be published, it's used API interally only!
 */
H.map.Object.prototype.resetZInfo = function() {
  // Warning: Group's implementation doesn't call its super. If you change this method align also Group's implementation
  this.zInfo = H.lang.UNDEF;
};


//#################################################  Z-ORDER  ##########################################################
/**
 * Compares two zInfos. It's used to sort objects by z-order.
 * Must NOT be published, it's used API interally only by Object.compareZOrder and during rendering!
 *
 * @param {Array.<number>} first The first z-info to compare
 * @param {Array.<number>} second The second z-info to compare
 * @param {boolean} onlyZIndex Indicates to compare only z-indexes but not sibling-indexes
 * @return {number}
 *      If less then {@code 0} the first z-info results to a lower z-order.
 *      If equal {@code 0} booth z-infos result tothe same z-order.
 *      If greater then {@code 0} the first z-info results to a higher z-order.
 */
H.map.Object.compareZInfo = function(first, second, onlyZIndex) {
  'use strict';
  // We compare the zInfos only if onlyZinfo is false or one of the zInfos have set the "isZindexed" flag:
  if (!onlyZIndex || first[0] | second[0]) {
    var length1 = first.length,
        length2 = second.length,
        length = H.math.min(length1, length2),
        difference,
        // We start at index 1 because the "isZIndexed" flag is stored at index 0:
        i = 1,
        step = 1 + onlyZIndex;
    for (; i < length; i += step) {
      // If two zInfo entries are not equal return the difference:
      if (difference = first[i] - second[i]) {
        return difference;
      }
    }
  }
  // No difference were found for zIndexes (respectively sibling-index)
  // If onlyZIndex is true we return "equality", otherwise compare the lengths of the zInfos:
  return onlyZIndex ? 0 : length1 - length2;
};


/**
 * Compares two objects regarding their z-order, useful to sort a list of objects via Array's sort() method
 *
 * @publish
 * @export
 * @param {H.map.Object} first The first object to compare
 * @param {H.map.Object} second The second object to compare
 * @return {number}
 *      If less then {@code 0} the first object has a lower z-order.
 *      If equal {@code 0} booth objects have the same z-order.
 *      If greater then {@code 0} the first object has a higher z-order.
 */
H.map.Object.compareZOrder = function(first, second) {
  'use strict';
  return first.compareZOrder(second);
};


/**
 * An index to use if different object types are compared for z-ordering.
 * Each concrete implementation of a map object has to provide a correct z-order index.
 * The current version of our API uses:
 *  * 0 for Spatial
 *  * 1 for Marker
 *  * 2 for DomMarker
 * @abstract
 * @protected
 * @type {number}
 */
H.map.Object.prototype.zOrderIdx;


/**
 * Compares the rendering z-order of this object with another object.
 * @publish
 * @export
 * @param {H.map.Object} other The other map object to compare with
 * @return {number}
 *      If less then {@code 0} the first object has a lower z-order.
 *      If equal {@code 0} booth objects have the same z-order.
 *      If greater then {@code 0} the first object has a higher z-order.
 */
H.map.Object.prototype.compareZOrder = function(other) {
  'use strict';
  var thisZInfo,
      otherZInfo;
  // If booth objects have the same zOrderIdx we compare the zInfos (iclusive sibling index):
  return this.zOrderIdx - other.zOrderIdx ||
      H.map.Object.compareZInfo(this.getZInfo(), other.getZInfo(), false);
};


//################################################  TREE PROPERTIES  ###################################################
/**
 * A reference to the previous sibling within this object's group
 * or {@code null} if this object is not yet attached to a group or this object is the first child in its group
 * Must NOT be published, it's used API interally only by Group!
 *
 * @type {?H.map.Object}
 */
H.map.Object.prototype.previousSibling = H.lang.NULL;


/**
 * A reference to the next sibling within this object's group
 * or {@code null} if this object is not yet attached to a group or this object is the last child in its group
 * Must NOT be published, it's used API interally only by Group!
 *
 * @type {?H.map.Object}
 */
H.map.Object.prototype.nextSibling = H.lang.NULL;


/**
 * The sibling index of this object.
 * The sibling index might not be identical with the child's index in the children collection of the group but it's
 * assured that each object has an higher siblingIndex than its previous sibling and a lower siblingIndex than
 * its next sibling.
 * Must NOT be published, it's used API interally only by Object and Group!
 *
 * @type {number|undefined}
 */
H.map.Object.prototype.siblingIndex = H.lang.UNDEF;


/**
 * A reference to the parent group of this object or {@code null} if this object is not yet attached to a group
 * Must NOT be published, it's used API interally only by Object and Group!
 *
 * @protected
 * @type {?H.map.Group}
 */
H.map.Object.prototype.parentGroup = H.lang.NULL;


//################################################  TREE RELATIONS  ####################################################
/**
 * Returns the parent group which contains this object or {@code null} if the object is not contained by any group.
 * @publish
 *
 * @export
 * @return {?H.map.Group}
 */
H.map.Object.prototype.getParentGroup = function() {
  'use strict';
  return this.parentGroup;
};


/**
 * To set the parent group which contains this object or {@code null} if it is not contained by any group
 * Must NOT be published, it's used API interally only by Group!
 * @method
 *
 * @protected
 * @param {?H.map.Group} parentGroup
 */
H.map.Object.prototype.setParentGroup = function(parentGroup) {
  // We call the super directly to prevent the additional check in Object's implementation of setParentEventTarget()
  this.parentGroup = parentGroup;
  H.map.Object.superClass_.setParentEventTarget.call(this, parentGroup);
};


/**
 * The root object in which this object is attached or the object itself.
 * @publish
 *
 * @export
 * @return {!H.map.Object}
 */
H.map.Object.prototype.getRootGroup = function() {
  'use strict';
  var object = this,
      parent;
  while (parent = object.parentGroup) {
    object = parent;
  }
  return object;
};


/**
 * Checks whether the given object is an inclusive descendant of this object
 * @publish
 *
 * @export
 * @param {*} object The object that's being compared against.
 * @return {boolean}
 */
H.map.Object.prototype.contains = function(object) {
  'use strict';
  // Warning: Group's implementation doesn't call its super. If you change this method align also Group's implementation
  return this === object;
};


//############################################  PARENT EVENT TARGET  ###################################################
/**
 * @override
 * @export
 */
H.map.Object.prototype.setParentEventTarget = function(parentEventTarget) {
  'use strict';
  // We not allow to set the parent EventTarget on an object which is currently contained by a group!
  if (this.parentGroup) {
    throw new H.lang.IllegalOperationError(this.setParentEventTarget, 'Not allowed for a child of a group');
  }
  H.map.Object.superClass_.setParentEventTarget.call(this, parentEventTarget);
};


//################################################  PROVIDER  ##########################################################
/**
 * Reference to the current provider of this object
 * Must NOT be published, it's used API interally only by Object and Group!
 *
 * @type {?H.map.provider.ObjectProvider}
 */
H.map.Object.prototype.provider = null;


/**
 * The current provider of this object
 * @publish
 *
 * @export
 * @return {?H.map.provider.ObjectProvider} the current provider
 */
H.map.Object.prototype.getProvider = function() {
  'use strict';
  return this.provider;
};


/**
 * Sets the provider of this object.
 * Must NOT be published, it's used API interally only by Group!
 *
 * @throws {H.lang.IllegalOperationError}
 * @param {?H.map.provider.ObjectProvider} newProvider
 * @param {Function=} opt_caller The caller to use for the error message.
 *      If omitted, the providers are NOT checked!
 *      It's usefull for Group#setProvider to prevent unnecessary checks for all descendants.
 * @return {boolean} whether the provider has been changed
 */
H.map.Object.prototype.setProvider = function(newProvider, opt_caller) {
  var oldProvider = this.provider,
      isProviderChange,
      isNotAGroup;

  // Prevent unnecessary checks and modifications:
  if (isProviderChange = oldProvider !== newProvider) {

    // Check involved providers for instance of LocalObjectPovider:
    if (opt_caller &&
        oldProvider && !H.lang.isInstanceOf(oldProvider, H.map.provider.LocalObjectProvider) ||
        newProvider && !H.lang.isInstanceOf(newProvider, H.map.provider.LocalObjectProvider)) {
      throw new H.lang.IllegalOperationError(opt_caller, 'Only LocalObjectProvider allows an object transfers');
    }

    isNotAGroup = this.type !== H.map.Object.Type['GROUP'];

    if (oldProvider) {
      this.invalidate(H.map.provider.Invalidations.Flag.REMOVE);
      if (isNotAGroup) {
        oldProvider.removeObject(this);
      }
    }

    this.invalidations_ = H.lang.NULL;
    this.provider = newProvider;

    if (newProvider) {
      if (isNotAGroup) {
        newProvider.addObject(this);
      }
      this.invalidate(H.map.provider.Invalidations.Flag.ADD);
    }

  }
  return isProviderChange;
};


//##############################################  INVALIDATION  ########################################################
/**
 * The Invalidations of this object
 * @private
 * @type {H.map.provider.Invalidations}
 */
H.map.Object.prototype.invalidations_;


/**
 * Returns the invalidations of this object
 * @publish
 *
 * @export
 * @return {H.map.provider.Invalidations} an invalidation object
 */
H.map.Object.prototype.getInvalidations = function() {
  'use strict';
  return this.invalidations_ || (this.invalidations_ = new H.map.provider.Invalidations());
};


/**
 * This method invalidates this map object.
 * @publish
 *
 * @export
 * @param {H.math.BitMask} flags The flags indicating the types of occurred changes
 * @return {boolean} indicates whether a validtion was executed (only if the object has a provider)
 */
H.map.Object.prototype.invalidate = function(flags) {
  'use strict';
  var provider = this.getProvider(),
      hasProvider;
  if (hasProvider = !!provider) {
    provider.invalidateObject(this, flags);
  }
  return hasProvider;
};


//################################################  DISPOSABLE  ########################################################
/**
 * @override
 */
H.map.Object.prototype.disposeInternal = function() {
  'use strict';

  //First let's remove the object from its parent group:
  if (this.parentGroup) {
    this.parentGroup.removeObject(this);
    //implies the following:
    // this.provider =
    // this.parentGroup =
    // this.previousSibling =
    // this.nextSibling =
    // H.lang.NULL;
  }
  //We will not need invalidations anymore
  delete this.invalidations_;

  //Assuming that EventTarget#disposeInternal will clear all event handlers
  H.map.Object.superClass_.disposeInternal.call(this);
};


//############################################ ARBITRARY DATA  #########################################################
/**
 * This method returns previously stored arbitrary data from this object.
 * @return {*} the previously stored data or {@code undefined} if not data was stored.
 * @export
 * @publish
 */
H.map.Object.prototype.getData = function() {
  'use strict';
  return this.data_;
};


/**
 * This method stores arbitrary data with this map object
 * @param {*} data the data to be stored
 * @return {H.map.Object} returns this map object instance
 * @export
 * @publish
 */
H.map.Object.prototype.setData = function(data) {
  'use strict';
  this.data_ = data;
  return this;
};


//################################################  OPTIONS  ###########################################################
/**
 * Options used to initialize a map object
 * @publish
 *
 * @property {number=} min
 *           The minimum zoom level for which the object is visible, default is {@code -Infinity}
 * @property {number=} max
 *           The maximum zoom level for which the object is visible, default is {@code Infinity}
 * @property {boolean=} visibility
 *           Indicates whether the map object is visible at all, default is {@code true}
 * @property {number=} zIndex
 *           The z-index value of the map object, default is {@code 0}
 * @property {(H.map.provider.Provider|null)=} provider
 *           The provider of this object. This property is only needed if a customized Implementation of ObjectProvider
 *           wants to instantiate an object.
 * @property {*} data
 *           Optional arbitrary data to be stored with this map object. This data can be retrieved by calling
 *           {@code getData}.
 *
 * @typedef {{
 *   min: (number|undefined),
 *   max: (number|undefined),
 *   visibility: (boolean|undefined),
 *   zIndex: (number|undefined),
 *   provider: (H.map.provider.ObjectProvider|null|undefined),
 *   data: *
 * }}
 */
H.map.Object.Options;

