goog.provide('H.map.ViewPort');

goog.require('H.global');
goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.map.IInteraction');
goog.require('H.map.render');
goog.require('H.math');
goog.require('H.math.Point');
goog.require('H.util.Event');
goog.require('H.util.EventTarget');



/**
 * ViewPort object holds information about the HTML element where the map is rendered. It contains information
 * regarding the element (view port) size and triggers events when the element size is changed.
 * @publish
 * @export
 * @implements {H.map.IInteraction}
 * @extends {H.util.EventTarget}
 * @final
 * @constructor
 * @param {Element} element html element were map will be rendered
 * @param {H.map.ViewPort.Options=} opt_options optional configuration parameters
 */
H.map.ViewPort = function(element, opt_options) {
  'use strict';
  var options = opt_options || {},
      el,
      option;

  if (!H.lang.isInstanceOf(element, Element)) {
    throw new H.lang.InvalidArgumentError(H.map.ViewPort, 0, element);
  }

  el = element.ownerDocument.createElement('div'),

  /**
   * Viewport container
   * @private
   */
  this.element_ = element;

  H.map.ViewPort.superClass_.constructor.call(this);

  // Initially we need instances for padding, center, margin and the viewBounds_ entries:
  this['padding'] = new H.math.Rect(0, 0, 0, 0);
  this['center'] = new H.math.Point(0, 0);
  this['margin'] = 0;

  // Initialize the element and size:
  this['element'] = el;
  // reset element styles as they influence dom surface and imprint
  el.style.zIndex = 0;
  el.style.height = el.style.width = '100%';

  //Set to absolute if parent is already relative.
  el.style.position = (element.style.position === 'relative') ? 'absolute' : 'relative';

  this.resize();

  element.appendChild(el);

  // Processing the options:
  if ((option = options['padding']) !== H.lang.UNDEF) {
    this.setPadding(option['top'], option['right'], option['bottom'], option['left']);
  }
  this.setMargin((option = options['margin']) !== H.lang.UNDEF ? option : 0);
  if ((option = options['fixedCenter']) !== H.lang.UNDEF) {
    this.fixedCenter = !!option;
  }

};
goog.inherits(H.map.ViewPort, H.util.EventTarget);


//#################################################  EVENTS  ###########################################################
/**
 * Events fired by ViewPort
 * @enum {string}
 */
H.map.ViewPort.prototype.EventType = {
  UPDATE: 'update',
  SYNC: 'sync'
};


/**
 * Fired when this view-port's properties padding, margin or size changes
 * @publish
 * @event H.map.ViewPort#update
 * @type {H.util.Event}
 */


/**
 * Fired when the rendered map is synchronized with this viewport's properties
 * @publish
 * @event H.map.ViewPort#sync
 * @type {H.util.Event}
 */


//######################################################################################################################
/**
 * See {@link H.map.ViewPort.Options#fixedCenter}  default is {@code true}
 * @type {boolean}
 */
H.map.ViewPort.prototype.fixedCenter = true;


/**
 * This property holds the HTML element, which defines the viewport.
 * @type {Element}
 * @publish
 */
H.map.ViewPort.prototype.element;


/**
 * This property holds this viewport's current width
 * @type {number}
 * @publish
 */
H.map.ViewPort.prototype.width;


/**
 * This property holds this viewport's current height
 * @type {number}
 * @publish
 */
H.map.ViewPort.prototype.height;


/**
 * This property holds this viewport's current margin value
 * @type {number}
 * @publish
 */
H.map.ViewPort.prototype.margin;


/**
 * This property holds this viewport's current padding
 * @type {H.map.ViewPort.Padding}
 * @publish
 */
H.map.ViewPort.prototype.padding;


/**
 * This property holds this viewport's current center point
 * @type {H.math.Point}
 * @publish
 */
H.map.ViewPort.prototype.center;


/**
 * This method sets the margin on the viewPort
 * @publish
 * @export
 * @fires H.map.ViewPort#marginchange
 * @param {number} margin margin which is used to fetch map data
 */
H.map.ViewPort.prototype.setMargin = function(margin) {
  'use strict';
  if (this['margin'] !== +margin) {
    if (H.lang.isNaN(this['margin'] = +margin)) {
      throw new H.lang.InvalidArgumentError(this.setMargin, 0, margin);
    }
    this.update_();
    this.dispatchEvent(this.EventType.UPDATE);
  }
};


/**
 * This method sets the padding on the viewport. Padding will result in shifted
 * map center which will be the visual center of the padded area.
 * @publish
 * @export
 * @fires H.map.ViewPort#paddingchange
 * @param {number} top padding from the top
 * @param {number} right padding from the right
 * @param {number} bottom padding from the bottom
 * @param {number} left padding from the left
 */
H.map.ViewPort.prototype.setPadding = function(top, right, bottom, left) {
  'use strict';
  var padding = this.padding;
  if (padding['top'] !== top || padding['right'] !== right || padding['bottom'] !== bottom ||
      padding['left'] !== left) {
    if (H.lang.isNaN(top + right + bottom + left)) {
      throw new H.lang.InvalidArgumentError(this.setPadding);
    }
    padding['top'] = top;
    padding['right'] = right;
    padding['bottom'] = bottom;
    padding['left'] = left;
    this.update_();
    this.dispatchEvent(this.EventType.UPDATE);
  }
};


/**
 * Updates the element size and the center property (by taking the size and the padding into account)
 * @private
 */
H.map.ViewPort.prototype.update_ = function() {
  'use strict';
  var width = this['width'],
      height = this['height'],
      padding = this['padding'];
  this['center'].set(
      (width + padding['left'] - padding['right']) / 2 << 0,
      (height + padding['top'] - padding['bottom']) / 2 << 0);
};


/**
 * @private
 * @type {H.math.ISize}
 */
H.map.ViewPort.prototype.oldSize_;


/**
 * This method updates size of the viewport according to container size. It must be called whenever the HTML element
 * changed size in order to update the map's viewport values.
 * @publish
 * @export
 */
H.map.ViewPort.prototype.resize = function() {
  'use strict';
  var width = this.element_.offsetWidth,
      height = this.element_.offsetHeight;
  if (this['width'] !== width || this['height'] !== height) {
    this.oldSize_ = new H.math.Size(this['width'], this['height']);
    this['width'] = width;
    this['height'] = height;
    this.update_();
    this.dispatchEvent(this.EventType.UPDATE);
  }
};


/**
 * A callback used by an engine to notify the view port that the engine is in sync after an "update" event was fired
 * @fires H.map.ViewPort#sync
 * @param {H.math.ISize} size
 */
H.map.ViewPort.prototype.synchronize = function(size) {
  this.dispatchEvent(new H.map.ChangeEvent(
      this.EventType.SYNC, this.oldSize_, size, H.map.ChangeEvent.prototype['SIZE']));
};


/**
 * Disposes the object.
 * @protected
 */
H.map.ViewPort.prototype.disposeInternal = function() {
  'use strict';
  H.global.clearTimeout(this.checkHandler_);
  this.checkHandler_ = null;
  this.element_.removeChild(this['element']);
  this.element_ = null;
  this['element'] = null;
  H.map.ViewPort.superClass_.disposeInternal.call(this);
};


/**
 * This method starts the interaction with the view port. Should be called every time when new
 * interaction is started i.e mouse grab, or touch start.
 * @param {number} modifiers a bitmask which specifies what operations should performed during every interaction
 * @param {H.util.kinetics.IKinetics=} opt_kinetics specifies kinetic move at the end of interaction
 * @export
 * @abstract
 * @method
 * @publish
 */
H.map.ViewPort.prototype.startInteraction = goog.abstractMethod; // function(modifiers, opt_kinetics) { };


/**
 * This method resolves direct screen (view port) interaction. This function will modify the current
 * view according to values passed in.
 * @param {number} x viewport x coordinate
 * @param {number} y viewport y coordinate
 * @param {number=} opt_bx x coordinate for second pointer/touch if present
 * @param {number=} opt_by y coordinate for secong pointer/touch if present
 * @param {number=} opt_timestamp known timestamp which should be passed
 * @export
 * @abstract
 * @method
 * @publish
 */
H.map.ViewPort.prototype.interaction = goog.abstractMethod; // function(x, y, opt_bx, opt_by, opt_timestamp) {


/**
 * This method ends interaction and applies kinetic movement if defined by
 * using startInteraction method
 * @param {boolean=} opt_preventKinetics if set to true will prevent kinetics behaviour at the end of interaction
 * @export
 * @abstract
 * @method
 * @publish
 */
H.map.ViewPort.prototype.endInteraction = goog.abstractMethod; // function(opt_preventKinetics) {


/**
 * Represents viewport padding definition.
 * @typedef {{
 *   top: number,
 *   right: number,
 *   bottom: number,
 *   left: number
 * }}
 * @property {number} top the padding on the top edge (in pixels)
 * @property {number} right the padding on the right edge (in pixels)
 * @property {number} bottom the padding on the bottom edge (in pixels)
 * @property {number} left the padding on the left edge (in pixels)
 * @publish
 */
H.map.ViewPort.Padding;


/**
 * Options which may be used to initialize new ViewPort instance
 * @publish
 *
 * @typedef {{
 *   margin: (number|undefined),
 *   padding: (H.map.ViewPort.Padding|undefined),
 *   fixedCenter: (boolean|undefined)
 * }}
 * @property {number=} margin
 *           The size in pixel of the supplemental area to render for each side of the map
 * @property {H.map.ViewPort.Padding=} padding
 *           The padding in pixels for each side of the map
 * @property {boolean=} fixedCenter
 *           Indicates whether the center of the map should remain unchanged if the viewport's
 *           size or or padding has been changed, default is {@code true}
 */
H.map.ViewPort.Options;
