goog.provide('H.math.Rect');

goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.math.Point');



/**
 * Class defines a rectangle in 2-dimensional geometric space. It is used to represent the area in projected space.
 * @publish
 * @export
 * @final
 * @constructor
 * @param {number} left The rectangle's left edge x value
 * @param {number} top The rectangle's top edge y value
 * @param {number} right The rectangle's right edge x value
 * @param {number} bottom The rectangle's bottom edge y value
 */
H.math.Rect = function(left, top, right, bottom) {
  'use strict';
  /*
  var invalidArgNr =
      H.lang.isNaN(this['left'] = +left) ? 1 :
      H.lang.isNaN(this['top'] = +top) ? 2 :
      H.lang.isNaN(this['right'] = +right) ? 3 :
      H.lang.isNaN(this['bottom'] = +bottom) ? 4 :
      0;

  if (invalidArgNr--) {
    throw new H.lang.InvalidArgumentError(opt_caller || this.set, invalidArgNr, arguments[invalidArgNr]);
  }
  */

  /**
   * The rectangle's left edge x value
   * @publish
   * @type {number}
   * @name {H.math.Point#left}
   */
  this['left'] = +left;

  /**
   * The rectangle's top edge y value
   * @publish
   * @type {number}
   * @name {H.math.Point#top}
   */
  this['top'] = +top;

  /**
   * The rectangle's right edge x value
   * @publish
   * @type {number}
   * @name {H.math.Point#right}
   */
  this['right'] = +right;

  /**
   * The rectangle's bottom edge y value
   * @publish
   * @type {number}
   * @name {H.math.Point#bottom}
   */
  this['bottom'] = +bottom;
};


/**
 * To set all values of the rectangle's edges
 * @export
 * @publish
 * @method
 * @param {number} left The rectangle's left edge x value
 * @param {number} top The rectangle's top edge y value
 * @param {number} right The rectangle's right edge x value
 * @param {number} bottom The rectangle's bottom edge y value
 */
H.math.Rect.prototype.set = H.math.Rect; // we can re-use the constructor function to set the values


/**
 * @private
 * @type {H.math.Point}
 */
H.math.Rect.prototype.topLeft_;


/**
 * To get the rectangle's top-left vertex
 * @export
 * @publish
 * @return {H.math.Point}
 */
H.math.Rect.prototype.getTopLeft = function() {
  'use strict';
  return this.topLeft_ || (this.topLeft_ = new H.math.Point(this['left'], this['top']));
};


/**
 * @private
 * @type {H.math.Point}
 */
H.math.Rect.prototype.bottomRight_;


/**
 * To get the rectangle's bottom-right vertex
 * @export
 * @publish
 * @return {H.math.Point}
 */
H.math.Rect.prototype.getBottomRight = function() {
  'use strict';
  return this.bottomRight_ || (this.bottomRight_ = new H.math.Point(this['right'], this['bottom']));
};


/**
 * Method checks if provided coordinates lie within rectangle.
 * @export
 * @publish
 * @param {number} x x-coordinate to check
 * @param {number} y y-coordinate to check
 * @return {boolean} returns true if coordinates lie within rectangle, if parameters are isNaN returns false
 */
H.math.Rect.prototype.containsXY = function(x, y) {
  'use strict';
  return this['left'] <= x && this['right'] >= x && this['top'] <= y && this['bottom'] >= y;
};


/**
 * To create a rectangle from a top-left and bottom-right point pair.
 * @publish
 * @export
 * @param {H.math.IPoint} topLeft the top-left vertex of the rectanle
 * @param {H.math.IPoint} bottomRight the bottom-right vertex of the rectanle
 * @return {H.math.Rect} returns the rectangular area defined by the top-left and bottom-right vertices
 */
H.math.Rect.fromPoints = function(topLeft, bottomRight) {
  'use strict';
  return new H.math.Rect(topLeft['x'], topLeft['y'], bottomRight['x'], bottomRight['y']);
};


/**
 * To clone a rectangle
 * @publish
 * @export
 * @return {H.math.Rect}
 */
H.math.Rect.prototype.clone = function() {
  'use strict';
  return new H.math.Rect(this['left'], this['top'], this['right'], this['bottom']);
};


