goog.provide('H.util.SequentialQueue');

goog.require('H.util.AsyncQueue');



/**
 * This class represents a task queue which processes work items in sequential mode. Each
 * cycle the queue will process as many items as possible within the allotted time frame
 * (maxTime).
 *
 * Note: The sequential queue expects each work item to fit into the provided time frame.
 * Since it runs at least one task each cycle the processing of the task should be small
 * enough to be completed (optimistically) within a cycle. The queue behaves optimistic
 * meaning that it will first run a task and check if it has exceeded the time frame.
 *
 * @export
 * @extends {H.util.AsyncQueue}
 * @final
 * @constructor
 * @param {number} maxTime the maximum time tasks may consume per cycle in milliseconds
 * @param {function(H.util.AsyncQueue.Task, *)} processFn the function which implements the acutal processing of
 *  tasks
 */
H.util.SequentialQueue = function(maxTime, processFn) {
  H.util.SequentialQueue.superClass_.constructor.call(
      this,
      new H.util.SequentialQueue.Mode(maxTime),
      processFn);
};
goog.inherits(H.util.SequentialQueue, H.util.AsyncQueue);



/**
 * @constructor
 * @implements {H.util.AsyncQueue.Mode}
 * @param {number} maxTime the maximum time tasks may consume per cycle in milliseconds
 */
H.util.SequentialQueue.Mode = function(maxTime) {
  this.setMaxTime(maxTime);

  /**
   * @private
   * @type {number}
   */
  this.startTime_ = H.util.shims.now();
};


/**
 * Sets the maximum processing time per cycle in milliseconds
 * @param {number} maxTime the maximum time tasks may consume per cycle in milliseconds
 */
H.util.SequentialQueue.Mode.prototype.setMaxTime = function(maxTime) {
  if (isNaN(maxTime) || maxTime < 0) {
    throw new H.lang.InvalidArgumentError(H.util.SequentialQueue, 1,
        'Argument "maxTime" must be a positive number');
  } else {
    /**
     * @private
     * @type {number}
     */
    this.maxTime_ = maxTime;
  }
};


/**
 * @override
 */
H.util.SequentialQueue.Mode.prototype.next = function(queuedItems, processingItems) {
  //console.log(H.util.shims.now() - this.startTime_);
  if (queuedItems.length && ((H.util.shims.now() - this.startTime_) < this.maxTime_)) {
    var item = queuedItems.shift();

    return item;
  } else {
    return null;
  }
};


/**
 * @override
 */
H.util.SequentialQueue.Mode.prototype.cycle = function() {
  this.startTime_ = H.util.shims.now();
};
