goog.provide('H.geo.PixelProjection');

goog.require('H.geo.IProjection');
goog.require('H.geo.Point');
goog.require('H.geo.mercator');
goog.require('H.lang');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.math');
goog.require('H.math.Point');



/**
 * PixelProjection is used to transform pixel world coordinates at certain scale (zoom level)
 * to geographical coordinates and vice-versa. By default it uses mercator geo projection
 * to transform geo points into the 2d plane map point which are adjusted to current scale.
 *
 * @export
 *
 * @constructor
 * @param {H.geo.IProjection=} opt_projection default is spherical mercator {@link H.geo.mercator}
 * @param {number=} opt_sizeAtLevelZero world size in pixels at zoom level 0, default is {@code 256}
 * @publish
 */
H.geo.PixelProjection = function(opt_projection, opt_sizeAtLevelZero) {
  'use strict';

  /**
   * Geographical projection that backs given PixelProjection.
   * @publish
   * @name H.geo.PixelProjection#projection
   * @type {H.geo.IProjection}
   */
  this['projection'] = opt_projection || H.geo.mercator;

  /**
   *
   * current zoom scale
   * @private
   */
  this.zoom_ = 0;

  /**
   * Default exponent. Value shows exponent that is used to fit the world in single tile
   * for the given tile size, on presumption that 2^0 covers the entire world over equator.
   * @name H.geo.PixelProjection#exp
   * @type {number}
   *
   */
  // TODO: should this concept be reconsidered?
  this['exp'] = H.math.log(opt_sizeAtLevelZero || 256) / H.math.LN2;

  /**
   * Tile exponent used world width/height calculation
   * @private
   */
  this.tileExponent_ = this['exp'];
  this.updateSize_();

  /**
   * X-offset in projection relative to top left corner.
   * @publish
   * @name H.geo.PixelProjection#x
   * @type {number}
   */
  this['x'] = 0;

  /**
   * Y-offset in projection relative to top left corner.
   * @publish
   * @name H.geo.PixelProjection#y
   * @type {number}
   */
  this['y'] = 0;
};


/**
 * Maximum world size exponent (30 bits)
 * @type {number}
 * @protected
 * @const
 */
H.geo.PixelProjection.MAX_WORLD_SIZE = 30;


/**
 * This method updates scale exponent for the pixel projection.
 * @export
 *
 * @param {number} zoom
 * @publish
 */
H.geo.PixelProjection.prototype.rescale = function(zoom) {
  'use strict';

  if (H.lang.isNaN(zoom)) {
    throw new H.lang.InvalidArgumentError(this.rescale, 0, zoom);
  }
  var pointX = this['x'] / this['w'],
      pointY = this['y'] / this['h'];

  this.zoom_ = zoom;
  this.tileExponent_ = this['exp'] + zoom;
  this.updateSize_();
  this['x'] = pointX * this['w'];
  this['y'] = pointY * this['h'];
};


/**
 * This method updates world width and height.
 * @private
 */
H.geo.PixelProjection.prototype.updateSize_ = function() {
  'use strict';
  //correct tileExponent if maximum world size is exceeded
  if (this.tileExponent_ > H.geo.PixelProjection.MAX_WORLD_SIZE) {
    this.tileExponent_ = H.geo.PixelProjection.MAX_WORLD_SIZE;
  }

  /**
   * Width of the world in pixels
   * @publish
   * @name H.geo.PixelProjection#w
   * @type {number}
   */
  this['w'] = H.math.pow(2, this.tileExponent_);

  /**
   * Height of the world in pixels
   * @publish
   * @name H.geo.PixelProjection#h
   * @type {number}
   */
  this['h'] = H.math.pow(2, this.tileExponent_);
};


/**
 * This method returns current zoom scale factor previously set by
 * {@link H.geo.PixelProjection#rescale} function.
 * @export
 *
 * @return {number}
 * @publish
 */
H.geo.PixelProjection.prototype.getZoomScale = function() {
  'use strict';
  return this.zoom_ || 0;
};


/**
 * This method translates geo point to pixel coordinates at given zoom level.
 * @export
 *
 * @param {H.geo.IPoint} geoPoint
 * @param {H.math.IPoint=} opt_out an optional point to store the result
 * @return {H.math.IPoint}
 * @publish
 */
H.geo.PixelProjection.prototype.geoToPixel = function(geoPoint, opt_out) {
  'use strict';
  var px = this['projection'].latLngToPoint(geoPoint['lat'], geoPoint['lng'], opt_out);
  px['x'] = px['x'] * this['w'] - this['x'];
  px['y'] = px['y'] * this['h'] - this['y'];
  return px;
};


/**
 * This method translates a pixel's point to the according geo point.
 * @export
 *
 * @param {H.math.Point} point
 * @param {H.geo.IPoint=} opt_out an optional point to store the result
 * @return {H.geo.IPoint}
 * @publish
 */
H.geo.PixelProjection.prototype.pixelToGeo = function(point, opt_out) {
  'use strict';

  return this['projection'].xyToGeo(
      (point['x'] + this['x']) / this['w'],
      (point['y'] + this['y']) / this['h'],
      opt_out);
};


/**
 * This method translates pixel's x and y coordinates to the according geo point.
 * @export
 *
 * @param {number} x
 * @param {number} y
 * @param {H.geo.Point=} opt_out an optional point to store the result
 * @return {H.geo.Point}
 * @publish
 */
H.geo.PixelProjection.prototype.xyToGeo = function(x, y, opt_out) {
  'use strict';

  return this['projection'].xyToGeo(
      (x + this['x']) / this['w'],
      (y + this['y']) / this['h'],
      opt_out);
};


/**
 * This method translates geographical coordinates (latitude, longitude)
 * supplied by the caller.
 * This method accepts longitudes outside of the normal longitude range.
 * into a point defined in terms of pixel coordinates.
 * @param {number} latitude The latitude to translate
 * @param {number} longitude The longitude to translate
 * @param {H.math.IPoint=} opt_out an optional point to store the result
 * @return {H.math.Point} A point object containing x and y
 *    coordinates (in pixels)
 * @export
 * @publish
 */
H.geo.PixelProjection.prototype.latLngToPixel = function(latitude, longitude, opt_out) {
  'use strict';
  var px = this['projection'].latLngToPoint(latitude, longitude, opt_out);
  px['x'] = px['x'] * this['w'] - this['x'];
  px['y'] = px['y'] * this['h'] - this['y'];
  return px;
};


/**
 * Method translates provided map point to world pixel coordinates relative
 * to current projection offset.
 * @param {H.math.IPoint} point
 * @return {H.math.Point} pixel coordinate
 * @export
 * @publish
 */
H.geo.PixelProjection.prototype.pointToPixel = function(point) {
  'use strict';
  return new H.math.Point(point['x'] * this['w'] - this['x'],
      point['y'] * this['h'] - this['y']);
};

