goog.provide('H.util.QuadTree');

//#############################################  Q U A D T R E E   #####################################################



/**
 * A quad tree is data structure to do space partitioning,
 * ray and square based intersecting lookup. The space spanned by the tree is always square
 * shaped by a given axis radius.
 *
 * NOTE: Be aware that y coordinate is growing from top to bottom!
 *
 * @final
 * @constructor
 * @param {?number} depth To limit the depth levels of the tree. Default is 10 if the value is a falsy.
 * @param {?number} radiusX The root-quad's half size on x-axis. Default is 1 if the value is a falsy.
 * @param {?number} radiusY The root-quad's half size on y-axis. Default is 1 if the value is a falsy.
 * @param {?number} centerX The x coordinate of the root-quad's center. Default is 0 if the value is a falsy.
 * @param {?number} centerY The y coordinate of the root-quad's center. Default is 0 if the value is a falsy.
 */
H.util.QuadTree = function(depth, radiusX, radiusY, centerX, centerY) {
  'use strict';

  /**
   * Holds a depth limit for the quad tree.
   * @private
   * @type {number}
   */
  this.depth_ = depth || 10;

  /**
   * Holds the root quad of the quad tree.
   * @private
   * @type {?H.util.QuadTree.Quad_}
   */
  this.root_ = null; // Will be initial set by flush()

  /**
   * Holds a hash table of extreme values for min/max X and Y
   * @private
   * @type {?H.util.QuadTree.Extremes}
   */
  this.extremes_ = null; // Will be set/used by getExtremes()

  /**
   * Holds a reference to the first quad which holds more then one sub-quad or one or more entries. Using the head_
   * instead of root_ optimizes read operations because during the intersection operation it ignores all parent quads of
   * the head_ but starts the directly on the head_ quad.
   * @private
   * @type {?H.util.QuadTree.Quad_}
   */
  this.head_ = null; // Will be initial set by flush()

  /**
   * Holds the x component of the root quad's center
   * @private
   * @type {number}
   */
  this.centerX_ = centerX || 0;

  /**
   * Holds the y component of the root quad's center.
   * @private
   * @type {number}
   */
  this.centerY_ = centerY || 0;

  /**
   * Holds the starting axis radius of the quad tree in x direction.
   * @private
   * @type {number}
   */
  this.radiusX_ = radiusX || 1;

  /**
  * Holds the starting axis radius of the quad tree in y direction.
  * @private
  * @type {number}
  */
  this.radiusY_ = radiusY || 1;

  // Invoke flush() to set up internal state:
  this.flush();

};


/**
 * A static helper method to determine the extreme values for the given quad candidates for each aspect (top, right,
 *   bottom, left)
 * @private
 * @param {H.util.QuadTree.Candidates_} candidates The quad candidates for all aspects
 * @param {H.util.QuadTree.Extremes} extremes The current extreme values for each aspect
 * @return {H.util.QuadTree.Extremes} The determined extreme values or null if the tree is empty
 */
H.util.QuadTree.getExtremes_ = function(candidates, extremes) {
  'use strict';
  var seekingMin,
      aspectCandidates,
      aspectCandidate,
      entries,
      entry,
      subCandidates,
      extreme,
      value,
      quad,
      subCandidatesFound = 0,
      i,
      j,
      aspect = 4,
      minMaxKey;
  // Iterate over all aspects:
  while (aspect--) {
    // Shift aspects (0...3) to MIN/MAX (4...7):
    minMaxKey = aspect + 4;
    subCandidates = [];
    seekingMin = +!(aspect % 3);
    extreme = extremes[aspect];
    i = (aspectCandidates = candidates[aspect]).length;
    // Iterate over all candidate quads for the current aspect:
    while (i--) {
      aspectCandidate = aspectCandidates[i];
      // Detect the extreme values in entries:
      if (entries = aspectCandidate.entries_) {
        j = entries.length;
        // Iterate over all entries for the current candidate quad:
        while (j--) {
          // Do we have a new extreme value? Assign it!
          if (((value = entries[j][aspect]) > extreme) - seekingMin) {
            extreme = value;
          }
        }
      }
      // Find all sub quads which are candidates:
      j = 4;
      while (j--) {
        if (quad = aspectCandidate[j]) {
          // Do we look for min extremes:
          if (seekingMin) {
            quad[minMaxKey] < extreme && subCandidates.push(quad);
          } else
            // Otherwise we look for max extremes:
            quad[minMaxKey] > extreme && subCandidates.push(quad);
        }
      }
    }
    // Memorize if at least one of the subCandidates lists has a entry:
    subCandidatesFound += subCandidates.length;
    // Save the (potentially new) extreme value for the current aspect:
    extremes[aspect] = extreme;
    // Save the (potentially) sub candidates for the current aspect:
    candidates[aspect] = subCandidates;
  }
  if (subCandidatesFound) {
    // RECURSION!
    extremes = H.util.QuadTree.getExtremes_(candidates, extremes);
  }
  return extremes;
};


/**
 * This method determines the extreme values of all stored entries.
 * @return {?H.util.QuadTree.Extremes} The determined extreme values or null if the tree is empty
 */
H.util.QuadTree.prototype.getExtremes = function() {
  'use strict';
  var head,
      candidates,
      extremes;
  if (!(extremes = this.extremes_)) {
    head = this.head_;
    // Tree is not empty? Determine the extremes!
    if (head.entries_ || head[0/*TL*/] || head[1/*TR*/] || head[2/*BL*/] || head[3/*BR*/]) {
      candidates = [head];
      extremes = H.util.QuadTree.getExtremes_(
          // Use for all 4 aspects the head quad as a single candidate:
          [candidates, candidates, candidates, candidates],
          // Use the opposite values of [MIN_Y, MAX_X, MAX_Y, MIN_X] as initial extremes:
          [
            head[6/*MAX_Y*/],
            head[7/*MIN_X*/],
            head[4/*MIN_Y*/],
            head[5/*MAX_X*/]
          ]);
      this.extremes_ = extremes;
    }
  }
  return extremes;
};


/**
 * This method removes all entries from the quad tree.
 */
H.util.QuadTree.prototype.flush = function() {
  'use strict';
  this.head_ = this.root_ = new H.util.QuadTree.Quad_(
      null,
      0,
      this.centerX_ - this.radiusX_,
      this.centerY_ - this.radiusY_,
      this.centerX_ + this.radiusX_,
      this.centerY_ + this.radiusY_);
  this.extremes_ = null;
};


/**
 * This method removes an entry from the tree.
 * @param {H.util.QuadTree.Entry} entry The quad tree entry to be removed.
 * @return {boolean} whether the given entry could be found and removed or not
 */
H.util.QuadTree.prototype.remove = function(entry) {
  'use strict';
  var result;
  if (result = entry.quad_.removeEntry_(entry, this)) {
    this.adjustExtremes_(entry, false);
  }
  return result;
};


/**
 * Adds or subtracts the given extremes from the extremes of the tree.
 * @param {H.util.QuadTree.Extremes} extremes
 * @param {boolean=} opt_wasAdded A flag to specify if the extremes should be added (true) or subtracted (false)
 * @private
 */
H.util.QuadTree.prototype.adjustExtremes_ = function(extremes, opt_wasAdded) {
  'use strict';
  var treeExtremes,
      aspect,
      isExtreme = false;
  if (treeExtremes = this.extremes_) {
    aspect = 4;
    while (aspect--) {
      if (isExtreme = aspect % 3 ?
          extremes[aspect] >= treeExtremes[aspect] :
          extremes[aspect] <= treeExtremes[aspect]
      ) {
        if (opt_wasAdded) {
          // Replace the tree extreme for this aspect with the according aspect from the given extremes
          treeExtremes[aspect] = extremes[aspect];
        } else { // was removed
          // TODO Delete only single aspects of the extremes instead of reset all.
          //      That would also cause changes for getExtremes_() algorithm
          this.extremes_ = null;
          break;
        }
      }
    }
  }
};


/**
 * Creates and inserts an entry into the tree for the given coordinates (recursively if necessary).
 * @private
 * @param {H.util.QuadTree.Quad_} quad The current quad of the tree.
 * @param {number} minX The smaller x The component of the coordinates.
 * @param {number} minY The smaller y The component of the coordinates.
 * @param {number} maxX The bigger x The component of the coordinates.
 * @param {number} maxY The bigger y The component of the coordinates.
 * @param {number} remainingDepth The number of remaining depth levels to fill the tree.
 * @return {H.util.QuadTree.Entry} The created entry
 */
H.util.QuadTree.prototype.insert_ = function(quad, minX, minY, maxX, maxY, remainingDepth) {
  'use strict';
  var entry,
      qMidX = quad[8/*MID_X*/],
      qMidY = quad[9/*MID_Y*/];

  // Check if the entry could fit into a sub quad:
  if (
      // Maximum level not reached?
      remainingDepth &&
      // Booth coordinates fit into left or right half? (remember the quad address bit for right):
      (maxX < qMidX || maxX < quad[5/*MAX_X*/] && minX >= qMidX) &&
      // Booth coordinates fit into top or bottom half? (remember the quad address bit for bottom):
      (maxY < qMidY || maxY < quad[6/*MAX_Y*/] && minY >= qMidY)
  ) {
    // RECURSION:
    entry = this.insert_(
        quad.getSubQuad_(
            // Compute the address of the sub quad (BOTTOM + RIGHT):
            (minY >= qMidY ? 2/*BOTTOM*/ : 0) | minX >= qMidX /*RIGHT*/
        ),
        minX, minY, maxX, maxY, remainingDepth - 1);
  }
  else {
    // Otherwise:
    entry = quad.addNewEntry_(minX, minY, maxX, maxY);
    this.adjustExtremes_(entry, true);
    this.adjustHead_(quad);
  }

  return entry;
};


/**
 * Inserts an entry (line ore square - given by min and max pair values for x and y) into the tree.
 * @param {number} minX The minimum x component.
 * @param {number} minY The minimum y component.
 * @param {number} maxX The maximum x component.
 * @param {number} maxY The maximum y component.
 * @return {H.util.QuadTree.Entry} The created quad tree entry.
 */
H.util.QuadTree.prototype.insertSorted = function(minX, minY, maxX, maxY) {
  'use strict';
  return this.insert_(this.root_, minX, minY, maxX, maxY, this.depth_);
};


/**
 * This method inserts an entry (line ore square - given by two spanning coordinate pairs) into the tree.
 * @param {number} x1 The x component of the first coordinate.
 * @param {number} y1 The y component of the first coordinate.
 * @param {number} x2 The x component of the second coordinate.
 * @param {number} y2 The y component of the second coordinate.
 * @return {H.util.QuadTree.Entry} The created quad tree entry.
 */
H.util.QuadTree.prototype.insert = function(x1, y1, x2, y2) {
  'use strict';
  var minX,
      minY,
      maxX,
      maxY;
  // Sorting the x and y arguments:
  if (x1 < x2) {
    minX = x1;
    maxX = x2;
  } else {
    minX = x2;
    maxX = x1;
  }
  if (y1 < y2) {
    minY = y1;
    maxY = y2;
  } else {
    minY = y2;
    maxY = y1;
  }
  // Now we can call insertSorted...
  return this.insertSorted(minX, minY, maxX, maxY);
};


/**
 * When item is inserted into tree shortcut to deepest parent quadrant of all entries is computed
 * and set as new head. Head is using for intersection, it is than not needed to traverse from
 * root to head quad. We are updating head when we remove some entry in that way: If we are deleting
 * quad because it doesn't have any entry, and this quad is head than we shift head pointer to quad
 * parent.
 * @private
 * @param {H.util.QuadTree.Quad_=} opt_quad The new added quad
 */
H.util.QuadTree.prototype.adjustHead_ = function(opt_quad) {
  'use strict';
  var head = this.head_,
      cur;
  // Invoked by insert_(), head was not changed during insert-operation.
  // Adjust the head to a quad on self/ancestor axes which has entries or more than one sub-quad:
  if (opt_quad) {
    cur = opt_quad;
    if (cur !== head) {
      head = cur;
      while (cur = cur.parent_) {
        // The quad has entries or less than 3 empty sub-quad slots?
        if (cur.entries_ || (!cur[0] + !cur[1] + !cur[2] + !cur[3] < 3)) {
          head = cur;
        }
      }
    }
  // Invoked by removeEntry_(), tree is already cleaned up, head_ points to an ancestor of the removed quad
  // which is the root quad or which has entries or sub-quads.
  // Adjust the head to a quad on self/descendant axes which has entries or more than one sub-quad:
  } else {
    // While head has no entries and more than 2 empty sub-quad-slots:
    while (!head.entries_ && (!head[0] + !head[1] + !head[2] + !head[3] > 2)) {
      // Does the head has at least one sub-quad?
      if (cur = head[0] || head[1] || head[2] || head[3]) {
        head = cur;
      } else {
        // We arrived the root quad, break up:
        break;
      }
    }
  }
  this.head_ = head;
};


/**
 * Intersect the tree by given rectangle coordinates.
 * This method is suited mainly for visibility lookup (view port) operations.
 *
 * NOTE: Basically quad tree returns all entries whose bounding boxes are partially intersecting with
 * intersecting rectangle (x1,x2,y1,y2) when intersect flag is set to true. It means that when we have a entry
 * with a big diagonal line and a small intersecting rectangle it is possible to get this entry in the resulting
 * entries list even if it is actually not intersecting this rectangle.
 *
 * Example is below:
 * <pre>
 * .|---|...../
 * .|...|..../.
 * .|---|.../..
 * ......../...
 * ......./....
 * ....../.....
 * ...../......
 * ..../.......
 * </pre>
 *
 * NOTE2: Be aware that y coordinate is growing from top to bottom! It means that y1 component must be
 * smaller or equal to y2 otherwise nothing will be found hence the result will be an empty array.
 *
 * @param {number} left The left edge of the rectangle.
 * @param {number} top The top edge of the rectangle.
 * @param {number} right The right edge of the rectangle.
 * @param {number} bottom The bottom edge of the rectangle.
 * @return {Array.<H.util.QuadTree.Entry>} The matching entries of the quad tree.
 */
H.util.QuadTree.prototype.intersect = function(left, top, right, bottom) {
  'use strict';
  var result = [];
  this.intersect_(this.head_, result, left, top, right, bottom);
  return result;
};


/**
 * Intersect the tree by a given rectangle.
 * @private
 * @param {H.util.QuadTree.Quad_} quad The current quad of the tree to use for intersection.
 * @param {Array.<H.util.QuadTree.Entry>} result A list to push encountered entries into.
 * @param {number} left The left (minimum x) value of the rectangle to intersect with
 * @param {number} top The top (minimum y) value of the rectangle to intersect with
 * @param {number} right The right (maximum x) value of the rectangle to intersect with
 * @param {number} bottom The bottom (maximum y) value of the rectangle to intersect with
 */
H.util.QuadTree.prototype.intersect_ = function(quad, result, left, top, right, bottom) {
  'use strict';
  var i,
      entry,
      entries = quad.entries_,
      minX = quad[7/*MIN_X*/],
      midX = quad[8/*MID_X*/],
      maxX = quad[5/*MAX_X*/],
      minY = quad[4/*MIN_Y*/],
      midY = quad[9/*MID_Y*/],
      maxY = quad[6/*MAX_Y*/],
      eMinX,
      eMinY,
      eMaxX,
      eMaxY;
  // Inspect the entries of the quad:
  if (entries) {
    i = entries.length;
    // Check that at least one edge of the given rectangle crosses the quad:
    if (left > minX || right < maxX || top > minY || bottom < maxY) {
      // Find all matching entries:
      while (i--) {
        entry = entries[i];
        eMinX = entry[7/*MIN_X*/ - 4];
        eMinY = entry[4/*MIN_Y*/ - 4];
        eMaxX = entry[5/*MAX_X*/ - 4];
        eMaxY = entry[6/*MAX_Y*/ - 4];

        // Check if the entry is NOT completely outside the given rectangle:
        if (!(eMinX > right || eMaxX < left || eMinY > bottom || eMaxY < top)) {
          result.push(entry);
        }
      }
    }
    // Given rectangle contains the whole quadQuad? Put all entries to the result list!
    else while (i--) result.push(entries[i]);
  }

  // Inspect the sub quads:
  // Rectangle intersects the right sub quads?
  if (right >= midX) {
    // Rectangle intersects the existing top right sub quad? Recursion!
    top <= midY && quad[1/*TR*/] &&
        this.intersect_(quad[1/*TR*/], result, left, top, right, bottom);
    // Rectangle intersects the existing bottom right sub quad? Recursion!
    bottom >= midY && quad[3/*BR*/] &&
        this.intersect_(quad[3/*BR*/], result, left, top, right, bottom);
  }
  // Rectangle intersects the left sub quads?
  if (left <= midX) {
    // Rectangle intersects the existing top left sub quad? Recursion!
    top <= midY && quad[0/*TL*/] &&
        this.intersect_(quad[0/*TL*/], result, left, top, right, bottom);
    // Rectangle intersects the existing bottom left sub quad? Recursion!
    bottom >= midY && quad[2/*BL*/] &&
        this.intersect_(quad[2/*BL*/], result, left, top, right, bottom);
  }
};


//######################################################################################################################


/**
 * To provide minimum and maximum extremes values for x and y
 * @typedef {Object}
 * @property {number} 0 The minimum y (top) value
 * @property {number} 1 The maximum x (right) value
 * @property {number} 2 The maximum y (bottom) value
 * @property {number} 3 The minimum x (left) value
 */
H.util.QuadTree.Extremes;


/**
 * Data structure to keep the quad candidates for all aspects
 * @private
 * @typedef {Object}
 * @property {Array.<H.util.QuadTree.Quad_>} 0 List of all quad candidates for the minimum y aspect (top)
 * @property {Array.<H.util.QuadTree.Quad_>} 1 List of all quad candidates for the maximum x aspect (right)
 * @property {Array.<H.util.QuadTree.Quad_>} 2 List of all quad candidates for the maximum y aspect (bottom)
 * @property {Array.<H.util.QuadTree.Quad_>} 3 List of all quad candidates for the minimum x aspect (left)
 */
H.util.QuadTree.Candidates_;


//######################################################################################################################


/**
 * The keys for minimum and maximum x and y values
 * @enum {number}
 */
H.util.QuadTree.Key = {
  //
  //      (aspect 3)            (aspect 1)
  //       MIN_X 7               MAX_X 5
  //          |       MID_X 8       |
  //          |          |          |
  //          V          V          V
  //          -----------------------  <-- MIN_Y 4
  //          |          |          |     (aspect 0)
  //          |   TL 0   |   TR 1   |
  //          |          |          |
  //          |---------------------|  <-- MID_Y 9
  //          |          |          |
  //          |   BL 2   |   BR 3   |
  //          |          |          |
  //          -----------------------  <-- MAX_Y 6
  //                                      (aspect 2)
  //
  // DON'T change the MIN/MAX values, because the getExtremes algorithm uses computed keys (4...7)!

  TL: 0, // Top Left Sub-Quad
  TR: 1, // Top Right Sub-Quad
  BL: 2, // Bottom Left Sub-Quad
  BR: 3, // Bottom Right Sub-Quad

  MIN_Y: 4,
  MAX_X: 5,
  MAX_Y: 6,
  MIN_X: 7,
  MID_X: 8,
  MID_Y: 9,

  RIGHT: 1,  // bit mask to detect TR and BR quads
  BOTTOM: 2  // bit mask to detect BL and BR quads
};



//#################################################  Q U A D   #########################################################



/**
 * A QuadTree node, holding the tree hierarchy informations (parent-quad and sub-quads) to traverse the QuadTreee.
 * It holds also min/mid/max values to specify the x/y range of the quad for performance optimization.
 * @constructor
 * @private
 * @param {?H.util.QuadTree.Quad_} parent The parent quad, needed if the quad has a parent (no root quad)
 * @param {?number} subQuadAddr The sub-quad-address, TL, TR, BL or BR, needed if the quad has a parent (no root quad)
 * @param {number=} opt_minX The minimum x value of the quad, only needed if the quad has no parent (root quad)
 * @param {number=} opt_minY The minimum y value of the quad, only needed if the quad has no parent (root quad)
 * @param {number=} opt_maxX The maximum x value of the quad, only needed if the quad has no parent (root quad)
 * @param {number=} opt_maxY The maximum y value of the quad, only needed if the quad has no parent (root quad)
 */
H.util.QuadTree.Quad_ = function(parent, subQuadAddr, opt_minX, opt_minY, opt_maxX, opt_maxY) {
  'use strict';
  // Has a parent quad? Compute the min/max/mid values out of the min/mid/max values of the  parent quad!
  if (parent) {

    /**
     * The reference to the parent quad or undefined if it has no no parent quad (root)
     * @type {?H.util.QuadTree.Quad_}
     * @private
     */
    this.parent_ = parent;

    /**
     * The sub-quad address of the quad, one of TL, TR, BL, BR or undefined if it is a root quad (has no parent)
     * @type {?number}
     * @private
     */
    this.subQuadAddr_ = subQuadAddr;

    // It's a sub quad in the right half?
    if (subQuadAddr & H.util.QuadTree.Key.RIGHT) {
      opt_minX = parent[H.util.QuadTree.Key.MID_X];
      opt_maxX = parent[H.util.QuadTree.Key.MAX_X];
    } else {
      opt_minX = parent[H.util.QuadTree.Key.MIN_X];
      opt_maxX = parent[H.util.QuadTree.Key.MID_X];
    }
    // It's a sub quad in the bottom half?
    if (subQuadAddr & H.util.QuadTree.Key.BOTTOM) {
      opt_minY = parent[H.util.QuadTree.Key.MID_Y];
      opt_maxY = parent[H.util.QuadTree.Key.MAX_Y];
    } else {
      opt_minY = parent[H.util.QuadTree.Key.MIN_Y];
      opt_maxY = parent[H.util.QuadTree.Key.MID_Y];
    }
  } else {
    this.parent_ = null;
    this.subQuadAddr_ = null;
  }
  /**
   * The minimum x (left) value (inclusive)
   * @type {number}
   * @private
   */
  this[H.util.QuadTree.Key.MIN_X] = opt_minX;
  /**
   * The maximum x (right) value (exclusive)
   * @type {number}
   * @private
   */
  this[H.util.QuadTree.Key.MAX_X] = opt_maxX;
  /**
   * The middle x (center) value
   * @type {number}
   * @private
   */
  this[H.util.QuadTree.Key.MID_X] = (opt_minX + opt_maxX) / 2;
  /**
   * The minimum y (top) value (inclusive)
   * @type {number}
   * @private
   */
  this[H.util.QuadTree.Key.MIN_Y] = opt_minY;
  /**
   * The maximum y (bottom) value (exclusive)
   * @type {number}
   * @private
   */
  this[H.util.QuadTree.Key.MAX_Y] = opt_maxY;
  /**
   * The middle y (center) value
   * @type {number}
   * @private
   */
  this[H.util.QuadTree.Key.MID_Y] = (opt_minY + opt_maxY) / 2;
};



if (goog.DEBUG) {
  /**
   * Returns a string representing the address of the quad by concatenating (from right to left) its sub-quad-address
   * and all sub-quad-addresses of the quads in the ancestor axis
   * @return {string} A string in form of e.g. '2013'
   * @override
   */
  H.util.QuadTree.Quad_.prototype.toString = function() {
    'use strict';
    var str = '',
        quad = this;
    while (quad.parent_) {
      str = quad.subQuadAddr_ + str;
      quad = quad.parent_;
    }
    return '<Quad ' + (str || 'root') + '>';
  };
}


/**
 * To fetch a specified sub quad. If the sub quad doesn't exists, a new Quad_ is created and registered.
 * @private
 * @param {number} subQuadAddr The address of the sub-quad, one of TL, TR, BL, BR
 * @return {H.util.QuadTree.Quad_} The requested sub quad
 */
H.util.QuadTree.Quad_.prototype.getSubQuad_ = function(subQuadAddr) {
  'use strict';
  return this[subQuadAddr] || (this[subQuadAddr] = new H.util.QuadTree.Quad_(this, subQuadAddr));
};


/**
 * To create a new entry and register it to the quad
 * @private
 * @param {number} minX The minimum x value of the entry
 * @param {number} minY The minimum y value of the entry
 * @param {number} maxX The maximum x value of the entry
 * @param {number} maxY The maximum y value of the entry
 * @return {H.util.QuadTree.Entry} The new created entry
 */
H.util.QuadTree.Quad_.prototype.addNewEntry_ = function(minX, minY, maxX, maxY) {
  'use strict';
  var entry = new H.util.QuadTree.Entry(this, minX, minY, maxX, maxY);
  (this.entries_ || (this.entries_ = [])).push(entry);
  return entry;
};


/**
 * To remove a entry from a quad.
 * @private
 * @param {H.util.QuadTree.Entry} entry The entry to remove
 * @param {H.util.QuadTree} tree The tree of the quad
 * @return {boolean} whether the given entry could be found and removed or not
 */
H.util.QuadTree.Quad_.prototype.removeEntry_ = function(entry, tree) {
  'use strict';
  var entries = this.entries_,
      idx;
  if (entries) {
    idx = entries.indexOf(entry);
    if (idx !== -1) {
      entries.splice(idx, 1);
      // No other entries left?
      if (!entries.length) {
        // Delete the entries property, clean up the tree and adjust the head!
        delete this.entries_;
        this.cleanUp_(tree);
        tree.adjustHead_();
      }
    }
  }
  return idx >= 0;
};


/**
 * To cleanup the tree recursively after an entry was removed from a quad.
 * @private
 * @param {H.util.QuadTree} tree The tree to clean up
 */
H.util.QuadTree.Quad_.prototype.cleanUp_ = function(tree) {
  'use strict';
  var quad = this,
      parent,
      head = tree.head_;
  // While quad is not the root quad (it has a parent) and has neither entries nor sub quads [0...3]?
  while ((parent = quad.parent_) && !(quad.entries_ || quad[0] || quad[1] || quad[2] || quad[3])) {
    // If the quad is the tree's head, set the tree's head to the quad's parent
    if (quad === head) {
      head = parent;
    }
    // Delete the references from quad to parent and parent to quad:
    delete quad.parent_;
    delete parent[quad.subQuadAddr_];
    // Continue the clean up with the quad's parent
    quad = parent;
  }
  // Update the tree's head to the (potentially new head)
  tree.head_ = head;
};



//################################################  E n t r y   ########################################################



/**
 * A Quad tree entry is a pure data object, holding a unique ID and a reference to the according quad plus the data for
 * a certain region, specified by the minX, minY, maxX and maxY parameters
 * @constructor
 * @param {?H.util.QuadTree.Quad_} quad The according quad.
 * @param {number} minX The minimum x value (left).
 * @param {number} minY The minimum y value (top)
 * @param {number} maxX The maximum x value (right)
 * @param {number} maxY The maximum y value (bottom)
 */
H.util.QuadTree.Entry = function(quad, minX, minY, maxX, maxY) {
  'use strict';
  /**
   * The unique ID of the entry
   * @type {number}
   * @publish
   */
  this['id'] = ++H.util.QuadTree.Entry.lastID_;
  this.quad_ = quad;


  /**
   * The minimum y (top) value
   * @type {number}
   * @publish
   */
  this[0] = minY;


  /**
   * The maximum x (right) value
   * @type {number}
   * @publish
   */
  this[1] = maxX;


  /**
   * The maximum y (bottom) value
   * @type {number}
   * @publish
   */
  this[2] = maxY;


  /**
   * The minimum x (left) value
   * @type {number}
   * @publish
   */
  this[3] = minX;
};


/**
 * The last used entry ID
 * @type {number}
 * @private
 */
H.util.QuadTree.Entry.lastID_ = 0;
