goog.provide('H.map.Group');

goog.require('H.geo.Rect');
goog.require('H.lang.InvalidArgumentError');
goog.require('H.map.Object');
goog.require('H.map.provider.Invalidations');
goog.require('H.math');

/*
 * TODO
 * For now we invoke invalidate(flags) on all children which can cause performance issues for big groups.
 *
 * We should optimize it in that way:
 *  - map.Group#invalidate() doesn't call through to its children's invalidate()
 *  - An ObjectProvider gets different Invalidations for:
 *    - any
 *    - spatial
 *    - marker
 *    - domMarker
 *    - group
 *  - map.Object must be changed / enhanced:
 *    - getVisibility() checks if the object's VISUAL invalidation is older than it's provider's VISUAL invalidation for
 *      group. If so getVisibility() must be recomputed otherwise it returns the current isVisible_ value
 *    - zInfo must become private => zInfo_
 *    - getZInfo() checks if the object's ZORDER invalidation is older than it's provider's ZORDER invalidation for
 *      group. If so zInfo must be recomputed otherwise it returns the current zInfo_ value
 *
 */



//#################################################  CONSTRUCTOR  ######################################################
/**
 * This class represents a map object which can contain other map objects.
 * It's visibility, zIndex and object-order influences the contained map objects
 * @publish
 * @export
 * @extends {H.map.Object}
 * @final
 * @constructor
 * @throws {H.lang.InvalidArgumentError} if the option's "objects" property is specified but not an Array
 *      of {@link H.map.Object} instances
 * @throws {H.lang.IllegalOperationError} if the option's "provider" and "objects" properties are specified
 *      but one of the objects' provider and the group's provider are different and
 *      neither {@code null} nor an instance of {@link H.map.provider.LocalObjectProvider}
 * @param {H.map.Group.Options=} opt_options an optional object containing initialization values
 */
H.map.Group = function(opt_options) {
  'use strict';
  var key,
      value,
      headAndTail = {};
  H.map.Group.superClass_.constructor.call(this, opt_options);

  headAndTail.previousSibling = headAndTail.nextSibling = headAndTail;
  this.headAndTail = /** @type {!H.map.Group.Sibling} */ (headAndTail);

  if (opt_options) {
    key = 'objects';
    if (key in opt_options) {
      this.addObjects(opt_options[key]);
    }
  }
};
H.lang.inherits(H.map.Group, H.map.Object);


//###################################################  TYPE  ###########################################################
/**
 * @override
 */
H.map.Group.prototype.type = H.map.Object.Type['GROUP'];


//###################################################  PROVIDER  #######################################################
/**
 * @override
 * @export
 */
H.map.Group.prototype.setProvider = function(provider, opt_caller) {
  'use strict';
  var isProviderChange;
  if (isProviderChange = H.map.Group.superClass_.setProvider.call(this, provider, opt_caller)) {
    // If the providers are diffeent we have to align the provider for all descendants (recursive):
    this.forEach(H.map.Group.forEach_setProvider_, true);
  }
  return isProviderChange;
};


/**
 * A forEach callback to recursivly align the descendents' providers with this group's provider.
 * @private
 * @param {!H.map.Object} object
 * @param {number} idx
 * @param {!H.map.Group} group
 */
H.map.Group.forEach_setProvider_ = function(object, idx, group) {
  'use strict';
  // Group#setProvider() has already checked the providers, so we call Object#setProvider without check:
  object.setProvider(group.provider);
};


//###############################################  TREE RELATIONS  #####################################################
/**
 * @override
 * @export
 */
H.map.Group.prototype.contains = function(object) {
  'use strict';
  // We don't call the super because it is just a simple "return this === object"
  var contains = this === object;
  if (!contains && this.headAndTail.nextSibling !== this.headAndTail &&
      H.lang.isInstanceOf(object, H.map.Object)) {
    while (!contains && object) {
      if (object === this) {
        contains = true;
      } else {
        object = object.parentGroup;
      }
    }
  }
  return contains;
};


//###################################################  Z-INFO  #########################################################
/**
 * @override
 * @export
 */
H.map.Group.prototype.resetZInfo = function() {
  'use strict';

  // We don't call the super because it is just a simple "this.zInfo = H.lang.UNDEF;"
  this.zInfo = H.lang.UNDEF;

  // We have to reset the zInfo of all descendants (recursively):
  this.forEach(H.map.Group.forEach_resetZInfo_, true);
};


/**
 * A forEach callback to recursivly set the provider for all descendents.
 * @private
 * @this {?H.map.provider.ObjectProvider}
 * @param {!H.map.Object} object
 * @param {number} idx
 * @param {!H.map.Group} group
 */
H.map.Group.forEach_resetZInfo_ = function(object, idx, group) {
  'use strict';
  object.resetZInfo();
};


//################################################  INVALIDATION  ######################################################
/**
 * The invalidation flags to set for all children, needed in Group#forEach_invalidate_()
 * @private
 * @type {number}
 */
H.map.Group.prototype.flags_;


/**
 * @override
 * @export
 */
H.map.Group.prototype.invalidate = function(flags) {
  'use strict';
  var retVal = H.map.Group.superClass_.invalidate.call(this, flags);
  if (retVal) {
    this.flags_ = flags;
    this.forEach(H.map.Group.forEach_invalidate_, true);
    delete this.flags_;
  }
  return retVal;
};


/**
 * A forEach callback to invalidate recursivly
 * @private
 * @this {?H.map.provider.ObjectProvider}
 * @param {!H.map.Object} object
 * @param {number} idx
 * @param {!H.map.Group} group
 */
H.map.Group.forEach_invalidate_ = function(object, idx, group) {
  'use strict';
  object.invalidate(group.flags_);
};


//#################################################  TRAVERSING  #######################################################
/**
 * To invokes a provided callback once per object in add order.
 * The function is invoked with the following arguments:<ul>
 *    <li>the currently traversed object</li>
 *    <li>the index of the object</li>
 *    <li>the group itself</li>
 *    </ul>
 * @publish
 *
 * @export
 * @param {function(!H.map.Object, number, !H.map.Group)} callback
 * @param {boolean=} opt_recursive Indicates whether sub groups should be traversed recursively
 * @param {*=} opt_context The context to use as "this" within the callback
 */
H.map.Group.prototype.forEach = function(callback, opt_recursive, opt_context) {
  var headAndTail = this.headAndTail,
      sibling = headAndTail.nextSibling,
      idx = 0,
      nextSibling;
  while (sibling !== headAndTail) {
    // We save the nextSibling reference before we invoke the callback, because it could be deleted afterwards:
    nextSibling = sibling.nextSibling;
    callback.call(opt_context, sibling, idx++, this);
    if (opt_recursive && H.lang.isInstanceOf(sibling, H.map.Group)) {
      sibling.forEach(callback, opt_recursive, opt_context);
    }
    sibling = nextSibling;
  }
};


//##################################################  OBJECTS  ########################################################
/**
 * To get a list of all objects of this group.
 * On groups with many chilren this method can cause a higher memory and CPU consumption.
 * Alternatively you case use {@link H.map.Group.prototype.forEach}
 *
 * @param {boolean=} opt_recursive Indicates whether objects in sub-groups are also collected .
 * @return {!Array.<!H.map.Object>}
 * @export
 * @publish
 */
H.map.Group.prototype.getObjects = function(opt_recursive) {
  'use strict';
  var objects = [];
  this.forEach(H.map.Group.forEach_collectObjects_, opt_recursive, objects);
  return objects;
};


/**
 * Method returns the bounding rectangle for the group. The rectangle
 * is the smallest rectangle that covers all objects. If group doesn't contains
 * objects method returns {@code null}.
 * @export
 * @publish
 * @return {H.geo.Rect} geo ractangle that covers all objects in the group
 */
H.map.Group.prototype.getBounds = function() {
  'use strict';
  var rect = null;
  this.forEach(function(object) {
    var subRect;
    if (rect) {
      if (object.getBounds) {
        subRect = object.getBounds();
        if (subRect) {
          rect = rect.mergeRect(subRect);
        }
      } else {
        rect = rect.mergePoint(object.getPosition());
      }
    } else {
      rect = object.getBounds ? object.getBounds() :
          H.geo.Rect.coverPoints([object.getPosition()]);
    }
  });
  return rect;
};


/**
 * A forEach callback to collect objects.
 * @private
 * @this {!Array}
 * @param {!H.map.Object} object
 * @param {number} idx
 * @param {!H.map.Group} group
 */
H.map.Group.forEach_collectObjects_ = function(object, idx, group) {
  'use strict';
  this.push(object);
};


/**
 * The number of child objects in this group, will be adjusted in addObject() and removeObject()
 * @private
 * @type {number}
 */
H.map.Group.prototype.childCount_ = 0;


/**
 * Returns the number of child objects of this group.
 * @export
 * @return {number}
 */
H.map.Group.prototype.getChildCount = function() {
  'use strict';
  return this.childCount_;
};


//##################################################  LINKED LIST  #####################################################
/**
 * A sibling which keeps references to its previous and next sibling
 * Must NOT be published, it's used API interally only by Group!
 *
 * @typedef {{
 *   previousSibling: (!(H.map.Object|H.map.Group.Sibling)),
 *   nextSibling: (!(H.map.Object|H.map.Group.Sibling))
 * }}
 * @property {!(H.map.Object|H.map.Group.Sibling)} previousSibling
 * @property {!(H.map.Object|H.map.Group.Sibling)} nextSibling
 */
H.map.Group.Sibling;


/**
 * A pseudo sibling which references the first (nextSibling) and the last (previousSibling) object in this group.
 * If the group is empty, booth properties references the pseudo sibling itself.
 * Must NOT be published, it's used API interally only by Group!
 *
 * @protected
 * @type {!H.map.Group.Sibling}
 */
H.map.Group.prototype.headAndTail;


/**
 * To interlink two siblings
 * The caller has to assure for each of the siblings that it is either a Object contained by this group or the
 * headAndTail of this group or {@code null}.
 * @private
 * @param {!(H.map.Object|H.map.Group.Sibling)} previousSibling
 * @param {!(H.map.Object|H.map.Group.Sibling)} nextSibling
 */
H.map.Group.prototype.linkSiblings_ = function(previousSibling, nextSibling) {
  'use strict';
  previousSibling.nextSibling = nextSibling;
  nextSibling.previousSibling = previousSibling;
};


//####################################################  ADD OBJECT  ####################################################
/**
 * The sibling index to use for the next new appended object
 * @private
 * @type {number}
 */
H.map.Group.prototype.nextSiblingIndex_ = H.math.MIN_IDX;


/**
 * To append an object to this group.
 *

 * @throws {H.lang.InvalidArgumentError} if the argument is not an instance of {@link H.map.Object}
 * @throws {H.lang.IllegalOperationError} if the object's provider or this group's provider are different and
 *      neither {@code null} nor an instance of {@link H.map.provider.LocalObjectProvider}
 * @param {!H.map.Object} object
 * @return {!H.map.Object} a reference to the appended object
 * @export
 * @publish
 */
H.map.Group.prototype.addObject = function(object) {
  'use strict';
  var headAndTail = this.headAndTail,
      lastObject = headAndTail.previousSibling,
      parentGroup,
      isProviderChange;

  // Check for map.Object:
  H.lang.isInstanceOf(object, H.map.Object, this.addObject, 0);

  // Check for HierarchyRequestError:
  if (object.contains(this)) {
    throw new H.lang.IllegalOperationError(this.addObject, 'The new object contains the parent group');
  }

  isProviderChange = object.setProvider(this.provider, this.addObject);

  // If the object is not already contained by this group:
  if ((parentGroup = object.parentGroup) !== this) {

    // If the object is contained by another group we have to remove it from these group first:
    if (parentGroup) {
      this.linkSiblings_(
          /** @type {!(H.map.Object|H.map.Group.Sibling)} */ (object.previousSibling),
          /** @type {!(H.map.Object|H.map.Group.Sibling)} */ (object.nextSibling));
    }

    // Adjust the parentGroup if necessary:
    object.setParentGroup(this);

    // Link the object into this group's linked list:
    this.linkSiblings_(lastObject, object);
    this.linkSiblings_(object, headAndTail);
    this.childCount_++;
  }

  // Update the sibling index:
  object.siblingIndex = this.nextSiblingIndex_++;

  // Reset the object's zInfo (recursively, if object is a group):
  object.resetZInfo();

  // If the provider hasn't been changed (no REMOVE / ADD invalidation triggered) we have to trigger a Z_ORDER
  if (!isProviderChange) {
    object.invalidate(H.map.provider.Invalidations.Flag.Z_ORDER);
  }
  return object;
};


/**
 * Appends a list of objects to this group
 *
 * @throws {H.lang.InvalidArgumentError} if the argument is not an Array of {@link H.map.Object} instances
 * @throws {H.lang.IllegalOperationError} if one of the object's provider and the group's provider are different
 *      and neither {@code null} nor an instance of {@link H.map.provider.LocalObjectProvider}
 * @param {Array.<!H.map.Object>} objects
 * @export
 * @publish
 */
H.map.Group.prototype.addObjects = function(objects) {
  'use strict';
  var length,
      i;
  H.lang.isArray(objects, this.addObjects, 0);

  // CC doesn't like the next line, it says forEach needs a function(*, number, Array) as callback  :-/
  // objects.forEach(this.addObject, this);
  // Therefore the "classical" for loop to add all objects:
  length = objects.length;
  for (i = 0; i < length; i++) {
    this.addObject(objects[i]);
  }
};


//###############################################  REMOVE OBJECT  ###################################################
/**
 * Removes an object from this group.
 * @throws {H.lang.InvalidArgumentError} if the given object is not a object of this group
 * @param {!H.map.Object} object The object to remove
 * @return {!H.map.Object} a reference to the removed object
 * @export
 * @publish
 */
H.map.Group.prototype.removeObject = function(object) {
  'use strict';
  if (!object || object.parentGroup !== this) {
    throw new H.lang.IllegalOperationError(this.removeObject, 'object not found');
  }

  // Remove the object from the linked list and clean it up:
  this.linkSiblings_(
      /** @type {!(H.map.Object|H.map.Group.Sibling)} */ (object.previousSibling),
      /** @type {!(H.map.Object|H.map.Group.Sibling)} */ (object.nextSibling));
  object.setParentGroup(null);
  delete object.previousSibling;
  delete object.nextSibling;

  // Only if the object is owned by a LocalObjectProvider we reset the provider to null:
  if (H.lang.isInstanceOf(object.provider, H.map.provider.LocalObjectProvider)) {
    object.setProvider(null, this.removeObject);
  }

  this.childCount_--;

  return object;
};


/**
 * Removes objects from this group.
 *
 * @throws {H.lang.InvalidArgumentError} if the list is not an Array or it contains others than a group's object
 * @param {!Array.<!H.map.Object>} objects The list of objects to remove
 * @export
 * @publish
 */
H.map.Group.prototype.removeObjects = function(objects) {
  'use strict';
  var length,
      i;
  H.lang.isArray(objects, this.removeObjects, 0);

  /* CC doesn't like the next line, it says forEach needs a function(*, number, Array) as callback
  objects.forEach(this.removeObject, this);
  */
  length = objects.length;
  for (i = 0; i < length; i++) this.removeObject(objects[i]);
};


/**
 * Method removes all objects from the group.
 * @publish
 * @export
 */
H.map.Group.prototype.removeAll = function() {
  'use strict';
  var objects = this.getObjects();

  this.removeObjects(objects);
};


/**
 * Options used to initialize a group
 * @publish
 * @property {number=} min
 *           The minimum zoom level for which the object is visible, default is {@code -Infinity}
 * @property {number=} max
 *           The maximum zoom level for which the object is visible, default is {@code Infinity}
 * @property {boolean=} visibility
 *           Indicates whether the map object is visible, default is {@code true}
 * @property {number=} zIndex
 *           The z-index value of the map object, default is {@code 0}
 * @property {(H.map.provider.Provider|null)=} provider
 *           The provider of this object. This property is only needed if a customized Implementation of ObjectProvider
 *           wants to instantiate an object.
 * @property {*} data
 *           Optional arbitrary data to be stored with this map object. This data can be retrieved by calling
 *           {@code getData}.
 * @property {Array.<H.map.Object>=} objects
 *           A list of map objects to add initially to this group.
 *
 * @typedef {{
 *   min: (number|undefined),
 *   max: (number|undefined),
 *   visibility: (boolean|undefined),
 *   zIndex: (number|undefined),
 *   provider: (H.map.provider.ObjectProvider|null|undefined),
 *   data: *,
 *   objects: ((Array.<H.map.Object>|undefined))
 * }}
 */
H.map.Group.Options;
