goog.provide('H.map.ChangeEvent');

goog.require('H.util.ChangeEvent');



//#############################################  Map.ChangeEvent  ######################################################
/**
 * An Event to signalize map related changes.
 * @extends {H.util.ChangeEvent}
 * @constructor
 * @param {string} type The type of the event
 * @param {Object} newValue The view values that were set after the change
 * @param {Object} oldValue The view values that were set before the change
 * @param {H.math.BitMask} modifiers Indicates which values were changed.
 *        See properties FOV, PITCH, POSITION, ROLL, SIZE, YAW and ZOOM for the meaning of the bits.
 * @export
 * @publish
 */
H.map.ChangeEvent = function(type, newValue, oldValue, modifiers) {
  'use strict';
  H.map.ChangeEvent.superClass_.constructor.call(this, type, newValue, oldValue);

  /**
   * Indicates which view values were changed.
   * See properties FOV, PITCH, POSITION, ROLL, SIZE, YAW and ZOOM for the meaning of the bits.
   * @type {H.map.Bitmask}
   */
  this['modifiers'] = modifiers;
};
H.lang.inherits(H.map.ChangeEvent, H.util.ChangeEvent);


/**
 * The modifier to signalize a change of the "fov" property
 * @publish
 * @const
 * @type {number}
 */
H.map.ChangeEvent.prototype['FOV'] = 1; //H.map.ViewModel.CamToSyncFlags_['fov'];


/**
 * The modifier to signalize a change of the "pitch" property
 * @publish
 * @const
 * @type {number}
 */
H.map.ChangeEvent.prototype['PITCH'] = 2; //H.map.ViewModel.CamToSyncFlags_['pitch'];


/**
 * The modifier to signalize a change of the "position" property
 * @publish
 * @const
 * @type {number}
 */
H.map.ChangeEvent.prototype['POSITION'] = 4; //H.map.ViewModel.CamToSyncFlags_['position'];


/**
 * The modifier to signalize a change of the "roll" property
 * @publish
 * @const
 * @type {number}
 */
H.map.ChangeEvent.prototype['ROLL'] = 8; //H.map.ViewModel.CamToSyncFlags_['roll'];


/**
 * The modifier to signalize a change of the "roll" property
 * @publish
 * @const
 * @type {number}
 */
H.map.ChangeEvent.prototype['SIZE'] = 16; //H.map.ViewModel.CamToSyncFlags_['roll'];


/**
 * The modifier to signalize a change of the "yaw" property
 * @publish
 * @const
 * @type {number}
 */
H.map.ChangeEvent.prototype['YAW'] = 32; //H.map.ViewModel.CamToSyncFlags_['yaw'];


/**
 * The modifier to signalize a change of the "zoom" property
 * @publish
 * @const
 * @type {number}
 */
H.map.ChangeEvent.prototype['ZOOM'] = 64; //H.map.ViewModel.CamToSyncFlags_['zoom'];
