goog.provide('H.map.ArrowStyle');

goog.require('H.lang');
goog.require('H.util');



/**
 * This class represents style attributes for arrows to be rendered along a polyline.
 * @publish
 * @export
 * @constructor
 * @param {(H.map.ArrowStyle|H.map.ArrowStyle.Options)=} opt_options
 */
H.map.ArrowStyle = function(opt_options) {
  'use strict';
  var isArrowStyle,
      key,
      value,
      isFilling;
  if (opt_options) {
    isArrowStyle = H.lang.isInstanceOf(opt_options, H.map.ArrowStyle);
    for (key in opt_options) {
      if (key in this) {
        value = opt_options[key];
        if (value !== this[key] && (key === 'fillColor' || (value = +value) > 0)) {
          this[key] = value;
        }
      }
    }
  }

  // Update isFilling if necessary:
  isFilling = isArrowStyle ? opt_options.isFilling : H.util.isNonTransparent(this['fillColor']);
  // Hardcoded: We assume that prototype.isFilling is true:
  if (!isFilling) {
    this.isFilling = isFilling;
  }

};


/**
 * Idicates wheter the style is effectively filling:
 * The fill-color is neither "none" nor it is non-transparent
 * DON'T publish, API internally use only by SpatialTile!
 * @type {boolean}
 */
H.map.ArrowStyle.prototype.isFilling = true;


H.lang.extend(H.map.ArrowStyle.prototype, /** @lends {H.map.ArrowStyle.prototype}*/ {
  /**
   * The CSS color value used to fill the arrow shapes, default is {@code "rgba(255, 255, 255, 0.75)"}
   * @type {string}
   */
  'fillColor': 'rgba(255,255,255,.75)',

  /**
   * The width factor of the arrow shape, default is {@code 1.2}
   * @type {number}
   */
  'width': 1.2,

  /**
   * The length factor of the arrow shape, default is {@code 1.6}
   * @type {number}
   */
  'length': 1.6,

  /**
   * The frequency factor to render the arrows, default is {@code 5}
   * @type {number}
   */
  'frequency': 5
});


/**
 * Creates a copy of this arrow style object.
 * DON'T publish, API internally used only!
 *
 * @return {H.map.ArrowStyle} a clone of this arrow style
 */
H.map.ArrowStyle.prototype.getClone = function() {
  return new H.map.ArrowStyle(/** @type {H.map.ArrowStyle.Options} */(this));
};


/**
 * This method checks value-equality with another arrow style.
 * @param {H.map.ArrowStyle|H.map.ArrowStyle.Options} other the arrow style to compare with
 * @return {boolean} true if the arrow styles are value-equal, otherwise false
 * @export
 * @publish
 */
H.map.ArrowStyle.prototype.equals = function(other) {
  'use strict';
  var i,
      key,
      val,
      equal = this === other;

  if (!equal && other) {
    equal = other['width'] === this['width'] &&
            other['fillColor'] === this['fillColor'] &&
            other['length'] === this['length'] &&
            other['frequency'] === this['frequency'];
  }
  return equal;
};


/**
 * An object type to specify the style of arrows to render along a polyline
 * @publish
 *
 * @typedef {{
 *   fillColor: (string|undefined),
 *   width: (number|undefined),
 *   length: (number|undefined),
 *   frequency: (number|undefined)
 * }}
 *
 * @property {string=} fillColor
 *        The CSS color value used to fill the arrow shapes.
 *        If omitted or the value evaluates to {@code false} it defaults to {@code "rgba(255, 255, 255, 0.75)"}
 * @property {number=} width
 *        The width of the arrow shape. The value is taken as a factor of the width of the line, where the arrow
 *        description is applied. If omitted or the value is {@code <= 0} it defaults to {@code 1.2}
 * @property {number=} length
 *         The length of the arrow shapes. The value is taken as a factor of the width of the line at the end of
 *         which the arrow is drawn. If omitted or the value is {@code <= 0} it defaults to {@code 1.6}
 * @property {number=} frequency
 *         The frequency of arrow shapes. The value is taken as factor of the length of the arrow. A value of 1 results
 *         in gapless arrows. If omitted or the value is {@code false} it defaults to {@code 5}
 */
H.map.ArrowStyle.Options;
