goog.provide('H.map.provider.Invalidations');



/**
 * This class represents invalidation states of a renderable object. A renderer
 * can optimize its rendering strategies based on the information in this
 * object.
 *
 * For example, renderers may differentiate between a purely visual change of
 * an object and a spatial change of an object. One may only require the
 * change of a color during rendering, the other requires to re-project the
 * object.
 * @publish
 * @export
 * @final
 * @constructor
 */
H.map.provider.Invalidations = function() {
  'use strict';
};


/**
 * This constant represents the initial invalidation mark an invalidations
 * object has.
 * @type {H.map.provider.Invalidations.Mark}
 * @const
 * @export
 * @publish
 */
H.map.provider.Invalidations.MARK_INITIAL = H.math.MIN_IDX;


/**
 * To update invalidation marks accordingly to the given the invalidation types.
 * @param {H.map.provider.Invalidations.Mark} mark The invalidation mark to set
 * @param {H.math.BitMask} types The descrete invalidation types to update
 * @export
 * @publish
 */
H.map.provider.Invalidations.prototype.update = function(mark, types) {
  'use strict';
  if (types !== H.map.provider.Invalidations.Flag.NONE) {
    this.anyMark_ = mark;
    if (types & H.map.provider.Invalidations.Flag.SPATIAL) {
      this.spatialMark_ = mark;
    }
    if (types & H.map.provider.Invalidations.Flag.VISUAL) {
      this.visualMark_ = mark;
    }
    if (types & H.map.provider.Invalidations.Flag.ADD) {
      this.addMark_ = mark;
    }
    if (types & H.map.provider.Invalidations.Flag.REMOVE) {
      this.removeMark_ = mark;
    }
    if (types & H.map.provider.Invalidations.Flag.Z_ORDER) {
      this.zOrderMark_ = mark;
    }
  }
};


/**
 * This method returns the current invalidation mark of this invalidations
 * object.
 * @return {H.map.provider.Invalidations.Mark} the current invalidation mark
 * @export
 * @publish
 */
H.map.provider.Invalidations.prototype.getMark = function() {
  'use strict';
  return this.anyMark_;
};


/**
 * The invalidation mark for ANY
 * @private
 * @type {number}
 */
H.map.provider.Invalidations.prototype.anyMark_ = H.map.provider.Invalidations.MARK_INITIAL;


/**
 * Checks whether any change occurred after the specified since mark
 * @publish
 *
 * @export
 * @param {H.map.provider.Invalidations.Mark} since The invalidation mark to check against
 * @return {boolean}
 */
H.map.provider.Invalidations.prototype.isAny = function(since) {
  'use strict';
  return this.anyMark_ > since;
};


/**
 * The invalidation mark for VISUAL
 * @private
 * @type {number}
 */
H.map.provider.Invalidations.prototype.visualMark_ = H.map.provider.Invalidations.MARK_INITIAL;


/**
 * Checks whether a visual change occurred after the specified since mark
 * @publish
 *
 * @export
 * @param {H.map.provider.Invalidations.Mark} since The invalidation mark to check against
 * @return {boolean}
 */
H.map.provider.Invalidations.prototype.isVisual = function(since) {
  'use strict';
  return this.visualMark_ > since;
};


/**
 * The invalidation mark for SPATIAL
 * @private
 * @type {number}
 */
H.map.provider.Invalidations.prototype.spatialMark_ = H.map.provider.Invalidations.MARK_INITIAL;


/**
 * Checks whether a spatial change occurred after the specified since mark
 * @publish
 *
 * @export
 * @param {H.map.provider.Invalidations.Mark} since The invalidation mark to check against
 * @return {boolean}
 */
H.map.provider.Invalidations.prototype.isSpatial = function(since) {
  'use strict';
  return this.spatialMark_ > since;
};


/**
 * The invalidation mark for ADD
 * @private
 * @type {number}
 */
H.map.provider.Invalidations.prototype.addMark_ = H.map.provider.Invalidations.MARK_INITIAL;


/**
 * Checks whether an add-operation occurred after the specified since mark
 * @publish
 *
 * @export
 * @param {H.map.provider.Invalidations.Mark} since The invalidation mark to check against
 * @return {boolean}
 */
H.map.provider.Invalidations.prototype.isAdd = function(since) {
  'use strict';
  return this.addMark_ > since;
};


/**
 * The invalidation mark for REMOVE
 * @private
 * @type {number}
 */
H.map.provider.Invalidations.prototype.removeMark_ = H.map.provider.Invalidations.MARK_INITIAL;


/**
 * Checks whether a remove operation occurred after the specified since mark
 * @publish
 *
 * @export
 * @param {H.map.provider.Invalidations.Mark} since The invalidation mark to check against
 * @return {boolean}
 */
H.map.provider.Invalidations.prototype.isRemove = function(since) {
  'use strict';
  return this.removeMark_ > since;
};


/**
 * The invalidation mark for Z_ORDER
 * @private
 * @type {number}
 */
H.map.provider.Invalidations.prototype.zOrderMark_ = H.map.provider.Invalidations.MARK_INITIAL;


/**
 * Checks whether a z-order change occurred after the specified since mark
 * @publish
 *
 * @export
 * @param {H.map.provider.Invalidations.Mark} since The invalidation mark to check against
 * @return {boolean}
 */
H.map.provider.Invalidations.prototype.isZOrder = function(since) {
  'use strict';
  return this.zOrderMark_ > since;
};


/**
 * This enumeration encapsulates bit flags for different invalidations of map objects.
 *
 * Flags can be combined to signal multiple changes at the same time.
 * These flags are used by providers to differentiate different kinds
 * of property changes in order to optimize the rendering.
 *
 * Values of this emumeration are:
 *
 * - VISUAL (an object's visual appearance has been changed)
 * - SPATIAL (an object's geospatial attribute has been changed)
 * - REMOVE (an item has been removed from a provider)
 * - ADD (an item has been added to a provider)
 * - Z_ORDER (an item's z-index or it's index within its parent's children collection has been changed)
 * - NONE (indicating that no change has occured)
 *
 * @enum {number}
 * @export
 * @publish
 */
H.map.provider.Invalidations.Flag = {
  NONE: 0,
  VISUAL: 1,
  SPATIAL: 2,
  ADD: 4,
  REMOVE: 8,
  Z_ORDER: 16
};


/**
 * The invalidation mark represents a counter which is increased whenever an
 * invalidation takes place.
 * @typedef {number}
 * @publish
 */
H.map.provider.Invalidations.Mark;

