goog.provide('H.math.Point');

goog.require('H.math.IPoint');



/**
 * Class represents a 2-dimensional point, defined by x and y coordinates.
 * @publish
 * @export
 * @implements {H.math.IPoint}
 * @final
 * @constructor
 * @param {number} x coordinate on x-axis
 * @param {number} y coordinate on y-axis
 */
H.math.Point = function(x, y) {
  'use strict';

  this['x'] = +x;
  this['y'] = +y;
};


/** @override */
H.math.Point.prototype.x;


/** @override */
H.math.Point.prototype.y;


/**
 * Sets the x and y coordinate of this point
 * @export
 * @publish
 *
 * @param {number} x coordinate on x-axis
 * @param {number} y coordinate on y-axis
 * @method
 */
H.math.Point.prototype.set = H.math.Point; // we can re-use the constructor function to set the values


/**
 * This method creates a copy of the current point.
 * @export
 * @publish
 * @param {H.math.Point=} opt_out An optional point to store the copied values
 * @return {H.math.Point} The clone of the point
 */
H.math.Point.prototype.clone = function(opt_out) {
  'use strict';
  var clone = opt_out;
  if (clone) {
    clone['x'] = this['x'];
    clone['y'] = this['y'];
  } else {
    clone = new H.math.Point(this['x'], this['y']);
  }
  return clone;
};


/**
 * This method adds given point coordinates to the current one.
 * @export
 * @publish
 *
 * @param {H.math.IPoint} other The point to add
 * @return {H.math.Point} the point itself after adding
 */
H.math.Point.prototype.add = function(other) {
  'use strict';
  this['x'] += other['x'];
  this['y'] += other['y'];
  return this;
};


/**
 * This method substract given point coordinates from the current point.
 * @export
 * @publish
 *
 * @param {H.math.IPoint} other The point to subtract
 * @return {H.math.Point} the point itself after substracting
 */
H.math.Point.prototype.sub = function(other) {
  'use strict';
  this['x'] -= other['x'];
  this['y'] -= other['y'];
  return this;
};


/**
 * This method scales the current point coordinates by the given factor(s).
 * @export
 * @publish
 *
 * @param {number} factor multiplication factor
 * @param {number=} opt_factorY If omitted, the factor argument is used
 * @return {H.math.Point} the point itself after scaling
 */
H.math.Point.prototype.scale = function(factor, opt_factorY) {
  'use strict';
  this['x'] *= factor;
  this['y'] *= opt_factorY === undefined ? factor : opt_factorY;
  return this;
};


/**
 * This method rounds the x and y coordinates of the point.
 * @export
 * @publish
 *
 * @return {H.math.Point} the point itself after rounding
 */
H.math.Point.prototype.round = function() {
  'use strict';
  this['x'] = H.math.round(this['x']);
  this['y'] = H.math.round(this['y']);
  return this;
};


/**
 * Rounds the x and y coordinates to the next smaller integer values.
 * @export
 * @publish
 *
 * @return {H.math.Point} the point itself after flooring
 */
H.math.Point.prototype.floor = function() {
  'use strict';
  this['x'] = H.math.floor(this['x']);
  this['y'] = H.math.floor(this['y']);
  return this;
};


/**
 * Rounds the x and y coordinates to the next greater integer values.
 * @export
 * @publish
 *
 * @return {H.math.Point} the point itself after ceiling
 */
H.math.Point.prototype.ceil = function() {
  'use strict';
  this['x'] = H.math.ceil(this['x']);
  this['y'] = H.math.ceil(this['y']);
  return this;
};


/**
 * This method compares current point coordinates with the supplied
 * point coordinates.
 * @export
 * @publish
 *
 * @param {H.math.IPoint} other The point to compare to.
 * @return {boolean} True if the points are equal
 */
H.math.Point.prototype.equals = function(other) {
  return !!(other && this['x'] === other['x'] && this['y'] === other['y']);
};


/**
 * This method calculates the distance to a point supplied by the caller.
 * @export
 * @publish
 *
 * @param {H.math.IPoint} other
 * @return {number}
 */
H.math.Point.prototype.distance = function(other) {
  'use strict';
  return H.math.sqrt(
      H.math.pow(this['x'] - other['x'], 2) +
      H.math.pow(this['y'] - other['y'], 2));
};
