goog.provide('H.geo.QuadTree');

goog.require('H.geo.Point');
goog.require('H.geo.Rect');
goog.require('H.util.QuadTree');



/**
 * A quad tree data structure which do space partitioning, ray and square based intersecting lookup.
 * The space spanned by the tree is always square shaped by a given axis radius.
 * @constructor
 * @param {number=} opt_depth To limit the depth levels of the tree. Default is {@code 10} if the value is a falsy.
 */
H.geo.QuadTree = function(opt_depth) {
  'use strict';

  /**
   * Holds the internally used quad tree.
   * @private
   * @type {H.util.QuadTree}
   */
  this.tree_ = new H.util.QuadTree(+opt_depth || 10, 180, 90, 0, 0);


  /**
   * A list of all entries which cross the date border (they are not stored in the internal quad tree).
   * The negative longitude will be stored as shifted by +360 degrees. latitudes are stored negated (* -1)
   * @private
   * @type {Array.<H.util.QuadTree.Entry>}
   * @example:
   *    leftLng:  -170  --> +190 --> maxX
   *    rightLng: +160  --> +160 --> minX
   *    topLat:    +10  --> -10  --> minY
   *    bottomLat: -20  --> +20  --> maxY
   */
  this.cdbEntries_ = [];


  /**
   * Indicates that at least one of the entries of the tree is crossing the date border.
   * @private
   * @type {boolean}
   */
  this.isCDB_ = false;


  /**
   * The boundary of all entries stored in the tree, if no entries stored the value is <code>null</code>
   * @private
   * @type {?H.geo.Rect}
   */
  this.bounds_ = null;

  /**
   * The boundary of all entries stored in the internal quad tree, if no entries stored the value is <code>null</code>
   * @private
   * @type {?H.geo.Rect}
   */
  this.treeBounds_ = null;

  /**
   * The boundary of all CDB entries stored in the tree, if no CDB entries stored the value is <code>null</code>
   * @private
   * @type {?H.geo.Rect}
   */
  this.cdbBounds_ = null;

};


/**
 * The count of all entries in this tree.
 * @private
 * @type {number}
 */
H.geo.QuadTree.prototype.entriesCount_ = 0;


/**
 * To get the count of all entries in this tree.
 * @return {number}
 */
H.geo.QuadTree.prototype.getEntriesCount = function() {
  return this.entriesCount_;
};


/**
 * This method returns a list of all entries which cross the date border.
 * The negative longitude will be stored as shifted by +360 degrees. latitudes are stored negated (* -1)
 * Be aware that the internal CDB entries list is returned by reference - therefore you should never ever modify it!
 * This method must not be exported! Only internal use (e.g. clipping algorithm) allowed.
 * @return {Array.<H.util.QuadTree.Entry>}
 */
H.geo.QuadTree.prototype.cdbEntries = function() {
  'use strict';
  return this.cdbEntries_;
};


/**
 * This method indicates if at least one of the entries of the tree is crossing the date border.
 * @return {boolean}
 */
H.geo.QuadTree.prototype.isCDB = function() {
  'use strict';
  return this.isCDB_;
};


/**
 * This method returns boundary of all entries stored in the tree or <code>null</code> if no entries stored in the tree.
 * @return {?H.geo.Rect}
 */
H.geo.QuadTree.prototype.getBounds = function() {
  'use strict';
  var extremes,
      bounds,
      boundsList;
  if (!this.bounds_) {
    boundsList = [];
    // Try to get the tree bounds
    if (!this.treeBounds_) {
      if (extremes = this.tree_.getExtremes()) {
        this.treeBounds_ = new H.geo.Rect(
            -extremes[0/*MIN_Y*/], extremes[3/*MIN_X*/],
            -extremes[2/*MAX_Y*/], extremes[1/*MAX_X*/]);
      }
    }
    if (bounds = this.treeBounds_) {
      boundsList.push(bounds);
    }
    // Try to get the CDB bounds
    if (bounds = this.getCdbBounds_()) {
      boundsList.push(bounds);
    }
    // Do we have at least one ore more bounds:
    if (bounds = boundsList[0]) {
      // Do we have two bounds:
      if (boundsList[1]) {
        bounds = bounds.mergeRect(boundsList[1], true);
      }
      this.bounds_ = bounds;
    }
  }
  return this.bounds_;
};


/**
 * This method returns boundary of all CDB entries (entries which cross the date-border).
 * @private
 * @return {?H.geo.Rect}
 */
H.geo.QuadTree.prototype.getCdbBounds_ = function() {
  'use strict';
  var top, left, bottom, right,
      cdbEntries,
      entry,
      i;
  // No CDB bounds computed yet but we have CDB entries?
  if (!this.cdbBounds_ && (i = (cdbEntries = this.cdbEntries_).length)) {
    // Set the initial extreme values to opposite values:
    top = 90;
    left = 360;
    bottom = -90;
    right = 0;
    while (i--) {
      entry = cdbEntries[i];
      top = H.math.min(top, entry[0/*MIN_Y*/]);
      left = H.math.min(left, entry[3/*MIN_X*/]);
      bottom = H.math.max(bottom, entry[2/*MAX_Y*/]);
      right = H.math.max(right, entry[1/*MAX_X*/]);
    }
    this.cdbBounds_ = new H.geo.Rect(-top, left, -bottom, right - 360);
  }

  return this.cdbBounds_;
};


/**
 * This method inserts a given entry into the Tree
 * @private
 * @param {H.geo.Latitude} top The top edge of the entry to insert.
 * @param {H.geo.Longitude} left The left edge of the entry to insert.
 * @param {H.geo.Latitude} bottom The bottom edge of the entry to insert.
 * @param {H.geo.Longitude} right The right edge of the entry to insert.
 * @param {boolean} isCDB A flag to signal, that the entry is crossing the date border
 * @return {H.util.QuadTree.Entry} The quad tree entry created for the given boundary.
 */
H.geo.QuadTree.prototype.insert_ = function(top, left, bottom, right, isCDB) {
  'use strict';
  var entry;
  if (isCDB) {
    // Create a parentless entry. Negate the top and bottom latitudes and use right as left and left+360 as right:
    entry = new H.util.QuadTree.Entry(null, right, -top, left + 360, -bottom);
    // Store it in to the list of CDB entries:
    this.cdbEntries_.push(entry);
    // Update the isCDB flag:
    this.isCDB_ = true;
    if (this.cdbBounds_) {
      this.cdbBounds_.mergeTopLeftBottomRight(top, left, bottom, right, true, this.cdbBounds_);
    }
  } else { // Isn't a CDB entry
    // Insert a tree entry which has negated top and bottom latitudes (QuadTree y axis grows from top to bottom)
    entry = this.tree_.insertSorted(left, -top, right, -bottom);
    if (this.treeBounds_) {
      this.treeBounds_.mergeTopLeftBottomRight(top, left, bottom, right, true, this.treeBounds_);
    }
  }
  // Increase the entries count:
  ++this.entriesCount_;
  // Reset the bounds_, must be re-computed (merge treeBounds_ and cdbBounds_) on getBounds()
  this.bounds_ = null;
  return entry;
};


/**
 * This method inserts a geographical point into the tree.
 * @param {H.geo.Point} point The point to insert
 * @return {H.util.QuadTree.Entry} The quad tree entry created for the point.
 */
H.geo.QuadTree.prototype.insertPoint = function(point) {
  'use strict';
  var lng = point['lng'],
      lat = point['lat'];
  return this.insert_(lat, lng, lat, lng, false);
};


/**
 * This method inserts a geographical boundary into the tree.
 * @param {H.geo.Rect} bounds The boundary to insert
 * @return {H.util.QuadTree.Entry} The quad tree entry created for the inserted boundary.
 */
H.geo.QuadTree.prototype.insertBounds = function(bounds) {
  'use strict';
  var topLeft = bounds.getTopLeft(),
      bottomRight = bounds.getBottomRight(),
      isCDB = bounds.isCDB();
  return isCDB ?
      // Switch left and right because insert_() expect left < right:
      this.insert_(topLeft['lat'], bottomRight['lng'], bottomRight['lat'], topLeft['lng'], isCDB) :
      // Otherwise pass it unmodified:
      this.insert_(topLeft['lat'], topLeft['lng'], bottomRight['lat'], bottomRight['lng'], isCDB);
};


/**
 * This method inserts a line into the tree.
 * The line presents always the shortest connection between booth points on longitude axis.
 * For example insertLine(-1,-91, 1,91) would create a line from {lat:0, lng: 91} to {lat:0, lng:-91} which crosses the
 * date border at {lat:0, lng: 180}
 * @param {H.geo.Latitude} lat1 The latitude of the start point.
 * @param {H.geo.Longitude} lng1 The longitude of the start point.
 * @param {H.geo.Latitude} lat2 The latitude of the end point.
 * @param {H.geo.Longitude} lng2 The longitude of the end point.
 * @return {H.util.QuadTree.Entry} The quad tree entry created for the given points.
 */
H.geo.QuadTree.prototype.insertLine = function(lat1, lng1, lat2, lng2) {
  'use strict';
  var left,
      top,
      right,
      bottom;
  if (lng1 < lng2) {
    left = lng1;
    right = lng2;
  } else {
    left = lng2;
    right = lng1;
  }
  if (lat1 < lat2) {
    top = lat2;
    bottom = lat1;
  } else {
    top = lat1;
    bottom = lat2;
  }
  return this.insert_(top, left, bottom, right, right - left > 180);
};


/**
 * This method removes an entry from the tree.
 * @param {H.util.QuadTree.Entry} entry The entry to remove.
 * @return {boolean} whether the given entry could be found and removed or not
 */
H.geo.QuadTree.prototype.remove = function(entry) {
  'use strict';
  var idx,
      cdbEntries,
      result = false;
  // Entry has a parent? Remove it from the internal tree:
  if (entry.quad_) {
    result = this.tree_.remove(entry);
    // Reset the tree bounds:
    this.treeBounds_ = null;
  } else {
    // Otherwise it's a CDB entry, remove it from the CDB entries list:
    idx = (cdbEntries = this.cdbEntries_).indexOf(entry);
    if (result = idx >= 0) {
      cdbEntries.splice(idx, 1);
      // update the isCDB_ flag:
      this.isCDB_ = cdbEntries.length > 0;
      // TODO: Optimization: if the extremes of the entry are not equal with one of the boundary sides, don't reset it!
      this.cdbBounds_ = null;
    }
  }
  if (result) {
    // Decrease the entries count:
    --this.entriesCount_;
    this.bounds_ = null;
  }
  return result;
};


/**
 * To determine all date border crossing entries which are covered by, or intersecting with the given rectangle.
 * @private
 * @param {H.geo.Latitude} top The top edge of the rectangle (negated latitude).
 * @param {H.geo.Longitude} left The left edge of the rectangle.
 * @param {H.geo.Latitude} bottom The bottom edge of the rectangle (negated latitude).
 * @param {H.geo.Longitude} right The right edge of the rectangle.
 * @return {Array.<H.util.QuadTree.Entry>} The list of all matching CDB entries
 */
H.geo.QuadTree.prototype.intersectCdbEntries_ = function(top, left, bottom, right) {
  'use strict';
  var cdbEntries = this.cdbEntries_,
      i = cdbEntries.length,
      entry,
      result = [],
      leftShifted,
      rightShifted;
  if (i) {
    leftShifted = left + 360,
    rightShifted = right + 360;
    while (i--) {
      entry = cdbEntries[i];
      // Intersect with original rectangle:
      if (this.intersectCdbEntry_(entry, top, left, bottom, right)) {
        result.push(entry);
      } else {
        // Original rectangle doesn't match, try it with a shifted rectangle (360 degrees):
        if (this.intersectCdbEntry_(entry, top, leftShifted, bottom, rightShifted)) {
          result.push(entry);
        }
      }
    }
  }
  return result;
};


/**
 * To determine if the given CDB entry is covered by, or intersecting with the given rectangle.
 * @private
 * @param {H.util.QuadTree.Entry} entry The entry to test
 * @param {H.geo.Latitude} top The top edge of the rectangle (negated latitude).
 * @param {H.geo.Longitude} left The left edge of the rectangle.
 * @param {H.geo.Latitude} bottom The bottom edge of the rectangle (negated latitude).
 * @param {H.geo.Longitude} right The right edge of the rectangle.
 * @return {boolean} The intersection test result
 */
H.geo.QuadTree.prototype.intersectCdbEntry_ = function(entry, top, left, bottom, right) {
  'use strict';
  var entryLeft = entry[3/*MIN_X*/],
      entryTop = entry[0/*MIN_Y*/],
      entryRight = entry[1/*MAX_X*/],
      entryBottom = entry[2/*MAX_Y*/];
  // Check if the entry intersects with the given rectangle:
  return !(entryLeft > right || entryRight < left || entryTop > bottom || entryBottom < top);
};


/**
 * A static helper function to merge a set of entries into another (ignores doublets).
 * @private
 * @param {Array.<H.util.QuadTree.Entry>} set1 Target result set to merge to.
 * @param {Array.<H.util.QuadTree.Entry>} set2 Source result set to merge from.
 */
H.geo.QuadTree.mergeEntries_ = function(set1, set2) {
  'use strict';
  var i,
      j,
      lookup,
      entry;
  // A alternative implementation with Array's indexOf is terrible slow!
  if (j = set2.length) {
    i = set1.length;
    lookup = {};
    // Create a lookup table for all entries of set1:
    while (i--) {
      lookup[set1[i]['id']] = 1;
    }
    // Iterate over set2 and copy all entries into set1 which are not already part of set1:
    while (j--) {
      if (!((entry = set2[j])['id'] in lookup)) {
        set1.push(entry);
      }
    }
  }
};


/**
 * This method method returns a list of entries intersected by or contained
 * within the given bounding rectangle.
 * @param {H.geo.Rect} bounds the bounding rectangle to intersect with
 * @return {Array.<H.util.QuadTree.Entry>} a list of entries intersected by or contained
 *    within the given bounding rectangle.
 */
H.geo.QuadTree.prototype.intersectBounds = function(bounds) {
  'use strict';
  var tree = this.tree_,
      topLeft = bounds.getTopLeft(),
      bottomRight = bounds.getBottomRight(),
      top = -topLeft['lat'], // negated latitude
      left = topLeft['lng'],
      bottom = -bottomRight['lat'], // negated latitude
      right = bottomRight['lng'],
      result;
  if (bounds.isCDB()) {
    // Intersecting the tree from -180 to the rectangle's right side:
    result = tree.intersect(-180, top, right, bottom);
    // Intersecting the tree from rectangle's left side to +180 and merge it into the result list:
    H.geo.QuadTree.mergeEntries_(result, tree.intersect(left, top, 180, bottom));
    // Intersecting the CDB entries and merge it into the result list:
    H.geo.QuadTree.mergeEntries_(result,
        // "Normalize" the rectangle (left becomes right, right becomes left + 360):
        this.intersectCdbEntries_(top, right, bottom, left + 360));
  } else {
    // Intersecting the tree with the bounding rectangle:
    result = tree.intersect(left, top, right, bottom);
    // Intersecting the CDB entries and merge it into the result list:
    H.geo.QuadTree.mergeEntries_(result,
        this.intersectCdbEntries_(top, left, bottom, right));
  }
  return result;
};


/**
 * This method intersects the tree using a geographical point, and returnes
 * all entries which are intersecting the given point.
 * @param {H.geo.Point} point The point to intersect with.
 * @return {Array.<H.util.QuadTree.Entry>} a list of entries intersected by the given point.
 */
H.geo.QuadTree.prototype.intersectPoint = function(point) {
  'use strict';
  var lat = -point['lat'],
      lng = point['lng'],
      // Intersecting the tree with the point:
      result = this.tree_.intersect(lng, lat, lng, lat);
  // Intersecting the CDB entries and merge it into the result list:
  H.geo.QuadTree.mergeEntries_(result, this.intersectCdbEntries_(lat, lng, lat, lng));
  return result;
};
