goog.provide('H.map.provider.Provider');

goog.require('H.lang');
goog.require('H.map.ICopyright');
goog.require('H.util');
goog.require('H.util.EventTarget');



/**
 * A Provider defines an object which works as a database for the map.
 * Providers can exists in different forms they can implement client
 * side object storage or they can request data from the remote service.
 *
 * @publish
 * @export
 * @extends {H.util.EventTarget}
 *
 * @constructor
 * @param {H.map.provider.Provider.Options=} opt_options
 */
H.map.provider.Provider = function(opt_options) {
  'use strict';
  opt_options = opt_options || /** @type {H.map.provider.Provider.Options} */ ({});
  this['min'] = opt_options.min || 0;
  this['max'] = opt_options.max || 22;
  this['uid'] = '' + H.util.getUid();
  H.map.provider.Provider.superClass_.constructor.call(this);
};
H.lang.inherits(H.map.provider.Provider, H.util.EventTarget);


//#################################################  EVENTS  ###########################################################
/**
 * Event types fired by Provider
 * @enum {string}
 */
H.map.provider.Provider.prototype.EventType = {
  UPDATE: 'update'
};


/**
 * Fired when this provider's data updates
 * @publish
 * @event H.map.provider.Provider#update
 * @type {H.util.Event}
 */


//######################################################################################################################
/**
 * Minimum zoom level at which provider can serve data, set at construction time
 * @publish
 * @type {number}
 */
H.map.provider.Provider.prototype.min;


/**
 * Maximum zoom level at which provider can server data, set at construction time
 * @publish
 * @type {number}
 */
H.map.provider.Provider.prototype.max;


/**
 * Provider instance unique identifier, generated at construction time
 * @publish
 * @type {string}
 */
H.map.provider.Provider.prototype.uid;


/**
 * This method dispatches update events
 * @fires H.map.provider.Provider#update
 * @param {Object=} opt_target The object which causes the update. It is set as the events "target" property.
 *        If ommited, the provider itself is set as the target.
 * @protected
 * @export
 */
H.map.provider.Provider.prototype.dispatchUpdate = function(opt_target) {
  'use strict';
  this.dispatchEvent(new H.util.Event(this.EventType.UPDATE, opt_target));
};


/**
 * This method disposes the ImageTileProvider
 * @protected
 */
H.map.provider.Provider.prototype.disposeInternal = function() {
  'use strict';
  H.util.EventTarget.prototype.disposeInternal.call(this);
};


/**
 * This method returns the copyrights of the provided content for a certain geographical area at a specified zoom level.
 *
 * Note: This function must be overridden by any class inheriting the Provider class.
 * The default implementation returns {@code null}.
 *
 * @export
 * @publish
 *
 * @param {H.geo.Rect} bounds The bounding area for which to retrieve the copyright information
 * @param {number} level The zoom level for which to retrieve the copyright information
 * @return {?Array.<H.map.ICopyright>} a list of copyright information objects for the provided area and zoom level
 */
H.map.provider.Provider.prototype.getCopyrights = function(bounds, level) {
  'use strict';
  return null;
};


/**
 * Options which are used to initialize the Povider
 * @typedef {{
 *   min: (number|undefined),
 *   max: (number|undefined)
 * }}
 * @property {number=} min The minimal supported zoom level, default is {@code 0}
 * @property {number=} max The max maximal supported zoom level, default is {@code 22}
 * @publish
 */
H.map.provider.Provider.Options;
